/* Mobile app css reset */
*, *:before, *:after {
  box-sizing: border-box;  
  -webkit-tap-highlight-color:  transparent;
}
*:not(input):not(textarea) {
  -webkit-touch-callout: none;
  -webkit-user-select: none; /* Disable selection/copy in UIWebView */
  user-select: none;
}
*:focus { outline: none; }

ul, h1, h2, h3, h4 { padding: 0; margin: 0; }
li { list-style-type: none; }
a { color: $dark; }

/* Prevent scrolling */
html {
  overflow: hidden;
  position: fixed;
}

body {
  font-family:'Visuelt', 'Roboto', 'Helvectica Neue', Helvetica, Arial, sans-serif;
  //font-size: 2vh;
  font-weight: 400;
  margin: 0;
  padding: env(safe-area-inset-top) env(safe-area-inset-right) env(safe-area-inset-bottom) env(safe-area-inset-left);
  &.fixed {
    position: fixed;
    height: 100%;
    width: 100%;
  }

  button {
    cursor: pointer;

    &.button-link{
      border: none;
      background: transparent;
      outline: none;
      color: #01b6de;
      text-decoration: underline;
      margin: 0;
      padding: 0;
      font-weight: 500;
      font-size: 12px;
    }
  }
  .button {
    cursor: pointer;
  }
  *[role="button"]{
    cursor: pointer;
  }
}

select, input, .input {
  appearance: none;
  border: none;
  border-bottom: 1px solid $light;
  width: 100%;
  padding: 0.5em 0 0.5em 2.2em;
  color: $dark;
  font-weight: 400;
  border-radius: 0;
  background-color: $transparent;
  &:focus { border-color: $secondary; }
  &:-webkit-autofill { -webkit-box-shadow: 0 0 0 30px $background inset; }
}

input[type='radio'] {
  -webkit-appearance:none;
  width:20px;
  height:20px;
  border:2px solid #00b6de;
  border-radius:50%;
  outline:none;
}
input[type='radio']:before {
  content:'';
  display:block;
  width:60%;
  height:60%;
  margin: 20% auto;    
  border-radius:50%;    
}
input[type='radio']:checked:before {
  background-color:#00b6de;
}

input[type=checkbox] {
  width: 20px;
  border-bottom: none;
  width: 18px;
  height: 18px;
}

input[type=checkbox]:before {
  border: 2px solid #545456;;
  content: "\00a0";
  display: inline-block;
  font: 16px/1em sans-serif;
  height: 18px;
  padding: 1px;
  vertical-align: top;
  width: 18px;
}
input[type=checkbox]:checked:before {
  color: #fff;
  content: "\2713";
  text-align: center;
  background-color:  #00b6de;
  border: none;
}

table{
  width: 100%;
}

.start-screen{
  display: flex;
  flex-flow: column;
  height: 100vh;
  width: 100vw;
  align-items: center;
  justify-content: center;
  background: #f37320;
  color: #ffffff;
  font-size: 20px;
  font-weight: 500;

  .brandIcon{
    display: flex;
    height: auto;
    background: white;
    padding: 7px 15px;
    margin-bottom: 10px;
  }
}

.enho__table{
  border-collapse: collapse;
  padding: 10px 0;

  tr{
    border-top: 1px solid #f4f3f4;
  }

  th, td{
    font-size: 14px;
    font-weight: 300;
    line-height: 1.36;
    color: #3f4347;
    padding: 10px;
    vertical-align: top;
    padding: 10px 0;
    @include body-1;
  }

  thead{
    th, td{
      font-size: 13px;
      font-weight: bold;
      line-height: 1.23;
      color: #919ca9;
      text-transform: uppercase;
      padding: 20px 0;
      @include caption;
    }
  }
}

.enho__accordion{
  &+.enho__accordion__contents, .enho__accordion__contents{
    display: none;
  }

  &.enho__accordion__active{
    &+.enho__accordion__contents, .enho__accordion__contents{
      display: block;
    }
  
    &+tr.enho__accordion__contents, tr.enho__accordion__contents{
      display: table-row;
    }

    &+td.enho__accordion__contents, td.enho__accordion__contents{
      display: table-column;
    }
  }
}

.enho__item__sorting{
  position: relative;
  width: 10px;
  height: 16px;
  cursor: pointer;

  &:before{
    content:'';
    border-style: solid;
    border-width: 0 5px 7px 5px;
    border-color: transparent transparent #919ca9 transparent;
    position: absolute;
    top: 0;
  }

  &:after{
    content:'';
    border-style: solid;
    border-width: 7px 5px 0 5px;
    border-color: #919ca9 transparent transparent transparent;
    position: absolute;
    bottom: 0;
  }
  
  &.enho__item__sorting-up{
    &:before{
      opacity: .5;
    }
  }

  &.enho__item__sorting-down{
    &:after{
      opacity: .5;
    }
  }
}

.hidden {
  display: none;
}

.page {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  bottom: env(safe-area-inset-bottom); /* iPhone X */
  position: fixed;
  border-radius: 0;
  overflow: hidden;
  background: linear-gradient(white 20%, $super-light);
}

#capture-energy {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

@keyframes spinner {
  to { transform: rotate(360deg); }
}

.spin-container {
  position: absolute;
  width: 5em;
  height: 5em;
  background-color: $screen;
  border-radius: 50%;
  top: 50%;
  left: 50%;
  margin-top: -2.5em;
  margin-left: -2.5em;
  z-index: 103;
  opacity: 1;
  transition: opacity 600ms;

  &.heatmap-spin {
    height: calc(100% - 170px);
    width: 100%;
    top: 80px;
    bottom: 0;
    left: 0;
    border-radius: 0;
    margin: 0;
    z-index: 105;
  }

  &.hidden {
    display: block;
    opacity: 0;
  }
  .spinner { 
    font-size: 2em;
  }
}

.spinner:before {
  content: '';
  box-sizing: border-box;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 1.2em;
  height: 1.2em;
  margin-top: -0.6em;
  margin-left: -0.6em;
  border-radius: 50%;
  border: 0.1em solid $super-light;
  border-top-color: $secondary;
  animation: spinner 700ms linear infinite;
}

@keyframes shake {
  20%, 60%, 100% { transform: translate3d( 0.4em, 0, 0); }
  40%, 80% { transform: translate3d(-0.4em, 0, 0); }
}

.shake {
  animation: shake 300ms ease-out 10ms 1;
}

.button {
  letter-spacing: 0.03em;
  font-size: 1.1em;
  padding: 0.8em 1.4em;
  color: $background !important;
  border-radius: 0.2em;
  border: none;
  position: relative;
  display: inline-block;
  .spinner { opacity: 0 };
  .spinner:before {
    border-color: $medium;
    border-top-color: $super-light;
  }
  .icon {
    float: right;
    height: 1em;
    margin-left: 0.8em;
    stroke: $background;
    path {
      fill: $background;
    }
  }
  &:active { background-color: $secondary-dark; }
  &.loading {
    background-color: $light;
    .action { opacity: 0 }
    .spinner { opacity: 1 }
  }
}

@keyframes blip {
	70%  { opacity: 0.8; transform: scale(1); }
	100% { opacity: 0; transform: scale(5); }
}

.cTgxiN {
  position: fixed !important;
  circle {
    stroke: $secondary !important;
  }
  path {
    fill: $secondary !important;
  }
}

.status-bar-banner{
  padding-top: $status-bar-height;

  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    background: #00b6de;
    width: 100%;
    height: $status-bar-height;
  }

  &.with-notch{
    padding-top: $status-bar-notch-height;
    
    &:before{
      height: $status-bar-notch-height;
    }
  }
}

.loader{
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.energy-loader{
  top: calc(#{$footer-height} + 1%)
}

.share-loader{
  top: 10%
}

.storage-array-loader{
  top: 19%
}

.mobile-browser {
  #energy-stats-and-graph {
    height: calc(100% - 175px);
    overflow-y: scroll;
  }
  .latest-status .latest-status-page {
    height: calc(91% - 100px);
    overflow-y: scroll;
    .latest-status-wrapper {
      height: calc(100% - 25px)!important;
      overflow-y: scroll !important;
    }
  }
  .array-viewer {
    height: calc(91% - 175px);
  }
}

.currency-conversion-menu, .weather-watch, .live-status__wrapper {
  .toggle-switch {
    &.toggle-swtich__button-checked {
      .react-switch-handle {
        background: rgb(1, 180, 222) !important;
        transform: translateX(20px) !important;
        right: auto !important;
      }
    }
    &.toggle-swtich__button-unchecked {
      .react-switch-handle {
        background: rgb(203, 203, 203) !important;
        transform: translateX(0px) !important;
      }
    }
  }
}
  
.comp-notification {
  .icon {
    width: 20px;
    height: 20px;
  }

  &.default-success, &.default-failure {
    z-index: 107;
    box-shadow: 1px 2px 10px 0 rgba(63, 67, 71, 0.08);
    background-color: #545456 !important;
  }

  &.comp-notification__mobile {
    position: fixed;
    left: 0;
    top: 46px;

    &.comp-notification__ios { top: 76px; }

    &.default-success, &.default-failure { font-size: 12px; }
  }
}

