.rootedAlert__header{
    text-align: center;
    font-size: 16px;
    color: #F15B5A;
    font-weight: 500;
    margin-bottom: 5px;
}

.rootedAlert__content{
    margin-top: 0px;

}

.alertOutline {
    svg {
      width: 48px;
      height: 48px;
    }
    path {
      stroke: #fff;
      stroke-width: 0.7px;
    }
  }
.night {
  .alertOutline {
    path {
      stroke:#262627;
      stroke-width: 0.7px;
    }
  }
}