.popup{
  position: fixed;
  background: #ffffff;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 200;
}


.popup__grid{  
  position: absolute;  
  width: 100%;  
  height: 100%;  
  top: 0;  
  left: 0;  
  right: 0;  
  bottom: 0;  
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 200;
  .popup__wrapper {  
    width: 100%;
    position: absolute;  
    bottom: 0;  
    box-shadow: 0 -2px 17px 0 rgba(0, 0, 0, 0.11);
    background-color: #ffffff;
    &.landscape-layout {
      top: 60%;
      padding-left: 20px;
    }
  }

  .popup__center{
    width: 800px;
    bottom: auto;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .popup__header{
    padding: 15px 20px;
    font-size: 16px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: .5px solid rgba(123,135,147,.3);
    line-height: normal !important;
    @include title-2;
    p{
      margin: 0px;
      font-size: 16px;
      font-weight: 500;
      color: #545456;
    }
  }
  .close_popup{
    cursor: pointer;
    
    .close{
      width: 12px;
      height: 12px;
      display: flex;
      align-items: center;
      svg{
        width: 12px;
        height: 12px;
        polygon{
          fill: #939598;
        }
      }
    }
  }
  .popup__body{
    .popup__body-content {
      margin: 0;
      padding: 15px 20px 25px 20px;
      font-size: 12px;
      line-height: 1.29;
      letter-spacing: normal;
      color: #545456;
    }
  }

  .popup__body_dark{
    .popup__body-content {
      margin: 0;
      padding: 15px 20px 25px 20px;
      font-size: 12px;
      line-height: 1.29;
      letter-spacing: normal;
      color: #545456;
    }
  }

  .popup__web{
    top: 50% !important;
    left: 50% !important;
    transform: translate(-50%, -50%);
    
    &.popup__data-upload{
      width: 450px;
      height: 200px;
    }
  }

  .popup__data-upload{
    top: auto;
  }
}

.night{
  .popup__grid{  
    .popup__wrapper {  
      box-shadow: 0 -2px 17px 0 rgba(0, 0, 0, 0.11);
      background-color: #262627;
    }

    .popup__header{
      p{
        color: #ffffff;
      }
    }

    .popup__body{
      .popup__body-content {
        color: #ffffff;
      }
      .close_popup{
        .close{
          svg{
            polygon{
              fill: #ffffff;;
            }
          }
        }
      }
    }
  }
}

@media screen and (min-width:320px) and (max-width:360px){
  .popup__grid{  
    
    .popup__center{
      top: 50%;
    }
  }
}

@media screen and (min-width:768px) and (max-width:1024px) and (orientation: Portrait){
  .popup__grid{  
    
    .popup__center{
      top: 50%;
    }
  }
}

@media screen and (min-width:768px) and (max-width:1024px) and (orientation: Landscape){
  .popup__grid{  

    .popup__center{
      top: 50%;
    }
  }
}