.connect-envoy-to-home {
  .connect-envoy-popup {
    z-index: 10;
    background-color: rgba(0,0,0,.7);
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    &.disabled {
      display: none;
    }
  }

  .wifi-not-found{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    padding: 30px;
    text-align: center;
    font-size: 12px;
  }

  li{
    margin: 0 20px;
    border-bottom: solid 1px #c6c8ca;
    .wifi-details{
      padding: 14px 0;
      display: flex;
      justify-content: space-between;
      align-items: center;
      p{
        margin: 0;
        color: #545456;
        font-size: 16px;
        word-break: break-all;
      }
      .wifi-icons{
        display: inline-flex;
        .icon.activeWifi{
          svg {
            width: 19px;
            height: 14px;
          }
        }
        .icon.lockIcon{
          padding-right: 5px;
          svg{
            width: 12px;
            height: 12px;
          }
        }
      }
    }
    &:last-child{
      border-bottom: none;
    }
  }

  .connect-envoy-to-home-wrapper {
    background-color: #fff;
    width: 65%;
    margin: 0 auto;
    border-radius: 10px;
    height: 300px;
  }

  .connect-wifi-wrapper{
    background-color: #fff;
    width: 65%;
    margin: 0 auto;
    border-radius: 10px;
    form{
      min-height: 0;
      margin: 0;
    }
  }

  .connect-envoy-to-home-header {
    height: 50px;
    font-size: 16px;
    font-weight: 500;
    text-align: center;
    color: #131415;
    padding: 5px 10px 0 10px;
    border-bottom: solid 1px #c6c8ca;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .connect-wifi-header,
  .connect-wifi-password{
    font-size: 16px;
    color: #545456;
    padding: 20px;
    border-bottom: solid 1px #c6c8ca;
    p{
      margin: 0 0 4px 0;
      font-size: 16px;
      font-weight: 500;
    }
    h2{
      font-size: 16px;
      font-weight: 400;

    }   
  }
  .connect-wifi-password{
    position: relative;
    p{ 
      margin: 0 0 4px 0;
      font-weight: 300;
    }
    input{
      padding: 0 0 10px 0;
      border-bottom: solid 1px #c6c8ca;
    }
    .toggle-password{
      position: absolute;
      top: 30px;
      right: 40px;
    }
    .incorrect-password {
      color: #ff5959;
    }
  }

  .connect-envoy-to-home-main {
    height: 200px;
    overflow-x: hidden;
    overflow-y: scroll;
  }

  .connect-envoy-to-home-footer {
    border-top: solid 1px #c6c8ca;
    .text {
      height: 50px;
      font-size: 16px;
      font-weight: 500;
      text-align: center;
      color: #01b4de;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
  .connect-wifi-footer{
    width: 100%;
    height: 55px;
    display: flex;
    a{
      width: 50%;
      font-size: 16px;
      text-align: center;
      display: flex;
      align-items: center;
      justify-content: center;
      border-right: solid 1px #c6c8ca;
    }
    .connect{   
      color: #01b4de;
      font-weight: 500;
    }
    .disable-button{
      pointer-events: none;
      color: #545456 !important;
      opacity: 0.5;
    }
  }
  .rm-border{
    border: none !important;
  }


  .reconnect-to-envoy-confirm {
    transition: height .6s;
    height: 100%;
    .container-form {
      padding: 1em 30px;
      height: auto!important;
      margin: auto;
      text-align: center;
      .confirm-check {
        display: flex;
        justify-content: center;
        padding-bottom: 2em;
        .title-confirmation {
          font-size: 16px;
          font-weight: 500;
          color: #545456;
          text-align: center;
        }
        .sub-title-confirmation {
          font-size: 12px;
          line-height: 1.25;
          text-align: center;
          color: #6f6f8c;
        }
      }
      .button {
        width: 55%;
        margin: 1em auto;
        font-size: 16px;
        font-weight: 500;
        border: solid 1px #01b4de;
        border-radius: 2em;
        background-color: #01b4de;
        color: #ffffff;  
      }
    }
  }
  .overlay {
    width:100%;
    height: calc(91% - 134px);
    border-radius: unset !important;
    margin: 0 !important;
    top:134px;
    left:0;
  }
}
.connect-apmode-wrapper{
  width: 65%;
  margin: 0 auto;
  border-radius: 10px;
  background-color: #fff;
  .alert-icon{
    margin: 30px 0 20px;
    .alert{
      svg{
        width: 48px;
        height: 48px;
        stroke: #ff4751 !important;
      }
    }
  }
  .connect-ap-text{
    font-size: 12px;
    margin-bottom:25px;
    padding: 0 10px;
    text-align: center;
    color: #545456;
  }
  .connect-ap-footer{
    border-top: solid 1px #c6c8ca;
    button{
      width: 100%;
      height: 55px;
      font-size: 16px;
      font-weight: 500;
      border: none;
      border-radius: 10px;
      color: #01b4de;
      background-color: #fff;
    }  
  }
}


/* dark mode css*/
.night{
  .connect-envoy-to-home{
    li{
      border-bottom: solid 1px rgba(123, 135, 147, 0.3);
      .wifi-details{
        p{
          color: #f8f8ff;
        }
        .wifi-icons{
          .icon{
            svg{
              path,
              circle {
                stroke:  #d8dcde !important;
                fill: #d8dcde !important;
              }
            }
          }
        }
      }
      &:last-child{
        border-bottom: none;
      }
    }
    .connect-envoy-to-home-wrapper{
      background-color: #262627;
    }
    .connect-envoy-to-home-header {
      color: #f8f8ff;
      border-bottom: solid 1px rgba(123, 135, 147, 0.3);
    }
    .connect-envoy-to-home-footer {
      border-top: solid 1px rgba(123, 135, 147, 0.3);
    }

    .connect-wifi-wrapper{
      background-color: #262627;
      .connect-wifi-header{
        border-bottom: solid 1px rgba(123, 135, 147, 0.3);
        p{
          color: #d8dcde;
        }
        h2{
          color: #f8f8ff;
        }
      }
      .connect-wifi-password{
        border-bottom: solid 1px rgba(123, 135, 147, 0.3);
        p{
          color: #a2a6a9;
        }
        input{
          border-bottom: solid 1px #7b8793;
          padding: 0 0 10px 0 !important;
          color: #a2a6a9;
        }
        .toggle-password {
          .icon {
            svg {
              g {
                path {
                  fill :#262627 !important;
                }
              }
            }
          }
        }
      }
      .connect-wifi-footer{
        a{
          color: #d8dcde;
          border-right: solid 1px rgba(123, 135, 147, 0.3);
        }
        .connect{
          color: #00b7e3;
        }
      }
    } 
  }
  .connect-apmode-wrapper{
    background-color: #262627;
    .connect-ap-text{
      color: #d8dcde;
    }
    .connect-ap-footer{
      border-top: solid 1px rgba(123, 135, 147, 0.3);
      button{
        background-color: #262627;
      }
    }
  }
}

@media screen and (min-width:768px) and (max-width:1024px) and (orientation: Portrait){
  .connect-envoy-to-home {
    .connect-envoy-to-home-wrapper,
    .connect-wifi-wrapper{
      width: 40%;
    }
  }
  .connect-apmode-wrapper{
    width: 40%;
  }
}


@media screen and (min-width:768px) and (max-width:1024px) and (orientation: Landscape){
  .connect-envoy-to-home {
    .connect-envoy-to-home-wrapper,
    .connect-wifi-wrapper{
      width: 35%;
    }
  }
  .connect-apmode-wrapper{
    width: 35%;
  }
}
