@import "fonts";

.menu {
  .header {
    background-color: $secondary;
    height: 110px;
    width: 100%;
    position: relative;
    z-index: 10;
    box-shadow: 0 0 2px $shadow;
    @media screen and (min-width:768px) and (max-width:1023px){
      height: 130px;
    }
    .background {
      background-size: cover;
      background-blend-mode: luminosity;
      background-color: $secondary;
      opacity: 0.1;
      width: 100%;
      height: 100%;
    }
    .siteid {
      margin: 0;
      font-size: 0.8em;      
      font-weight: 100;
    }
    h2 {
      bottom: 26px;
      display: inline-block;
      font-size: 21px;
      font-weight: 400;
      color: $background;
      div{
        display: inline-block;
      }
      .site_name{
        max-width: 250px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        @media screen  and (min-width:768px) and (max-width:1024px){
         max-width:none;
        }
      }
    }
    .dropdown {
      margin-left: 0.4em;
      transition: transform 250ms ease-out;
      &.open { transform: rotate(180deg); }
    }
    .home {
      bottom: -.6em;
      height: 3.5em;
      position: absolute;
      left: 1.8em;
    }
    .icon {
      display: inline-block;
      polygon, path { fill: $background; }
    }
  }

  .sites {
    top: -100%;
    position: absolute;
    background-color: $secondary;
    color: $background;
    transition: top 250ms ease-out;
    overflow: scroll;
    -webkit-overflow-scrolling: touch; /* momentum scrolling */
    &.open { 
      top: 110px;  
      height: calc(100% - 110px - #{$footer-height}); 
      @media screen and (min-width:768px) and (max-width:1023px){
        top: 130px;
      }    
     }
  }

  .sites,
  .items {
    height: 78%;
    width: 100%;
    z-index: 1;
    overflow: auto;
    @media screen and (min-width:768px) and (max-width:1023px){
      height: 81%;
    } 
    li {
      cursor: pointer;
      font-size: 1.4em;
      font-weight: 300;
      padding: 0.9em 1.5em 0.9em 3.5em;
      & + li {
        border-top: 1px solid $faint;
      }
      &.link { 
        padding: 0; 
        a {
          text-decoration: none;
          padding: 0.9em 1.5em 0.9em 3.5em;
          display: block;
        }
      }
      &:first-child { padding-top: 1.1em; }
      &:active { background-color: $faint; }
      span{
        word-break: break-all;
      }
      .siteid-list {
        font-size: 0.7em;
      }    
    }
  }

  .items {
    .icon {
      height: 1.8em;
      float: right; 
      margin-top: -0.5em;
      polygon, path { fill: $medium; }
    }
    .icon.link {
      stroke: #939598;
      svg {
        height: 110%;
      }
      polygon, path , polyline, line{
       fill: none;
       stroke-width: .1em;
      }
    }
    .notices {
      background-color: $secondary-super-light;
      position: relative;
      label {
        position: absolute;
        display: flex;
        color: $background;
        background-color: $primary;
        border-radius: 1em;
        font-size: 0.8em;
        font-weight: 400;
        min-width: 1.6em;
        min-height: 1.6em;
        line-height: 0;
        justify-content: center;
        align-items: center;
        margin-top: -0.2em;
        left: 1.6em;
      }
    }
  }

  &.old-menu{
    .header {
      h2{
        bottom: 0.5em;
        left: 70px;
        @media screen and (min-width:768px) and (max-width:1024px){
          left: 95px;
        }
      }
    }
  }

}

.version {
  padding: 3em 0em 3em 0em;
  cursor: default;
  font-size: 12px;
  font-weight: 300;
  letter-spacing: normal;
  color: #939598;
}

.isDisabled {
  pointer-events: none;
}


