.browser {
  #feedbackify {
    #fby-form.smaller-screen {
      height: 70%;
      .tbody {
        position: absolute !important;
        height: 100% !important;
      }
      .fby-d-main {
        position: absolute !important;
        height: 100% !important;
        border-radius: 5px;
        top: 0;
        .dlg-content {
          height: 100% !important;
        }
        .dlg-sizer {
          display: flex;
          flex-direction: column;
          justify-content: space-around;
          height: 100% !important;
          top: 0;
        }
        .main-content {
          max-height: 60%;
          overflow: scroll !important;
          tbody {
            position: static !important;
            height: auto !important;
          }
        }
        .dlg-ftr {
          padding-bottom: 20px !important;
        }
      }
    }
    #fbya #fbyb .fby-form {
      .fby-main .fby-nav-container {
        display: flex !important;
        justify-content: space-between !important;
        align-items: center !important;
        span {
          width: 45% !important;
          display: flex !important;
          align-items: center !important;
          justify-content: center !important;
        }
      }
      &.default-comment-open {
        top: 0 !important;
        left: 0 !important;
        zoom: 0.7 !important;
        right: 0 !important;
        bottom: 0!important;
        margin: auto !important;
        width: 100% !important;
        .fby-form-scale {
          width: 360px !important;
          margin: auto !important;
        }
        .fby-main {
          zoom: 0.9 !important;
        }
        .content-width {
          width: 360px !important;
        }
      }
    }
  }
  .feedback-button-wrapper {
      position: absolute;
      max-width: 1440px;
      right: 0;
      top: 0;
      padding-right: 32px;
      transform: translate(0px, -40px);
      z-index: 1;
  
      .feedback-button {
          width: 144px;
          height: 40px;
          display: flex;
          justify-content: center;
          align-items: center;
          background: #01a2c6;
          border-radius: 3px 3px 0px 0px;
          font-size: 17px;
          font-weight: 500;
          cursor: pointer;
          color: #ffffff;
      }
  }
}