.battery-charge-settings {
  .light {
    font-weight: 300;
  }

  &.desktop {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 20px 20px 0;
    .time-heading {
      font-size: 16px;
      margin-bottom: 10px;
    }
    .half-width-excluding-margin {
      width: calc(50% - 10px);
    }
    .quiet-time {
      margin: 0;
      padding: 20px 15px;
      .show-quiet-more {
        font-size: 15px;
      }
      .quiet-time--parent {
        .sub-heading {
          font-size: 17px;
        }
        .add-new {
          align-items: center;
          cursor: pointer;
          display: flex;
          margin-bottom: 5px;

          .icon {
            line-height: 0;
          }

          & > div {
            margin: 0 0.3rem;
          }
        }
      }
      .queit-setting {
        background: #fff;
        padding: 10px 0 0;
        .icon {
          margin-right: 20px;
          line-height: 0;
          &.storageBattery {
            margin-right: 25px;
            position: relative;
            left: 4px;
          }
        }
        .queit-setting--parent {
          .timer-data {
            font-size: 14px;
          }
        }
      }
    }
    .battery-threshold {
      width: 100%;
      margin: 0;

      .limit {
        font-size: 16px;
        margin-left: 0;
      }
      &.battery-quiet-time {
        width: 100%;
      }
      .charge-rate {
        font-size: 17px;
        .limit {
          font-size: 16px;
        }
        &.border {
          border-bottom: none;
          padding-bottom: 0;
        }
      }
      .icon {
        &.storageBattery, &.Timer {
          margin-right: 15px;
          align-self: center;
        }
      }
      &.quiet-timer {
        margin: 20px 0 0;
        .charge-rate {
          font-size: 12px;
          .limit {
            .upper {
              margin: 0;
            }
          }
        }
      }
    }
  }
  .battery-threshold {
    display: flex;
    padding: 15px;
    margin: 15px 25px;
    align-items: center;
    background: #EDF7FA;
    border-radius: 10px;

    .limit {
      display: flex;
      justify-content: space-between;
      color: #7B8793;
      opacity: 0.65;
      font-size: 12px;
      width: auto;
      flex-direction: column;
      margin-left: 10px;

      .upper {

        &.edit {
          span {
            color: #01b5de;
            font-size: 16px;
          }
        }
      }

      .lower {
        margin-bottom: 3px;
        &.edit {
          span {
            color: #01b5de;
            font-size: 16px;
          }
        }
      }
    }

    &.quiet-timer {
      padding-bottom: 0;
      padding: 0;
      margin: 20px 4px 0;
      border-bottom: none;

      .slider {
        padding: 0px 2rem;
        margin-bottom: 1rem;
      }
    }

    .charge-rate {
      display: flex;
      flex-direction: column;
      width: 100%;
      font-size: 14px;

      .charge {
        color: #545456;
        margin-bottom: 5px;
      }


    }

    .icon {
      &.storageBattery {
        margin-top: 2px;
      }
    }
  }

  .quiet-time {
    justify-content: space-between;
    display: flex;
    flex-direction: column;
    margin: 0 25px;
    padding: 15px;
    background: #EDF7FA;
    border-radius: 10px;

    .edit-btn {
      padding: 0.5rem 0;
    }

    .add-new {
      align-items: center;
      cursor: pointer;
      display: flex;
      justify-content: center;
      color: #47b5de;

      .icon {
        line-height: 0;
      }

      & > div {
        margin: 0 0.3rem;
      }
    }

    .quiet-time--parent {
      display: flex;
      justify-content: space-between;
      align-items: center;

      & * {
        line-height: 1;
      }

      .sub-heading {
        display: flex;
        align-items: center;
        font-size: 14px;
        &.distance {
          justify-content: space-between;
          width: 100%;
          margin-bottom: 10px;
          .add-btn {
            color: #00b6de;
            cursor: pointer;
            &.disable {
              color: #c7c8ca;
            }
          }
        }
        .icon {
          &.open {
            transform: rotate(180deg);
            transition-duration: 0.8s;
          }
        }
      }
    }

    .show-quiet-more {
      display: flex;
      flex-direction: column;
      text-align: left;
      font-size: 12px;
      color: #6f6f8c;
      font-weight: 300;
      margin-bottom: 0.6rem;
    }

    .queit-setting {
      background: #edf7fa 0% 0% no-repeat padding-box;
      padding: 10px;
      border-radius: 5px;
      font-size: 12px;

      .icon {
        margin-right: 15px;
        line-height: 0;
        svg {
          fill: #545456;
        }
      }

      .queit-setting--parent {
        display: flex;
        align-items: center;
        .timer-data {
          display: flex;
          justify-content: space-between;
          width: 100%;
          font-size: 12px;
        }
      }
    }
  }
}

.night {
  .battery-charge-settings {
    .quiet-time {
      background: #262627;
      box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.25);
    }
    .heading {
      &.inactive {
        color: #a2a6a9 !important;
      }
    }

    .save-button {
      background: #262627;
      box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.25);
    }
    .battery-threshold {
      background: #262627;
      box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.25);
      .charge-rate {
        color: white;

        .charge {
          color: #f8f8ff;
        }
      }
    }
    .quiet-time {
      .show-quiet-more {
        color: #a2a6a9;
      }
      .queit-setting {
        background-color: #262627;
      }
      .add-new {
        color: #a2a6a9 !important;

        svg {
          path {
            fill: #a2a6a9 !important;
          }
        }
      }

      .quiet-time--parent {
        svg {
          fill: white;
        }
      }
    }
  }
}

.itk {
  .battery-charge-settings {
    .quiet-time {
      .quiet-time--parent {
        .add-new {
          color: #f37320;
          svg {
            path {
              fill: #f37320;
            }
          }
        }
      }
    }
  }
}
