.grid-status-info{
  .gsi__header{
    padding: 18px 15px 15px;
    font-size: 16px;
    font-weight: 500;
    color: #131415;
    border-bottom: 1px solid #d8dcde;
  }

  .gsi__body{
    padding: 15px;

    .gsi__desc{
      font-size: 12px;
      color: #5F6062;
    }
  }

  .gsi__appliances{
    margin-top: 16px;

    >p{
      margin: 0;

      &:first-child{
        font-size: 12px;
        font-weight: 500;
        color: #363636;
      }
      &:nth-child(2){
        font-size: 12px;
        margin-top: 2px;
        font-weight: 300;
        color: #5F6062;
      }
    }

    .gsi__appliance{
      margin-top: 12px;
    }
  }

  .gsi__appliances__list{
    >div{
      display: inline-flex;
      align-items: center;
      width: 50%;

      .icon{
        display: flex;
        width: 31px;
        min-width: 31px;
        justify-content: center;
      }

      .gsi__appliance__name{
        margin-left: 5px;
        font-size: 12px;
        color: #8E8E8E;
      }
    }
  }

  .essential-appliances{
    .gsi__appliances__list{
      .gsi__appliance__name{
        color: #47b4de;
      }
    }
  }
}
.grid-outage-info-popup {
  .grid-status-info {
    .gsi__info{
      padding-bottom: 10px;
    }
    .gsi__sectionHeader{
      display: flex;
      justify-content: space-between;
      .type{
        @include body-2;
        line-height: normal;
      }
      .advice{
        color: #8E8E8E;
      }
    }
    .gsi__appliances__list{
      margin-top: 10px;
      padding: 0px 15px 15px 15px;
      border-radius: 5px;
      background-color: #F9F9FA;
      &.large-loads .gsi__appliance .icon{
        filter: grayscale(1);
      }
      &.essentials .gsi__appliance__name{
        color: #545456;
      }
    }
    .gsi__desc.new {
      @include title-2;
    }
    .learnMore{
      width: 80%;
      @include title-2;
      line-height: normal;
      color: #01B4DE;
      margin: 20px auto;
      display: flex;
      align-items: center;
      justify-content: center;
      border: 1px solid #01B4DE;
      height: 40px;
      border-radius: 20px;
      background-color: white;
      .btntxt{
        margin-left: 8px;
      }
      .icon.externalLink{
        display: flex;
        align-items: center;
      }
    }
  }
}

.browser{
  #popUpWrapper.grid-outage-info-popup {
    max-width: 600px;
  }
}

.night{
  .grid-status-info{
    .gsi__header{
      color: #f8f8ff;
      border-color: rgba(123, 135, 147, 0.3);
    }
  
    .gsi__body{
      .gsi__desc{
        color: #d8dcde;
      }
    }
  
    .gsi__appliances{
      >p{
        &:first-child{
          color: #d8dcde;
        }
        &:nth-child(2){
          color: #8e9091;
        }
      }
    }
  
    .gsi__appliances__list{
      >div{
        .gsi__appliance__name{
          color: #8E8E8E;
        }
      }
    }
  
    .essential-appliances{
      .gsi__appliances__list{
        .gsi__appliance__name{
          color: #47b4de;
        }
      }
    }
  }
  .grid-outage-info-popup {
    .gsi__appliances__list{
      background-color: #2c2c2c;
      box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.1);
      &.essentials .gsi__appliance__name{
        color: white;
      }
    }
    .learnMore{
      background-color: #2c2c2c;
    }
  }
}