.addStorageLayout-container{
    width: 100%;
    border: solid 1px #d8dcde;
    border-radius: 3px;
    margin-bottom: 20px;

 

    .contents{
        padding: 17px;
        display: flex;
        flex-direction: column;
        .addStorage {
            margin-left: 7px;
        }

        .icon{
            align-self: start;

            svg {
                g > g > rect,
                g > g > path { fill: #00b6de; }
            }
        }

        .details{
            width: 100%;
            margin-top: 13px;

            & > *{
                margin: 0;
            }

            .addStorage{
                margin-bottom: 3px;  
                font-size: 16px;
                color: #545456;            
            }

            .info{
                font-size: 12px;
                color: #7B8793;
            }

            .details-footer{
                display: flex;
                justify-content: space-between;
                align-items: center;
                margin-top: 5px;

                & > *{
                    margin: 0;
                }

                .status{
                    font-size: 12px;
                    color: #7B8793;
                }

                .cta{
                    padding: 5px 20px;
                    border-radius: 13px;
                    background-color: #01b4de;
                    font-size: 12px;
                    color: #ffffff;

                }
            }
        }
    }
}

.night{
    .addStorageLayout-container{
        border: 1px solid #3c4044;
        .contents{
            .details{
                .addStorage{
                    color: #f8f8ff;
                }
                .info{
                    color: #a2a6a9;
                }
                .details-footer{
                    .status{
                        color: #a2a6a9;
                    }
                }
            }
            .storageIcon svg > g > path{
                fill: transparent;
            }
        }
    }

    .add-storage--wrapper {
        .information-wrapper {
            .individual-info {
                .icon {
                    svg {
                        fill: #fff;
                            g {
                                fill: #fff;
                                path {
                                    fill: #fff;
                                }
                            }
                    }
                }
            }
        }    
    }
}

@media (min-width: 767px) {
    .addStorageLayout-container{
        .contents{
            .details{
                .details-footer{
                    .cta{
                        zoom: 1.4;
                    }
                }
            }
        }
    }
  }

  .add-storage--wrapper {
    padding: 20px;
    .button-add-storage {
        box-shadow: 1px 2px 10px 0 rgba(63, 67, 71, 0.08);
        border: 1px solid #00b6de;
        padding: 10px 0;
        background: #00b6de;
        color: white;
        text-align: center;
        border-radius: 2rem;
        justify-content: center;
        display: flex;
        font-size: 16px;
        cursor: pointer;
    }
    .information-wrapper {
        display: flex;
        .individual-info {
            font-size: 14px;
            flex: 1 1 0;
            margin: 20px 5px 0;
            .icon {
                height: inherit;
                align-self: center;
                svg {
                    height: 30px;
                }
            }
            div {
                text-align: center;
                color: #7B8793;
                font-size: 10px;
                line-height: 13px;
            }
        }
    }
  }

  .browser {
    .add-storage--wrapper {
        padding: 0;
        .button-add-storage {
            margin-top: 25px;
        }
         .information-wrapper {
             
            .individual-info {
                div {
                    font-size: 13px;
                    line-height: 1.4;
                    margin-top: 10px;
                }
            }
         }
    }
}