.dialog-container.tou-alert-dialog {
  overflow: hidden;

  .data-area {
    text-align: center;
    border-bottom: none;
  }

  .footer-area {
    flex-direction: column;

    button {
      width: auto;
      margin: 0px;
      border-top: 1px solid #d8d8d8;

      &:last-child {
        border-left: 0px;
      }
    }
  }

  .savings-mode-alert-tou-msg{
    .icon{
      margin-top: 10px;

      svg{
        width: 48px;
        height: 48px;
        stroke-width: 1;
        stroke: #f7ba00;
      }
    }

    .smatm__content{
      margin: 20px 0 15px;
    }
  }
}

.scrim-pop-up {
  .popup__grid {
    #popUpWrapper{
      &.weather-watch-info {
        top: auto;
        .popup__header {
          min-height: 50px;
          height: auto;
        }
        .weather-watch-status {
          &.active,
          &.enabled {
            color: #f37320;
          }
          &.disabled {
            color: #8e9091 !important;
          }
        }
        .info-popup {
          a {
            color: #01b5de;
          }
        }
        .details-button {
          display: flex;
          align-items: center;
          margin: 15px 0 0;
          padding: 0;
          border: unset;
          font-size: 14px;
          font-weight: 500;
          line-height: 1.29;
          color: #01b5de;
          background: unset;
          @include body-1;

          .icon {
            width: auto;
            height: 16px;
            margin-left: 5px;
          }
        }
      }
      &.weather-watch-disclaimer {
        max-width: 100%;
        padding: env(safe-area-inset-top) env(safe-area-inset-right) env(safe-area-inset-bottom) env(safe-area-inset-left);
        top: 0;
        .popup__header {
          height: auto;
          padding: 25px 0px 20px;
          justify-content: center;
          border: 0;
          P {
            font-size: 16px;
          }
          .close_popup {
            display: none;
          }
        }
        .popup__body {
          height: calc(100% - 65px);
          .disclaimer-content, .popup__body-content {
            height: 100%;
          }
          .popup__body-content {
            padding: 0;
          }
        }
        .disclaimer-content-wrapper {
          height: 100%;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          .disclaimer-content {
            height: 75%;
            font-size: 16px;
            color: #8e9091;
            position: relative;
            padding: 0px 12px 0px 30px;
            p {
              margin: 0px;
              padding: 0 14px 36px 0;
              overflow: auto;
              height: 100%;
              width: 100%;
              line-height: normal;
              color: #8e9091;
              &::-webkit-scrollbar-thumb, &::-webkit-scrollbar {
                width: 4px;
                border-radius: 4px;
              }
              &::-webkit-scrollbar-thumb {
                background-color: rgba(216, 220, 222, 0.5);
              }
              &::-webkit-scrollbar-track {
                background-color: rgba(216, 220, 222, 0.3);
              }
            }
            .shadowOverlay {
              width: calc(100% - 60px);
              height: 36px;
              top: calc(100% - 36px);
              left: 30px;
              background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0.36), #ffffff);
              position: absolute;
            }
          }
          .disclaimer-button-wrapper {
            padding: 0px 30px;
            margin-bottom: 10px;
            .disclaimer-button {
              width: 100%;
              height: 50px;
              border-radius: 25px;
              text-align: center;
              font-size: 18px;
              line-height: normal;
              border: none;
              padding: 0;
              &.agree-button {
                font-size: 18px;
                color: #ffffff;
                background-color: #00b6de;
              }
              &.disagree-button {
                width: auto;
                display: flex;
                align-items: center;
                margin: auto;
                font-size: 16px;
                color: #02b6de;
                background-color: transparent;
              }
            }
          }
        }
      }
    }
  }
}

.storage {
  .success-message {
    position: absolute;
    height: auto;
    max-height: 10%;
    box-shadow: 1px 2px 10px 0 rgba(63, 67, 71, 0.08);
    background-color: #545456;
    color: #fdfefe;
    padding: 1em 2em;
    width: 100%;
    display: flex;
    font-size: 12px;
    font-weight: normal;
    align-items: center;
    z-index: 103;
    line-height: 1.2;
    
    &.disabled {
      display: none;
    }

    .icon{
      margin-right: 5px;
      svg {
        height: 20px;
        width: 20px;
        g {
          fill: #ffffff;
        }
        path {
          fill: #ffffff;
        }
      }
      &.tickWhiteSmall {
        svg {
          g {
            stroke: #ffffff;
            fill: none;
          }
        }
      }
    }
  }

  .weather-watch {
    padding: 20px;
    border-bottom: solid 0.5px rgba(123, 135, 147, 0.3);
    background: #ffffff;

    .weather-watch-wrapper {
      display: flex;
      justify-content: space-between;
      align-items: center;
      .weather-watch-title-status {
        width: auto;
        .weather-watch-title-wrapper {
          display: flex;
          align-items: center;
          .weather-watch-title {
            line-height: normal;
            font-size: 16px;
            font-weight: 500;
            margin-right: 7.5px;
          }
          .infoIcon {
            align-self: flex-start;
            margin-top: 2px;
          }
          .weather-watch-infoIcon, .infoIcon {
            display: flex;
            margin-bottom: 2px;
            svg {
              width: 14px;
              height: 14px;
            }
          }
        }
      }

      .toggle-switch__weather-watch{
        min-width: 50px;
        display: flex;
        justify-content: flex-end;
      }
    }
    .weather-watch-description {
      font-size: 12px;
      font-weight: 300;
      line-height: 1.25;
      color: #6f6f8c;
      margin-top: 5px;
    }
    .weather-watch-status {
      line-height: normal;
      font-size: 12px;
      font-weight: 500;
      &.disabled {
        color: rgba(84, 84, 86, 0.5) !important;
      }
      &.enabled,
      &.active {
        color: #f37320;
      }
    }
  }

  .toggleblk__wrapper{
    padding: 20px;
  }

  .storage-mgmt{
    display: flex;
    padding: 20px;
    align-items: center;
    background: #ffffff;

    .img-blk{
      width: 100px;
      height: 67px;
      background-image: url('../images/storage.png');
      background-repeat: no-repeat;
      margin-right: 15px;
    }
    .text-blk{
      width: 60%;
      font-size: 14px;
      font-weight: 500;
      color: #545456;
      @include body-2;

      .help-icon {
        margin-left: 5px;
        vertical-align: middle;
        
        .icon { display: inline; }
      }
    }
  }

  .storage-blk {
    border-radius: 10px;
    margin: 0 20px 15px 20px;
    box-shadow: 1px 2px 10px 0 rgba(63, 67, 71, 0.08);
    background-color: #ffffff;

    .toggleblk{
      .edit-block{  
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 6px;
        div{
          display: flex !important;
          svg {
            width:19px;
            height: 19px;
            margin-right: 10px;
          }
          .clockCircular {
            g {
              fill: #545456;
            }
          }
          &.active {
            .storage-title {
              color: #35c37d;
              margin-bottom: 0;
            }
            .clockCircular {
              g {
                fill: #35c37d;
              }
            }
          }
        }
        .lock {
          width: 14px;
          margin-right: 5px;
          svg {
            width: 14px;
            height: 14px;
          }
        }
        button {
          font-size: 12px;
          font-weight: 500;
          border: none;
          color: #01b5de;
          background-color: transparent;
          cursor: pointer;
          padding: 0;
        }
      }
      .storage-title{
        font-size: 16px;
        font-weight: bold;
        margin-bottom: 5px;
        line-height: normal;
        display: flex;
        flex-wrap: wrap;
        span {
          font-weight: normal;
        }
      }
      .storage-text {
        font-size: 12px;
        font-weight: 300;
        line-height: 1.25;
        color: #6f6f8c;
        margin: 0;
      }
    }

    .electricity-rate{
      padding: 15px 20px;
      margin-top: -30px;
    }

    .activate-mode {
      .padding-sides{
        padding: 0 20px 20px 20px;
        .reserveBattery{
          display: flex;
          align-items: center;
          margin-bottom: 15px;
          svg {
            width: 24px;
            height: 24px;
            margin-right: 16px;
          }
          span {
            font-size: 12px;
            color: #545456;
            line-height: normal;
          }
        }
        .selfconsumption{
          display: block;
          color: #545456;
          line-height: normal;
          width: 100%;

          .contents {
            opacity: 0.8;
            font-size: 12px;
            line-height: 16px;
            margin: 0;            
          }
          .mainContent {
            font-size: 12px;
            margin: 0;
          }

          .content_banner{
            font-size: 13px;
            font-weight: 500;
            padding: 10px;
            border-radius: 5px;
            background: #EDF7FA;
            color: #545456;
          }
        }
        .m-b-0{
          margin-bottom: 0;
        }
      }
      .storage-button {
        width: 100%;
        height: 45px;
        border-bottom-right-radius: 10px;
        border-bottom-left-radius: 10px;
        text-align: center;
        font-size: 18px;
        line-height: normal;
        border: none;
        padding:0;
        color: #fff;
        @include title-2;
      }
      .buttonActivate {
        background-color: #01b5de;
      }
      .buttonInActive {
        background-color: #a2a6a9;
      }
      .buttonCancel {
        font-size: 16px;
        background-color: #fff;
        color: #01b5de;
        border-top: solid 1px rgba(198, 200, 202, 0.5);

        &.buttonDisabled {
          color: #C7C8CA
        }
      }
    }

    .dr-events__wrapper{
      margin-top: -15px;
      padding: 0 20px 20px;

      .dr-events__content{
        display: flex;

        .dr-events__desc{
          font-size: 12px;
          margin-left: 10px;
          color: #545456;
        }
      }

      .dr-events__action{
        font-size: 12px;
        margin-top: 20px;
        color: #01b5de;
        cursor: pointer;
      }
    }

    &:last-child{
      margin-bottom: 45px;
    }

    &.sb__disabled{
      .icon g,path{
        fill: #c8c8c8 !important;
      }
      .icon.infoIcon g path:last-child{
        fill: #fff !important;
      }
      .edit-block button,
      .toggleblk,
      .storage-title,
      .storage-text,
      .contents,
      .mainContent,
      .electricity-rate,
      .content_banner,
      .reserveBattery span{
        color: #b1afaf !important;
        pointer-events: none;
      }

      .energyIndependenceBS .info .description,.switchStatus, .eISwitch .toggle-switch{
        color: #b1afaf !important;
        pointer-events: none;
      }
      .buttonActivate{
        background-color: #b1afaf !important;
        pointer-events: none;
      }
    }
  }

  .m-b{
    margin-bottom: 20px;
  }

  .buttonInProgress{
    position: relative;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    padding: 0 0 15px;

    .icon.loaderV2{
      display: flex;
      position: relative;
    }
  }
}

.deactive-button{
  pointer-events: none;
}

.browser{
  .dialog-container.tou-alert-dialog {
    .data-area {
      padding: 45px 60px 20px;
    }

    .footer-area {
      button {
        padding: 0 20px 20px;
      }
    }
  
    .savings-mode-alert-tou-msg{
      .icon{
        margin-top: 10px;
  
        svg{
          width: 48px;
          height: 48px;
        }
      }
  
      .smatm__content{
        margin-bottom: 0;
      }
    }
  }
}

.night {
  .storage {
    .weather-watch{
      background-color: #232323;

      .weather-watch-title {
        color: #f8f8ff;
      }
      .weather-watch-description {
        color: #a2a6a9;
      }
      .weather-watch-status.disabled{
        color: #A2ABB3 !important;
      }
    }

    .storage-mgmt{
      background-color: #232323;
    }

    .storage-blk {
      .activate-mode {
        .padding-sides {
          .selfconsumption {
            .content_banner{
              background-color: #323f42;
              color: #f8f8ff;
            }
          }
        }
      }
    }
  }

  .shadowOverlay {
    background-image: linear-gradient(180deg,hsla(0,0%,100%,.36),#262627) !important;
  }

  .storage-blk.sb__disabled{
    .energyIndependenceBS .info .description,.switchStatus{
      color: #b1afaf !important;
      pointer-events: none;
    }
  }
}

$font_shade1 : #545456; 
$font_shade2 : #6F6F8C;
$font_weight_thin : 100;
$font_weight_extraLight : 200; 
$font_weight_light : 300;
$font_weight_normal_regular : 400;
$font_weight_medium : 500;
$font_weight_semiBold : 600;
$font_weight_bold : 700;
$font_weight_extraBold : 800; 
$font_weight_heavy : 900;
$font_weight_ultraBlack : 950; 
.prfAssist{
  text-align: center;
  min-height: 100%;
  padding: 25px;
  
  .t1{
    color: $font_shade1;
    font-size: 20px;
    font-weight: $font_weight_medium;
    margin-bottom: 15px;
    margin-top: 25px;
  }
  .t2{
    color: $font_shade2;
    font-size: 12px;
    font-weight: $font_weight_normal_regular;
    margin-top: 15px;
    margin-bottom: 25px;
  }
  .prfAssist_footerActions{
    margin-top: 25px;
    position: sticky;
  }
  .editPref{
    color: #01B4DE;
    font-size: 16px;
    margin-top: 25px;
    font-weight: $font_weight_normal_regular;
    font-weight: 500;
  }
  .learnHow{
    color: #01B4DE;
    border-radius: 25px;
    font-size: 16px;
    border: 1px solid #01B4DE;
    padding: 10px;
    min-width: fit-content;
    max-width: 80vw;
    margin: auto;
    font-weight: $font_weight_normal_regular;
    font-weight: 500;
  }
  .asCard {
    background-color: #F2F2F7;
    border-radius: 10px;
    &.savings__card{
      .energyIndependenceBS{
        padding: 15px 0 0;
        .eIinfoIcon{
          display: none;
        }
      }
    }
  }

  .content{
    padding: 20px;
  }
  .withTitleDescr{
    text-align: left;
    .prfAssistWithtitleDescr__title{
      font-size: 16px;
      font-weight: $font_weight_bold;
    }
    .prfAssistWithtitleDescr__descr{
      font-size: 12px;
      font-weight: $font_weight_light;
      margin-top: 5px;
      font-weight: 400;
    }
  }
  .reserveBattery {
    display: flex;
    align-items: center;
    margin: 15px 0 0;
    .text{
      font-size: 12px;
      font-weight: $font_weight_normal_regular;
      margin-left: 10px;
    }
  }
  .button {
    width: 100%;
    height: 45px;
    border-radius: 0px;
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;
    text-align: center;
    font-size: 18px;
    line-height: normal;
    border: none;
    padding:0;
    color: #fff;
    align-items: center;
    margin-top: 0;
    @include title-2;
  }
  .buttonActivate {
    background-color: #01b5de;
  }
  .buttonInActive {
    background-color: #a2a6a9;
    pointer-events: none;
  }
  
  &.already-recommended-mode{
    .t1{
      font-size: 16px;
    }
  }
}
.icon.prfAssistant {
  .a {
    fill: #ebebeb;
  }
  .b {
    fill: #f0f0f0;
  }
  .c {
    fill: #f5f5f5;
  }
  .d {
    fill: #fafafa;
  }
  .e,
  .f {
    fill: #e6e6e6;
  }
  .e,
  .l {
    opacity: 0.7;
  }
  .e {
    mix-blend-mode: multiply;
    isolation: isolate;
  }
  .g,
  .j {
    fill: #fff;
  }
  .h {
    fill: #407bff;
  }
  .i {
    fill: #01b5de;
  }
  .j {
    opacity: 0.8;
  }
  .k {
    fill: #54d3e8;
    opacity: 0.3;
  }
  .l {
    fill: #9deefc;
  }
  .m {
    fill: #b55b52;
  }
  .n {
    fill: #263238;
  }
  .o {
    fill: #a02724;
  }
  .p {
    opacity: 0.2;
  }
  .q {
    fill: #afc7f2;
  }
}
.storagePreferenceChoiceCard{
  position: relative;
  display: flex;
  align-items: center;
  padding: 15px;
  border-radius: 10px;
  box-shadow: 1px 2px 10px 0 rgba(63, 67, 71, 0.08);
  margin: 15px 0px;
  background-color: #FFFFFF;

  .icon.draggable{
    position: absolute;
    right: 10px;
    top: 10px;
  }

  .cardIcon{
    margin-right: 15px;
    .background{
      fill: #f2fbfe;
    }
  }
  .title{
    font-size: 18px;
    font-weight: $font_weight_medium;
    line-height: 21px;
    @include title-2;
  }
  .subtitle{
    font-size: 12px;
    font-weight: $font_weight_normal_regular;
    margin-top: 5px;
    color: #6B7073;
  }
}
.storagePrefListScreen{
  min-height: 100%;
  padding: 15px 20px 25px 20px;

  .stickyAtBottom{
    position: sticky;
    top: 100%;
    margin-top: 100px;
    margin-bottom: 50px;
    @include title-2;
  }
  .ques{
    font-size: 18px;
    font-weight: $font_weight_medium;
    @include title-2;
  }
  .priority{
    font-size: 14px;
    margin: 5px 0 40px;
    @include body-1;
  }
  .c2,.c1{
    font-size: 16px;
    color: #6C7074;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .icon.dropdown {
      display: flex;
      svg {
        height: auto;
        width: auto;
      }
    }
  }
  .c2{
    .opt{
      font-size: 12px;
      color: #6C7074;
      font-weight: $font_weight_normal_regular;
    }
  }
}
.choose-profile__popup{
  .popup__header{
    p{
      font-size: 16px;
    }
  }

  &.popup__wrapper{
    &.popup__web#popUpWrapper {
      height: 80vh;
      max-width: 425px;
      .popup__body .popup__body-content{
        max-height: calc( 80vh - 47px );
        overflow: scroll;
      }
    }
    .popup__body .popup__body-content{
      padding: 0px;
    }
  }
}
.storageProfileAssistant{
  max-height: calc( 100vh - 100px );
  overflow: auto;

  .display__none{
    display: none;
  }
  & > div{
    height: 100%;
  }

  .ftrp__tutorial{
    .ftrp__tutorial__wrapper{
      text-align: center;
    }

    &.ftrp__tutorial__uphand-draggable{
      position: absolute;
      left: 0;
      right: 0;
      top: -37px;
    }

    &.ftrp__tutorial__desc__button{
      position: absolute;
      left: 0;
      right: 0;
      top: -15px;
    }

    .ftrp__tutorial__desc{
      font-size: 14px;
      line-height: 17px;
      margin: 90px 20px 20px;
      color: #FFFFFF;
      @include body-1;
    }

    button{
      color: #FFFFFF;
      padding: 10px;
      background: transparent;
      outline: none;
      border: 1px solid;
      width: 135px;
      border-radius: 4px;
      text-transform: uppercase;
      font-size: 16px;
    }
  }

  .ftrp__has__tutorial{
    height: inherit;
    background-color: rgba(0, 0, 0, 0.85);

    [data-rbd-draggable-id="1"]{
      position: relative;
      z-index: 10;
    }

    [data-rbd-draggable-id="0"],
    [data-rbd-draggable-id="2"]{
      position: relative;
      z-index: 10;
      opacity: .3;
    }

    .storagePrefListScreen{
      .ques, .priority{
        opacity: .3;
      }
      .stickyAtBottom{
        z-index: -1;
      }
    }

    &.ftrp__no_enpower{
      .ftrp__tutorial{
        #Path_9145{
          display: none;
        }
        
        .ftrp__tutorial__desc{
          margin-top: 20px;
        }
      }
    }
  }
}

.prfAssist__show-activate-popup{
  &.showup__action__inline{
    .showup__action{
      &:not(:first-child){
        border-left: none;
      }

      &.showup__action__text{
        &.showup__action__text_circuit_details{
          order: unset;
          border-left: unset;
          padding-left: 80px;
        }
      }

      

      &:first-child{
        order: 2;
        border-left: 1px solid #ecedee;
      }
    }
  }

  .prfAssist__popup__content{
    .prfAssist__popup__heading{
      font-size: 18px;
      line-height: 21px;
      padding: 15px;
      text-align: center;
      color: #131415;
      border-bottom: 1px solid #ecedee;
      @include title-2;
    }
    .prfAssist__popup__desc{
      font-size: 14px;
      line-height: 19.6px;
      color: #545455;
      padding: 15px;
      @include body-1;
      &.description-circuit{
        padding:24px 24px 36px;
        font-size:14px;

        font-weight:300;
      }
    }
    .prfAssist__popup__activate-profile{
      margin-top: 20px;
    }
  }
}

.night {
  .storagePrefListScreen{
    .c1,.c2{
      color: #7b8793;
    }
    .storagePreferenceChoiceCard{
      box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.25);
      background-color: #262627;
      .cardIcon {
        .background {
          fill: #333333;
        }
      }
    }
  }
  .prfAssist  {
    .t1,.t2{
      color: #f8f8ff;
    }
    .asCard{
      background-color: #333333;
      .reserveBattery{
        .text div:first-child{
          color: #f8f8ff;
          opacity: 0.8;
        }
        .icon{
          &.fullBattery,&.storageBattery{
            svg g{
              fill: #f8f8ff;
              path{
                fill: #f8f8ff;
              }
            }
          }
          &.fullBattery{
            svg g path:nth-child(3){
              fill: #262627;
            }
          }
          
        }
      }
    }
    .withTitleDescr{
      .prfAssistWithtitleDescr__title{
      }
      .prfAssistWithtitleDescr__descr{
        color: #a2a6a9;
      }
    }
  }

  .prfAssist__show-activate-popup{
    .prfAssist__popup__content{
      .prfAssist__popup__heading{
        color: #ffffff;
        border-bottom-color: rgba(123, 135, 147, 0.3);
      }
      .prfAssist__popup__desc{
        color: #a2a6a9;
      }
    }

    &.showup__action__inline{
      .showup__action{
        &:first-child{
          border-left-color: rgba(123, 135, 147, 0.3);
        }
      }
    }
  }
}


@media (max-width:374px){
  .storageProfileAssistant{
    & > div{
      height: auto;
    }
  }
}