.logged_in_as {
    background-color: #FFDF00;
    -moz-box-shadow: 0 2px 2px 2px rgba(51,51,51,0.22);
    -webkit-box-shadow: 0 2px 2px 2px rgba(51,51,51,0.22);
    box-shadow: 0 2px 2px 2px rgba(51,51,51,0.22);
    color: #44524f;
  font: 600 16px/60px 'Roboto','Open Sans','Helvectica Neue',Arial,sans-serif;
    text-align: center;
    margin-bottom: 15px;
    font-size: 14px;
    .logout_as_link {

      background: linear-gradient(top,#FFF 0,#D5E7F2 100%);
      background: -moz-linear-gradient(top,#FFF 0,#D5E7F2 100%);
      background: -webkit-linear-gradient(top,#FFF 0,#D5E7F2 100%);
      border: 1px solid #C6C6C6;
      border-radius: 6px;
      -webkit-box-shadow: 0 -1px 3px 1px rgba(0,0,0,0.1), inset 0 0 0 2px #FFF;
      -moz-box-shadow: 0 -1px 3px 1px rgba(0,0,0,0.1),inset 0 0 0 2px #FFF;
      box-shadow: 0 -1px 3px 1px rgba(0,0,0,0.1), inset 0 0 0 2px #FFF;
      color: #06C;
      margin-left: 15px;
      padding: 8px;
      text-decoration: none;
    }
  }