.feedback {
  .feedback_overlay {
      position: fixed;
      z-index: 102;
      background-color: #000000;
      width: 100%;
      height: 100%;
      top: 0;
      opacity: 0.75;
      -webkit-backdrop-filter: blur(2px);
      backdrop-filter: blur(2px);
    }
  .feedback_content {
      position: fixed;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      height: auto;
      border-radius: 10px;
      background-color: #ffffff;
      z-index: 103;
      width: 91%;
      height: 91%;
      max-height: 625px;
      max-width: 341px;

      &.without-installer {
        max-height: 405px;
      }

      &.feedback-ratings_comments {
        &.without-installer {
          max-height: 460px;
        }
      }

      .feedback-ratings_button-group {
          display: flex;
          flex-direction: column;
          align-items: center;
          margin-bottom: 24px;
          .feedback-ratings_submit-buttom {
              width: 275px;
              height: 50px;
              border: 1px solid #00b6de;
              border-radius: 25px;
              background-color: #00b6de;
              font-size: 18px;
              color: #ffffff;
              display: flex;
              align-items: center;
              justify-content: center;
              text-align: center;

              &.disable {
                  background-color: #c7c8ca;
                  border: 1px solid #c7c8ca;
                  pointer-events: none;
              }
          }
          .feedback-ratings_ignore-buttom {
              width: 275px;
              margin-top: 20px;
              font-size: 16px;
              line-height: 1.13;
              text-align: center;
              color: #01b4de;
              display: flex;
              align-items: center;
              justify-content: center;
          }
      }
      
      &.feedback-ratings_comments {
        .feedback-ratings_subtitle {
            display: flex;
            align-items: flex-start;
            .feedback-ratings_subtitle-text {
                display: flex;
                margin: auto 0px;
            }
            .feedback-ratings_icon-wrapper {
                display: flex;
                margin-left: auto;
                height: 27px;
                align-items: center;
                padding: 0 10px;
                border-radius: 15px;
                border: solid 1px #e2e2e2;
                .icon {
                    display: flex;
                    height: 17px;
                    margin-right: 5px;
                    svg {
                        height: 17px;
                        width: 20px;
                    }
                }
                .feedback-ratings_rating {
                    font-size: 16px;
                    font-weight: bold;
                    &.rating0,&.rating1,&.rating2 {
                        color: #e10d12;
                    }
                    &.rating3,&.rating4 {
                        color: #ff763d;
                    }
                    &.rating5,&.rating6 {
                        color: #fb940a;
                    }
                    &.rating7,&.rating8 {
                        color: #ffd301;
                    }
                    &.rating9 {
                        color: #b8ee00;
                    }
                    &.rating10 {
                        color: #1da92f;
                    }
                }
            }
        }
    }
  }
}

.feedback-survey__alert {
  .icon svg {
      width: 52px!important;
      height: 52px!important;
  }
}

.night {
  .feedback {
      .feedback_overlay { 
          background-color: rgba(0, 0, 0, 1);
      }
      .feedback_content {
          background-color: #262627;
      }
  }
}