.request-replacement, .change-shipaddress {
  .checkbox-container {
    position: relative;
    cursor: pointer;
    display: block;
    height: 19px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    padding-left: 20px;
  }
  
  /* Hide the browser's default checkbox */
  .checkbox-container input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
  }
  
  /* Create a custom checkbox */
  .checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 19px;
    width: 19px;
    background-color: #d0d0d0;
    border-radius: 50%;
  }
  
  
  /* When the checkbox is checked, add a blue background */
  .checkbox-container input:checked ~ .checkmark {
    background-color: #01b4de;
  }
  
  /* Create the checkmark/indicator (hidden when not checked) */
  .checkmark:after {
    content: "";
    position: absolute;
  }
  
  /* Style the checkmark/indicator */
  .checkbox-container .checkmark:after {
    left: 7px;
    top: 4px;
    width: 5px;
    height: 9px;
    border: solid white;
    border-width: 0px 2px 2px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
  }
}


.night {
  .request-replacement, .change-shipaddress {
     /* When the checkbox is checked, add a blue background */
    .checkbox-container input:checked ~ .checkmark {
      background-color: #01b4de;
      opacity: inherit;
    }
  
    .checkmark {
      background-color: #d8dcde;
      opacity: 0.2;
    }

    .checkmark:after {
      content: "";
      position: absolute;
    }
  
    /* Style the checkmark/indicator */
    .checkbox-container .checkmark:after {
      left: 7px;
      top: 4px;
      width: 5px;
      height: 9px;
      border: solid #262627;
      border-width: 0px 2px 2px 0;
      -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
      transform: rotate(45deg);
    }
  }
}