.browser {
  .latest-status {
    .latest-status-wrapper {
      &.desktop {
        overflow-y: scroll;
        position: relative;
        padding: 0px;
        height: 100% !important;
        display: flex;
        flex-direction: column;

        .latest-status-detail-container {
          display: flex;
          min-height: fit-content;

          .grid-container,
          .energy-stats-wrapper,
          .device-carousel-wrapper {
            height: 100%;
            min-height: fit-content;
          }

          .middle-wrapper {
            display: flex;
            flex-direction: column;
            height: fit-content;
            width: calc(100% - 480px);

            .energy-stats-wrapper {
              width: 100%;
              margin-bottom: 30px;
            }

            .energy-layout {
              height: auto;
            }

            .add-site-image {
              position: relative;
              background: #F6F7FB;
              width: 100%;
              height: 203px;

              .site-image {
                height: 100%;

                .editIcon {
                  height: 32px;
                  position: absolute;
                  right: 10px;
                  bottom: 10px;
                  cursor: pointer;
                }

                img {
                  width: 100%;
                  height: 100%;
                }
              }

              .add-site-image__no-site-image {
                height: 100%;

                .touch-area {
                  margin: auto;
                  display: grid;
                  place-content: center;
                  width: 33%;
                  height: 100%;
                  cursor: pointer;

                  .site-image-cta {
                    margin: 0;
                    color: #01B5DE;
                    @include caption;
                    margin-top: 3px;
                    text-align: center;
                  }
                }

                .siteImageBannerIntersection {
                  position: absolute;
                  bottom: 0;
                  height: auto;

                  &.top-right {
                    position: absolute;
                    right: 0;
                    top: 0;
                    transform: rotate(180deg);

                    svg {
                      position: absolute;
                      top: 123px;
                    }
                  }
                }
              }

              .add-site-image__upload-image-icon {
                position: absolute;
                bottom: 0;
                right: 17px;
              }

              .add-site-image__button {
                position: absolute;
                bottom: 7px;
                right: 18px;
              }
            }

            .performance-title {
              font-size: 25px;
              font-weight: 500;
              margin: 40px 32px 24px 32px;
            }

            .status-forecast .forecast-card-title {
              font-size: 25px;
              font-weight: 500;
              margin: 40px 32px 24px 32px;
            }

            .iq8d-card-wrapper.web-view-wrapper.performance-card-view {
              background-color: white;
              border: 1px solid #D8DCDE;
              margin: 32px;
              padding: 25px 31px;

              .title-wrapper {
                margin: 0px 0px 20px;

                .title h1 {
                  font-size: 25px;
                }
              }
              .icon-slider-wrapper{
                display: grid;
                grid-template-columns: 2fr 2fr;
                // max-width: 450px;
                // margin-left: 14px;

                .text-block {
                  h3 {
                    @include title-2;
                    font-size: 20px;
                  }

                  .infoDesc {
                    font-size: 15px;
                  }
                }
                .slider-wrapper{
                  width: 100%;
                  .slides {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    margin: 0;

                    h1 {
                      font-size: 20px;
                    }

                    p {
                      display: flex;
                      text-align: center;
                      margin: 2px 0px 0px;
                      font-size: 15px;
                    }
                  }

                  .icon.arrowCarousel {
                    svg {
                      display: flex;
                      height: 12px !important;
                      width: auto !important;
                    }
                  }
                }
              }
            }
          }

          .wider-right-panel{
            width: 480px;
            background-color: #F6F7FB;
            .performanceCard.statusPage.webView {
              .performance-card-list{
                .card-item {
                  .currency-conversion-carousel {
                    .carousel-type-1{
                      .carousel__container{
                        margin: 0;
                        padding: 0;

                        .carousel__wrapper.withArrows {
                          border: none;
                          padding: 0 !important;
                          margin: 2px;

                          .carousel__item-wrapper {
                            display: flex;
                            align-items: center;

                            .carousel-equal-currency{
                              margin: 0 13px;
                            }

                            .energy-type__details {
                              margin: 0;
                              .energy-type__value {
                                height: auto;
                              }
                              .energy-type__name {
                                color: #7a8692;
                              }
                            }
                          }
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }
        .prodConsumptionOnlyLatestStatusContainer{
          height: 100%;
        }

        .envoyDataUpload {
          width: 100%;
          background-color: rgba(71, 181, 222, 0.07);
          display: flex;
          flex-direction: column;
          padding: 10px;
          align-items: center;
          border-bottom: solid 1px rgba(123, 135, 147, 0.3);

          .envoyDataUpload__header {
            display: flex;
            align-items: center;
            margin-top: 5px;
            margin-bottom: 3px;

            .image {
              width: 20px;
              height: 20px;
              background-size: 20px;
              margin-right: 5px;
              background-image: url(../../images/envoyDataUploading.svg);
            }

            .title {
              margin-left: 5px;
              font-size: 16px;
              font-weight: 700;
              text-align: center;
            }
          }

          .envoyDataUpload__content {
            margin-bottom: 5px;
            font-size: 14px;
            text-align: center;
          }
        }

        .grid-container {
          display: flex;
          border: none;
          height: 100%;
          width: 100%;
          margin: 0;

          div {
            height: auto;
          }

          .grid-sub-container {

            .grid-columns {
              width:max-content;

              &:first-child {
                border-top: none;
              }

              &.add-storage {
                .contents {
                  padding: 17px;
                  display: flex;
                  flex-direction: column;

                  .addStorage {
                    margin-left: 7px;
                    font-size: 20px;
                  }

                  .icon {
                    align-self: start;

                    svg {
                      width: 20px;
                      height: 20px;

                      g>g>rect,
                      g>g>path {
                        fill: #00b6de;
                      }
                    }
                  }

                  .details {
                    width: 100%;
                    margin-top: 13px;

                    &>* {
                      margin: 0;
                    }


                    .info {
                      font-size: 12px;
                      color: #7B8793;
                    }

                    .details-footer {
                      display: flex;
                      flex-direction: column;
                      margin-top: 5px;

                      &>* {
                        margin: 0;
                      }

                      .status {
                        font-size: 15px;
                        color: #7B8793;
                      }

                      .cta {
                        padding: 5px 20px;
                        border-radius: 13px;
                        background-color: #01b4de;
                        font-size: 15px;
                        color: #ffffff;
                        margin-top: 7px;

                      }
                    }
                  }
                }
              }

              .info-icon {
                .infoIcon {
                  padding-left: 5px;
                }
              }

              .energy-grid__icon {
                margin: 0px;
                width: 20px;
                height: 20px;

                .icon.storageIcon {
                  padding: 0px;
                  margin: 0px;
                }

                .icon {
                  svg {
                    width: 20px;
                    height: 20px;
                  }
                }
              }

              .battery-charge-grid {
                display: flex;
                align-items: center;
                margin-left: -12px;

                .energy-grid__icon {
                  height: 20px;
                  width: 20px;

                  .icon,
                  .icon svg {
                    height: 20px;
                    width: 20px;
                  }
                }

                .battery-charge__wrapper {
                  display: flex;
                  margin-left: 5px;
                  font-size: 15px;
                  color: #6C7074;
                  align-items: center;

                  .battery-charge__info-icon {
                    cursor: pointer;
                    margin-left: 8px;
                    position: relative;
                    top: 2px;
                  }

                  .battery-charge__info {

                    .battery-charge__value {
                      margin-left: 5px;

                    }

                    .battery-charge__backup-time {
                      font-size: 15px;
                      margin-left: 5px;
                      white-space: nowrap;
                      opacity: 0.5;
                    }
                  }
                }
              }

              &.battery-profile {
                display: flex;
                margin-left: 30px;
                cursor: pointer;
                font-size: 15px;
                  color: #6C7074;
                  align-items: center;

                .battery-profile__info {
                  margin-left: 5px;
                  margin-right: 8px;
                }
                .iconAlign{
                  position: relative;
                  top: 2px;
                }
              }

              .battery-profile__title,
              .battery-charge__title {
                display: flex;
                text-transform: capitalize;
                font-size: 15px;
                align-items: center;

                .icon,
                .icon svg {
                  height: 15px;
                  width: 15px;
                }
                .profileInfo{
                  height: 20px;
                  width: 20px;
                  margin-right: 8px;
                  svg {
                    height: 20px;
                    width: 20px;
                  }
                }
              }

              .header {
                padding-left: 10px;
                margin-top: 2px;
                font-size: 15px;

                .infoIcon {
                  display: none;
                  width: 16px;
                }
              }

              .text {
                font-size: 20px !important;
                font-weight: bold;
                padding-left: 6px;
                color: #545456;
              }

              .text-units {
                font-size: 13px;
                margin-left: 2px;
              }
            }
          }

          .addConsumptionMeter {
            padding: 20px 25px 20px 25px;
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 100%;
            height: auto;
            border-top: 1px solid #D8DCDE;

            .consumptionMeterButton {
              display: flex;
              height: 40px;
              width: 100%;
              font-size: 14px;
              font-weight: 500;
              margin-bottom: 10px;
              border-radius: 20px;
              align-items: center;
              justify-content: center;
              text-align: center;
              background-color: #00b6de;
              color: #ffffff;
              cursor: pointer;
            }

            .infoText {
              display: flex;
              flex-direction: row;
              align-items: center;
              justify-content: center;
              margin-top: 5px;
              font-size: 10px;
              font-weight: 400;

              .icon.consumptionMeter {
                width: 22px;
                height: auto;
                margin-right: 6px;

                svg {
                  width: 22px;
                  height: 22px;
                }
              }
            }
          }
        }
        .peakPowerGridContainer{
          background-color: white;
          .grid-columns{
            display: flex;
            align-items: center;
          }
          .grid-columns-two{
            display: flex;
            align-items: center;
            margin-left: 40px;
          }
          .grid-seperator {
            border-left: 1px solid #D8DCDE !important;
            height: 48px !important;
          }
          .energy-details_text{
            font-size: 15px;
            margin-left: 10px;
            color: #828E99;
          }
          .infoIconLatestPower{
            position: relative;
            top: 2px;
            left: 8px;
            cursor: pointer;
          }
        }

        .peakPowerWithStorageGridContainer{
          background-color: white;

          .grid-columns{
            display: flex;
            align-items: center;
            margin-left: 40px;
          }
          .grid-columns:first-child{
            margin-left: -15px;
          }

          .grid-seperator {
            border-left: 1px solid #D8DCDE !important;
            height: 48px !important;
          }
          .energy-details_text{
            font-size: 15px;
            margin-left: 10px;
            color: #828E99;
          }
          .infoIconLatestPower{
            position: relative;
            top: 2px;
            left: 8px;
            cursor: pointer;
          }
        }

        .latest-status_detail-heading,
        .grid-container_heading {
          font-size: 25px;
          color: #545456;
          font-weight: 500;
          margin: 32px 32px 20px 32px;
        }

        .grid-container_heading {
          margin-bottom: 0px;
        }

        .latest-status_detail-heading {
          height: 28px;
        }

        .energy-stats-wrapper {
          width: 51.3%;
          left: 24.3%;

          .energy-stats-status {
            margin: 0;
            border: none;
            height: auto;

            &.consumption-only {
              .energy-stats__wrapper {
                .previous {
                  .icon {
                    path {
                      fill: #a6490b;
                    }

                    circle {
                      stroke: #a6490b;
                    }
                  }
                }

                .current {
                  .icon {
                    path {
                      fill: #f37320;
                    }

                    circle {
                      stroke: #f37320;
                    }
                  }
                }
              }
            }

            &.production-only {
              .energy-stats__wrapper {
                .previous {
                  .icon {
                    path {
                      // fill: #008cab;
                    }

                    circle {
                      // stroke: #008cab;
                    }
                  }
                }

                .current {
                  .icon {
                    path {
                      // fill: #01b4de;
                    }

                    circle {
                      // stroke: #01b4de;
                    }
                  }
                }
              }
            }

            .energy-stats__wrapper {
              padding: 0 32px;
              display: grid;
              grid-row-gap: 32px;
              grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));

              .energy-type__details {
                padding-left: 0px;
              }

              .energy-type__name {
                font-size: 15px;
                font-weight: normal;
                margin-top: 6px;
              }

              .icon {
                margin-right: 0;
                text-align: left;

                svg {
                  width: 38px;
                  height: 38px;
                }
              }

              .energy-type {
                .energy-type__value {
                  font-size: 20px;
                  line-height: 1;

                  span {
                    font-size: 12px;
                    margin-left: 5px;
                  }
                }
              }

              .grid-seperator {
                display: none;
                height: 86px;
              }

              .energy-stats__left {
                display: flex;
                padding-left: 0;
                width: auto;

                .icon {
                  width: 38px;
                }

                .energy-type {
                  margin-left: 16px;
                }
              }

              .energy-stats__right {
                display: flex;
                padding-left: 0;
                width: auto;

                .icon {
                  width: 38px;
                  height: 38px;
                }

                .energy-type {
                  margin-left: 16px;
                }

                circle {
                  fill: none;
                }
              }
            }

            .energy-stats-header {
              margin: 0;
              width: 100%;

              h4 {
                font-size: 25px;
                padding: 32px 33px 46px;
                width: 100%;
              }
            }
          }
        }

        .energy-layout {
          margin: 0;
          left: 24.3%;
          align-items: normal;
          flex-direction: column;
          // width: 56%;
          // height: 100% !important;

          .energy-layout-wrapper {
            margin: 0 0 30px;
            height: calc(100% - 60px);

            .energy-grid {
              .energy-grid-wrapper {
                .energy-grid-center {
                  .energy-consumed {
                    display: flex;
                    justify-content: center;
                    align-items: center;

                    .energy-type__icon {
                      margin-bottom: 5px;
                    }

                    .energy-type__value {
                      span {
                        margin-left: 4px;
                      }
                    }
                  }
                }

                .net-energy {
                  width: 152.3px;

                  .net-energy-wrapper {
                    margin: 0;

                    .text-units {
                      margin-left: 5px;
                      top: -1em;
                    }

                    .icon {
                      width: 26px;
                      height: 26px;
                    }

                    .text {
                      font-size: 26px;
                      opacity: 0.7;

                      span {
                        font-size: 13px;
                      }
                    }
                  }

                  .net-energy-text {
                    padding: 0;
                    opacity: 0.7;
                    font-size: 15px;
                    margin: 0;
                    margin-top: 5px;
                  }
                }

                .energy-grid-column {
                  width: 174px;

                  .energy-independence {
                    .energy-type .energy-type__icon-with-details {
                      text-align: left;
                      align-items: flex-start;
                    }
                  }
                }

                .energy-grid-column.net-energy {
                  margin-left: 22px;
                  width: 150px;
                }

                .energy-type__details {
                  margin-bottom: 4px;

                  .energy-type__value {
                    font-size: 25px !important;
                    font-weight: 500;

                    .energy-type__unit {
                      margin-left: 5px;
                      font-size: 12px !important;
                    }
                  }

                  .energy-type__name {
                    font-size: 15px;
                    margin-top: 5px;
                  }
                }

                .extra-margin-right {
                  width: 153.7px;
                  height: 42.7px;
                  margin-right: 18px!important;
                }

                .energy-arrow__icon {
                  width: 173.42px;
                  height: 75.15px;
                  padding: 0;
                  //min-width: 156.8px;

                  .icon {
                    // width: 158px!important;
                    // height: 43px!important;

                    svg {
                      // width: 158px!important;
                      // height: 43px!important;
                    }
                  }
                }

                .energy-arrow-straight__icon,
                .energy-arrow-straight-down__icon {
                  // height: 43.2px!important;

                  span {
                    // height: 43.2px!important;
    
                    svg {
                      // height: 43.2px!important;
                    }
                  }
                }

                .energy-type {
                  width: 100%;
                  &.energy-consumed .energy-type__icon-with-details .energy-type__icon {
                    margin-bottom: 2px;
                  }

                  .energy-type__icon-with-details {
                    display: flex;
                    flex-direction: column;
                    align-items: center;

                    .energy-type__icon {
                      width: auto;
                      height: auto;
                      margin-bottom: 8px;

                      .icon {
                        width: 70px;
                        height: 70px;

                        &.consumedHome {
                          width: 45px;
                          height: 45px;
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }

        .device-carousel-wrapper {
          width: 24.4%;
          right: 0px;
          border: none;
          border-left: solid 1px rgba(123, 135, 147, 0.3);
          height: 100%;
          margin: 0;

          .carousel-type-1 {
            border-top: solid 1px rgba(123, 135, 147, 0.3);
          }
        }

        .device-widget {
          border: none;

          h4 {
            margin: 0 32px;
            padding: 0;
            padding-top: 32px;
            font-size: 25px;
            font-weight: 500;
            display: flex;
          }

          .device__error {
            margin: 10px 0 0 32px;
            color: #7b8793;
          }

          .list-view {
            margin: 0px 33px;
            border-bottom: solid 1px rgba(123, 135, 147, 0.3);
            padding: 32px 0px;
            height: 108px;
            display: flex;
            align-items: center;
            height: 108px;

            &:first-child {
              border-top: none;
            }

            .list-view__wrapper {
              width: 100%;
              padding: 0;

              .list-view__content {
                width: 100%;
                align-items: flex-start;

                .list-view__icon {
                  margin: 0;
                  width: 24px;
                  height: 24px;

                  .icon {
                    svg {
                      width: 24px !important;
                      height: 24px !important;
                    }
                  }
                }

                .list-view__content-wrapper {
                  padding-left: 6px;
                  width: 100%;
                }

                .list-view__title {
                  font-size: 20px;
                  font-weight: 400;
                }

                .list-view__subtitle {
                  font-size: 15px;
                  margin-top: 2px;
                }
              }

              .list-view__action {
                width: 9.6px;
                height: 16.1px;

                .list-view__icon {
                  .icon {
                    svg {
                      width: 9.6px;
                      height: 16.1px;

                      path {
                        fill: #a2abb3;
                      }
                    }
                  }
                }
              }
            }
          }
        }

        .carousel-type-1 {
          margin: 0 !important;

          .carousel__container {
            border: none !important;
            display: flex;
            flex-direction: column;
            zoom: 0;
            padding: 0 32px 40px;

            .solar_heading {
              font-size: 25px;
              font-weight: 500;
              color: #545456;
              padding-top: 27px;
            }

            .carousel__static-info {
              padding: 0;
              padding-top: 39px;
              margin-bottom: 30px;
              border: none;

              .energySolar {
                margin: 0;
              }

              .energySolar svg {
                width: 40px !important;
                height: 40px !important;
              }

              .carousel__static-info__wrapper {
                margin: 0;
                margin-left: 21px;

                .carousel__static-info__value {
                  font-size: 30px;
                  font-weight: bold;
                  line-height: normal;
                  color: #01b5de;

                  span {
                    font-size: 12px;
                    font-weight: 500;
                    color: #01b5de;
                    text-align: right;
                  }
                }

                .carousel__static-info__name {
                  font-size: 15px;
                  line-height: normal;
                  color: #7a8692;
                }
              }
            }

            .carousel__wrapper {
              padding: 39px 0 40px !important;
              justify-content: space-evenly;
              border-bottom: solid 1px rgba(123, 135, 147, 0.3);

              .carousel__arrow {
                svg {
                  width: 13px !important;
                  height: 21px !important;
                }
              }

              .carousel__arrow-left {
                left: 0;
              }

              .carousel__arrow-right {
                right: 0;
              }

              .carousel__item {
                margin: 0;
                width: 75%;

                .energy-type {
                  width: auto;
                  justify-content: normal;

                  .energy-type__icon-with-details {
                    width: 100%;
                    display: flex;
                    justify-content: center;

                    .energy-type__details {
                      margin-left: 10px;

                      .energy-type__value {
                        font-size: 20px;
                        font-weight: 500;
                        line-height: normal;

                        span {
                          font-size: 15px;
                        }
                      }

                      .energy-type__name {
                        font-size: 15px;
                        line-height: normal;
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }

      .energy-heading {
        &.desktop {
          padding:0;
          border-bottom: solid 1px rgba(123, 135, 147, 0.3);
        }
      }
    }
  }

  &.not-english {
    @media only screen and (max-width: 1140px) {
      .latest-status {
        .latest-status-wrapper {
          .latest-status-detail-container {
            .grid-container {
              width: 25%;
            }

            .energy-layout {
              width: 45%;

              .energy-layout-wrapper {
                zoom: 0.8;
              }
            }

            .device-carousel-wrapper {
              width: 30%;
            }
          }
        }
      }
    }
  }

  .popup-container {
    &.battery-info {
      .popup-body {
        display: flex;
        align-items: center;
        padding: 15px 20px 25px 20px;

        .battery-info__content {
          font-size: 12px;
          line-height: 1.4;
          color: #545456;
          padding: 5px 10px 5px 10px;

          .battery-info__backup-time {
            font-weight: bold;
          }
        }
      }
    }
  }
}

.site-image-popups {
  .footer-area {
    button {
      &:first-child {
        order: 1;
      }
    }
  }

  &.alertPopUp {
    &.web-warningPopUp {
      .data-area {
        .site-image-popups__wrapper {
          .error-text {
            text-align: center;
          }
        }
      }

      .footer-area {
        border-top: 1px solid #7B87934D;
        padding: 22px 0;

        button {
          &:first-child {
            width: auto;
            background: transparent;
            color: #01b4de;
            border: 1px solid #01b4de;
          }

          margin: 0;
        }
      }
    }
  }

  &.maxImageUploaded {
    .data-area>.site-image-popups__wrapper>.error-text>.site-details-button {
      color: #01b4de;
      text-decoration: underline;
      cursor: pointer;
    }
  }
}
