.feedbackAlertToast {
    width: 100%;
    height: 59px;
    background-color: #ff5959;
    z-index: 1000;
    position: fixed;

    .messageWrapper {
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        padding: 20px 20px 19px;
        .icon {
            width: 20px;
            height: 20px;
            margin-right: 10px;
        }
    
        .toastMessage {
            font-size: 12px;
            color: #ffffff;
        }
    }
}