.explore-enphase-page {
  .eep__wrapper{

  }

  .eep__list{
    height: calc(100vh - 9vh - 185px);
    overflow: auto;

    &.eep__list-prelogin {
      height: calc(100vh - 130px);
    }
  }

  .eep__list__wrapper{
    height: inherit;

    .eep__list__searchInput {
      margin: 20px;
      position: relative;

      input {
        padding: 8px 15px;
        border-radius: 5px;
        border: solid 1px #f4f3f4;
        background-color: #fafafb;
        font-size: 14px;
        font-weight: normal;
        color: #555555;
        opacity: 0.8;
        width: 100%;

        &::placeholder {
          color: #a6a6a8;
          opacity: 1;
        }
        
        &:-ms-input-placeholder {
         color: #a6a6a8;
        }
        
        &::-ms-input-placeholder {
         color: #a6a6a8;
        }
      }


      .search, .close {
        width: 17px;
        height: 17px;
        display: inline-block;
        top: 0;
        right: 0;
        position: absolute;
        margin: 7px 15px 0 0;

        svg {
          height: 17px;
          width: 17px;

          path {
            fill: #A6A6A8;
          }
        }
      }

      .close svg polygon {
        fill: #A6A6A8;
      }
    }

    .empty__list{
      flex: 1;
      text-align: center;
      padding: 50px;
      .empty__text {
        color: #808080;
      }
    }
  }

  .eep__list__item{
    position: relative;
    margin: 20px;
  }

  .eep__list__title{
    font-size: 16px;
    font-weight: 500;
    line-height: 1.5;
    color: #4a4a4a;
    margin-top: 10px;
  }

  .eep__list__sub-title{
    font-size: 12px;
    color: #7b8793;
    margin-top: 2px;
  }

  .eep__list__new{
    position: absolute;
    top: 20px;
    padding: 2px 6px;
    font-size: 12px;
    font-weight: 300;
    color: #ffffff;
    border-radius: 0 4px 4px 0;
    background-color: #f37320;
  }
}

.MuiTabs-root{
  border-bottom: 1px solid rgba(123, 135, 147, 0.3);

  .MuiTab-root{
    font-size: 16px;
    font-weight: 400;
    color: #a6a6a8;
    text-transform: initial;
  }

  .MuiTab-wrapper{
    //line-height: 1.2;
    font-family: 'Visuelt', 'Roboto', 'Helvectica Neue', Helvetica, Arial, sans-serif;
  }

  .Mui-selected{
    font-weight: 500;
    color: #01b5de !important;
  }

  .MuiTabs-indicator{
    background-color: #01b5de !important;
  }
}

.night{
  .explore-enphase-page{
    .eep__list__title{
      color: #f8f8ff;
    }
  
    .eep__list__sub-title{
      color: #d8dcde;
    }
  }
}

.eep__list__picture{
  width: 100%;
  height: 0;
  padding-bottom: 56.25%; /* 16:9 Aspect Ratio */
  border-radius: 10px;
  position: relative;
  background: #c6c8ca;

  iframe, .eep__list__picture-wrapper {
    width: 100%;
    height: 100%;
    border-radius: 10px;
    position: absolute;
    top: 0;
    left: 0;
  }

  .yt-video__header-overlay {
    width: 100%;
    height: 50px;
    background-color: transparent;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
  }

  .eep__list__image{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 10px;

    .spin-container {
      border-radius: 10px;
      -webkit-backdrop-filter: blur(2px);
      backdrop-filter: blur(2px);
      background-color: rgba(0, 0, 0, 0.75) !important;
    }
  }

  .eep__list__video-play__button{
    cursor: pointer;

    .icon{
      display: flex;
    }
  }

  .spin-container {
    background: transparent;
  }
}
