@import "../font-style";

.carousel-type-1{
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  margin: 15px;

  .carousel__container{
    display: flex;
    width: -webkit-fill-available;
    max-width: 375px;
    border: 1px solid rgba(123,135,147,.3);
    border-radius: 5px;
    margin: auto;

    .carousel__static-info{
      min-width: 120px;
      height: inherit;
      border-right: 1px solid rgba(123,135,147,.3);
      padding: 15px;
      display: flex;
      align-items: center;

      .carousel__static-info__wrapper{
        height: inherit;
        margin: auto;
      }

      p{
        margin: 0;
      }

      .carousel__static-info__value{
        font-size: 14px;
        font-weight: bold;
        @include title-2;

        span{
          font-weight: 400;
          font-size: 10px;
          margin-left: 2px;
          align-self: flex-end;
          @include body-1;
        }
      }

      .carousel__static-info__name{
        font-size: 10px;
        color: #7b8793;
        margin-top: 2px;
        @include body-1;
      }
    }

    .carousel__wrapper{
      position: relative;
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      padding: 15px 20px;

      .carousel__arrow{
        position: absolute;
        display: flex;
        min-width: 12px;
        width: 12px;
        height: 50px;
        visibility: hidden;

        .icon{
          height: auto;

          path{
            fill: #01b4de;
          }
        }

        &.carousel__arrow-left{
          left: 20px;
          visibility: visible;
          transform: rotate(180deg);
        }

        &.carousel__arrow-right{
          right: 20px;
          visibility: visible;
        }
      }

      .carousel__item{
        display: none;
        width: calc(100% - 60px);
        margin: auto;

        &.active{
          display: block!important;
        }

        .energy-type{
          width: auto;
          min-width: 110px;
          justify-content: center;

          .energy-type__icon-with-details{
            align-items: center;
          }
          
          .energy-type__icon{
            width: 40px;
            height: 40px;
          }

          .energy-type__value{
            @include title-2;
          }

          .energy-type__name{
            font-size: 12px;
            color: #7b8793;
            margin-top: 2px;
            @include body-1;
          } 
        }
      }
    }
  }
}

/* Screen Size :: 320 */
@media (max-width: 374px) {
  .carousel-type-1{
    .carousel__container{
      .carousel__static-info{
        .carousel__static-info__wrapper{
        }
  
        .carousel__static-info__value{
          span{
          }
        }
  
        .carousel__static-info__name{
        }
      }
  
      .carousel__wrapper{
        padding: 15px;

        .carousel__arrow{
          &.carousel__arrow-left{
            left: 15px;
          }
  
          &.carousel__arrow-right{
            right: 15px;
          }
        }
  
        .carousel__item{
          width: calc(100% - 35px);

          .energy-type{
            .energy-type__icon{
            }
  
            .energy-type__name{
            } 
          }
        }
      }
    }
  }
}

/* Screen Size :: 480 and above (Tablet) */
@media (min-width: 480px) {
  .carousel-type-1 {
    .carousel__container {
      zoom: 1.2;
    }
  }
}
