  background-color: #262727;

  .lightStatus .description .descrContent .descrTitle {
    color: #ffffff;
  }

  .lightStatus .description .img {
    .ojasBrightness {
      svg {
        path {
          fill: #ffffff;
        }
      }
    }
  }

  .lightSettingItem__wrapper .greyBackground {
    background-color: #1b1a1a;
  }

  .powerSaveItem__wrapper .header-container {
    color: #ffffff;
  }

  .powerSaveItem__wrapper .description-container {
    color: #ffffff;
  }

  .powerSaveItem__wrapper .description-container-fchg {
    color: #ffffff;
