.OM_pesSubmenu .ojasAdvanceSetting-Container {
  background-color: #ffffff;
  border-radius: 8px;
  box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.10);

  &>div:last-child {
    .powerSaveItem__wrapper {
      border-bottom: 0;
    }
  }

  .powerSaveItem__wrapper {
    margin: 0 20px 0 20px;
    border-bottom: 0.5px solid #7B87934D;
    padding: 18px 0 13px 0;

    .header-container {
      display: flex;
      justify-content: space-between;
      @include title-1;
      color: #545456;
      align-items: center;
      gap: 16px;
    }

    .description-container {
      font-size: 12px;
      color: #545456;
      padding-top: 32px;
      font-weight: normal;
      line-height: normal;
    }

    .description-container-fchg {
      font-size: 12px;
      color: #545456;
      padding-top: 5px;
      font-weight: normal;
      line-height: normal;
    }

    .description-container span {
      font-weight: 500;
    }

    .dropdown-container {
      display: flex;
      align-items: center;
      gap: 13.5px;

      span {
        @include body-2;
        color: #01B5DE;
        font-size: 14px;
      }

      .dropdown-arrow {

        &.arrow-down {
          margin-top: 4px;
          transform: rotate(0deg);
          transition: transform .2s linear;
        }

        &.arrow-up {
          margin-top: -9px;
          transform: rotate(-180deg);
          transition: transform .2s linear;
        }
      }
    }

    .dropdown-container-fchg {
      font-size: 12px;
      font-weight: 300;
      color: #7b8793;

      &.on-text {
        span {
          color: #F37320;
        }
      }

      &.off-text {
        span {
          color: #A9A9AA;
        }
      }
    }

    .slider-container {
      opacity: 0;
      visibility: hidden;

      &.arrow-down {
        opacity: 0;
        visibility: hidden;
      }

      &.arrow-up {
        visibility: visible;
        transition: ease-in-out .2s;
        opacity: 1;
        // display: flex;
        align-items: flex-end;
        margin-top: 9.5px;
        // margin-bottom: 15.5px;
      }
    }

    .slider-container-fchg {
      opacity: 0;
      visibility: hidden;

      &.arrow-down {
        opacity: 0;
        visibility: hidden;
      }

      &.arrow-up {
        visibility: visible;
        transition: ease-in-out .2s;
        opacity: 1;
        align-items: flex-end;
      }
    }
  }

  .hide-slider {
    display: none;
  }
}

.ojasAdvanceSettings .button-wrapper {
  position: absolute;
  bottom: 16px;
  text-align: center;
  width: 100%;

  .save-button {
    box-shadow: 1px 2px 10px 0 rgba(63, 67, 71, 0.08);
    border: 1px solid #00b6de;
    padding: 8px 0 !important;
    background: #00b6de;
    color: #FFFFFF;
    text-align: center;
    border-radius: 8px;
    width: 100%;
    justify-content: center;
    display: flex;
    font-size: 15px !important;
    font-weight: normal !important;
    @include title-2;
    margin-left: auto;
    margin-right: auto;

    &.buttonCancel {
      background-color: transparent;
      color: #01b5de;
      border: 1px solid #01B5DE;
      font-size: 15px;

      &.buttonDisabled {
        color: #c7c8ca;
      }
    }

    &.quiet-margin {
      margin-top: 40px;
    }
  }

  .text {
    svg {
      g {
        g {
          fill: #ffffff;
        }
      }

      path {
        stroke: #ffffff;
        stroke-width: 1px;
      }
    }
  }
}

.bleConnected {
  bottom: 140px !important;
}

.ojasAdvanceSetting-Container {
  .dropdown-arrow {
    .dropdown {
      color: #555557 !important
    }
  }
}

