  }
}

.Web-Advance-Settings-Container {
  height: calc(100vh - 330px);
  width: 740px !important;

  .button-wrapper {
    margin-bottom: 20px;
    width: 45%;
  }
}

.pesWebCardView {

  .main-Conatiner {
    padding: 24px;

    .ProfileStatus,
    .main-container {
      padding: 24px;
    }

    .ProfileStatus,
    .lightStatus {
      background-color: #F6F7FB;
      border-radius: 8px;
      border-bottom: 0;
      margin: 0;

      .descrContent {
        .descrTitle {
          color: #545456;
          font-weight: 500 !important;
        }

        .descrSubTitle {
          color: #A9A9AA;
        }
      }
    }

    .toggle-swtich__button-unchecked {

      .toggle-switch__container {
        .react-switch-handle {
          background-color: #6B7073 !important;
        }

        .react-switch-bg {
          background-color: #DFDFDF !important;
        }
      }
    }


    .slidecontainer {
      .ReactSwipeButton {
        width: 283px !important;
        padding: 4px 10px;
        background: #DFDFDF;
        border-radius: 30px;
        height: 59px;
      }

      .rsbContainer-inner {
        background-color: transparent;
      }
    }
  }

  .ojasAdvanceSetting-Container .powerSaveItem__wrapper {
    border-bottom: none !important;
  }

  .header-container {
    margin-bottom: 20px;
  }

  .header-container,
  .dropdown-container span {
    font-size: 20px !important;
    font-weight: 700 !important;
  }

  .description-container {
    font-size: 16px !important;
  }

  .dropdown-container-fchg {
    font-size: 16px !important;
    font-weight: 700 !important;
  }

  .description-container-fchg {
    font-size: 16px !important;
  }




  .ojasAdvanceSetting-Container {
    padding: 24px;

    .powerSaveItem__wrapper {
      margin: 0;
      padding: 24px;
      background-color: #F6F7FB;
      border-radius: 8px;

      &.fast-charging-wrapper {
        .header-container {
          margin-bottom: 0;
        }

        .slider-container-fchg {
          margin-top: 0;
        }
      }

      .toggle-switch {
        &.toggle-swtich__button-unchecked {
          .react-switch-bg {
            background-color: #DFDFDF !important;
          }

          .react-switch-handle {
            background-color: #6B7073 !important;
          }
        }
      }
    }

    .description-container,
    .description-container-fchg {
      font-size: 16px;
      margin-top: 24px;

      span {
        font-weight: 500;
      }
    }

    .hide-slider {
      display: none;
    }

    .slider-container-fchg {
      span {
        font-weight: normal;
      }
    }
  }
}

.web-msg-content {
  font-size: 16px !important;
  font-weight: 400px !important;
  text-align: left;
}

.pes-confirm-popup {

  .container,
  .reset-done {
    width: 672px;

    .reset-header {
      color: #545456;
      font-size: 20px;
      padding: 24px;
    }

    .icon {
      margin-bottom: 0 !important;
    }

    .reset-error-text,
    .reset-timeout-text {
      margin-top: 32px;
      margin-bottom: 0;
    }

    .reset-error-message,
    .reset-timeout-message {
      margin-top: 16px;
      margin-bottom: 56px;
      height: initial;
      width: 100%;
    }

    .reset-text,
    .reset-done-text {
      width: 100%;
      text-align: left;
      line-height: 28px;
      height: initial;
      margin: 30.5px 24px;
      font-weight: 400;
      font-size: 16px;
    }

    .reset-done-text {
      text-align: center;
    }

    .footer-with-two-buttons {
      border-top: 0;
      height: initial;

      div {
        width: 324px !important;
        border: 1px solid #01B5DE;
        border-radius: 8px;
        margin: 0 24px 24px;
        padding: 16px 24px 16px 24px;
        height: 60px;
        font-size: 20px;

        &.button-left {
          margin-right: 0;
          color: #00B6DE;
          border-color: #01B5DE;
        }

        &.button-right {
          background: #00B6DE;
          color: #fff;
        }
      }

    }

    .reset-error-btn,
    .reset-timeout-btn,
    .dashboard-btn {
      width: 440px;
      height: 60px;
      border-radius: 8px;
    }
  }
}

.fc-settings-toast {
  position: absolute;
  top: 134px;
  left: 0;
}

.pes-success-message-toast {
  background-color: #14C415 !important;
}

.pes-error-message-toast {
  background-color: #DE3C41;
}

.dialog-container {
  &.pes-confirmation-popup {
    width: 300px;

    &.settings {
      .data-area {
        border-bottom: 0;
        text-align: left;

        .title-area {
          padding: 16px;
        }

        .data-area {
          padding: 24px 16px !important;

          .pes-confirm-content-area {
            padding: 0;
            margin: 0;
          }
        }
      }

      .footer-area {
        padding: 0 16px 16px;

        button {
          border: 1px solid #01B5DE;
          border-radius: 8px;
          padding: 8px 16px;
          color: #01B5DE;

          &.positive-action {
            color: #fff;
            background-color: #01B5DE;
            margin-left: 16px;
          }
        }
      }
    }
  }
}
