.navigation-browser {
  height: 80px;
  display: flex;
  position: relative;
  .header-support-wrapper {
    width: 100%;
    padding: 12px 32px;
  }
  .header-support-menu-wrapper {
    display: flex;
    align-items: center;
  }
  .nav-menu-details {
    display: flex;
    align-items: center;
    .classic-view-button {
      font-size: 20px;
      font-weight: 500;
      color: #ffffff;
    }
    @media only screen and (max-width: 1140px) {
      .classic-view-button {
        font-size: 16px !important;
      }
    }
    li {
      flex-grow: 1;
      text-align: center;
      font-size: 1.8vh;
      align-items: center;
      justify-content: center;
      margin-left: 32px; 
      cursor: pointer;
      color: $medium;

      .group {
        display: flex;
        align-items: center;
        .icon {
          height: 31px;
      
          svg {
            margin-top: 0px;
            margin-left: 0px;
            height: 31px;
            width: 31px;
          }
          &.browsermenu {
            svg {
              width: 34px;
              padding-bottom: 2px;
            }
          }
        }
        &.active::after{
          content: "";
          width: 15px;
          height: 15px;
          position: absolute;
          bottom: 0;
          background-color: white;
          margin-left: 7.5px;
          transform: rotate(45deg);
          top: 95%;
        }
      }
    }
    .support-enphase-icon {
      z-index: 0;
    }
  }
  .support-enphase-logo {
    margin-right: 20px;
    .brandIcon {
      width: 170px;
      height: 31px;
    }
    svg {
      width: 100%;
      height: 100%;
    }
  }
  .notification-count {
    display: flex;
    background-color: #ff5959;
    border-radius: 1em;
    width: 20px;
    height: 20px;
    align-items: center;
    justify-content: center;
    align-self: flex-start;
    position: absolute;
    margin-left: 18px;

    font-size: 12.6px;
    font-weight: 500;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #fdfefe;
  }

  .nav-browser {
    .small-text-title {
      font-size: 1.8vh;
    }
  
    @media only screen and (min-width: 320px) and (max-width: 480px) {
      .small-text-title {
        font-size: 1vh;
      }
    }
  
    ul {
      height: 100%;
      display: flex;
      align-items: center
    }
    li {
      flex-grow: 1;
      text-align: center;
      font-size: 1.8vh;
      align-items: center;
      justify-content: center;
      margin-left: 32px; 
      cursor: pointer;
      color: $background;
      rect, path {
        fill: $background;
      }
  
      .group {
        display: flex;
        align-items: center;
        opacity: 0.6;
        
        .icon {
          height: 21px;
      
          svg {
            margin-top: 0px;
            margin-left: 0px;
            height: 21px;
            width: 21px;
          }
          &.layout {
            height: 30px;
      
            svg {
              height: 30px;
              width: 30px;
            }
          }
        }
        
        .nav-name {
          padding-left: 10px;
          font-size: 20px;
          font-weight: 500;
          font-style: normal;
          font-stretch: normal;
          line-height: normal;
          letter-spacing: normal;
          color: #ffffff;
        }
        &.active {
          font-weight: 500;
          opacity: 1;
        }
      }
    }
  }
}
