.popup-container {
  .overlay {
    position: absolute;
    left: 50%;
    top: 50%;
    background-color: #000000;
    opacity: 0.6;
    height: 100%;
    width: 100%;
    transform: translate(-50%, -50%);
    z-index: 105;
  }
  .popup-wrapper {
    position: absolute;
    left: 50%;
    top: 50%;
    background-color: #ffffff;
    transform: translate(-50%, -50%);
    z-index: 107;
    max-height: 80%;
    max-width: 90%;
    padding: 25px 20px;
    border-radius: 8px;

    .popup-title-wrapper {
      display: flex;
      justify-content: space-between;
      color: #7b8793;
      @include body-1;

      .close-button {
        .close {
          height: 16px;
          svg {
            width: 16px;
            height: 16px;
            polygon {
              fill: #7c828a;
            }
          }
        }
      }
    }

    .react-image-crop {
      margin: 25px 0px;
      width: 295px;
      height: 164px;
      border: 1px solid #70707033;
      display: flex;
      justify-content: center;
      align-items: center;

      .no-image {
        @include caption;
        width: 295px;
        height: 164px;
        display: grid;
        place-content: center;
        color: #01b4de;

        .add-site-image-button {
          margin-top: 4px;
        }
      }

      &.fileSizeError {
        border: 1px solid #ED655F;
      }
    }

    .size-error-text {
      color: #ED655F;
      @include body-1;
    }

    .edit-mode-actions {
      display: flex;
      justify-content: space-between;
      @include body-1;

      .hide-image-button,
      .change-button,
      .apply-button {
        border-radius: 20px;
        border: none;
        background: transparent;
        padding: 4px 12px;
      }
      .hide-image-button,
      .change-button {
        color: #01b4de;

        &.bg-white {
          border: 1px solid #01b4de;
        }
      }

      .apply-button {
        background: #01b4de;
        color: white;
        margin-left: 8px;

        &.fileSizeError {
          background: grey;
        }
      }

      &.no-siteImage {
        display: flex;
        justify-content: center;
        align-items: center;
      }

      .actions-left {
        .hide-image-button {
          display: flex;
          .hideImage {
            margin-right: 5px;
            svg {
              width: 13px;
              height: 13px;
              margin-right: 5px;
            }
          }
        }
      }
    }
  }
}


.night {
  .popup-container{
    .popup-wrapper{
      background: #262627;

      .no-siteImage > .no-image > .uploadImageSmall{
        svg {
          g > g {
            #Group_12045 {
              circle { 
                fill: #262627;
              }
            }
            #Group_12769{
              g {
                path, circle, line { stroke: #01b4de;}
              }
              circle { fill: #262627; }
            }
            path { stroke: #01b4de;}
          }
        }
      }
    }
  }

  .hide-add-siteImage > .showup__wrapper > .showup__inner-wrapper > .showup__body{
    .hide-site-image-title{
      color: white;
    }
    .hide-image-content{
      color: #A2A6A9;
    }
  }
}