@import '../../../offline/styles/font-style.scss';

.tutorial_wifi__grid {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 200;

    .tutorial_wifi__container {
        width: 100%;
        position: fixed;
        bottom: 0%;
        z-index: 999;
        box-shadow: 0 -2px 17px 0 rgba(0, 0, 0, 0.11);
        background-color: #ffffff;
        top: auto !important;

        .tutorial_wifi__box {
            .tutorial_wifi__header {
                display: flex;
                align-items: center;
                justify-content: space-between;
                width: 90%;
                margin: auto;

                p {
                    font-weight: 500;
                }

                span {
                    svg {
                        height: 12px;
                        fill: grey;
                    }
                }
            }
        }
    }
}

.tutorial_description {
    @include body-1;
    margin-top: 0.4rem;
}
.border-bottom {
    border-bottom: 1px solid #e6e6e6;
}
.tutorial_ap__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 15px;
    margin: auto;
    gap: 1rem;
    white-space: nowrap;

    .tutorial_ap_title {
        @include title-2;
        vertical-align: -webkit-baseline-middle;
        height: 20px;
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;
        font-weight: 500;
        // white-space: nowrap;
    }

    .tutorial_ap_back {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;
        font-weight: 500;
        span {
            flex: 1;
            flex-direction: row;
            justify-content: center;

            svg {
                height: 16px;
            }
        }
    }

    .tutorial_ap_back_text_container {
        flex: 1;
        flex-direction: row;
        align-content: center;
    }

    .tutorial_ap_back_text {
        vertical-align: -webkit-baseline-middle;
        color: #00b6de;
    }

    .tutorial-right {
        width: 2rem;
    }
}

.svg-color {
    fill: #00b6de;
}

.instruction-check {
    @include body-2;
}

.tutorial-instruction {
    display: flex;
    align-items: center;

    span {
        padding-right: 0.5rem;
    }

    p {
        margin: 0.5rem 0;
        font-size: 12px;
        font-weight: 500;

        span {
            color: #00b6de;
            text-decoration: underline;
            padding: 0;
        }
    }
}

.tutorial_btn_container {
    display: flex;
    justify-content: space-between;
    flex-direction: row-reverse;
    margin: auto;
    padding-bottom: 4rem;
    width: 90%;

    .slide3-button {
        width: 100%;
        animation: get-started-animation;
        animation-duration: 0.5s;

        .Obutton {
            width: 100%;
            text-align: center;
        }
    }

    button {
        border: none;
        background-color: transparent;
        color: #00b6de;
        font-weight: 500;
        animation-name: prev-animation;
        animation-duration: 0.2s;
        margin-left: 2rem;
    }
}

.tutorial-sliderbtn{ 
    .default {
        border-radius: 1.5em;
        font-size: 16px;
        padding-right: 2.5em;
        padding-left: 2.5em;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0.5em 2em 0.5em
    }
    .android{
        padding-top: 10px;
    }
}

.tutorial_slide_content {
    margin: auto;
    width: 90%;
    height: 15rem;
    margin-top: 1.2rem;
    position: relative;

    .tutorial_title {
        @include title-2;
        color: #545456;
        margin: 0;
    }

    .tutorial_step {
        color: #919CA9;
        font-size: 12px;
        text-transform: uppercase;
        @include caption;
    }

    .tutorial_wifi_description {
        @include body-1;
    }

    .tutorial_link {
        position: absolute;
        top: 140px;
        @include body-2;

        p {
            &:nth-child(1) {
                @include body-2;
                margin: 0;
                color: #545456;
            }

            &:nth-child(2) {
                color: #00B6DE;
                text-decoration: underline;
                margin: 6px 0;
            }
        }
    }
}

.img-container {
    width: 100%;
    background-color: #01B4DE0F;
    margin: auto;
    position: relative;
    display: grid;
    min-height: 13rem;
    
    .img-container-wifi-img{
        display: flex;
        align-items: center;
        justify-content: center;
        height: 220px;
        margin: auto;
    }

    .tutorial-info {
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;
        top: 15px;
        z-index: 100;
        right: 15px;
        fill: #00b6de;

        p {
            font-size: 12px;
            margin: -2px;
            margin-right: 0.5rem;
            z-index: -10;
            border-bottom: 1px solid #00b6de;
            animation-name: p-animation-front;
            background-color: #fff;
            padding: 1px;
            animation-duration: 0.2s;

            a {
                color: #00b6de;
            }
        }
    }
}

.device-container {
    margin: auto;
    width: 90%;
    margin-bottom: 5.6rem;
    @include title-2;

    .device-img-container {
        background-color: #F5F5F8;
        margin-bottom: 1rem;
        height: 13rem;
        display: flex;
        align-items: center;
        justify-content: center;
        img{
            height:100%;
        }
    }
}

.tutorial-instruction {
    p {
        @include body-1;

        span {
            @include body-2;
        }
    }
}


.tutorial_starter__grid {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 200;

    .tutorial_starter__container {
        width: 100%;
        position: fixed;
        bottom: 0%;
        z-index: 999;
        box-shadow: 0 -2px 17px 0 rgba(0, 0, 0, 0.11);
        background-color: #ffffff;
        top: auto !important;

        .tutorial_starter__box {
            .tutorial_starter__header {
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding: 0rem 1.5rem;
                border-bottom: 1px solid rgb(234, 231, 231);

                p {
                    @include title-2;
                }

                div {
                    span {
                        svg {
                            height: 12px;
                            fill: grey;
                        }
                    }
                }
            }

            .tutorial_starter__content {
                margin: auto;
                padding-bottom: 3rem;

                .button_container {
                    display: flex;
                    align-items: center;
                    justify-content: space-around;
                    margin: 2rem 0rem;

                    .bluetooth_container,
                    .wifi_container {
                        padding: 1.5rem;
                        background-color: #01B5DE0B;
                        width: 40%;
                        display: flex;
                        flex-direction: column;
                        border-radius: 30px;
                        // align-items: flex-start;

                        span {
                            margin-bottom: 1rem;
                            text-align: left;
                            width: 100%
                        }

                        p {
                            color: #00B6DE;
                            width: 100%;
                            margin: 0;
                            padding: 2px;
                            @include title-2;
                        }
                    }
                }

                p {
                    font-size: 12px;
                    margin-bottom: 2rem;
                }
            }
        }
    }
}

.svg-color {
    fill: #00b6de;
}


@keyframes p-animation-front {
    0% {
        transform: translateX(60%);
        opacity: 0;
    }

    100% {
        transform: translateX(0%);
        opacity: 1;
    }
}

@keyframes p-animation-back {
    0% {
        transform: translateX(0%);
        opacity: 0;
    }

    100% {
        transform: translateX(60%);
        opacity: 1;
    }
}

@keyframes prev-animation {
    0% {
        transform: translateX(70%);
    }

    100% {
        transform: translateX(0%);
    }
}

@keyframes get-started-animation {
    0% {
        width: 50%;
    }

    100% {
        width: 100%;
    }
}

.night {
    .device-img-container {
        background-color: #01B5DE0B;
    }
    .border-bottom{
        border:none;
    }
    .tutorial_starter__grid {
        .tutorial_starter__container {
            .tutorial_starter__box {
                .tutorial_starter__header {
                    div {
                        span {
                            svg {
                                fill: #fff;
                            }
                        }
                    }
                }

                background-color: #262627;
                color: #fff;
            }
        }

    }

    .tutorial_wifi__grid {
        .tutorial_wifi__container {
            background-color: #262627;
            color: #fff;

            .tutorial_link {
                p:nth-child(1) {
                    color: #fff;
                }
            }

            .tutorial_step {
                color: #919ca9;
            }

            .tutorial_title {
                color: #fff;
            }
        }
    }

    .tutorial-info {
        p {
            background-color: #262627;
        }
    }
}