/* Energy Stats */
.energy-stats {
  border-bottom: solid 1px rgba(123, 135, 147, 0.3);

  .energy-stats__wrapper{
    width: 375px;
    display: flex;
    justify-content: space-between;
    padding: 0 15px 20px 15px;
    margin: auto;

    & > div {
      display: grid;
      align-items: center;

      &.energy-stats__right,
      &.energy-stats__woSolar {
        align-items: unset;

        & > div:first-child { align-items: flex-start; }
        & > div:last-child { align-items: flex-end; }
      }
    }
  }

  &.energy-stats__has-border{
    .productiononlycard{
      display: flex;
      height: 82px !important;
      justify-content: flex-end;
      align-items: center; 
      background-color: #FAFAFB;
      border: 1px solid #F4F4F5 !important;
      .compTitle{
        display: flex;
        align-items: center;
        width: -webkit-fill-available;
      } 
      .prod-value{
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0 10px 0 55px;
        width: 50%;
        .energy-type.energy-current .energy-type__value {
          color: #545456;
          @include title-2;
      }
      }
    }

    .energy-stats__wrapper{
      width: auto;
      height: 70px;
      padding: 10px 0;
      border-radius: 3px;
      border: solid 1px rgba(123, 135, 147, 0.3);
      margin: 15px 17px 20px;

      &.has-two-items{
        .energy-stats__item{
          width: 50%;
        }
      }

      &.has-three-items{
        .energy-stats__item{
          width: 33.33%;
        }
      }

      &.has-four-items{
        .energy-stats__item{
          width: 25%;
        }
      }
      .energy-type{
        width: 100%;
      }
    }
  }

  .energy-stats__item{
    width: 100%;
    height: 100%;
    display: inline-flex;
    padding: 0 15px;

    &:not(:first-child){
      border-left: solid 1px rgba(123, 135, 147, 0.3);
    }

    .energy-type__value{
      font-size: 16px;
      font-weight: bold;
    }

    .energy-type__name{
      font-size: 12px;
      line-height: 1.17;
      color: #7b8793;
    }
  }

  .energy-type__arrow-wrapper{
    align-items: unset !important;

    & > div:first-child { align-self: flex-start; }
    & > div:last-child { align-self: flex-end; }
  
    .energy-type__arrow{
      display: flex;
      min-width: 12px;
      width: 12px;
      height: 40px;
      margin: 0 5px;
      visibility: hidden;

      .icon{
        height: auto;

        path{
          fill: #a2abb3;
        }
      }

      &.left{
        visibility: visible;
        transform: rotate(180deg);
      }

      &.right{
        visibility: visible;
      }

      .energy-produced{
        path{
          fill: #01b4de;
        }
      }
      .energy-imported{
        path{
          fill: #6c7073;
        }
      }
      .energy-generator{
        path{
          fill: #9062AC;
        }
      }
      .energy-discharged{
        path{
          fill: #7ecd40;
        }
      }
      .energy-exported{
        path{
          fill: #6c7073;
        }
      }
      .energy-charged{
        path{
          fill: #7ecd40;
        }
      }
    }
  }

  .energy-donut-chart{
    position: relative;
    width: calc(2*#{$energy-chart-radius});
    height: calc(2*#{$energy-chart-radius});
    border-radius: 50%;
    margin: auto;

    .energy-type{
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      justify-content: center;

      .energy-type__icon-with-details{
        display: block;
        text-align: center;
      }

      .energy-type__icon{
        display: block;
        margin: auto;
        min-width: 18px;
        width: 18px;
        height: 18px;

        svg{
          width: 18px;

          path{
            fill: #f37320;
          }
        }
      }

      .energy-type__details{
        display: block;
        margin-top: 5px;
      }
    }
  }

  /* Production Only */
  &.production-only{
    .energy-stats__item{
      .energy-current{
        .energy-type__value{
          color: #01b4de;
        }
      }
      .energy-previous{
        .energy-type__value{
          color: #008cab;
        }
      }
      .energy-last-year{
        .energy-type__value{
          color: #6c7073;
        }
      }
    }
  }

  /* Consumption Only */
  &.consumption-only{
    .energy-stats__item{
      .energy-current{
        .energy-type__value{
          color: #f37320;
        }
      }
      .energy-previous{
        .energy-type__value{
          color: #a6490b;
        }
      }
      .energy-last-year{
        .energy-type__value{
          color: #6c7073;
        }
      }
    }
  }

}

.page:not(.en){
  .energy-stats{
    .energy-donut-chart{
      .energy-type{
        &.energy-consumed{
          .energy-type__name{
            font-size: 10px;
          }
        }
      }
    }
  }
}


/* Screen Size :: upto 359 */
@media (max-width: 359px) {
  .energy-stats {
    .energy-stats__wrapper{
      width: 100%;
    }

    .energy-type__arrow-wrapper{
      .energy-type__arrow{
        height: 75px;
      }
    }

    .energy-donut-chart{
      width: calc(2*#{$energy-chart-radius-xxs});
      height: calc(2*#{$energy-chart-radius-xxs});
    }
  }
}

/* Screen Size :: 360 - 374 */
@media (min-width: 360px) and (max-width: 374px) {
  .energy-stats {
    zoom: .95;
  }
}

/* Screen Size :: 375 - 424 */
@media (min-width: 375px) and (max-width: 424px) {
  .energy-stats {
    .energy-donut-chart{
      width: calc(2*#{$energy-chart-radius-xs});
      height: calc(2*#{$energy-chart-radius-xs});
    }
  }
}

/* Screen Size :: 480 and above (Tablet) */
@media (min-width: 480px) {
  .energy-stats {
    .energy-stats__wrapper {
      zoom: 1.3;
    }
  }
}

.night {
  .energy-type.energy-netenergy .energy-type__value {
    color: #a2a6a9;
  }
  .energy-stats {
    &.energy-stats__has-border{
  .productiononlycard{ 
    background-color: #262627;
    border: solid 1px #3c4044 !important;
    .compTitle{
      display: flex;
      align-items: center;
      width: -webkit-fill-available;
      color: #a2a6a9;
    } 
    .prod-value{
      .energy-type.energy-current .energy-type__value {
        color:#FAFAFB;
    }
    }
  }
}
}
  .energy-stats {
    .energy-type__arrow-wrapper{
      .energy-type__arrow{
        .energy-generator{
          path{
            fill: #dca2ff;
          }
        }
      }
    }
  }
}
