.browser {
	.content-popup-wrapper {
		height: 100%;
		width: 100%;
		z-index: 104;
		background-color: #fafafb;
		&.notices {
			margin: 0px;
    }
    
    .content-popup__header__right{
      display: inline-flex;
      align-items: center;
    }
  }
  
	.title-wrapper {
		height: 97px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 32px;
		background-color: #ffffff;
		border-bottom: solid 1px #d8dcde;
		.title {
			font-size: 30px;
			font-weight: 500;
			color: #545456;
			text-transform: capitalize;
		}
		.close {
			height: 22px;
			width: 22px;
			cursor: pointer;
      z-index: 50;

			svg {
				height: 22px;
				width: 22px;
				fill: #545456;
			}
		}
  }
  
  .content-popup__header__events-history{
    font-size: 17px;
    color: #01b4de;
    margin-right: 30px;
    cursor: pointer;
  }

	.content-body-wrapper {
		height: calc(100% - 97px);
		background: none !important;
		.ls__dialog-wrapper {
			height: 100%;
			display: flex;
			overflow: hidden;
			.latest-support, .support-new {
				overflow-y: auto;
				scroll-behavior: smooth;
			}
		}
		.latest-support, .device-menu-wrapper {
			.overlay {
				top: 176px;
			}
		}
		.latest-support {
			.support-side-panel {
				height: 100%;
				width: 30%;
				background-color: #ffffff;
				padding: 0px 30px;
				.support-side-panel-title {
					font-size: 20px;
					font-weight: 500;
					padding: 25px 0px;
					text-transform: capitalize;
					cursor: pointer;
					border-bottom: solid 1px rgba(123, 135, 147, 0.3);
					&.active {
						color: #01b5de;
					}
				}
			}
			.support-new {
				width: 100%;
				height: 100%;
				padding: 32px;
        .support-new__frequentlyAsked{
					background-color: #ffffff;
					border-radius: 3px;
  				box-shadow: 0 2px 10px 0 rgba(63, 67, 71, 0.08);
					.support-new__title {
						border-bottom: solid 1px rgba(123, 135, 147, 0.3);
						margin: 0px 30px;
						padding: 30px 0px;
						font-size: 20px;
						font-weight: 500;
					}
					.list-view {
						margin: 0px 30px;
						&:not(:first-child) {
							border-top: solid 1px rgba(123, 135, 147, 0.3);
						}
					}
					.list-view__wrapper {
						padding: 30px 0px;
					}
					.list-view__title {
						font-size: 17px;
          }
          
					.icon.menuNext {
						svg {
							width: 12px;
							height: 19px;
							opacity: 0.5;
						}
          }
          
          &.support-new__need-more-help{
            margin-top: 32px;
          }
				}
			}
		}
		
		.overlay {
			position: fixed;
			z-index: 105;
			background-color: $screen;
			width: 100%;
			left: 0;
			height: calc(100% - 265px);
		}
		&.content-scroll {
			overflow: scroll;
		}

		#grid_services_root {
			overflow: scroll;

			#grid_services_pdf_container {
            	height: calc(100% - 73px);
          	}

			.MuiSnackbar-root {
				bottom: 15%;
			}

			.MuiLinearProgress-root {
				background-color: #fff;
			}
		}
	}
	.ls__wrapper {
		padding: 32px;
    display: inline-grid;
    grid-template-columns: auto auto;
    grid-column-gap: 32px;
    grid-row-gap: 32px;
    width: 100%;

		.contact-details-card, .ls__contact-card {
			border-radius: 3px;
			box-shadow: 0 2px 10px 0 rgba(63, 67, 71, 0.08);
			background-color: #ffffff;
      padding: 32px;
      margin: 0;
    }
    
		.ls__contact-card {
			.contact-card {
				.cc__wrapper {
					.ls__talk-to-us {
						.cc__sub-heading {
							color: #7b8793;
						}
					}
				}
			}
		}
		.ls__heading {
			padding-bottom: 15px;
			opacity: 0.5;
			font-size: 17px;
			font-weight: bold;
			letter-spacing: 0.5px;
			color: #7b8793;
		}
		.contact-card {
			box-shadow: none;
			margin-top: 0;
			.cc__wrapper {
				padding: 0;
				.cc__heading {
					font-size: 20px;
					font-weight: 500;
					color: #545456;
				}
				.cc__sub-heading {
					font-size: 17px;
					font-weight: 400;
					line-height: 1.41;
          color: #545456;
          margin-top: 15px;
				}
			}
			.cc__action-button {
				.cc__text {
					font-size: 17px;
					font-weight: 500;
				}
				.cc__icon {
					width: 20px;
					height: 20px;
				}
			}
			.cc__hr {
				margin: 32px 0;
			}
		}
	}
}