@import '../../styles/mixin.scss';
@import '../../styles/font-style.scss';

.om-e_dialog__lc {
  &.om-enho-dialog {
    .e_dialog__header {
      box-shadow: none;
      padding: 15px 20px;
    }

    .e_dialog__body{
      background-color: #ffffff;
    }
  }

  .comp-radio-button{
    font-weight: 500;
    
    input{
      margin-right: 10px;
      border: 2px solid #01B5DE;

      &:before{
        width: 9px;
        height: 9px;
        display: block;
      }
    }
  }

  .om-lc-info, .lc-scheduled-mode, .lc-advanced-mode {
    padding: 15px 20px 25px;
  }

  .om-lc-info {
    color: #545456;

    p {
      font-size: 12px;
      line-height: 1.29;
      letter-spacing: normal;
      margin: 0;
    }
  }

  .save-button {
    @include save-button;
  }

  .lc-scheduled-mode{
    p{
      @include body-1;
      margin: 5px 0;
  
      span{
        font-weight: 500;
  
        &.value{
          color: #01b5de;
        }
      }
    }
  
    .lsam__time-wrapper{
      display: flex;
      align-items: center;
      font-size: 14px;
      margin: 25px 0 0;
      color: #545456;
      line-height: 25px;
      @include body-1;
  
      .lsam__timer-icon{
        margin-right: 10px;
  
        .icon{
          height: 100%;
        }
      }
  
      .lsam__time{
        display: flex;
        align-items: flex-end;
        font-size: 12px;
        font-weight: 400;
        margin-right: 15px;
        color: #545456;
        line-height: inherit;
  
        >div{
          font-size: 16px;
          font-weight: 500;
          color: #47B4DE;
          margin-left: 5px;
        }
      }
    }
  
    .overlay{
      position: fixed;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      background-color: #000000;
      opacity: 0.6;
      height: 100%;
      width: 100%;
      z-index: 1;
    }
  
    .time-keeper{
      position: fixed;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      z-index: 2;
  
      .react-timekeeper__time-dropdown{
        display: none;
      }

      .done-btn{
        text-align: center;
        padding: 16px 0; 
        background-color:#F4F4F4; 
        color:#68687F;
      }
    }
  
    .time-error{
      font-size: 12px;
      line-height: 16px;
      margin: 15px 0 20px;
      color: #E3655E;
    }
  }

  .lc-advanced-mode{
    .lc-advanced-mode__subtitle{
      color: #545456;
      font-size: 14px;
      margin: 11px 0px;
      @include title-1;
    }

    .lc-advanced-mode__cards{
      background: #F5F5F8;
      border: 1px solid #DFE5E9;
      border-radius: 10px;

      .lc-advanced-mode__card{
        margin-left: 15px;
        padding-top: 15px;
        padding-right: 15px;

        &:not(:first-child){
          border-top: 1px solid #D8E1E4;
        }

        .lc-advanced-mode__card__title{
          display: flex;
          align-items: center;

          h3{
            @include title-2;
            margin-top: 2px;
            color: #01B5DE;
          }
        }

        .lc-advanced-mode__card__desc{
          padding: 15px 0;

          p{
            @include body-1;
            margin: 1px 0;

            span{
              margin-right: 5px;
              font-weight: 600;
            }

            .soc-value{
              color: #01B5DE;
              font-size: 14px;
            }
          }
        }

        &.lc-advanced-mode__card__disabled{
          .lc-advanced-mode__card__title{
            label{
              input{
                border-color: #CBCBCB;
              }
            }

            h3{
              color: #545456;
            }
          }

          .lc-advanced-mode__card__desc{
            p, span{
              color: #A8ADAE;
            }
          }
        }
      }

      .lc-advanced-mode__card__battery{
        .lsam__slider{
          margin-bottom: 10px;
          
          div.multi-slider > div:first-child{
            margin: 0px !important;
          }
        }
      }
    }
  }
}

.night {
  .om-e_dialog__lc {
    .lc-scheduled-mode {
      .lsam__time-wrapper {
        color: #cbced1;
    
        .lsam__time {
          color: #cbced1;
        }
      }

      .time-keeper {
        .done-btn {
          background-color: #333;
          color: #fff;
          border-color: #333;
        }
      }
    }

    .lc-advanced-mode {
      .lc-advanced-mode__subtitle {
        color: #f5f5f8;
      }
  
      .lc-advanced-mode__cards {
        background: transparent;
        border-color: #4a4a4a;
  
        .lc-advanced-mode__card {
          &:not(:first-child) {
            border-color: #545456;
          }

          .lc-advanced-mode__card__desc {
            color: #ffffff;
          }

          &.lc-advanced-mode__card__disabled {
            .lc-advanced-mode__card__title {
              label {
                input {
                  border-color: #ffffff;
                }
              }
  
              h3 {
                color: #ffffff;
              }
            }
          }
        }

        .lc-advanced-mode__card__battery{
          .lsam__slider{
            color: #ffffff;
          }
        }
      }
    }
  }
}