.showup{
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 250;
  background-color: rgba(0, 0, 0, 0.7);

  .showup__wrapper{
    width: 100%;
    max-width: 350px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    .showup__inner-wrapper{
      margin: 20px;
      border-radius: 10px;
      background-color: #ffffff;
      box-shadow: 0 2px 10px 0 rgba(63, 67, 71, 0.08);
    }
  }

  .showup__body{
    max-height: 450px;
    overflow: hidden;
  }

  .showup__footer{
    padding: 20px;
    border-top: 1px solid #d8dcde;
  }

  .showup__actions{
    font-size: 16px;
    font-weight: 500;
    text-align: center;
    color: #01b4de;
  }

  .showup__action{
    span{
      padding: 7px 20px;
      min-width: 140px;
      max-width: 100%;
      border-radius: 20px;
    }

    &:not(:first-child){
      margin-top: 20px;
    }

    [role='button']{
      cursor: pointer;
    }
  }

  .showup__action__text{
    span{
      display: inline-block;
      padding: 0;
    }
  }

  .showup__action__text__cancel{
    color: #545456;
  }

  .showup__action__btn{
    span{
      display: inline-block;
      font-size: 14px;
      background-color: #01b4de;
      color: #ffffff;
    }
  }

  .showup__action__outline-btn{
    span{
      display: inline-block;
      font-size: 14px;
      border: 1px solid #01b4de;
      color: #01b4de;
    }
  }

  &.showup__action__inline{
    .showup__footer{
      padding: 0;
    }

    .showup__actions{
      display: flex;
      align-items: center;
    }

    .showup__action__btn{
      span{
        background-color: transparent;
        color: #01b4de;
        padding: 0;
      }
    }

    .showup__action{
      padding: 15px;
      font-weight: 400;
      font-size: 16px;
      width: 50%;
      
      &:not(:first-child){
        margin: 0;
        border-left: 1px solid #ecedee;
      }
    }
  }
}

.night{
  .showup{
    .showup__wrapper{
      .showup__inner-wrapper{
        background-color: #262627;
        box-shadow: 0 2px 10px 0 rgba(0,0,0,.25);
      }
    }

    .showup__footer{
      border-color: rgba(123,135,147,.3);
    }

    &.showup__action__inline{
      .showup__action{
        &:not(:first-child){
          border-left-color: rgba(123, 135, 147, 0.3);
        }
      }
    }

    .showup__action__text__cancel{
      color: #ffffff;
    }
  }

  .app-tutorial-banner {
    .body-wrapper {
      background-color: #505454;

      .bold-content{
        color:#FFFFFF;
      }
      .small-content{
        color:#FFFFFF;
        opacity: .81;
      }
    }
    .showup__footer{
      background-color: #505454;
      border-top: unset;
    }
  }
}


