.container.list-container, .pageContent {
  .sticky-notification {
    position: absolute;
    width: 100%;
    font-size: 12px;
    .comp-notification.comp-notification__failure {
      background-color: #545456;
    }
    .icon.cautionAlert {
      svg g {
        path:first-child {
          fill: #545456;
          stroke: #fff;
        }
        path:nth-child(2) {
          fill: #fff;
        }
      }
    }
  &.disabled {
    visibility: hidden;
  }
  }
}

.reconnect-to-envoy {
  padding: 85px 30px 40px 30px;

  .main-header {

    font-size: 12px;
    font-weight: bold;
    color: #f37320;
  }

  .sub-header {

    font-size: 16px;
    font-weight: bold;
    color: #545456;
    padding-top: 10px;
    @include title-2;
  }

  .submit-container {
    margin-left: 32px;
    width: 80%;
    height: 40px;

    .button {
      width: 100%;
      margin: 1em auto 1em;
      border-radius: 2em;
      background-color: $secondary;
      letter-spacing: normal;
      font-size: 16px;
      font-weight: 500;
      color: #f8f8ff; 
      padding-top: calc( 0.8em + 2px );
    }

    @media screen and (min-width:768px) and (max-width:1024px) {
      width: 75%;
    }
  }

  .card-details {
    padding-top: 20px;
    display: flex;
    padding-left: 32px;
  }

  .reconnect-to-envoy-confirm {
    transition: height .6s;
    height: 100%;
    .container-form {
      padding: 1em 30px;
      height: auto!important;
      margin: auto;
      text-align: center;
      .confirm-check {
        display: flex;
        justify-content: center;
        margin-bottom: 30px;
      }
        .title-confirmation {
          font-size: 16px;
          margin-bottom: 10px !important;
          font-weight: 500;
          color: #545456;
          text-align: center;
        }
        .sub-title-confirmation {
          margin-bottom: 30px;
          font-size: 12px;
          line-height: 1.25;
          text-align: center;
          color: #6f6f8c;
        }
        .confirm-notice{
          margin-bottom: 30px;
          font-size: 12px;
          line-height: 1.25;
          color: #f37320;
          text-align: center;
        }
      
      .button {
        width: 75%;
        margin: 1em auto;
        font-size: 16px;
        font-weight: 500;
        border: solid 1px #01b4de;
        border-radius: 2em;
        background-color: #01b4de;
        color: #ffffff;  
      }
    }
    &.disabled{
      display: none;
    }
    &.enabled{
      display: block !important;
    }
  }
  .disabled{
    display: none;
  }
  .overlay{
    width:100%;
    height: calc(91% - 134px);
    border-radius: unset !important;
    margin: 0 !important;
    top:134px;
    left:0;
  }
}
.asSubmenu{
  .reconnect-to-envoy {
    padding-bottom: 10.5vh;
    padding-top: 10px;
  }
  .sticky-notification.reconnectToEnvoy{
    position: sticky;
    top: 0;
    .comp-notification{
      position: relative;
      top: 0;
      &.comp-notification__failure{
        background-color: #545456;
        z-index: 2;
        svg g{
          path:first-child{
            stroke: #fff;
          }
          path:last-child{
            fill:#fff;
          }
        }
      }
    }
  }
}
.night{
  .reconnect-to-envoy {
    .sub-header {
      color: #f8f8ff;
    }
    .envoyGroupMobileData {
      #hide-egmd {
        display: none;
      }
    }
  }
  .reconnect-to-envoy-confirm {
    .title-confirmation {
      color: #f8f8ff !important;
    }
    .sub-title-confirmation {
      color: #a2a6a9 !important;
    }
    .cancel{
      background-color: #262627 !important;
      color: #01b4de !important;
    }
  }
}


// Offline styles

.reconnect-to-envoy.offline-mode {
  padding: 19px 14px 19px 20px;

  .sub-header {
    font-size: 16px;
    color: #545456;
    letter-spacing: 20;
    // line-height: 21;
    font-weight: normal;
    font-weight: 500;
  }
}