.browser {
  .currency-conversion-menu {
    padding: 0px 32px;
    display: block;
    .currency-conversion-menu-list {
      padding: 0px;
      .currency-factor-menu-list-wrapper, .currency-conversion-menu-list-wrapper {
        .list-view__currencyFactor, .list-view__showcurrency {
          .list-view__wrapper {
            .list-view__title, .list-view__currency {
              font-size: 17px;
              font-weight: 500;
            }
            .list-view__subtitle {
              margin-top: 7px;
              font-size: 15px;
              font-weight: 300;
              color: #7b8793;
            }
          }
        }
      }
      .conversion-factor-wrapper {
        margin-top: 24px;
        #currencyValueEdit {
          padding: 10px 0px;
          border: none;
          &::-webkit-outer-spin-button,
          &::-webkit-inner-spin-button {
            -webkit-appearance: none;
            margin: 0;
          }
        }
      }
      .conversion-factor-header,
      .conversion-factor-unit {
        font-size: 17px;
      }
      .list-view__showcurrency, .list-view__subtitle  {
        padding-bottom: 0px !important;
      }
      .currency-note-menu-list-wrapper {
        font-size: 12px;
        padding-top: 20px;
        font-weight: normal;
        color: #e3655e;
        border-top: 1px solid #d8dcde;
      }
      .currency-conversion-menu-list-wrapper, .currency-factor-menu-list-wrapper {
        padding: 20px 0px;
        margin: 0px;
        border-bottom: solid 1px #d8dcde;
      }
      .currency-factor-menu-list-wrapper {
        border-bottom: none;
        padding-bottom: 11px;
        .menuNext {
          display: flex;
          height: 20px;
          svg {
            width: 7px;
            height: 20px;
          }
        }
      }
      .list-view.list-view__currencyFactor {
        margin: 0;
      }
    }
    .list-view__content, .list-view__content-wrapper {
      width: 100%;
    }
    .list-view__content-wrapper {
      padding-bottom: 9px;
    }

    .currency-submit-button {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 65px 0px;
      button {
        width: 315px;
        height: 50px;
      }
    }
  }
  .currency-conversion-notification {
    .comp-notification {
      margin-top: 0 !important;

      &.comp-notification__active {
        position: fixed;
      }
    }
  }
}