/* Energy Heading */
.browser {
    .energy-heading {
        &.desktop {
            margin: 0px;
            height: 97px;
    
            .energy-summary-info {
                display: flex;
                flex-wrap: wrap;
                .title {
                    flex-basis: 100%;
                    font-size: 30px;
                    font-weight: 500;
                }
                .updated-info {
                    font-size: 15px;
                    font-weight: 300;
                    padding: 0;
                    order: 1;

                    .connection-type-icon {
                      margin-right: 5px;

                      .wifi, .ethernet, .cellular{
                        width: auto;
                        height: 15px;

                        svg {
                          width: inherit;
                          height: inherit;
                        }
                      }

                      .icon {
                        margin:0;
                      }
                  }
                  
                  .updated-time {
                    margin-left: 5px;
                  }
                }
                .system-status {
                    order: 0;
                    padding-right: 20px;
                    .right-check {
                        svg {
                            height: 15px;
                            width: 15px;
                        }
                    }
                    .system-normal, .system-error {
                        font-size: 15px;
                    }
                    .systemError {
                        margin-right: 5px;
                    }
                }
            }
    
            .weather-and-live-status{
                display: flex;
                flex-direction: row-reverse;
                align-items: center;
                .weather-wrapper{
                    .weather {
                        font-size: 20px;
                        font-weight: 300;              
                        opacity: 1;
                        img {
                            margin-right: 8px;
                            width: auto;
                            height: 23px;
                        }
                    }
                }

                .live-status-detail__wrapper {
                    margin-right: 32px;
                    display: flex;

                    & > div:last-child { margin-left: 20px; }
                }

                .live-status-detail {
                    display: flex;
                    text-transform: uppercase;
                    align-items: center;
                    justify-content: flex-end;
                    font-size: 14px;
                    font-weight: 500;
                    line-height: 0.86;

                    .live-status-info {
                        margin-left: 5px;
                        white-space: nowrap;
                    }

                    .onGrid, .generatorOn, .offGrid, .generatorOff {
                        width: 10px;
                        height: 10px;
                        object-fit: contain;
                        border-radius: 50%;
                    }

                    .onGrid { background-color: #35c37d; }
              
                    .offGrid { background-color: #ff6464; }

                    .systemError,
                    .systemError svg {
                      width: 15px;
                      height: 15px;
                    }
                    
                    .systemError svg path { fill: #FF8B0C; }

                    button.live-status-refresh {
                      width: 15px;
                      height: 15px;
                      padding: 0;
                      margin: 0 0 0 5px;
                      border: unset;
                      background: unset;
                      cursor: pointer;
                
                      span.icon,
                      span.icon svg {
                        width: 15px;
                        height: 15px;
                      }
                    }
                }

                .live-status-wrapper {
                    margin-top: 0px;
                    margin-right: 32px;
                    .live-status {
                        height: 25px;
                        border-radius: 15px;
                        padding: 3px 13px;
                        .live-status-text {
                            font-size: 14px;
                            font-weight: 500;
                            letter-spacing: 0.1px;
                            color: #ffffff;
                            white-space: nowrap;
                            padding-top: 4px;
                        }
                    }
                }
            }
        }
    }
}
    
.night {
    .energy-heading {
        &.desktop {
            .energy-summary-info {
                .title {
                    color: #f8f8ff;
                }
                .updated-info{
                    color: #c6c7c9;
                }
            }
        }
    }
}
