.fwgt-pwd {
  height: 50%;
  margin: 20px 22px;
  overflow: auto;
}
.forgot-password {
    transition: height 600ms;
    width: 100%;

    &.show {
        display: block;
    }
    &.hide {
        display: none;
    }
    
    .button {
        width: 100%;
        margin: 1.5em auto 1em;
        border-radius: 2em;
        background-color: $secondary;
        color: #fff;
        @media (min-width:768px) and (max-width:1024px) {
            width: 70%;
        }
    }

    .container-form {
      .title {
        margin: 0 auto;
        margin-bottom: 2.5em;
        @include body-1;
      }
      .item {
        margin: 0 auto;
      }
    }

    .forget-password-button {
      font-size: 16px;
    }
  
    .item {
      position: relative;
      @media (min-width:768px) and (max-width:1024px) {
        width: 70%;
      }
      &.error {
        label { height: 2em; }
      }
      input {
        padding: 10px 0 10px 28px;
        font-size: 16px;
        line-height: 24px;
        vertical-align: middle;
        padding-bottom: 2px;
        &::placeholder {
          font-weight: 300;
          font-size: 16px;
          color: #939598;
       }
     }

      .icon {
        position: absolute;
        margin-top: calc( 0.5em + 5px );
        height: 14px;
        &.email svg { height: 100% }
      }
      
      label {
        color: #ff5959;
        height: 0;
        transition: height 300ms;
        font-size: 12px;
        overflow: hidden;
        display: inline-block;
        line-height: 2em;
      }
    }
  }

  .forgot-password-confirmation {
    transition: height 600ms;
    padding: 4em 1em;
    height: calc(100vh - 30%);
    justify-items: center;
    align-items: center;
    &.show {
        display: flex !important;
    }
    &.hide {
        display: none;
    }
    
    .button {
        margin: 1.5em auto 1em;
        width: 159px;
        height: 40px;
        border-radius: 20px;
        background-color: #fff;
        border: 1px solid;
        border-color: #00b6de;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0;
        &.text {
            color: #00b6de !important;
        }
    }

    .button:active{
        background-color: #fff;
        opacity: .7;
        &.text {
            color: #00b6de !important;
        }
    }

    .confirm-check {
        display: flex; 
        justify-content: center;
        padding-bottom: 2em;
        &.confirm-check svg path { fill: #5cd482 }
    }

    .email-confirmation {
        font-weight: bold;
    }
  }
  .forgot-header{
    left: 55px;
    font-size: 16px;
  }