.grid-container {
  height: 56px;
  border-radius: 3px;
  border: solid 1px #d8dcde;
  background-color: #ffffff;
  margin: 15px 0 20px 0;

  .system-estimator-iframe {
    .iframe-header {
      min-height: 134px;
      padding: 77px 50px 20px 15px;
    }
    
    iframe { height: calc(100% - 46px); }

    .exit-system-estimator {
      height: calc(91% - 46px);
      top: 46px;
    }
  }

  @media screen and (max-width:360px) {
    height: 54px;
  }

  @media screen and (min-width:768px) and (max-width:1024px) {
    height: 100px;
  }

  @media screen and (min-width:768px) and (max-width:1024px) and (orientation : landscape) {
    height: 76px;
    margin: 10px 0;
  }

  > div{
    height: inherit;
    width: 33.3%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }

  .grid-columns {
    width: 33.33%;
    padding: 0 10px;
    .header {
      font-size: 12px;
      color: #7b8793;
      word-break: break-word;

      @media screen and (min-width:768px) and (max-width:1024px){
        font-size: 18px !important;
      }
    }

    .info-icon{
      display: flex;
      cursor: pointer;
      span{
        padding-left: 3px;
      }
    }

    .energy-storage {
      padding-top: 6px;

      & > p{
        margin: 0;
      }
    }

    .text {
      font-size: 16px;
      color: #6c7073;

      &.addstorage__text {
        font-size: 14px;
        line-height: 16px;
        .addstorageText{
          font-weight: initial;
          color: #545456;
        }
      }

      @media screen and (min-width:768px) and (max-width:1024px){
        font-size: 22px !important;
      }

      @media screen and (max-width:320px) {
        font-size: 14px !important;
      }
    }

    .cta{
      padding: 5px 20px;
      border-radius: 13px;
      background-color: #01b4de;
      font-size: 15px;
      color: #ffffff;
      margin-top: 8px;
      width: max-content;
    }

    .text-units {
      position: relative;
      font-size: 10px;
      font-weight: normal;
      color: #6c7073;
      margin-left: 2px;
      @include body-1;

      @media screen and (min-width:768px) and (max-width:1024px){
        font-size: 15px !important;
      }
    }

    .energy-grid__icon {
      float: left;
      height: 16px;

      &.addstorage__icon {
        margin-right: 5px;
      
        .icon {
          svg {
            g > g > rect,
            g > g > path { fill: #00b6de; }
          }
        }
      }

      @media screen and (min-width:740px) and (max-width:1024px) {
        span{
          svg{
            width:18px;
            height: 18px;
          }
        }
      }
    }
  
  }

  .grid-columns-two {
    width: 50%;
    padding: 0 15px;
  }

  .grid-columns-three {
    width: 30% !important;
    padding-left: 10px !important;
  }

  .grid-columns__addStorage {
    width: 40% !important;
    padding-left: 10px !important;
  }

  .grid-seperator {
    border-left: solid 1px #d8dcde;
    height: 45px;

    @media screen and (max-width:360px) {
      height: 40px;
    }
  }

  .grid-sub-container {
    width: 100% !important;
    .power{
      .dataContainer{
        display: flex;
        align-items: baseline;
        .text{
          margin-left: 5px;
        }
      }
    }

    .storage-newUI{
      display: flex;
      justify-content: space-between;
      width: 100%;
      align-items: center;
      padding: 13px 15px ;
      .iconWrapper{
        display: flex;
        align-items: center;
        .icon.ensembleBatteryCharge svg{
          height: 30px;
        }
        .text{
          margin-left: 10px;
          color: #a2abb3;
        }
      }
    }

    .add-storage{
      box-sizing: initial;
      .content{
        display: flex;
        .details{
          margin: 6px;
           & > *{
            padding-left: 0 !important;
          }
        }
      }

      .info{
        margin: 3px 0 0 0;
        font-size: 15px;
        color: #7B8793;
      }
    }
  }

  &.grid-container__ensemble {
    height: auto;
    font-size: 16px;

    & .grid-sub-container {
      padding: calc(0.9375em - 2px) 0;

      & .grid-columns {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        border-right: 1px solid #d8dcde;
        padding-right: 10px;

        &:last-child { border: unset; }

        & .energy-grid__icon {
          height: 1.875em;
          float: unset;
          margin-right: 0.625em;

          & span.icon,
          & span.icon svg { height: 100%; }
        }

        & .energy-grid__Details {
          flex-grow: 1;
          display: flex;
          flex-direction: column;

          & p.header {
            margin: 0;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            font-size: unset;


            & strong {
              font-size: 12px;
              font-weight: lighter;
              line-height: 1em;
              letter-spacing: 0.36px;
              color: #a2abb3;
              @include caption;
              padding-top: 5px;
            }

            & span.icon {
              width: 1em;
              height: 1em;
              margin-left: 0.3125em;
              display: flex;
              align-items: center;
              justify-content: center;

              & svg { width: 0.875em; height: 0.875em; }
            }
          }

          & .description {
            margin: 0.125em 0 0;
            font-size: 12px;
            font-weight: 500;
            color: #545456;
            @include body-2;

            & strong {
              @include body-2; 
            }
            & strong.lessBattery { color: #ff6464; }
            & span {
              font-size: 12px;
              font-weight: normal;
              white-space: nowrap;
              text-align-last: left;
              opacity: 0.5; 
            }
          }
        }
      }
    }

    @media screen and (min-width:480px) { font-size: 20px; }
  }
}

.night {
  .grid-container {
    &.grid-container__ensemble {
      & .grid-sub-container {
        & .grid-columns {
          border-color: #3c4044;
          & .energy-grid__Details {
            & .description { color: #f8f8ff !important; }
          }
        }
      }
    }
  }
}

@media screen and (max-width:320px){
  .grid-container {
    .grid-columns {
      padding: 0 0 0 2px;
    }
    .grid-columns-two {
      padding: 0 15px;
    }
  }
}

@media screen and (min-width:321px) and (max-width:360px) {
  .grid-container {
    .grid-columns {
      padding: 0 5px;
    }
    .grid-columns-two {
      padding: 0 15px;
    }
  }
}

@media screen and (min-width:768px) and (max-width:1024px) {
  .grid-container {
    .grid-columns {
      padding: 0 15px;
    }
  }
}
