.icon {
    height: 67%;
    display: block;
    text-align: center;
    align-self: center;
    transition: opacity 0.5s;
    .icon-orange {
        fill: #f37321;
    }
    &.history svg {
        height: 65%;
        margin-top: 0.5em;
    }
    &.layout svg {
        height: 80%;
        margin-top: 0.3em;
        margin-left: 0.2em;
    }
    &.menu svg {
        height: 70%;
        margin-top: 0.3em;
        margin-left: 0.8em;
    }
    &.today svg {
        height: 60%;
        margin-top: 0.6em;
    }
    &.next svg,
    &.prev svg {
        height: 60%;
        margin-top: 0.4em;
    }
    &.dropdown svg {
         width: 20px; 
         height: 20px; 
    }
    &.calendar svg {
        height: 93%;
    }
    &.grid svg {
        height: 100%;
    }
    &.battery svg {
        height: 100%;
    }
    &.close svg {
        height: 120%;
    }
    &.enabled svg {
        height: 60%;
    }
    &.disabled svg {
        height: 60%;
    }
    &.unavailable svg {
        height: 65%;
        margin: -0.2em 0.4em;
    }
    &.expand svg {
        height: 160%;
    }
    &.password svg {
        margin-top: -1em;
        height: 130%;
    }
    &.home svg {
        height: 60%;
    }
    &.email svg {
        height: 80%;
        path {
            stroke-width: 0;
        }
    }
    &.link svg {
        height: 110%;
        path,
        polyline,
        line {
            fill: none;
            stroke-width: 0.1em;
        }
    }
    &.phone svg {
        path {
            fill: none;
            stroke-width: 0.1em;
        }
    }
}