.browser {
  width: 100vw;
  height: 100vh;
  background: linear-gradient(white 20%, $super-light);
  .ios-browser {
    #page-wrapper *, .content-popup-wrapper .reports .card.body {
      perspective: 100px;
      backface-visibility: hidden;
    }
  }
  .tablet-view {
    .titlebar .period-select li {
      margin: 0px 15px;
      &.active {
        padding: 4px 20px;
      }
    }
  }
  #content {
    overflow: scroll;
  }
  .page-view {
    min-width: 1024px;
   }
  #content, .page-view {
    height: 100%;
    background-color: #fafafb;
    
    #page-container {
      overflow-y: scroll;
      overflow-x: hidden;
      height: calc(100% - 170px);
      background: none;
    }
    .latest-status, .energy, .layout {
      #page-wrapper, .content-body-wrapper {
        max-width: 1440px;
        display: flex;
        flex-direction: column;
        margin: auto;
        background: #ffffff;
      }
    }
    .latest-status, .energy, .layout {
      #page-wrapper {
        height: 100%;
        display: flex;
        flex-direction: column;
        &.hide {
          display: none;
        }
      }
    }
  }
  .notification {
    height: auto;
    bottom: 90px;
    z-index: 107;
  }
  .latest-status, .energy, .layout {
    height: calc(100% - 170px);
  }
  .popup-container, .header-menu-wrapper {
    .overlay {
      position: absolute;
      left: 50%;
      top: 50%;
      background-color: #000000;
      opacity: 0.6;
      height: 100%;
      width: 100%;
      transform: translate(-50%, -50%);
      z-index: 105;
    }
  }

  .popup-container {
    .overlay {
      position: fixed;
      z-index: 106;
    }
  }
  
  .accounts-wrapper {
    overflow: scroll;
    -webkit-overflow-scrolling: touch;
    overflow-y: scroll;
    height: 100%;
    .offline-background {
      height: 96%;
      padding-bottom: 0px;
      .offline-container {
        position: relative;
      }
    }
  }
  .data-not-available {
    background: #ffffff;
    display: flex;
    margin: auto;
  }
  #popUpWrapper {
    max-width: 90%;
  }
  
  .comp-notification {

    .icon {
      width: 24px;
      height: 24px;
    }

    &.default-success, &.default-failure {
      z-index: 107;
      background-color: #545456;
    }
  }
}


.grid-control__mobile {
  .overlay {
    position: absolute;
    left: 50%;
    top: 50%;
    background-color: #000000;
    opacity: 0.6;
    height: 100%;
    width: 100%;
    transform: translate(-50%, -50%);
    z-index: 105;
  }
}

.night {
  .grid-control__mobile {
    .overlay {
      backdrop-filter: blur(2px);
      background-color: rgba(0, 0, 0, 0.75);
    }
  }
}