.searchInput {
  margin: 10px 0;
  position: relative;

  .searchInput input {
    width: 100%;
    border-radius: 5px;
    border: solid 0.5px rgba(123, 135, 147, 0.3);
    padding: 7px 15px;
    font-size: 14px;
    font-weight: 300;
    opacity: 0.8;
    color: #545456;
  }
  
  .icon.search {
    width: 17px;
    height: 17px;
    display: inline-block;
    top: 0;
    right: 0;
    position: absolute;
    margin: 7px 15px 0 0;
    &.close {
      width: 12px;
      height: 12px;
      margin-top: 9px;
      svg {
        width: 12px;
        height: 12px;
        fill: #545456;
      }
    }
  }

  .spin-container {
    width: 20px;
    height: 20px;
    margin: 95px 63px 0 0;
    display: inline-block;
    position: absolute;
    top: 0;
    right: 3%;
    left: auto;
    transform: translateY(-12px);

    & .spinner {
      font-size: 1em;

      &:before { border-top-color: #545456;}
    }
  }

  .icon.close {
    cursor: pointer;
    float: right;
    font-size: 14px;
    margin-top: -24px;
    margin-right: -4px;
    height: 15px;
  }
}
