.date-inline-picker {

  margin: 1em 0;

  .input {
    display: inline-block;

    &.disabled:focus { border-color: #c6c8ca; }
  }

  .done {
    position: absolute;
    z-index: 5;
    width: 100%;
    height: 3em;
    line-height: 2em;
    text-align: right;
    font-size: 0.8em;
    text-transform: uppercase;
    font-weight: 600;
    letter-spacing: 0.06em;
    .button { 
      margin: 0.3em 0 0 0 !important;
      border-radius: 32px 32px 0 0 !important;
    }
  }

  .label {
    position: absolute;
    padding: 0.6em;
    margin: 0 0 0 2em;
    width: 80%;
    font-weight: 400;
    background-color: $background;
  }

  .rdt {
    top: 1.1em;
  }
  .rdtOpen .rdtPicker {
    height: auto;
    border-width: 6px;
  }
  .rdtPicker { 
    width: 100%;
    height: 0;
    padding: 0;
    margin-top: 0;
    display: block;
    position: relative;
    border-style: solid;
    border-color: $super-light;
    border-width: 0 6px;
    border-radius: 0 0 1em 1em;
    box-shadow: 0.5px 0.5px 2px $shadow;
    transition: all 300ms ease-in-out;
    overflow: hidden;
    table {
      border-spacing: 0;
    }

    th, td { line-height: 4em; }
    .rdtDays { th, td { line-height: 2em; }}
    .rdtSwitch,
    .dow {
      font-weight: 400;
    }
    .rdtDay:hover { background: $background; }
    .rdtActive, .rdtActive:hover {
      background-color: $super-light;
      font-weight: 400;
      text-shadow: none;
      span {
        min-width: 1.9em;
        padding: 0.05em 0.5em 0;
        display: inline-block;
        border-radius: 1.8em;
        line-height: 1.8em;
        background-color: $secondary !important;
      }
    }
    .rdtDisabled {
      color: $light;
    }
    .rdtYears, .rdtMonths {
      .rdtPrev, .rdtNext {
        width: 20%;
      }
    }
    .between {
      background-color: $super-light;
    }
  }
}
.night .date-inline-picker{
  .rdt {
    background-color: #262627 !important;
    .rdtPicker {
      border-style: none !important;
      border-color: none !important;
      border-width: 0 !important;
      border-radius: none;
      thead tr:first-child th{
        color: #fff;
        &:hover{
          background-color: transparent;
        }
      }
      .dow{
        color: #fff;
        font-weight: 400;
        opacity: 0.7;
      }
      table,
      tr,
      td {
        border-collapse: collapse;
        border: none !important;
        thead {
          background-color: #333333;
          th {
            border: none;
          }
        }
        tbody {
          background-color: #262627 !important;
          td {
            &.rdtOld,
            &.rdtNew {
              color: #636363 !important;
            }
            &.rdtToday {
              background-color: #262627 !important;
              color: #fff;
            }
            &.rdtDay,&.rdtMonth,&.rdtYears {
              color: #fff;
              &.rdtDisabled{
                color: #999999;
              }
              &.between{
                background-color: transparent ;
              }
              &.rdtActive, &:hover{
                background-color: transparent;
              }
            }
          }
        }
      }
    }
  }
}