@import "fonts";
.latest-support{
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  font-weight: normal;
  letter-spacing: normal;
  width: 100%;
  height: 100%;
  background-color: #fafafb;

  .comp-notification.comp-notification__failure {
    background-color: #545456;
  }

  .ls__dialog-wrapper {
    height: calc(100% - 120px);
    overflow-y: auto;
    scroll-behavior: smooth;
  }

  .ls__header {
    min-height: 134px;
    background-color: #01b5de;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 77px 20px 15px;
    margin: 0 auto;
    color: #fff;
    font-size: 19px;
    font-weight: normal;

    &.contact-support {
      justify-content: flex-start;

      .icon {
        opacity: 1;
        display: inline-block;
        margin-left: 8px;
        margin-right: 15px;
        height: 32px;
        fill: #fff;
        display: flex;
        align-items: center;

        svg {
          margin-top: 0px;
        }
      }
    }
  }

  .ls__wrapper{
    padding: 20px;

    .comp-notification{
      position: absolute;
      top: $header-height;
      left: 0;
    }
  }

  .ls__contact-enphase{
    .ls__talk-to-us{
      margin-top: 20px;
    }
  }
}

.support-new {
.support-wrapper {
  .app-tutorial-card {
    .grey-block{
      height: 10px;
      background-color:#f6f7fb;
    }
    .list-view__content {
      align-items: center;
    }
  }
  .support-new__frequentlyAsked {
    margin-bottom: 10px;

    .support-new__title {
      padding: 20px;
      font-size: 14px;
      text-transform: uppercase;
      color: #545456;
      opacity: .5;
      @include caption;
    }

    .list-view__content {
      width: 95%;
    }

    .list-view__browseallfaq {
      .list-view__title {
        color: #01b5de;
      }
    }

    &.support-new__need-more-help {
      .list-view__content .list-view__title {
        color: #01b5de !important;
      }
    }
  }
}
}

/* Dark Mode */
.night{
  .latest-support{
    background-color: #262627;

    .ls__header {
      background-color: #333;
    }

    .contact-details-card{
      .cc__sub-heading{
        color: #a2a6a9;
      }
    }
  }
  .support-new {
    .support-new__frequentlyAsked {
      .support-new__title {
        color: #8e9091;
        opacity: 1;
      }
    }
  }
    .grey-block {
      background-color: #2c2c2c !important;
      border-top: unset !important;
    }
}

  .system-statistics{
    .app-tutorial-card {
      .grey-block {
        height: 25px;
        background-color: #f6f7fb;
        border-top: 1px solid #d8dcde;;
      }

      .list-view__content {
        align-items: center;
      }
    }
  }