@import './colors.scss';

.system-estimator {
    height: calc(100% - #{$footer-height});
    z-index: 11;

    &.se-webview {
        padding: 0;
        top: #{$footer-height};

        & .appDemo-header {
            width: 100%;
            max-width: 775px;
            padding: 0 20px;
    
            & .appDemo-get-started {
                & h3 { text-align: center; }
    
                & button {
                    max-width: 295px;
                    margin-left: auto;
                    margin-right: auto;
                }
            }
        }
    }

    & .appDemo-header { margin-bottom: 0; }
}

.otp-overlay {
    position: absolute;
    left: 50%;
    top: 50%;
    background-color: #000000;
    opacity: 0.2;
    height: 100%;
    width: 100%;
    transform: translate(-50%, -50%);
    z-index: 107;
  
    &.otp-overlay__mobile {
      position: fixed;
    }
  }
  
  .otp-spin-container {
    position: absolute;
    width: 5em;
    height: 5em;
    border-radius: 50%;
    top: 50%;
    left: 50%;
    margin-top: -2.5em;
    margin-left: -2.5em;
    z-index: 107;
    opacity: 1;
    transition: opacity 600ms;
  
    &.otp-spin-container__mobile { position: fixed; }
  
    .spinner:before {
      content: '';
      box-sizing: border-box;
      position: absolute;
      top: 50%;
      left: 50%;
      width: 1.2em;
      height: 1.2em;
      margin-top: -0.6em;
      margin-left: -0.6em;
      border-radius: 50%;
      border: 0.1em solid $super-light;
      border-top-color: $secondary;
      animation: spinner 700ms linear infinite;
    }
  }

.scrim-pop-up{
    z-index: 10000;
}

.popup-container {
    &.verify-otp, &.verify-mobile{
        z-index: 10000;
    }
}

#se-footer-nav.nav {
    & li {
        max-width: 50%;

        & .group.active {
            color: $secondary;
        
            & path,
            & rect { fill: $secondary; }
        }
    }

    &.se-footer-webview {
        display: flex;
        justify-content: space-between;
        padding: 24px 32px;
        background-color: $secondary;
        box-shadow: unset;
        bottom: unset;
        top: 0;

        & .appDemo-logo {
            max-width: 170px;
            display: flex;

            & > span.enphaseLogo,
            & > span.enphaseLogo svg {
                width: 100%;
                height: auto;
                text-align: left;

                & .a,
                & .b,
                & .c { fill: $background; }
            }
        }

        & ul {
            padding: 0;

            & li { max-width: unset;

                .icon.menu {
                    height: unset;

                    svg {
                        height: unset;
                        padding: 0 0 .3em .5em;
                        margin: 0;

                        rect {
                            fill: #ffffff;
                            margin-right: 10px;
                        }
                    }
                }

                &:first-child { display: none; }

                &.post-login_wos-support,
                &.post-login_wos-notification {
                    margin-right: 30px;

                    .icon {
                        height: 29px;

                        svg {
                            width: auto;
                            height: 100%;

                            path {
                                stroke: #00b6de;
                                fill: #00b6de;

                                &:first-child {
                                    stroke: #FFFFFF;
                                    fill: #FFFFFF;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

.system-estimator-iframe {
    width: 100% !important;
    height: 100%;
    display: block !important;
    background-color: #ffffff;
    overflow-x: hidden;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 11;

    & .spin-container {
        -webkit-backdrop-filter: blur(2px);
        backdrop-filter: blur(2px);
        background-color: rgba(0, 0, 0, 0.75) !important;
    }

    & .iframe-header {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        padding: 43px 20px 15px;
        background-color: $primary;

        &.existing {
            background-color: #01b5de;
        }

        & button {
            width: 22px;
            height: 20px;
            padding: 0;
            margin: 1px 35px 1px 0;
            border: unset;
            background: unset;
            outline: unset;

            & span.icon {
                height: 20px;

                & svg {
                    height: 20px;
                    margin: 0;
                    fill: #ffffff;
                }
            }
        }

        & p {
            margin: 0;
            font-size: 19px;
            font-weight: normal;
            line-height: 22px;
            letter-spacing: normal;
            color: #ffffff;
        }
    }

    & iframe {
        width: 100%;
        height: calc(100% - 80px);
    }

    &.render-at-iOS iframe { height: calc(100% - 80px); }

    & .exit-system-estimator {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        -webkit-backdrop-filter: blur(2px);
        backdrop-filter: blur(2px);
        background-color: rgba(0, 0, 0, 0.75);
        overflow: hidden;
        position: fixed;
        left: 0;
        top: 0;
        z-index: 12;

        & .dialog-wrapper {
            max-width: 285px;
            border-radius: 10px;
            background-color: #ffffff;

            & >  p {
                margin: 0;
                padding: 20px;
                font-size: 14px;
                font-weight: normal;
                line-height: 1.57;
                letter-spacing: normal;
                color: #545456;
            }

            & .dialog-actions {
                display: flex;
                justify-content: flex-start;
                justify-content: center;
                border-top: 1px solid #e5e5e5;

                & button {
                    width: 50%;
                    padding: 16px 0;
                    border: unset;
                    outline: unset;
                    font-size: 16px;
                    font-weight: normal;
                    line-height: normal;
                    letter-spacing: normal;
                    text-align: center;
                    color: #545456;
                    background-color: unset;

                    &:last-child {
                        border-left: 1px solid #e5e5e5;
                        font-weight: 500;
                        color: #01b4de;
                    }
                }
            }
        }
    }

    &.se-iframe-webview,
    &.se-without-site {
        & .iframe-header {
            padding: 24px 32px;
            justify-content: space-between;
            flex-direction: row-reverse;

            & button {
                margin-right: 0;
    
                & span.icon {
                    height: 20px;
                    position: relative;
                    top: 0;
                    right: 0;
    
                    & svg {
                        height: 20px;
                        margin: 0;
                        fill: #ffffff;
    
                        & polygon { fill: #ffffff; }
                    }
                }
            }
        }

        & iframe { height: calc(100% - 70px); }
    }

    &.se-without-site {
        top: #{$footer-height};

        & .iframe-header { padding: 15px 32px;}

        & iframe { height: calc(100% - 52px); }
    }

    &.se-with-site {
        height: 100%;
        position: relative;
        z-index: unset;

        & iframe { height: 100%; }

        & .exit-system-estimator {
            position: absolute;
        }
    }
}

#content {
    & .system-estimator-iframe {
        z-index: 999;

        &.se-webview { z-index: unset; }
    }
}

.browser {
    .system-estimator-iframe {
        z-index: unset !important;
        overflow: hidden !important;
    }
}

.night {
    .system-estimator-iframe {
        & .exit-system-estimator {
            backdrop-filter: blur(2px);
            background-color: rgba(0, 0, 0, 0.75);

            & .dialog-wrapper {
                background-color: #262627;

                & > p { color: #d8dcde; }

                & .dialog-actions {
                    border-color: #333537;

                    & button {
                        color: #ffffff;

                        &:last-child {
                            color: #01b4de;
                            border-color: #333537;
                        }
                    }
                }
            }
        }
    }

    .postlogin__get-system {
        background: #272728;

        & > div > div h6 { color: #FFFFFF; }
    }

    #se-footer-nav.nav {
        li {
            .group {
                color: #FFFFFF;

                .icon path,
                .icon rect { fill: #FFFFFF; }

                &.active {
                    color: #00b6de;

                    .icon path,
                    .icon rect { fill: #00b6de; }
                }
            }
        }
    }
}

.sgip-incentive {
    height: 100% !important;
    position: relative;

    iframe { height: 100%; }
}

.postlogin__get-system {
  width: calc(100% - 56px);
  margin: 28px;
  padding: 15px 20px;
  border-radius: 12px;
  background: $background;
  position: absolute;
  left: 0;
  bottom: 0;

  & > div {
    display: flex;

    .icon {
      width: 20px;
      height: 20px;
      overflow: hidden;
      align-self: flex-start;

      svg g > g path { fill: $secondary; }
    }

    & > div {
      h6 {
        margin: 0;
        font-size: 16px;
        line-height: 21px;
        font-weight: normal;
        letter-spacing: 0px;
        color: #545456;
      }

      p {
        margin: 0;
        font-size: 12px;
        line-height: 16px;
        letter-spacing: 0px;
        color: #7B8793;
      }
    }
  }

  button {
    width: 100%;
    padding: 6px 10px;
    margin: 12px 0 0;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0px;
    color: #FFFFFF;
    border: unset;
    border-radius: 17px;
    background: #01B4DE;
    position: relative;
    @include title-2;
  }
}

.postlogin__web {
  background: $background;

  .postlogin__web-title {
    padding: 20px 32px;
    border-bottom: 1px solid rgba(123,135,147,.3);
    font-size: 26px;
    line-height: 34px;
    font-weight: 500;
    letter-spacing: .3px;
    color: #5f6062;
  }

  .postlogin__web-content {
    display: grid;
    grid-template-columns: 25% 50% 25%;

    & > div { padding: 32px; }

    .postlogin__web-left {
      .icon {
        width: 20px;
        height: 20px;
        margin-top: 3px;
        overflow: hidden;
        align-self: flex-start;

        svg g > g path { fill: $secondary; }
      }

      & > div {
        h6 {
          margin: 0;
          font-size: 20px;
          line-height: 26px;
          font-weight: normal;
          letter-spacing: 0px;
          color: #545456;
        }

        p {
          margin: 0;
          font-size: 15px;
          line-height: 20px;
          letter-spacing: 0px;
          color: #7B8793;
        }

        button {
          width: 100%;
          max-width: 214px;
          padding: 6px 10px;
          margin: 16px 0 0;
          font-size: 15px;
          line-height: 20px;
          letter-spacing: 0px;
          color: #FFFFFF;
          border: unset;
          border-radius: 17px;
          background: #01B4DE;
        }
      }
    }

    .postlogin__web-center {
      border-width: 0 1px;
      border-style: solid;
      border-color: rgba(123,135,147,.3);

      .appDemo-header {
        max-width: 600px;
        padding: 0;

        .appDemo-get-started {
          max-width: unset;
          padding: 0;
          box-shadow: unset;
        }
      }

      h5 {
        margin: 35px 0 0;
        font-size: 25px;
        font-weight: 500;
        line-height: 33px;
        text-align: center;
        color: #545456;
      }
    }
  }
}

.postlogin__video-popup {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    position: fixed;
    left: 0;
    top: 0;
    -webkit-backdrop-filter: blur(2px);
    backdrop-filter: blur(2px);
    background-color: rgba(0, 0, 0, 0.75);
    z-index: 99;

    .closeDemoVideo {
        width: 40px;
        height: 40px;
        border: unset;
        margin: 0;
        padding: 0;
        background: unset;
        position: absolute;
        right: 10px;
        top: 10px;
        z-index: 9;

        .icon {
            width: 20px;
            height: 20px;
            position: relative;
            left: 10px;
            right: 10px;
            top: 0;

            svg {
                height: 100%;

                polygon { fill: $background; }
            }
        }
    }

    .appDemo-header {
        width: 100%;
        max-width: 90%;
        position: relative;

        .appDemo-get-started {
            padding: 0;
            background-color: unset;
        }
    }
}

#se-footer-nav { z-index: 10; }

.system-estimator_header {
    background: #545456;

    & > div {
        padding: 14px 20px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .page-title {
        font-size: 14px;
        line-height: 18px;
        color: #FFFFFF;
    }

    .close-btn {
        margin: 0;
        padding: 0;
        border: unset;
        background: unset;

        .icon {
            height: 16px;

            & > svg {
                width: auto;
                height: 100%;

                & > polygon { fill: #FFFFFF; }
            }
        }
    }
}