.livechat{
  width: 100%;
  height: 100%;

  .livechat__wrapper {
    position: relative;
    width: inherit;
    height: inherit;

    .livechat__body{
      height: inherit;
      overflow: hidden;

      iframe{
        height: inherit;
      }

      .offline-background{
        background-color: transparent;
      }
    }
  }

  &.livechat__web {
    width: 375px;
    height: 500px;
    min-height: 500px;
    max-height: 500px;
    position: fixed;
    bottom: 0;
    right: 20px;
    z-index: 2;
    background-color: #ffffff;
    border-radius: 15px 15px 0 0;
    box-shadow: 0 2px 10px 0 rgba(63, 67, 71, 0.08);

    .livechat__body{
      border-radius: 15px 15px 0 0;
    }
  }
}

.embeddedServiceHelpButton { display: none !important; }

.embeddedServiceSidebar {
  background: unset;

  h2[embeddedService-chatHeader_chatHeader] { color: #ffffff !important; }

  .minimizedContainer { display: none !important; }

  .dockableContainer {
    max-width: 100% !important;
    height: 100% !important;
    margin: auto !important;
    border-radius: 0 !important;
    box-shadow: unset !important;
    right: 0 !important;
  }

  & [embeddedService-chatHeader_chatHeader-host] {
    height: 134px;
    min-height: 134px;

    .sidebarHeader {
      padding: 77px 15px 20px !important;
      background-color: #00b6de !important;
      box-shadow: 0 0 2px rgba(0,0,0,.4);
      flex-direction: row-reverse;
    }
  }

  .embeddedServiceSidebarForm.formContent {
    height: calc(100% - 150px);
    margin-bottom: 150px;
  }

  .embeddedServiceSidebarForm.buttonWrapper { background: #fff; }

  .embeddedServiceSidebarButton {
    background: #00b6de !important;

    &:focus,
    &:hover {
      background: #00b6de !important;
      text-decoration: unset;
      text-decoration-color: unset !important;
    }

    .label { color: #ffffff !important; }

    &.saveTranscriptButton { display: none !important; }
  }

  button[embeddedService-chatHeader_chatHeader] {
    padding: 5px !important;
    text-align: right;

    &.minimizeButton { display: none; }

    &.closeButton {
      &::before {
        top: unset;
        right: unset;
        bottom: 22px;
        left: 15px;
      }

      .slds-icon-utility-close {
        &::before {
          content: '';
          border: solid #ffffff;
          border-width: 0 2px 2px 0;
          display: inline-block;
          padding: 7px;
          transform: rotate(135deg);
          -webkit-transform: rotate(135deg);
        }

        svg { display: none; }
      }
    }
  }

  .disabled { color: rgba(47, 47, 48, .75) !important; }

  .embeddedServiceSidebarDialogState #dialogTextBody {
    .uiOutputRichText { display: none !important; }
  }

  .embeddedServiceSidebarFormField .uiInput {
    position: relative;

    & > span.blur-overlay {
      width: 100%;
      height: 44px;
      display: block;
      border-radius: 4px;
      border: 1px solid #949494;
      -webkit-backdrop-filter: blur(4px);
      backdrop-filter: blur(4px);
      position: absolute;
      left: 0;
      bottom: 0;
    }
  }
}

.embeddedServiceLiveAgentStateChat {
  .endChatContainer .saveTranscriptButton {
    margin-top: 10px !important;
    margin-bottom: 10px !important;
  }

  .embeddedServiceLiveAgentStateChatInputFooter {
    .footerMenuWrapper { display: none !important; }
    &.chasitorInputWrapper,
    .chasitorControls { border-radius: unset !important; }
    .chasitorControls .chatActionButton { margin-left: 15px; }
  }
}

.exit-live-chat {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, .8);
  overflow: hidden;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 5001;

  & .dialog-wrapper {
      max-width: 285px;
      border-radius: 10px;
      background-color: #ffffff;

      & >  p {
          margin: 0;
          padding: 20px;
          font-size: 14px;
          font-weight: normal;
          line-height: 1.57;
          letter-spacing: normal;
          color: #545456;
      }

      & .dialog-actions {
          display: flex;
          justify-content: flex-start;
          justify-content: center;
          border-top: 1px solid #e5e5e5;

          & button {
              width: 50%;
              padding: 16px 0;
              border: unset;
              outline: unset;
              font-size: 16px;
              font-weight: normal;
              line-height: normal;
              letter-spacing: normal;
              text-align: center;
              color: #545456;
              background-color: unset;

              &:last-child {
                  border-left: 1px solid #e5e5e5;
                  font-weight: 500;
                  color: #01b4de;
              }
          }
      }
  }
}

.offline-toast-message {
  font-size: 14px;
    line-height: 1.5;
    margin: 15px 24px 10px;
    color: #939598;
    text-align: left;
    line-height: 16px;
    font-weight: normal;
}

.night {
  .embeddedServiceSidebar {
    & [embeddedService-chatHeader_chatHeader-host] {
      .sidebarHeader {
        background-color: #333 !important;

        h2 { color: #ffffff !important; }
      }
    }

    .sidebarBody,
    .sidebarLoadingIndicator,
    .embeddedServiceSidebarForm.formContent,
    .embeddedServiceSidebarForm.buttonWrapper {
      background: #2c2c2c !important;
    }

    .embeddedServiceSidebarDialogState #dialogTextBody {
      color: #a2a6a9 !important;
    }

    .embeddedServiceSidebarDialogState #dialogTextTitle,
    .embeddedServiceSidebarFormField .uiInput .form-element__label {
      color: #fff !important;
    }
  }

  .exit-live-chat {
    backdrop-filter: blur(2px);
    background-color: rgba(0, 0, 0, 0.75);

    & .dialog-wrapper {
      background-color: #262627;

      & > p { color: #d8dcde; }

      & .dialog-actions {
        border-color: #333537;

        & button {
          color: #ffffff;

          &:last-child {
            color: #01b4de;
            border-color: #333537;
          }
        }
      }
    }
  }

  .embeddedServiceLiveAgentStateChat {
    .messageArea,
    .chatSessionStartTime,
    ul.messageWrapper,
    ul.messageWrapper li,
    .chasitorInputWrapper,
    .endChatContainer {
      background-color: #262627 !important;
    }

    .chatSessionStartTime span,
    .embeddedServiceLiveAgentStateChatEventMessage .eventMessage { color: #666; }

    .chasitorInputWrapper { border-top: 1px solid #666; }

    .endChatContainer::before { background: unset !important; }
  }

  .embeddedServiceLiveAgentStateChatInputFooter .unseenMessageNotificationWrapper .scrollDownButton .slds-button { background: unset !important; }
}

.browser .embeddedServiceSidebar,
.wo-site_live-chat.embeddedServiceSidebar {
  .dockableContainer {
    height: 500px !important;
    border-radius: 8px 8px 0 0 !important;
    box-shadow: 2px 2px 20px rgba(0, 0, 0, 0.2) !important;
    right: 20px !important;
  }

  & [embeddedService-chatHeader_chatHeader-host] {
    height: 50px;
    min-height: 50px;

    .sidebarHeader {
      padding: 0 15px !important;
      flex-direction: row;
    }
  }

  button[embeddedService-chatHeader_chatHeader] {
    &.closeButton {
      &::before {
        top: unset;
        right: 15px;
        bottom: 9px;
        left: unset;
      }

      .slds-icon-utility-close {
        &::before { display: none; }

        svg { display: block; }
      }
    }
  }
}