#offline-mode {
    .pageBody_new {
        &.OM_pesSubmenu {
            .header {
                .no-live-status-header {
                    position: relative;
                    height: 100%;
                    width: 100%;
                    background-color: #01b5de;
                }
            }
            .live-status-popup {
                height: 100%;

                .pes-system-status {
                    .pes-system-status-text {
                        transform: translateY(1.25px);
                    }
                }
            }
        }
    }

    .night {
        .live-status-popup {
            .popup-header {
                .popup__close {
                    .icon {
                        svg {
                            polygon {
                                fill: #fff;
                            }
                        }
                    }
                }
            }
        }
    }
}
