.siteempty {

  background-color: $background;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 20px;
  position: fixed;
  
  transition: opacity 2s;
  z-index: 1000;
  overflow: hidden;
  
  margin: 0;
  box-sizing: border-box;
  list-style: none;
  background: #fff;

  &.b2c-supported {
    min-height: 100%;
    position: relative;
  }

  .title {
    font-weight: bold;
    font-size: 1.2em;
    margin-bottom: 7px;
  }

  .box {
    margin-top: 20px;
    margin-bottom: 20px;
  }

  .detail {
    font-size: 1.1em;
  }

  .button {
    background-color:#7ecd40;
  }

  .signout {
    margin-top: 50px;
  }

}

.night {
  .siteempty {
    &.b2c-supported {
      svg.logo {
        path:nth-child(1),
        path:nth-child(2) { fill: rgb(243, 115, 33) !important; }

        path { fill: #FFFFFF !important; }
      }

      .title { color: #FFFFFF; }
      .detail {
        color: #FFFFFF;
        opacity: .6;
      }
    }
  }
}