@import "../../../style/font-style.scss";

.main-wrapper-enter {
  opacity: 0;
  transform: scale(0.9);
}

.main-wrapper-enter-active {
  opacity: 1;
  transform: translateX(0);
  transition: opacity 300ms, transform 300ms;
}

.main-wrapper-exit {
  opacity: 1;
}

.main-wrapper-exit-active {
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 300ms, transform 300ms;
}

.slidecontainer {
  transition: left 1s ease-in-out;
  overflow: hidden;
  //margin: 613px 0 0 60px;
  // top: 613px;
  left: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  z-index: 999;

  .ReactSwipeButton {
    transition: left 1s ease-in-out;
    float: left;
    width: 250px;
    height: 50px;
    position: relative;
  }

  .ReactSwipeButton,
  .ReactSwipeButton * {
    -webkit-touch-callout: none;
    /* iOS Safari */
    -webkit-user-select: none;
    /* Chrome/Safari/Opera */
    -khtml-user-select: none;
    /* Konqueror */
    -moz-user-select: none;
    /* Firefox */
    -ms-user-select: none;
    /* Internet Explorer/Edge */
    user-select: none;
    /* Non-prefixed version, currently
                                    not supported by any browser */
  }

  .rsbContainer {
    float: left;
    width: 100%;
    height: 100%;
    //background: #eee;
    border-radius: 50px;
    position: relative;
    /* box-shadow: inset 1px 1px 4px rgba(0,0,0,0.1); */
    //overflow: hidden;
  }

  .rsbContainer-inner-container {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 50px;
    position: absolute;
    display: flex;
    justify-content: center;
    /* box-shadow: inset 1px 1px 4px rgba(0,0,0,0.1); */
    //overflow: hidden;
  }

  .rsbContainer-inner {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    background-color: #5454560D;
    border-radius: 50px;
    position: relative;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    /* box-shadow: inset 1px 1px 4px rgba(0,0,0,0.1); */
    //overflow: hidden;
  }

  .rsbContainerUnlocked {
    width: 0 !important;
    //margin-left: 25%;
    //transition: 0.5s;
    cursor: default;

    .rsbcSliderCircle {
      width: 0 !important;
    }
  }

  .rsbContainerUnlocked .rsbcSlider {
    //left: 100%!important;
    width: 0 !important;
    cursor: default;
    pointer-events: none;
  }

  .rsbContainerUnlocked .rsbcSliderCircle {
    width: 0 !important;
    //margin-right: -60px;
  }

  .rsbcSlider {
    float: left;
    width: 46px;
    position: absolute;
    height: 46px;
    top: 2px;
    left: 50px;
    margin-left: -20%;
    background: url(../../images/slider/slider-thumb.svg);
    border-radius: 25px;
    z-index: 100;
    /* box-shadow: 1px 1px 5px rgba(0,0,0,0.3); */
    cursor: pointer;
  }

  .rsbcSliderText {
    position: absolute;
    top: 16px;
    left: 0;
    right: 0;
    line-height: 50px;
    text-align: center;
    @include body-2;
    color: #fff;
  }

  .rsbcSliderCircle {
    position: absolute;
    right: 0;
    background: url(../../images/slider/slider-thumb.svg);
    top: 0;
    height: 46px;
    width: 46px;
    border-radius: 100%;
    z-index: 999;
  }

  .rsbcText {
    //float: left;
    // position: absolute;
    // top:16px;
    // left:64px;
    // right:0;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    margin-left: 39px;
    min-width: 110px;
    line-height: 50px;
    text-align: center;
    @include body-2;
    color: #545456;
    font-size: 14px;
    font-weight: normal;
  }
}

