.otp__input-fields {
  border: none;
  border-bottom: 1px solid #c6c8ca;
  width: 56px !important;
  height: 36px;
  padding: 8px 0;
  color: #545456;
  font-size: 17px;
  font-weight: bold;
}

.otp__input-separator {
  width: 20px;
}

.otp__input-fields::-webkit-outer-spin-button,
.otp__input-fields::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
.otp__input-fields[type=number] {
  -moz-appearance: textfield;
}

.grid-control__mobile {
  .otp__input-fields {
    width: 46px !important;
    font-size: 14px;
  }
}