.soc-message {

  &.soc-message-status {
    margin-top: 15px;
  }

  &.soc-message-storage {
    margin-top: 16px;
  }

  .sm__wrapper {
    padding: 15px 20px;
    border-radius: 10px;
    border: dashed 1px #ff6464;

    .sm__message {
      font-size: 12px;
      font-style: italic;
      line-height: 1.31;
      color: #ff6464;
    }
  
    .sm__view-details {
      font-size: 12px;
      color: #00b6de;
      cursor: pointer;
      margin-top: 5px;
    }
  }
}

.browser {
  .soc-message {

    &.soc-message-status {
      margin-top: 20px;
    }

    .sm__wrapper {
      padding: 20px;

      .sm__message {
        font-size: 15px;
        line-height: 1.4;
      }
    
      .sm__view-details {
        font-size: 15px;
        margin-top: 10px;
      }
    }
  }
}