#offline-mode {
  .live-status {
    .live-status__wrapper {
      .live-status__header {
        .live-status__heading {
          font-size: 16px;
        }
      }
      .live-status__body {
        .grid-status__wrapper {
          text-transform: unset;
        }
        .mqtt-status-text {
          text-transform: capitalize;
        }
        .live-status__energy-flow .live-status__energy-flow-wrapper .live-status__energy-flow-inner-wrapper {
          .energy-type__icon-with-details {
            .energy-type__details {
              margin-left: 0px;
            }
          }
        }
      }
    }
    .live-status__timer {
      .live-status__timer-text {
        font-size: 12px;
      }
    }
  }

  .night {
    .icon{
      &.mqtt-status-icon{
        svg g {
          path:first-child {
            fill: #ffffff;
          }
        }
      }
    }

    .live-status {
      .live-status__body {
        .live-status__energy-flow {
          .live-status__energy-flow-wrapper {
            .energy-type {
              .energy-type__icon-with-details {
                .energy-type__icon {
                  svg{
                    g:first-child{
                      fill: none;
                    }
                  }
                  .energyGridCircle {
                    circle {
                      stroke: #cbced1;
                    }

                    path {
                      fill: none !important;
                    }
                  }
                }
                .energy-type__name {
                  color: #fff !important;
                }
              }
            }
          }
        }
        .energy-type .energy-type__icon-with-details .energy-type__sub-title{
          color: #a2a6a9;
        }
      }
    }
    .energyGridToBattery{
      svg {
        #Arrow_head { fill: #cbced1; }
        #motion-path-grid-battery { stroke: #cbced1; }
      }
      svg g {
        g {
          #Oval_Copy_9, #Oval_Copy_8{
            fill: #cbced1;
          }
        }
      }
    }


    .energyGridToHomeConnected{
      svg g g {
        path:nth-child(1){
          stroke: #cbced1;
        }
        path:nth-child(2){
          fill: #cbced1;
        }
      }
      svg g{
        g{
          #Oval_Copy_13, #Oval_Copy_12{
            fill: #cbced1;
          }
        }
      }
    }
  }
}