.scrim {
    position: fixed;
    width: 100vw;
    height: 100%;
    top: 0;
    z-index: 10001;
    -webkit-backdrop-filter: blur(2px);
    backdrop-filter: blur(2px);
    background-color: rgba(0, 0, 0, 0.75);

    &.blur {
      -webkit-backdrop-filter: blur(10px);
      backdrop-filter: blur(10px);
      background-color: #000000A7;
    }

    &.ojas-dialog__other {
      height: calc(100% - #{$footer-height} -  46px);
      top: 46px;
    }
    
    &.ojas-dialog__ios {
      height: calc(100% - 80px - #{$footer-height});
      top: 80px;
    }

    &.scrim__pre-login { z-index: 99999; }

    &.scrim__post-login {
      height: calc(100% - 1px - #{$footer-height}) !important;

      .ls__header {
        min-height: 1px;
        padding: 39px 20px 26px;
        line-height: 22px;

        .icon.prev {
          height: 20px;

          svg {
            height: 100%;
          }
        }
      }
    }

    @media screen and (min-width:1024px) {
      -webkit-backdrop-filter: unset;
      backdrop-filter: unset;
    }
}

.dialog-container {
    width: 285px;
    margin: auto;
    background-color: #ffffff;
    border-radius: 10px;
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    color: #545456;

    .title-area {
      padding: 20px;
      border-bottom: 1px solid #c6c8ca;
      font-size: 16px;
      font-weight: 500;
      line-height: 19px;
      color: #545456;
    }

  .data-area {
    padding: 20px;
    border-bottom: solid 1px #d8d8d8;
    font-size: 14px;
    line-height: 1.57;
    @include body-1;
  }

  .footer-area {
    width: 100%;
    display: flex;

    .positive-action, .negative-action {
      &.disabled {
        cursor: not-allowed;
        pointer-events: none;
      }
    }
  }

  .footer-area button {
    border: unset;
    background-color: transparent;
    width: 50%;
    margin: 0;
    padding: 16px 0;
    font-size: 16px;
    font-weight: normal;
    outline: none;
    color: inherit;

    &:disabled {
      opacity: .75;
      cursor: not-allowed;
    }
  }

  .footer-area button:first-child {
    border-right: solid 1px #d8d8d8;
    color: #01b4de;
    border-bottom-left-radius: 10px;
  }

  .footer-area button:last-child { border-bottom-right-radius: 10px; }

  &.exit-se-dialog {
    width: 100%;
    max-width: 335px;

    & .data-area {
      padding: 20px 40px;
      font-size: 18px;
      font-weight: bold;
      line-height: normal;
      letter-spacing: normal;
      text-align: center;
      color: #545456;
    }

    & .footer-area {
      display: flex;
      justify-content: flex-start;
      justify-content: center;

      & button {
        width: 50%;
        padding: 16px 0;
        margin: 0;
        border: unset;
        outline: unset;
        font-size: 16px;
        font-weight: 500;
        line-height: normal;
        letter-spacing: normal;
        text-align: center;
        color: #545456;
        background-color: unset;

        &:first-child { border-right: 1px solid #e5e5e5; }
        &:last-child { color: #01b4de; }
      }
    }
  }

  &.back-button-confirmation {
    .footer-area button {
      color: #545456;

      &:last-child{
        color: #01b4de;
      }
    }
  }
}

.browser {
  .dialog-container {
    min-width: 450px;
    border-radius: unset;

    .title-area {
      padding: 32px 32px 20px;
      font-size: 20px;
      line-height: 24px;
    }

    .data-area { padding: 32px; }

    .footer-area {
      button {
        padding: 20px;
        font-size: 17px;
        font-weight: 500;
        line-height: 20px;
      }
    }

    &.tou-alert-dialog {
      .data-area { border-bottom: unset; }

      .footer-area {
        max-width: 315px;
        margin: 20px auto 32px;
        align-items: center;

        button {
          border: unset;

          &:first-child { color: #01b4de; }

          &:last-child {
            width: 100%;
            padding: 15px;
            margin-bottom: 15px;
            border-radius: 25px;
            color: #ffffff;
            background-color: #00b6de;
          }
        }
      }
    }
  }
}

.night {
  .dialog-container {
    background-color: #262627;

    &.exit-se-dialog {
      & .data-area {
        color: #d8dcde;
        border-bottom: solid 1px #333537;
      }

      & .footer-area button:first-child {
        color: #d8dcde;
        border-right-color: #333537;
      }
    }
  }

  .title-area {
    color: #d8dcde;
    border-color: #333537;
  }

  .data-area {
    color: #d8dcde;
    border-bottom: solid 1px #333537;
  }
  
  .footer-area button {
    background-color: #262627;
    color: #ffffff;
  }

  .footer-area button:first-child {
    border-right-color: #333537;
  }
}

@media screen and (min-width:1024px) {
  .desktop-replacement-submit {
    width: 450px;
    border-radius: 0;

    & .data-area {
      padding: 32px;
      font-size: 17px;
      border: unset;
    }

    & .replacement-dialog-header {
      padding: 0 32px 24px;
      margin: 0 -32px 26px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      border-bottom: solid 1px rgba(123, 135, 147, 0.3);

      & h3 {
        font-size: 20px;
        font-weight: 500;
        line-height: 26px;
        color: #545456;
      }

      & span.close {
        width: 15px;
        height: 15px;

        & span.icon { height: 100%; }

        & svg {
          width: 15px;
          height: 15px;
          fill: #545456;
        }
      }
    }

    & .footer-area {
      width: 315px;
      flex-direction: column;
      align-items: center;
      margin: 38px auto 0;

      /* Standard syntax */
      & button {
        padding: 0;
        font-size: 17px;
        font-weight: 500;
        line-height: 23px;
        text-transform: uppercase;
      }
      & button#negativeAction {
        width: auto;
        margin: 32px 0;
        border: unset;
        color: #00b6de;
        background-color: unset;
        -webkit-order: 2;/* Safari 6.1+ */
        order: 2;
      }
      
      & button#positiveAction {
        width: 100%;
        height: 50px;
        margin: 0;
        border-radius: 25px;
        border: solid 1px #01b4de;
        color: #ffffff;
        background-color: #00b6de;
        -webkit-order: 1;/* Safari 6.1+ */
        order: 1;
      }
    }
  }
}
