.time-keeper{
  .react-timekeeper{
    background-color: transparent;
    border-radius: 0;
    font-family: $fallbackFontsSeq;

    .css-upwegn-TopBar{
      border-radius: 0;
    }
  }
  .done-btn{
    text-align: center;
      padding: 16px 0; 
      background-color:#F4F4F4; 
      color:#68687F;
  }

  .react-timekeeper__top-bar{
    .react-timekeeper__tb-hour--active, .react-timekeeper__tb-minute--active{
      color: var(--top-selected-color,#47B4DE);
    }
  }

  .react-timekeeper__clock-wrapper{
    .react-timekeeper__meridiem-toggle{
      &.css-7cuz4l, &.css-ktukdg, &.css-xbtxlx-Meridiems, &.css-wufnqa-Meridiems{
        background: var(--meridiem-selected-bg-color,#47B4DE);
        color: var(--meridiem-selected-text-color,#ffffff);
      }
    }
  }
}

.night{
  .time-keeper{
    .react-timekeeper__top-bar{
      background: #333333;
    }

      .done-btn{
        text-align: center;
        padding: 16px 0; 
        background-color:#333333; 
        color: #ffffff;
        border-color: #333333;
      }

    .react-timekeeper__clock-wrapper{
      background: #202020;

      .react-timekeeper__clock{
        background: #333333;

        .css-1ddlhy{
          color: var(--numbers-text-color,#ffffff);
        }

        svg{
          g{
            .react-timekeeper__hand-circle-outer{
              fill: #47B4DE;
            }
          }
        }
      }
    }

    .react-timekeeper__done-button{
      background: #333333;
      color: #ffffff;
      border-color: #333333;
    }
  }
}