@import '../../../style/font-style';
.learn-more-about-profiles{
  overflow: hidden;

  .lmap__wrapper{
    height: inherit;
    background-color: #fafafb;
  }

  .lmap__contents{
    overflow: unset;
  }

  .lmap__contents__nav{
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background: linear-gradient(to top, #ffffff 50%, #ffffff00 100%);
    height: 50px;

    ul{
      display: flex;
      align-items: center;
      justify-content: center;
      height: inherit;
    }

    li{
      position: relative;
      display: inline-block;
      width: 10px;
      height: 10px;
      border-radius: 50%;
      margin: 4px;
      background-color: rgba(1, 180, 222, .3);
      cursor: pointer;
    }
    
    .lmap__nav__active{
      background-color: #01B4DE;

      &:before{
        content: "";
        border: 1px solid #06b5de;
        display: inline-block;
        position: absolute;
        left: -3px;
        top: -3px;
        width: 16px;
        height: 16px;
        border-radius: 50%;
      }
    }
  }

  .lmap__profile{
    .lmap__profile__wrapper{
      padding: 15px 20px;
      background-color: #ffffff;
    }
  }

  .lmap__heading{
    font-size: 18px;
    font-weight: 500;
    line-height: 21px;
    color: #545456;
    @include title-2;
  }

  .lmap__status{
    display: flex;
    align-items: center;
    font-size: 12px;
    font-weight: 400;
    line-height: 16px;
    margin-top: 10px;
    color: #6C7074;

    .lamp__status__battery{
      display: flex;
      align-items: center;

      .icon{
        display: flex;
        margin-right: 4px;

        svg{
          width: 16px;
          height: 16px;
        }
      }

      .lamp__status__battery__text{
        height: 13px;
      }
    }

    .lamp__status__grid{
      display: flex;
      align-items: center;
      margin-left: 20px;

      .lamp__status__grid__indicator{
        margin-right: 5px;
        
        .icon {
          width: auto;
          height: 16px;
          margin-top: -1px;

          svg{
            width: inherit;
            height: inherit;
          }
        }
      }

      .lamp__status__grid__text{
        height: 13px;
      }
    }
  }

  .lmap__ei{
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 12px;
    font-weight: 400;
    line-height: 14px;
    height: 40px;
    color: #595959;

    &+.lmap__status{
      margin-top: 0;
    }

    .lmap__ei__title{
      padding: 6px 10px;
      border-radius: 78px;
      background-color: #f0f0f0;

      .lmap__ei__enabled{
        color: #F37320;
      }

      .lmap__ei__disabled{
        color: #7B8793;
      }
    }

    .toggle-switch__container{
      display: flex;
    }
  }

  .lmap__live-status{
    margin-top: 15px;

    &.noEnergyIndependence{
      margin-top: 45px;
    }
  }

  .lmap__desc{
    padding: 25px 25px 45px 25px;
    background-color: #FAFAFB;

    .lmap__desc__title{
      font-size: 16px;
      font-weight: 500;
      margin-bottom: 10px;
      color: #909396;
    }

    ul{
      margin-left: 15px;
    }

    li{
      list-style-type: disc;
      color: #6B7073;

      &:not(:first-child){
        margin-top: 15px;
      }
    }
  }

  .MuiTabs-root{
    min-height: 30px;
    border: none;
    padding: 15px 18px;
    background-color: #ffffff;
    border-bottom: .5px solid rgba(123,135,147,.3);

    .MuiTab-root{
      min-height: 30px;
      font-size: 12px;
      line-height: 14px;
      border-radius: 100px;
      padding: 8px;
      color: #545456;
      background-color: #E9E9E9;
      word-break: break-word;

      .MuiTab-wrapper{
        width: auto;
        padding: 0;
        font-family: "Visuelt", "Roboto", "Helvectica Neue", Helvetica, Arial, sans-serif;
      }
      
      .MuiTouchRipple-root{
        border-radius: 100px;
      }

      &.Mui-selected{
        color: #ffffff !important;
        background-color: #01b5de;
      }

      &:nth-child(2){
        margin: 0 5px;
        min-width: 130px;
      }
    }

    .MuiTabs-indicator{
      display: none;
    }
  }

  .react-swipeable-view-container{
    height: calc(100vh - 180px);
    max-height: calc(100vh - 180px);
  }

  .lmap__tutorial{
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, .95);
    z-index: 1;

    .lmap__tutorial__wrapper{
      margin-top: 180px;
      text-align: center;
    }

    .lmap__tutorial__desc{
      font-size: 14px;
      line-height: 17px;
      margin: 15px 20px 20px;
      color: #FFFFFF;
      @include body-1;
    }

    button{
      color: #FFFFFF;
      padding: 10px;
      background: transparent;
      outline: none;
      border: 1px solid;
      width: 135px;
      border-radius: 4px;
      font-size: 16px;
    }

    &.lmap__tutorial__swipe-left-right{
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      z-index: 11;

      .lmap__tutorial__wrapper{
        margin-top: 0;
      }

      .icon{
        height: 55px;
      }
    }
  }

  .lmap__has__tutorial{
    .MuiTabs-root{
      position: relative;
      z-index: 10;
    }
  }
  &.emeaGriedTied .MuiTabs-root .Mui-selected{ 
    color: #545456 !important;
    background-color: #e9e9e9;
    opacity: 0.7;
    font-weight: 400;
  }


}

.browser{
  .lmap__profile{
    display: flex;

    .lmap__profile__wrapper{
      min-width: 330px;
      background-color: transparent;
    }
  }

  .lmap__ei{
    margin-top: 5px;

    &+.lmap__status{
      margin-top: 5px;
    }
  }
}

.night{
  .learn-more-about-profiles{
    .lmap__wrapper{
      background-color: #313131;
    }

    .MuiTabs-root{
      background-color: #262627;
    }

    .lmap__profile__wrapper{
      background-color: #262627;
    }

    .lmap__heading{
      color: #f8f8ff;
    }

    .lmap__status{
      color: #d8dcde;
    }

    .lmap__ei{
      .lmap__ei__title{
        color: #d8dcde;
        background-color: #313131;
      
          .lmap__ei__disabled{
            color: #aeaeae;
          }
        }
    }

    .lmap__desc{
      background-color: #313131;

      li{
        color: #d8dcde;
      }
    }

    .lmap__contents__nav{
      background: linear-gradient(to top, #262627 50%, #00000000 100%);
    }
  }
}

@media (min-width:360px) and (max-width:374px){
  .learn-more-about-profiles {
    .MuiTabs-root{
      padding: 15px 10px;
    }
  }
}

@media (max-width:359px){
  .learn-more-about-profiles {
    .MuiTabs-root{
      .MuiTab-root{
        white-space: break-spaces;
      }
    }

    .lmap__ei{
      .lmap__ei__title{
        span{
          &:before{
            content: "\A";
          }
        }
      }

      .toggle-switch__eiswitch_toggle{
        margin-left: 3px;
      }
    }
  }
}

@media only screen and (min-device-width: 720px){
  .learn-more-about-profiles {
    .react-swipeable-view-container{
      height: auto;
    }
  }
}

.om__lmap__stickyBottomButton{
  width: 80%;
  margin: 1.5em auto 1em;
  border-radius: 2em;
  background-color: #00b6de;
  color: #fff;
  display: flex;
  justify-content: center;
  padding: 0.8em 1.4em;
}