.ensemble-dialog-container {
  .data-area {
    .bluetooth-popup {
      .try-ap-mode {
        color: #01b4de;
        text-decoration: underline;
        margin: 0.2em 0;
      }

      .alertContent {
        text-align: center;
        .dialogIcon {
          text-align: center;
  
          .icon {
            padding: 2em 1em 0em 1em;
          }
        }
      }
      
      .deviceEnvoy {
        svg {
          width: 40px;
          height: 40px;
        }
      }
    }
    .ensemble-wifi-popUp, .bluetooth-popup {
      .dialogIcon {
        text-align: center;
        .iconTitle{
          @include title-2;
          &.boldBlack{
            color: black;
          }
          &.danger{
            color: #F24445;
        }
        }
        
      }
      .description{
          a{
              color: #01B5DE;
              text-decoration: underline;
          }
      }
      .try-ap-mode{
        color: #01b4de;
        text-decoration: underline;
        margin: 0.2em 0;
      }
    }

    .description,
    .try-ap-mode {
      padding: 0em 1em;
      text-align: center;
      margin: 0.2em 0;
      
    }

    .list-popUp > .list {
      .listWrapper {
        max-height: 60vh;
        overflow: scroll;
        padding: 10px 15px;

        li {
          position: relative;
          border-bottom: 1px solid #d8d8d8;

          .wifi-details,
          .ble-details {
            padding-bottom: 1em;
            padding-top: 1em;
            white-space: nowrap;
            width: 80%;
            overflow: hidden;
            text-overflow: ellipsis;
          }
          .wifi-icons,
          .ble-icons {
            float: right;
            position: absolute;
            right: 0%;
            top: 1em;
            .icon {
              float: right;
              padding-right: 5px;

              &.bluetooth {
                svg {
                  width: 1em;
                  height: 1em;
                  circle {
                    fill: #545456;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
  .footer-area {
    button{ width: 100% !important; }
    button:first-child {
      color: #545456;
    }
    button:last-child {
      color: #01b4de;
    }
    #negativeAction {
      width: 50%;
    }
    #positiveAction {
      width: 50%;
      border-left: none;
      font-weight: 500;
    }
  }

  &.ble-connect-success {
    background: #000000bf;
    .data-area {
      .dialogIcon {
        .greenTickOutline {
          svg {
            width: 66px;
            height: 66px;
          }
        }
      }
      .description {
        @include title-1;
        text-align: center;
        color: #fafafb;
      }
    }
  }
  &.noPadding {
    .data-area {
      padding: 0%;
      padding-bottom: 2em;
      .wifi-password > .dialogHeader, .list-popUp > .dialogHeader {
        padding: 1em;
        border-bottom: 1px solid #c6c8ca;
      }

      .wifi-password > .form-container {
        padding: 1em 1em 0em;
        .noMargin{
          margin: 0em;
        }
      }
    }
  }
  .gateway-connection-success {
    .description {
      @include title-2;
      color: #5cd482;
      margin: 14px 0;
    }
    .buttonWrapper {
      .ok-got-it {
        @include body-2;
        padding: 10px 67px;
        color: #ffffff;
        background: #01b4de;
        border-radius: 20px;
        border: none;
        margin: auto;
      }
    }
  }
}

.night {
  .footer-area {
    button:first-child {
      color: #ffff;
    }
  }
}
