@import '../../../style/font-style.scss';

.night .OM_pesSubmenu .ojasBrightnessContainer {
  background-color: #262727;

  .lightStatus .description .descrContent .descrTitle {
    color: #ffffff;
  }

  .descrSubTitle {
    color: #A9A9AA !important;
  }

  .header-container {
    color: #ffffff !important;
  }

  .lightStatus .description .img {
    .ojasBrightness {
      svg {
        path {
          fill: #ffffff;
        }
      }
    }
  }

  .lightSettingItem__wrapper .greyBackground {
    background-color: #1b1a1a;
  }
}

.OM_pesSubmenu .ojasBrightnessContainer {
  background-color: #ffffff;

  .lightStatus {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 18px 20px;

    .description {
      display: flex;
      align-items: initial;

      .img {
        width: 24px;
        height: 24px;
        margin-right: 15px;
      }

      .descrContent {
        .descrTitle {
          font-size: 16px;
          color: #545456;
          line-height: 1;
          font-weight: normal;
        }

        .descrSubTitle {
          font-size: 12px;
          font-weight: 300;
          color: #7b8793;
        }

        .descSubTitleEnabled {
          font-size: 12px;
          color: #F37320;
          font-weight: normal;
        }
      }
    }
  }

  .lightSettingItem__wrapper {
    margin-left: 20px;
    margin-right: auto;
    padding-bottom: 30px;
    margin-right: 20px;

    .header-container {
      margin: 14px 0 8px 0;
      display: flex;
      font-size: 16px;
      color: #545456;
      font-weight: normal;

      .brightness-value {
        font-size: 14px;
        margin-left: auto;
        color: #01B5DE;
      }
    }

    .multiSlider-conatiner {
      width: 100%
    }

    .slider-container {
      display: flex;
      align-items: flex-end;
      gap: 14.58px;
    }
  }
}

.button-wrapper {
  position: absolute;
  bottom: 105px;
  text-align: center;
  width: calc(100% - 32px);
  left: 16px;

  .save-button {
    box-shadow: 1px 2px 10px 0 rgba(63, 67, 71, 0.08);
    border: 1px solid #00b6de;
    padding: 8px 0 !important;
    background: #00b6de;
    color: #FFFFFF;
    text-align: center;
    border-radius: 8px;
    width: 100%;
    justify-content: center;
    display: flex;
    font-size: 15px !important;
    font-weight: normal !important;
    @include title-2;
    margin-left: auto;
    margin-right: auto;

    &.buttonCancel {
      background-color: transparent;
      color: #01b5de;
      border: 1px solid #01B5DE;
      font-size: 15px;

      &.buttonDisabled {
        color: #c7c8ca;
      }
    }

    &.quiet-margin {
      margin-top: 40px;
    }
  }

  .text {
    svg {
      g {
        g {
          fill: #ffffff;
        }
      }

      path {
        stroke: #ffffff;
        stroke-width: 1px;
      }
    }
  }
}

.multiSlider-conatiner {
  &.slider-disabled {
    .slider-handles {
      display: none !important;
    }

    .slider-tracks {
      div {
        background-color: #00b6de !important;
      }
    }
  }
}

.ojasBrightness {
  padding-bottom: 3px;
}

.brightness-close-popup {
  height: 12px;
  width: 12px;
  float: right;
}

.brightness-close-popup span svg path {
  fill: #54545699;
}

.ojasBrightnessContainer {
  .react-switch-bg {
    margin-right: 0px !important;
  }
}

.ojasBrightnessContainer {

  #el-tick-dev0,
  #el-tick-dev100 {
    background: none !important;
  }
}

.ojasBrightnessContainer.webViewContainer {
  border-radius: 10px;
  height: calc(100vh - 340px);
}

.webViewContainer {
  .lightStatus {
    padding: 30px;
  }

  .lightSettingItem__wrapper {
    padding: 30px;
    margin-left: 0px !important;
    margin-right: 0px !important;
  }

  .slider-container {
    margin-top: 16px;
  }
}

.pesWebCardView {
  position: relative;

  .button-wrapper {
    width: 100% !important;
    height: 60px !important;
    border-radius: 8px !important;
    font-size: 20px !important;
    font-weight: 500 !important;
    bottom: 26px;

    .save-button {
      padding: 12px 0 !important;
      width: 440px !important;
      font-size: 18px !important;
    }
  }

  .descTitle {
    font-size: 20px !important;
    font-weight: 700 !important;
  }

  .descSubTitle {
    font-size: 16px !important;
    font-weight: 700 !important;
  }
}

.page-view {
  .ojasAdvanceSetting-Container {
    background: none;
    width: 100%;
  }
}

.pes-advance-placeholder {
  padding: 16px;

  .pes-webview-margin {
    width: 100%;
  }
}
