$paddingForStatus: 15px;
$headerHeight: 52px;
$siteDetailsBarHeight: 25px;

.latest-status {
  background: #ffffff;

  &.gray-bg {
    background-color: #F6F7FB;
  }

  .comp-notification.comp-notification__failure {
    background-color: #545456;

    &.permission-denied-toast {
      background: #ff5959;
    }
  }

  .data-not-available {
    text-align: center;
    padding: 10px;
    margin: 0 -15px;
    background: #f9f9f9;
  }

  .latest-status-page {
    background: #ffffff;
    height: calc(100% - #{$footer-height} - #{$headerHeight} - #{$siteDetailsBarHeight});

    .add-site-image {
      position: relative;
      background: #F6F7FB;
      width: 100vw;
      height: 97px;
      left: -$paddingForStatus;

      .site-image {
        height: 100%;

        .editIcon {
          height: 32px;
          position: absolute;
          right: 10px;
          top: 62px;
        }

        img {
          width: 100%;
          height: 100%;
        }
      }

      .add-site-image__no-site-image {
        height: 100%;

        .touch-area {
          margin: auto;
          display: grid;
          place-content: center;
          width: 33%;
          height: 100%;

          .site-image-cta {
            margin: 0;
            color: #01B5DE;
            @include caption;
            margin-top: 3px;
            text-align: center;
          }
        }

        .siteImageBannerIntersection {
          position: absolute;
          top: 19px;

          &.top-right {
            right: 0;
            top: 4px;
            transform: rotate(180deg);
          }
        }
      }

      .add-site-image__upload-image-icon {
        position: absolute;
        bottom: 0;
        right: 17px;
      }

      .add-site-image__button {
        position: absolute;
        bottom: 7px;
        right: 18px;
      }
    }

    .latest-status-wrapper {
      height: calc(100% - 25px);
      width: 100%;
      overflow-x: hidden;
      padding: 0 $paddingForStatus;

      &.divide-top-bottom {
        height: 100%;

        &.gray-bg {
          background-color: #F6F7FB;
        }

        padding: unset;

        .top-wrapper {
          background-color: #ffffff;
          padding: 0 $paddingForStatus $paddingForStatus;

          .energy-heading {
            margin: unset;
          }

          .energy-layout {
            margin: 30px 0px 30px;
            height: auto;
          }

          &> :nth-last-child(1):not(:empty) {

            &.iq8d-card-wrapper,
            &.energy-stats-status {
              margin-bottom: 5px;
            }
          }
        }

        .bottom-wrapper {
          left: unset;
        }
      }

      .bottom-wrapper {
        width: 100vw;
        position: relative;
        left: -$paddingForStatus;

        &>* {
          padding: 0px 16px;
        }

        &.gray-bg {
          &>* {
            padding: 22px 16px 0px;
          }
        }

        &> :last-child {
          padding-bottom: 20px;
        }

        &:empty {
          padding: unset;
        }

        .forecast-card {
          &.gray-bg {
            background-color: transparent;

            .forecast-card-title {
              @include caption;
              color: #A2ABB3;
              margin-bottom: 12px;
            }

            .solarforecastPage.fromStatusPage {
              background-color: #FFFFFF;
              border: unset;
            }
          }
        }
      }

      .energyLayoutWithMargin {
        margin: 0 auto 26px;
      }

      .carousel-type-1 {
        background-color: #ffffff;
        margin: 0 0 20px 0;
      }

      .energy-heading {
        padding: 15px 0 10px 0;
        @include body-1;

        ::first-letter {
          text-transform: capitalize;
        }
      }

      .carousel__container {
        border-radius: 3px;
        width: 100%;
        margin: none;
        max-width: 100%;
        padding: 10px;
      }

      .carousel__static-info {
        padding-left: 2px !important;

        @media screen and (min-width:768px) and (max-width:1024px) {
          min-width: 50% !important;
          display: block !important;
          padding-left: 8px !important;
        }
      }

      .carousel__wrapper {
        padding: 0 18px !important;
      }

      .carousel__static-info__name {
        @media screen and (min-width:768px) and (max-width:1024px) {
          font-size: 16px !important;
          ;
        }
      }

      .carousel__static-info__value {
        @media screen and (min-width:768px) and (max-width:1024px) {
          font-size: 22px !important;
          ;
        }

        span {
          @media screen and (min-width:768px) and (max-width:1024px) {
            font-size: 15px !important;
          }
        }
      }

      .carousel-type-1 .carousel__container .carousel__wrapper .carousel__arrow.carousel__arrow-right {
        @media (max-width: 320px) {
          right: 2px;
        }
      }

      .addConsumptionMeter {
        padding: 20px 25px 20px 25px;
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
        height: auto;
        border: 1px solid #D8DCDE;
        border-radius: 3px;

        .consumptionMeterButton {
          display: flex;
          height: 40px;
          width: 100%;
          font-size: 14px;
          font-weight: 500;
          margin-bottom: 10px;
          border-radius: 20px;
          align-items: center;
          justify-content: center;
          text-align: center;
          background-color: #00b6de;
          color: #ffffff;
          cursor: pointer;
          @include title-2;
        }

        .infoText {
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: center;
          font-size: 10px;
          font-weight: 400;
          margin-top: 5px;
          @include body-1;

          .icon.consumptionMeter {
            width: 22px;
            height: auto;
            margin-right: 6px;

            svg {
              width: 22px;
              height: 22px;
            }
          }
        }
      }

      .envoyDataUpload {
        width: 100%;
        background-color: rgba(71, 181, 222, 0.07);
        display: flex;
        flex-direction: column;
        padding: 10px;
        align-items: center;
        border-radius: 5px;
        margin-bottom: 15px;

        .envoyDataUpload__header {
          display: flex;
          align-items: center;
          margin-top: 5px;
          margin-bottom: 3px;

          .image {
            width: 20px;
            height: 20px;
            background-size: 20px;
            margin-right: 5px;
            background-image: url(../../src/images/envoyDataUploading.svg);
          }

          .title {
            margin-left: 5px;
            font-size: 16px;
            font-weight: 700;
            text-align: center;
          }
        }

        .envoyDataUpload__content {
          margin-bottom: 5px;
          font-size: 14px;
          text-align: center;
          @include body-1;
        }
      }

      .energy-layout {
        height: calc(max(410px, calc(100% - 180px)));
      }

      .energy-type__value {
        color: #6c7073;

        @media screen and (min-width:768px) and (max-width:1024px) {
          color: #6c7073;
        }
      }

      &.ojas-status__wrapper {
        .ojas-status-screen {
          height: 100%;

          .live-status {
            height: calc(100% - 150px);

            .live-status__wrapper {
              .live-status__body {
                overflow-x: hidden;
                margin: 0;
                padding: 0;
              }
            }
          }
        }
      }
    }

    .modified-latest-status-wrapper {
      padding: 0px;

      .custom-latest-wrapper {
        padding: 0 $paddingForStatus;
        height: calc(100% - 25px)
      }
    }
  }

  .status-height-ios {
    height: calc(100% - 80px - #{$footer-height});
  }

  .status-height-android {
    height: calc(100% - #{$footer-height} - #{$headerHeight} - #{$siteDetailsBarHeight});
  }

  .device-widget {
    margin-bottom: 20px;
    border-radius: 3px;
    border: 1px solid #d8dcde;
    background-color: #fff;

    h4 {
      font-size: 16px;
      font-weight: 500;
      color: #545456;
      line-height: normal;
      padding: 20px 20px 25px 20px;
    }

    .list-view {
      border: none;
      margin-bottom: 20px;

      .list-view__wrapper {
        padding: 0 20px;
      }
    }

    .devices-link {
      height: 50px;
      display: flex;
      padding: 14px 20px;
      cursor: pointer;
      border-top: solid 1px #d8dcde;

      .list-view {
        .list-view__content {
          margin: 0 0 0 -20px;

          .list-view__title {
            font-size: 14px;
            font-weight: normal;
            color: #01b5de;
            padding-right: 5px;
          }
        }

        .list-view__action {
          .list-view__icon {
            span {
              svg {
                path {
                  fill: #01b5de;
                }
              }
            }
          }
        }
      }

      p {
        margin: 0;
        display: flex;
      }

      span {
        font-size: 14px;
        padding-right: 5px;
      }

      .next {
        svg {
          width: 13px;
          height: 12px;
          margin-top: 0;
          fill: #01b5de;
        }
      }
    }
  }

  .site-details {
    height: 25px;
    position: fixed;
    text-align: center;
    width: 100%;
    bottom: $footer-height;
    font-size: 12px;
    letter-spacing: -0.13px;
    display: inline-flex;
    justify-content: center;
    opacity: 0.8;
    background-color: #fff;

    span {
      color: #545456;
      padding: 5px 10px;
      border-radius: 15px;
      @include caption
    }

    &.gray-bg {
      background-color: #F6F7FB;
      opacity: 1;

      span {
        background-color: #F6F7FB;
        color: #A2ABB3;
        opacity: 1;
      }
    }
  }

  .notification-has-alert-message {
    // bottom: calc(#{$footer-height} + #{$alert-notification-height});
  }

  .notification-has-error-message,
  .notification-has-offlineInternet-message {
    // bottom: calc(#{$footer-height} + #{$error-notification-height});
  }

  &.de {
    .grid-columns.grid-columns-two {
      width: 61%;
      padding: 0 10px;
    }
  }
}

.site-image-popups {
  &.maxImageUploaded {
    .data-area {
      padding: 16px;

      .site-image-popups__wrapper {
        .error-text {
          text-align: left !important;

          .site-details-button {
            color: #01b5de;
            text-decoration: underline;
          }
        }
      }
    }
  }

  &.mobileView {
    &.alertPopUp {
      .footer-area {
        button {
          width: 100%;
        }
      }
    }
  }

  &.mobileView.hide-site-image {
    .footer-area {
      button {
        &:first-child {
          border-right: none;
        }

        &:last-child {
          border-right: solid 1px #d8d8d8;
          border-bottom-right-radius: 0;
        }
      }
    }
  }
}

.showup.backdrop {
  background-color: rgba(0, 0, 0, 0.9);
  animation: fadeInOut 4.9s;

  .showup__wrapper {
    top: unset;
    left: unset;
    max-width: unset;
    transform: unset;
    padding: 0px 15px;

    .showup__inner-wrapper {
      margin: unset;
      background-color: transparent;
    }
  }

  .showup__body {
    .content-wrapper {
      display: flex;
      width: 55%;
      margin: 0px auto;
      margin-bottom: 4px;
      height: 134px;

      .text-content {
        display: flex;
        flex-direction: column;
        color: #ffffff;
        text-align: right;
        margin-right: 10px;

        .title {
          @include body-2;
        }

        .subtitle {
          @include body-1;
        }
      }

      .icon.forecastpointer {
        display: flex;
        margin-top: 10px;
        height: 124px;
      }
    }
  }

  .showup__footer {
    display: none;
  }
}

.no-access_consumption {
  width: 300px;
  text-align: center;
  font-size: 16px;
  line-height: 20px;
  color: #7B8793;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
}

.hide-add-siteImage {
  .showup__wrapper>.showup__inner-wrapper {
    .showup__body {
      .hide-site-image-title {
        text-align: center;
        padding: 15px;
        border-bottom: 1px solid #D8DCDE;
        @include title-2;
      }

      .hide-image-content {
        padding: 16px 27px;
        color: #545455;
        @include body-1;
      }
    }
  }
}

.list-view__browseallfaq {
  .list-view__wrapper {
    .list-view__content {
      .list-view__content-wrapper {
        .list-view__title {
          span {
            color: #01b5de;
          }
        }
      }
    }
  }
}

.night {
  .latest-status {
    &.gray-bg {
      background-color: #2F302F;
    }

    .latest-status-page {
      background-color: #262627;

      .addConsumptionMeter {
        border: 1px solid #3c4044;

        .infoText .icon.consumptionMeter {
          svg {
            filter: invert(100%);
          }
        }
      }
    }

    .latest-status-wrapper {
      background-color: #262627;

      &.divide-top-bottom {
        &.gray-bg {
          background-color: #2F302F;
        }

        .top-wrapper {
          background-color: #262627;
        }
      }

      .bottom-wrapper {
        background-color: transparent;

        .forecast-card {
          &.gray-bg {
            .solarforecastPage.fromStatusPage {
              background-color: #262627;
            }
          }

          .status-forecast {
            box-shadow: 0px 2px 10px #3f434714;
          }
        }
      }

      .device-widget {
        background-color: #262627;
        border: 1px solid #3c4044;

        h4 {
          color: #f8f8ff;
        }

        .list-view {
          border: none;
        }

        .devices-link {
          border: 1px solid #3c4044;

          .list-view {
            .list-view__content {
              .list-view__title {
                color: #01b5de !important;
              }
            }
          }
        }
      }

      .carousel-type-1 {
        background-color: #262627;

        .energy-type__value {
          color: #fff;
        }
      }

      .envoyDataUpload {
        background-color: rgba(71, 181, 222, 0.3);
        color: #ffffff;
      }

      .energy-heading {
        background-color: #262627;
      }

      .energy-type__value {
        color: #cbced1;
      }

      .add-site-image {
        background-color: #FFFFFF0D;

        .add-site-image__no-site-image>.siteImageBannerIntersection {
          svg>path {
            fill: #262627;
          }
        }

        .site-image {
          .editIcon {
            svg>g {
              &:first-child {
                g>g>circle {
                  fill: #333333;
                }
              }

              #edit_black_24dp {
                #Path_18461 {
                  fill: white;
                }
              }
            }
          }
        }

        .add-site-image__no-site-image>.touch-area>.cameraBlueIcon>svg {
          g>g>g>g {
            circle {
              fill: #333333
            }
          }
        }
      }
    }

    .site-details {
      background-color: #2F302F;

      &.gray-bg span {
        background-color: #2F302F;
      }
    }
  }

  .site-details {
    background-color: #262627;

    span {
      color: #ffffff;
      opacity: 0.5;
    }
  }

  .no-access_consumption {
    color: #939598;
  }

  .showup.backdrop {
    .showup__wrapper .showup__inner-wrapper {
      background-color: transparent;
    }
  }

  .pes-latest-status {
    .live-status__system-grid-status {
      .energyGridCircle {
        svg {
          #grid-circle {
            stroke: none
          }
        }
      }
    }
  }
}

@media screen and (max-width:320px) {
  .latest-status {
    .latest-status-page {
      .latest-status-wrapper {
        .carousel__wrapper {
          padding: 0 3px !important;
        }
      }

      .carousel-type-1 .carousel__container .carousel__wrapper .carousel__arrow.carousel__arrow-left {
        left: 5px;
      }
    }

    &.de {
      .grid-columns.grid-columns-two {
        width: 50%;
        padding: 0 0 0 7px;

        .description {
          word-break: break-all;
        }
      }
    }
  }
}

@media (min-width:320px) and (max-width:360px) {
  .latest-status {
    .adjust-height {
      bottom: calc(#{$footer-height} + 18px);
    }
  }
}

@media (min-width:320px) and (max-width:380px) {
  .pes-latest-status {
    .live-status__energy-flow__arrows {
      left: 12px;
    }
  }

}

@media (min-width:370px) and (max-width:380px) {
  .pes-latest-status {
    .live-status__energy-flow__arrows {
      left: 3px;
    }
  }

}

@media (min-width:768px) and (max-width:1024px) {
  .latest-status {
    .latest-status-wrapper {
      .energy-layout {
        height: calc(100% - 240px) !important;
      }
    }

    .status-height-ios {
      height: calc(100% - 90px - #{$footer-height});
    }

    .site-details {
      font-size: 16px;
    }
  }
}

@media (min-width:768px) and (max-width:1024px) and (orientation : landscape) {
  .latest-status {
    .latest-status-wrapper {
      height: calc(100% - 18px) !important;
      overflow: hidden;

      .energy-layout {
        height: calc(100% - 225px) !important;
      }
    }

    .site-details {
      height: 18px !important;

      span {
        padding: 0 !important;
      }
    }
  }
}

.popup__grid {
  .popup__wrapper {
    &.battery-charge-popup {
      top: auto;
    }

    &.battery-charge-popup.popup__web {
      width: 450px;
      height: fit-content;

    }
  }
}

.zero__margin {
  margin-top: 0px;
}

@keyframes fadeInOut {
  0% {
    opacity: 0;
  }

  30%,
  85% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

.events-notification-warning-popup-dialog-main {
  z-index: 189 !important;

  .events-notification-warning-popup-dialog {
    padding-left: 24px;
    padding-right: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    .events-notification-warning-popup-dialog-icon {
      margin-top: 30px;
    }

    .events-notification-warning-popup-dialog-title {
      font-size: 16px;
      font-weight: 500;
      margin-bottom: 13px;
      margin-top: 17px;
      text-align: center;
    }

    .events-notification-warning-popup-dialog-text {
      font-size: 14px;
      text-align: center;
      margin-bottom: 31px;
    }
    
    .events-notification-recommended-action{
      min-width: 249px;
      max-height: 260px;
      background-color: #F4F4F4;
      padding: 12px 16px 10px;
      margin-top: 12px;
      margin-bottom: 20px;
      margin-right: 4px;
      margin-left: 4px;
      border-radius: 8px;
      font-size: 14px;

      .recommended-action-heading{
        color: #7B8793;
      }
    }
  }
}

