.live-status-popup {
  position: fixed;
  top: 0px;
  left: 0;
  width: 100%;
  background: #fff;
  height: calc(100% - 60px);
  z-index: 999;

  .popup-header {
    display: flex;
    justify-content: space-between;
    padding: 13px 16px;
    align-items: self-start;
    border-bottom: 1px solid rgba(0, 0, 0, 0.06);

    .popup-title {
      display: flex;
      align-items: flex-start;

      .back-button {
        display: flex;
        margin-right: 12.3px;

        .icon {
