            width: 7px;
            height: 14px;
            margin-top: 4px;
          }
        }
      }


      .popup-title-text {
        display: flex;
        flex-direction: column;
        line-height: 1;
      }
    }

    .popup__close {



      .icon {
        text-align: right;

        svg {
          width: 16px;
          display: inline-block;
          position: relative;
          top: -1px;

          polygon {
            fill: #6C7073;
          }
        }
      }
    }
  }

  .pes-system-status {
