@import "../font-style";
.browser {
  .array-viewer-wrapper {
    display: flex;
    height: 100%;
    background-color: #ffffff;
  
    .array-viewer-container {
      flex-basis: 77%;
      height: 100%;
      position: relative;
      .array-viewer {
        height: calc(100% - 150px);
        .message {
          width: 100%;
          min-height: 65px;
          display: flex;
          align-items: center;
          padding: 0px 32px;
          z-index: 104;
          .crossMark {
            position: relative;
            padding: 0;
            height: auto;
            margin-top: 0;
            order: 1;
            display: flex;
            margin-left: auto;
            .icon {
              height: 13px;
              width: 13px;
              display: flex;
              opacity: 0.7 !important;
              svg {
                width: 13px;
                height: 13px;
                polygon {
                  fill: #889094;
                }
              }
            }
          }
          img {
            width: 20px;
            margin: 0px;
          }
          .alarm-message {
            padding-left: 15px;
            font-size: 15px;
            color: #545456;
          }
        }
        .zoom {
          z-index: 104;
        }
        .spin-container {
          width: 100%;
          height: 100%;
          display: flex;
          align-items: center;
          background-color: #ffffff;
          justify-content: center;
          top: 0;
          left: 0;
          margin: 0;
        }
        .empty-array {
          width: 100%;
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
        }
        &.alarms {
          .message {
            z-index: 104;
          }
        }
      }
      .scale {
        height: 70px;
        position: relative;
        background-color: transparent;
        .production-array {
          justify-content: center;
          height: 70px;
          margin: 0px;
          background-color: #ffffff;
          align-items: center;
          border-top: solid 1px #d8dcde;
          .prod-details {
            padding-right: 20px;
            margin-top: 0px;
            .produced {
              padding-right: 20px;
              font-size: 15px;
              font-weight: 500;
              letter-spacing: 3px;
              padding-bottom: 0px;
            }
            .power {
              font-size: 30px;
              font-weight: normal;
              color: #545456;
            }
          }
          .site-details {
            display: none;
          }
          .prod-details, p {
            display: flex;
            align-items: center;
          }
        }
        .exchange-warranty {
          opacity: 0.9;
          background-color: #ffffff;
          height: 70px;
          display: flex;
          align-items: center;
          padding-left: 15%;
          border: none;
          @media screen and (min-width:1024px) {
            padding-left: 0;
            justify-content: center;
          }
          .exchange-warranty-container {
            display: flex;
            align-items: center;
            .exchange-warranty-wrapper {
              margin: 0px;
              display: flex;
              align-items: center;
              font-size: 15px;
            }
            .request-replacement-scale {
              font-size: 15px;
              align-items: center;
              .icon {
                display: flex;
                align-items: center;
              }
            }
          }
        }
        ul {
          display: none;
        }
      }
    }
    
    .array-stats-wrapper {
      flex-basis: 23%;
      overflow: scroll;
      border-left: 1px solid #d8dcde;
  
      div:last-child {
        border-bottom: none;
      }
  
      .array-stats-column {
        border-bottom: 1px solid #d8dcde;
        flex-direction: column;
        margin: 0 7%;
        justify-content: center;
        padding: 25px 0px;
        display: flex;
        
        .energy_produced_title {
          font-size: 25px;
          font-weight: 500;
          color: #545456;
          .accordion-title {
            font-size: 20px;
            font-weight: 500;
          }

          &.has_info_icon{
            .icon{
              margin-left: 5px;
              width: 16px;
              height: 16px;
            }
          }
        }
  
        .energy_produced_value {
          font-size: 30px;
          font-weight: bold;
          color: #01b5de;
          margin-top: 14px;
  
          span {
            font-size: 14px;
            font-weight: 500;
            text-align: right;
            color: #01b5de;
            margin-left: 2px;
            position: relative;
            top: -.8em;
          }
        }
      }
  
      .array-list-row {
        border-bottom: 1px solid #d8dcde;
        flex-direction: column;
        padding: 24px 0px;
        display: flex;

        .accordion-title {
          font-size: 20px;
        }
  
        .array-stats {
          font-size: 17px;
          line-height: 0.82;
          margin-bottom: 12px;
          color: #7b8793;
          &:nth-of-type(2){
            margin-top: 15px;
          }
          &:last-child {
            margin-bottom: 0px;
          }
        }
      }
    }
  }

  .layout{
    .hide{
      display: none;
    }
    .unhide{
      display: initial;
    }
    .pvContainer {
      height: 100%;
      width: 100%;
      .pvlayoutTitle {
        display: flex;
        justify-content: space-between;
        flex-wrap: nowrap;
        align-items: center;
        & > div {
          flex-grow: 1;
        }
        .comp-cw__date-selector{
          flex-grow: 1;
        }
      }
      .period-select {
        height: 45%;
        margin: 0;
        display: flex;
        position: relative;
        z-index: 1;
        text-transform: uppercase;
        .focus {
          position: absolute;
          transition: left 0.3s;
          padding: 0.5em 0.2em;
          div {
            height: 2em;
            background-color: $secondary-dark;
            border-radius: 2em;
          }
        }
    
        li {
          text-align: center;
          font-size: 12px;
          letter-spacing: 0em;
          white-space: nowrap;
          overflow: hidden;
          cursor: pointer;
          color: #6c7073;
          flex-grow: 0;
          padding: 0;
          margin: 0 20px;
          @include caption;
          &.active {
            padding: 4px 7px;
            color:#fff;
            font-weight: 600;
            opacity: 1;
            z-index: 1;
            -webkit-transform: translate3d(0,0,1px);
            transform: translate3d(0,0,1px);
            background-color: #01b5de;
            border-radius: 20px;
          }
          span {
            display: flex;
            font-size: 15px;
            font-weight: 500;
            font-style: normal;
            font-stretch: normal;
            line-height: normal;
            letter-spacing: normal;
            height: 100%;
            align-items: center;
            justify-content: center;
            padding: 0 7px;
            vertical-align: middle;
          }
        }
    
        .extra-small-text-title {
            font-size: 0.8em;
        }
    
        .small-text-title {
          font-size: 0.8em;
        }
    
        @media only screen and (max-width: 375px) {
          .extra-small-text-title {
              font-size: 0.6em;
          }
          .small-text-title {
            font-size: 0.9em;
          }
        }
    
      }
    }
    .storageArrayContainer {
  
    }
    .tab {
      margin-left: 20px;
      margin-right: 20px;
      display: flex;
      flex-wrap: nowrap;
      justify-content: flex-start;
      button {
        background: transparent;
        border: none;
        padding: 10px;
        flex-basis: auto;
        flex-shrink: 1;
        font-size: 15pt;
        letter-spacing: 0;
        &.active {
          color: #00b6de;
          border-bottom: 3px solid #00b6de;
          font-weight: 700;
          @include title-2;

        }
        &.inactive {
          color:#939598;
          border-bottom: 1px solid rgba(123,135,147,.3);
          padding-top: 13px;
        margin-bottom: 1px;
          @include title-1;
        }
        &.placehoder{
          flex-grow: 1;
          border-bottom: 1px solid rgba(123,135,147,.3);
          margin-bottom: 1px;
          cursor: auto;
        }
  
      }
    }
  
  }
  
}