.history .weather {
  border-radius: 0.1em;
  z-index: 5;
  margin: 1em 0.9em 0 0;
}

ul.stats {
  top: 20%;
  height: 32%;
  width: 100%;
  display: flex;
  padding: 0.6em;
  flex-flow: wrap;
  align-content: space-between;
  justify-content: space-between;

  li {
    height: 46%;
    width: 48%;
    padding: .9em 0 0 .9em;
    position: relative;
    color: $background;
    border-radius: 1em;
    box-shadow: 0 0 4px 1px $screen;
    z-index: 5;
    &.disabled,
    &.unavailable {
      background-color: $super-light;
      polygon { fill: $light; }
      .grid.icon, .battery.icon { 
        background-color: $super-light; 
        path, .soc  { fill: $light !important; }
      }
    }
    h4 {
      font-size: .7em;
      font-weight: 500;
      letter-spacing: 2px;
      text-transform: uppercase;
      overflow-wrap: break-word;
      margin-bottom: .3em;
    }
    h3 {
      margin-bottom: .2em;
      font-size: 1.6em;
      font-weight: 300;
      display: flex;
      p{
        margin: 0;
        padding-left: 5px;
      }
      span {
        font-size: 1.8vh;
        font-weight: 500;
        vertical-align: top;
        margin: .3em .5em;
        display: inline-block;
        letter-spacing: 0;
      }
    } 
    .disabled,
    .enabled,
    .unavailable {
      position: absolute;
      right: 0;
      margin: -0.2em 0.6em;
      height: 1.5em;
      stroke-width: 0;
    }
    .disabled,
    .enabled {
      path { fill: none !important; }
    }
    .grid.icon {
      height: 2.2em;
      margin: 0.6em 0.5em 0 -0.3em;
    }
    .battery.icon {
      height: 2.1em;
      margin: 0.6em 0.7em 0 0;
      stroke-width: 0;
      .soc { fill: $background !important; }
    }
  }
}
