#offline-mode {
    .pes-energy-header {
        .pes-energy-header-details {
            .pes-system-status {
                .pes-system-status-icon {
                    .pes-status-icons {
                        margin-top: 0;
                    }
                }
            }
        }
    }
    .pes-popup {
        .wrapper {
            .container {
                .pes-popup-header {
                    .header-device-not-found,
                    .header-bluetooth-connection-error {
                        padding: 24px 16.5px 22.45px;
                        border-bottom: 1px solid #C6C8CA;
                        font-size: 18px;
                        font-weight: 500;
                        color: #131415;
                    }
                }
                .pes-popup-content {
                    display: flex;
                    flex-direction: column;

                    .message-device-not-found,
                    .message-bluetooth-connection-error {
                        font-size: 14px;
                        text-align: center;
                        color: #545456;
                        margin-bottom: 33px;
                        width: 220px;
                        margin-left: auto;
                        margin-right: auto;
                    }
                }
                .pes-popup-action {
                    .footer-bluetooth-connection-error {
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        color: #fff;
                        font-weight: 500;
                        padding: 12px 16px 12px 16px;
                        margin: 16px;
                        border-radius: 8px;
                        background: #01B5DE;
                        cursor: pointer;
                    }
                }
            }
        }
    }

    .night {
        .pes-popup {
            .wrapper {
                .container {
                    .pes-popup-header {
                        .header-device-not-found,
                        .header-bluetooth-connection-error {
                            color: #FFFFFF;
                            border-bottom: 1px solid #545456;
                        }
                    }
                    .pes-popup-content {
                        .message-device-not-found,
                        .message-bluetooth-connection-error {
                            color: #FFFFFF;
                        }
                    }
                }
            }
        }
    }
}
