.night {

  color: $medium;
  a { color: $medium; }
  select, input, .input {
    color: #ffffff;
    border-bottom: solid 1px rgba(123, 135, 147, 0.3);
  }
  input:-webkit-autofill {
    background-color: $transparent !important;
    border-bottom-color: $secondary;
    -webkit-text-fill-color: $medium;
    -webkit-box-shadow: 0 0 0px 1000px $super-dark inset;
  }

  .spin-container {
    background-color: $dark-screen;
  }

  .splash {
    background-color: $super-dark;
    .logo .b {
      fill: #999;
    }
    .progress {
      background-color: $dark;
    }
    .notices:after {
      background: linear-gradient($super-dark, $transparent 5%, $transparent 95%, $super-dark);
    }
    .version {
      color: $dark;
    }
  }

  .page {
    background: #262627;
  }

  .titlebar {
    color: #ffffff;
    background-color: $super-dark;
    .period-select .focus div {
      background-color: #505050;
    }
  }

  .nav {
    background-color: $super-dark;
    .group {
      color: #929699;
    }
    circle {
      stroke: $super-dark;
      fill: #ff5959;
    }
    rect, path {
      fill: $dark;
    }
  }

  .history {
    .selection .label {
      background-color: $super-dark;
    }
  }

  .menu {
    .notices {
      background-color: $faint;
    }
    .submenu {
      background-color: $super-dark;
      h3 {
        color: $light;
      }
    }
  }

  .expander.unavailable {
    background-color: $faint;
  }

  .date-picker {
    background-color: $super-dark;
    .rdt {
      background-color: $super-dark;
    }
  }

  .date-inline-picker {
    .label {
      background-color: $super-dark;
    }
    .calendar{
      svg{
        fill:#f8f8ff;
      }
    }
  }

  .graph {
    rect {
      fill: $super-dark;
    }
    circle {
      fill: $super-light !important;
      stroke: $super-dark !important;
    }
    .tracker {
      line, path {
        stroke: $medium;
      }
      polyline {
        fill: $super-dark;
      }   
    }
  }

  .stats li {
    box-shadow: 0 0 4px 1px $faint;
    &.disabled,
    &.unavailable {
      background-color: $faint;
    }
  }

  .array-viewer {
    background-color: #262627;
    .zoom, .input {
      color: #d8dcde;
      background-color: inherit;
      border: solid 1px #8e9091;
    }
    .zoom div{
      box-shadow: 0 0 1px #8e9091;
    }
  }

  .unavailable,
  .unavailable .production,
  .unavailable .consumption,
  .unavailable .grid,
  .unavailable .battery, 
  .unavailable path {
    color: $dark !important;
    fill: $dark !important;
  }

  .disabled,
  .disabled .production,
  .disabled .consumption,
  .disabled .grid,
  .disabled .battery {
    color:  $dark !important;
    /* graph fill */
    stroke: $dark !important;
    fill:   $faint !important;
    /* icons */
    path, rect {
      fill:   $dark !important;
      stroke: #545456!important;
    }
  }
  .scale {
    background-color: #262627;
    .production-array {
      .prod-details{
        color: #f8f8ff;
      }
      .site-details {
        color: #d8dcde;
      }
    }
  }
  .storage {
    background-color: inherit;
    .storage-mgmt{
      .text-blk{
        color: #f8f8ff;
      }
    }
    .storage-blk{
      box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.25);
      background-color: #262627;
      .toggleblk {
        .storage-text {
          color: #a2a6a9;
        }
        .storage-title {
          color: #f8f8ff;
        }
        .active{
          color: #35c37d;
        }
        .clockCircular {
          svg g {
            fill: #f8f8ff;
          }
        }
      }
      .activate-mode{
        .padding-sides{
          .reserveBattery {
            span,
            .selfconsumption p{
              color: #f8f8ff;
            }
            svg g,
            svg path{ 
              fill:  #f8f8ff;
            }
            .fullBattery{
              svg path:nth-child(3){
                fill:#262627;
              }
            }
          }
        }
        .buttonCancel {
          background-color: #262627;
          border-top: 1px solid rgba(198,200,202,.2);

          &.buttonDisabled {
            color: #A2A6A9;
          }
        }
      }
    }
  }
  .battery-reserve {
    .reserve-container {
      color: #f8f8ff;
    }
    .reserve-progress-slider{
      .css-1vx7jc5  {
        background-color: #262627;
      }
    }
    .reserve-percent-container {
      color: #d8dcde;
      .oval-icon {
        .smallOval{
          svg{
            circle{
              fill: #d8dcde;
            }
          }
        }
      }
    }
  }
  .list-view{
    .batteryAccount{
      svg{
        g{
          rect{
            fill: #f8f8ff;
            stroke: #262627;
          }
          path:nth-child(2){
            stroke: #262627;
            fill: #262627 !important;
          }
          path{
            stroke: #262627;
          }
        }  
      }
    }
  }

/* CSS for dark mode for new slides*/ 


  .menu-list{
    .menu-list__wrapper{
      height: inherit;
      background-color: #2c2c2c;
      .menu-list__body-wrapper{
        background-color: #2c2c2c;
        .logout{
          background-color: #2c2c2c !important;
        }
      }
      .menu-list__sites-available {
        opacity: .99;
        background-color: #262627;
        color: #fff;
        .unordered-list{
            li{
            &.unordered-list__selected{
              background-color: #262627;
            }
            :active { 
              background-color: #3B3B3B; 
            }
            &:hover:not(.unordered-list__selected){
              background-color: #262627;
            }
          }
        }
      }
      .menu-list__header{
        background-color: #333333;
      }
      .menu-list__header__selected-site-id{
        color: #8e9091 !important;
      }
    }
    .menu-list__body{
      background-color: #2c2c2c;
    }
    .menu-list__footer{
      background: #2c2c2c !important;
      .menu-list__brand-icon-and-app-version span:last-child {
        color: #c7c8ca;
      }
      .brandIcon{
        svg{
          g{
            g:nth-child(3),g:nth-child(4),g:nth-child(5),g:nth-child(6),g:nth-child(7),g:nth-child(8),g:nth-child(9){
              path{
                fill: #f8f8ff !important;
              }
            }
          }
        }
      }
    }
  }

  .list-view {
    opacity: 0.99;
    background-color: #262627;
    color: #fff;
    &:not(:first-child){
      border-top: solid 1px rgba(123, 135, 147, 0.3);
    }
    &.list-view__logout{
      background: #2c2c2c;
    }
    .deviceMicroinverters{
      svg{
        g{
          rect{
            fill:#fff;
          }
          path{
            fill: #262627 !important;
          }
        }
      }
    }
    .deviceStorage{
      svg{      
          g{
            rect{
              fill: #f8f8ff;
            }
            path{
              stroke:#262627;
              fill: #f8f8ff;
            }
            path:nth-child(2){
              fill: #262627 !important;
            }
          }                 
      }
    }
    .deviceEnpower{
      svg{
        rect{
          fill:#f8f8ff;
        }
        path, circle{
          fill:#262627;
        }
      }
    }
  }
  .list-view__title {
    opacity: 0.99;
    color: #f8f8ff !important;
    .notices {
      display: inline-block;
      color: $background;
      background-color: #ff5959;
      border-radius: 1em;
      font-size: 0.6em;
      font-weight: 500;
      min-width: 1.7em;
      text-align: center;
      min-height: 1.7em;
      line-height: 1.7em;
      margin: 0 0.6em;
      vertical-align: top;
    }
  }
  .list-view .list-view__icon path{
    fill: #f8f8ff;
  }
  .list-view .list-view__icon .connectedAppliancesIcon path {
    stroke: #f8f8ff;
  }
  .submenu {
    background-color: #262627;
    .header{
      background-color: #333333;
    }

    &:not(.gridServices) {
      p,
      &.reports .content .email {
        color: #8e9091;
      }
    }
    
    .container{
      background-color: #262627;
      .menu-list__body{
        .menu-list__body-wrapper {
          background-color: #262627 !important;
        }
      }
      .padding{
        .outer-block {
          box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.25);
          background-color: #262627;
          .address-details{
            color: #a2a6a9;
            p:first-child {
              color: #f8f8ff;
            }
            p:nth-child(2) {
              color: #a2a6a9;
            }
          }
          .sys-info{
            p:nth-child(1){
              color: #f8f8ff;
            }
            p:nth-child(2){
              color: #a2a6a9;
            }
          }
          .contact-info{
            a{
              span:nth-child(1){
                svg{
                  path {
                    fill: #f8f8ff;
                  }
                }
              }
            }
          }
        }
      }
      .content:not(.disconnected) {
        h5, h4{
          color: #939597;
        }
        a{
          color: #01b4de;
        }
        .button {
          background-color: #01b4de;
        }
        .alert{
          color: #ff4751;          
        }
      }
    }
    .content .alert.icon svg{
      stroke: #ff4751;
    }
    &.support {
      .container {
        background-color: #2c2c2c;
      }
    }
  }

  .energy-type .energy-type__icon {
    background-color: inherit;
  }
  .energy-crosshair.energy-crosshair__positive .rv-crosshair__inner {
    background-color: #262627;
    border: solid 1px #3c4044;
    color: #d8dcde;
    h3{
      color: #8e9091;
    }
  }

  .rv-crosshair{
    .rv-crosshair__line{
      &::before{
        border-color: #262627 transparent transparent;
      }
      &::after{
        border-color: transparent transparent #262627;
      }
    }
    .rv-crosshair__inner{
      &::before{
        border-color: transparent transparent transparent #3c4044;
      }
      &::after{
        border-color: transparent transparent transparent #262627;
      }
      &.rv-crosshair__inner--right{
        &::before{
          border-color: transparent #3c4044 transparent transparent;
        }
        &::after{
          border-color: transparent #262627  transparent transparent;
        }
      }
    }

  }


  .energy-graph .energy-graph__wrapper .series-xaxis-line {
    background: rgba(123, 135, 147, 0.3);
  }
  .energy-crosshair__import,
  .energy-crosshair__export {
    color: #d8dcde !important;
  } 

  .energy-type .energy-type__icon {
    background-color: inherit;
  }


  .energy-heading  .energy-summary-info .title,
  .toggle-switch .toggle-switch__label{
    color: #f8f8ff;
  }

  .energy-type .energy-type__name { color: #a2a6a9; }

  .toggle-switch .toggle-switch__label.toggle-switch__label-unchecked {
    color: #8e8e93;
  }
  .toggle-switch .toggle-switch__handler .react-switch-bg {
    background-color: rgba(182, 182, 182, 0.3) !important;
  }

  .energy-heading .energy-summary-info .updated-info{
    color: #ffffff;
  }
  .energy-type.energy-imported .energy-type__value,
  .energy-type.energy-exported .energy-type__value{
    color: #cbced1;
  }

  .carousel__static-info,
  .energy-type__value{
    color: #ffffff;
  }
  .carousel-type-1 .carousel__container {
    border: solid 1px #3c4044;
  }

  .carousel-type-1 .carousel__container .carousel__wrapper .carousel__item .energy-type .energy-type__name,
  .carousel-type-1 .carousel__container .carousel__static-info .carousel__static-info__name {
    color: #c6c7c9;
  }

  .rv-xy-plot,
  .rv-xy-plot__inner {
    color: #8e9091;
  }
  .energy-graph .rv-xy-plot__axis__line {
    stroke: rgba(123, 135, 147, 0.3);
  }

  .grid-container {
    border: solid 1px #3c4044;
    background-color: #262627;
    .grid-columns{
      .energy-grid__icon {
        .solarPower {
          svg {
            path{
              fill: #f8f8ff;
            }
          }
        }
        .storageIcon{
          svg{
            g{
              path{
                fill:#262627;
              }
              g{
                rect{
                  fill: #f8f8ff;
                }
                path{
                  stroke:#262627;
                  fill: #f8f8ff;
                }
                path:nth-child(2){
                  fill: #f8f8ff;
                }
              }
            }           
          }
        }
      }
      .text,
      .text-units{
        color: #f8f8ff;
      }
      .header {
        color: #8e9091;
      }
    }
    .grid-seperator{
      border: solid 1px #3c4044;
    }
  }  
  .energy-type .energy-type__icon {
    background-color: inherit;
  }
  
  .popup {
    background-color: #262627;
  }
  
  .latest-status{
    .energy-stats-status {
      background-color: #262627;
      border: 1px solid #3c4044;
      .grid-seperator{
        border: solid 1px #3c4044 !important;
      }
    }
  }

  .energy-graph{
    .energy-graph__wrapper{
      .series{
        .energy-legends{
          .graph-contols {
            .toggle-switch__yesterday,
            .toggle-switch__last-week,
            .toggle-switch__last-month,
            .toggle-switch__last-year,    
            .toggle-switch__previous_day,
            .toggle-switch__previous_week,
            .toggle-switch__previous_year,
            .toggle-switch__imported-exported{
              background-color: inherit !important;
            .react-switch-handle{
              background-color: #e5e5e5 !important;
            }
          } 
          .toggle-swtich__button-unchecked{
            .react-switch-handle{
              background-color: #a2a6a9 !important;
            }  
          }
         }
        }
      }
    }
  }

  .energy-graph{
    .energy-graph__wrapper{
      .series{
        .energy-legends{
          .graph-contols {
            .yesterday,.ieri,.gestern,.ayer,.gisteren,.hier,
            .last-week,.letzte-woche,.la-semana-pasada,.la-semaine-dernière,.la-settimana-scorsa,.vorige-week,
            .last-month,.im-vergangenen-monat,.el-mes-pasado,.le-mois-dernier,.lo-scorso-mese,.vorige-maand,
            .last-year,.vergangenes-jahr,.el-año-pasado,.l-année-dernière,.L-anno-scorso,.vorig-jaar,    
            .imported-exported,.importiert-exportiert,.importado-exportado,.importé-exporté,.importata-esportata,.geïmporteerd-geëxporteerd{
              background-color: inherit !important;
              .react-switch-handle{
                background-color: #e5e5e5 !important;
              }
            } 
            .toggle-swtich__button-unchecked{
              .react-switch-handle{
                background-color: #a2a6a9 !important;
              }  
            }
          }
        }
      }
    }
  }

  .popup {
    background-color: #262627;
  }
}
