@import '../fonts';
.browser {
    .live-status {
        .comp-notification {
            position: absolute;
            margin: 0px;
            bottom: 0;
            left: 50%;
            right: 50%;
            width: 450px;
            height: 67px;
            transform: translate(-50%,-145%);
        }
        &.popup {
            width: 712px;
            max-width: 90%;
            max-height: 648px;
            zoom: 1;
            margin: auto;
            &.pvOnlyWeb {
                  border-radius: 10px;
                  height: 460px;
                  width: 450px;
                  .pvOnly-multienvoy.custom-dropdown{
                    margin-left: 32px;
                    .dropdown__button .dropdown__buttonText .gateway_num{
                        margin-left: 20px;
                    }
                  } 
                  .live-status__body{ 
                    margin: 0px 32px 32px 32px;
                    .pvOnly-multienvoy.custom-dropdown{
                        margin-left: 0px;
                    }
                }
              }
        }
        .live-status__wrapper {
            .live-status__header {
                display: flex;
                padding: 25px 32px;

                .live-status__icon-with-text {
                    .live-status__heading-wrapper {
                        display: flex;
                        .live-status__heading {
                            margin: 0;
                            color: #565657;
                            @include title-2;
                        }
        
                        .live-status__timer  {
                            margin: 0 10px;
                            .live-status__timer-icon .icon svg{
                                width: 16px !important;
                                height: 16px !important;
                            }
                            .live-status__timer-text {
                                display: flex;
                                align-items: center;
                                margin-left: 4px;
                                font-size: 14px;
                                line-height: 1.21;
                            }
                        }
                    }
                }

                .live-status__close-live-status .popup__close .icon svg {
                    width: 18px !important;
                    height: 18px !important;
                    fill: rgb(108, 112, 115);
                }
            }

            .live-status__body {
                padding: 0;

                .live-status__grid-wrapper {
                    padding: 0px 15px;
                }

                .live-status__grid-connectivity {
                    padding: 15px 32px 15px 29px;

                    .live-status__icon-with-text {
                        .icon svg {
                            width: 53px !important;
                            height: 54px !important;
                        }
                        .live-status__heading-wrapper {
                            margin-left: 10px;
                            .live-status__heading {
                                margin: 4px 0 0;
                                font-size: 20px;
                            }
                            .live-status__system-grid-status-text {
                                margin: 0;
                                font-size: 14px;
                            }
                        }
                    }

                    .live-status__grid-switch {
                        .toggle-switch {
                            flex-direction: row-reverse;
                            .toggle-switch__label  {
                                font-size: 15px;
                                margin: 0;
                                margin-right: 10px;
                            }
                            .toggle-switch__handler {
                                .react-switch-bg {
                                    width: 36.8px;
                                    height: 15.1px;
                                }
                                .react-switch-handle {
                                    height: 21.6px;
                                    width: 21.6px;
                                }
                            }
                        }
                    }
                }
                .live-status__energy-flow {
                    .energy-type {
                        &.pv_flag_on{
                            .energy-type__icon-with-details .energy-type__details{
                                .energy-type__value{
                                    @include title-2;
                                }
                                .energy-type__name{
                                    @include body-1;
                                }
                            }
                        }
                        .energy-type__icon-with-details {
                            display: flex;
                            flex-direction: column;
                            align-items: center;
                            .energy-type__icon {
                                width: 58px;
                                height: 58px;
                            }
                            .energy-type__details {
                                display: flex;
                                align-items: center;
                                flex-direction: column;
                                .energy-type__value {
                                    font-size: 19px;
                                    font-weight: bold;
                                    text-align: right;
                                    span {
                                        font-size: 12px;
                                        text-align: right;
                                        font-weight: normal;
                                    }
                                }
                                .energy-type__name {
                                    font-size: 15px;
                                }
                            }
                        }  
                    }
                }
                .cfgmsg{
                    margin: 0px auto;
                }
            }
        }
    }
    .content-popup-wrapper.live-status.ls__web{
        &.multi-envoy{
            height: 100% !important;
            display: flex;
            flex-direction: column;
            .live-status__body{
                margin: 0px 15px 15px;
                .live-status__energy-flow {
                    display: flex;
                    height: auto;
                    margin: auto;
                }
                .energy-type.energy-type__icon__has-animation{
                    margin: unset;
                }
            }
            .custom-dropdown{
                .overlay{
                    z-index: 98;
                }
            }
            .pvOnly-multienvoy.custom-dropdown{
                display: none;
            } 
            &.pvOnlyWeb{
                .content-popup__header.title-wrapper{
                    .pvOnly-multienvoy.custom-dropdown {
                        display: flex;
                        background-color: #F6F7FB;
                        @include caption;
                        text-transform: capitalize;
                        border-radius: 6px;
                        margin-right: 20px;
                        width: max-content;

                        &.disabled{
                            .dropdown__button{
                                cursor: unset;
                            }
                        }
              
                        .dropdown__button {
                          padding: 8px 6px 6px 12px;
                          cursor: pointer;
              
                          .icon.envoyDropDown {
                            margin-left: 5px;
              
                            rect {
                              opacity: 0;
                            }
                          }
              
                          .dropdown__buttonText {
                            display: flex;
                            font-size: 16px;
              
                            .gateway_num {
                              margin-left: 10px;
                              color: #01B4DE;
                            }
                          }
                        }
              
                        .dropdown__content {
                          display: flex;
                          flex-direction: column;
                          align-items: center;
                          background-color: #F6F7FB;
                          box-shadow: none;
                          max-height: 100px;
              
                          .dropdown-item {
                            width: 100%;
                            font-size: 16px;
                            padding: 8px;
                            justify-content: center;
                            cursor: pointer;
                            &.selected{
                              color: #01B4DE;
                            }
                          }
              
                          .icon.SelectedIcon {
                            display: none;
                          }
              
                          li {
                            border-bottom: 0.5px solid #B4B6B8;
                            border-left: 0.5px solid #B4B6B8;
                            border-right: 0.5px solid #B4B6B8;
                          }
              
                          li:first-child {
                            border-radius: 6px 6px 0px 0px;
                            border-top: 0.5px solid #B4B6B8;
                          }
              
                          li:last-child {
                            border-radius: 0px 0px 6px 6px;
                          }
                        }
              
                        .overlay {
                          display: none;
                        }
                      }
                }
            }
        }
    } 
    .live-status-overlay {
        position: fixed;
        left: 50%;
        top: 50%;
        opacity: 0.6;
        height: 100%;
        width: 100%;
        transform: translate(-50%, -50%);
        z-index: 105;
        backdrop-filter: blur(1px);
        -webkit-backdrop-filter: blur(1px);
        background-color: rgba(0, 0, 0, 0.75);
    }
}