.events-history-details{
  margin: 20px 0 0;
  padding: 0 20px 20px;
  max-height: 430px;
  overflow-y: auto;

  .ehd__list{
    &:not(:first-child){
      margin-top: 20px;
    }

    .ehd__list__wrapper{
      display: flex;

      >div{
        &:not(:last-child){
          margin-right: 30px;
        }
      }
    }

    .ehd__title{
      font-size: 12px;
      color: #a2a6a9;
    }

    .ehd__details{
      font-size: 14px;
      font-weight: 300;
      margin-top: 4px;
      color: #545456;
      @include body-1;

      a, .link{
        cursor: pointer;
        color: #00b6de;
        text-decoration: none;
      }
    }

    .ehd__status{
      text-transform: capitalize;
    }
    .ehd__details{
      .link{
        color: #00b6de;
      }
    }
    
  }
}
.events-history-details-popup{
  z-index: 189 !important;
}

.night{
  .events-history-details{
    .ehd__list{
      .ehd__title{
        color: #8e9091;
      }

      .ehd__details{
        color:#f8f8ff;
      }
    }
  }
}
