.scale {
  position: fixed;
  display: flex;
  align-items: center;
  padding-bottom: 10px;
  height: 80px;
  width: 100%;
  background-color:  #F7F8FD;
  cursor: auto !important;

  &.replacement-warranty {
    height: 158px;
    @media screen and (min-width:768px) and (max-width:1023px){
      height: 168px;
    }
  }

  @media screen and (min-width:768px) and (max-width:1023px){
    height: 90px;
  }

  .exchange-warranty {
    border: solid 0.5px rgba(123, 135, 147, 0.3);
    border-right: none;
    border-left: none;
    &.disabled {
      display: none;
    }

    .exchange-warranty-container {
      margin: 14px 18px 12px;
    }

    .exchange-warranty-wrapper {
      display: flex;
      margin: 10px 0;

      font-size: 12px;
      color: #545456;

      .text {
        padding-left: 10px;
      }
    }

    .request-replacement-scale {
      font-size: 14px;
      font-weight: bold;
      color: #01b4de;
      padding-left: 30px;
      display: flex;
      &.disabled {
        display: none;
      }

      .icon{
        padding-left: 6px;
        margin-top: 1px;

        path{
          fill: #01B4DE;
        }
      }
    }
  }

  .production-array {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin: 0 18px;
    color: #545456;
    @include body-1;
    letter-spacing: normal;
    .prod-details {
      margin-top: 7px;
      .produced {
        letter-spacing: normal;        
        text-transform: capitalize;
        padding-bottom: 2px;
        margin: 0;
      }
     .power {
        font-size: 18px;
        font-weight: 500;
        margin: 0;
        @include title-2;
        text-transform: none;
        sup {
          font-size: 12px;
          top: -1px;
          padding-left: 5px;
          @include body-1;
        }
      }    
    }
    .site-details {
      line-height: 18px;
      text-transform: none;
      .site-name {
        margin: 0;
      }
      .site-id {
        font-size: 12px;
        margin: 0;
        text-align: end;

        span{
          margin-left: 10px;
        }
      } 
    }
  }

  label {
    color: $medium;
    margin: 0 1em;
    font-size: 0.85em;
  }
  label:nth-child(2) {
    float: right;
  }
  ul {
    height: 0.8em;
    width: 100%;
    bottom: 0;
    position: absolute;
  }
  li {  
    width: 1%;
    height: 100%;
    display: inline-block;
  }
}

.night {
  .scale {
    .exchange-warranty {
      .exchange-warranty-wrapper {
        .text {
          color: #f8f8ff;
        }
      }
    }
  }
}
