@import '../../../style/font-style.scss';

.om-popup__grid .om-configure-wifi-popUp {
  .popup__header {
    border-bottom: unset;

    .step {
      display: flex;
      flex-grow: 1;
      justify-content: center;
    }
  }

  .popup__body {
    height: fit-content;
  }

  .popup__body-content {
    padding-top: 2px;
    overflow-y: scroll;

    .progressBar {}

    .step1 {
      margin-top: 36px;

      .iconBlock {
        display: flex;
        justify-items: center;
        margin-bottom: 8px;

        .text {
          display: flex;
          @include caption;
          align-items: center;
          line-height: normal;
          margin-left: 8px;
        }
      }

      .infoBlock {
        .title {
          @include title-2;
          color: #000000;
        }

        .subtitle {
          @include body-1;
          color: #7B8793;
        }
      }
    }

    .step2 {
      margin-top: 36px;

      .iconBlock {
        display: flex;
        flex-direction: column;
        margin-bottom: 8px;

        .first {
          display: flex;

          .icon {
            display: flex;
          }

          .text {
            display: flex;
            align-items: center;
            @include title-2;
            margin-left: 8px;
            color: #000000;
          }
        }

        .joiningLine {
          margin: 0px 14px;
          display: flex;
        }

        .second {
          display: flex;

          .icon {
            display: flex;

          }

          .text {
            display: flex;
            @include caption;
            line-height: normal;
            align-items: center;
            margin-left: 8px;
          }
        }
      }

      .infoBlock {
        .title {
          @include title-2;
          color: #000000;
        }

        .subtitle {
          @include body-1;
          color: #7B8793;
        }
      }
    }

    .actionPane {
      height: 350px;
      margin-top: 20px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      // overflow-y: scroll;

      .refreshButton {
        display: flex;
        height: 40px;
        width: 80%;
        @include title-2;
        color: #FFFFFF;
        background-color: #01B5DE;
        align-items: center;
        border-radius: 20px;
        justify-content: center;
        margin: 0px auto 20px;
      }

      .loadingScreen {
        height: 100%;
        background-color: #F5F5F8;
        border-radius: 8px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 30px 40px;

        .icon.loaderV3 {
          display: flex;
          height: unset;
      
          circle {
            fill: none
          }
        }

        .title {
          @include title-2;
          color: #000000;
          margin-top: 20px;
          text-align: center;
        }

        .subtitle {
          @include body-1;
          margin-top: 8px;
          text-align: center;
        }
      }

      .connectActionScreen {
        height: 100%;
        background-color: #F5F5F8;
        border-radius: 8px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 30px;

        .icon {
          display: flex;
          height: fit-content;
        }

        .title {
          margin-top: 20px;
          @include title-2;
          text-align: center;
        }

        .subtitle {
          margin: 8px 0px 20px;
          text-align: center;
        }

        .actionButton {
          display: flex;
          height: 40px;
          width: 100%;
          @include title-2;
          color: #FFFFFF;
          background-color: #01B5DE;
          align-items: center;
          border-radius: 20px;
          justify-content: center;
        }

        &.scanWifi {
          .clickArea {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
          }

          .icon {
            &.activeWifi svg {
              width: 40px;
              height: 31px;

              g {
                fill: #01B5DE;
              }
            }
          }

          .title {
            color: #01B5DE;
          }
        }

        &.bleErrTryAp {
          .title {
            margin-top: 12px;
            text-align: center;
          }

          .tryApButton {
            margin-top: 20px;
            color: #01B5DE;
            background-color: transparent;
            border: 1px solid #01B4DE;
          }
        }

        &.noWifisFound {}
      }

      .wifiList {
        height: 100%;
        overflow-y: scroll;
        background-color: #F5F5F8;
        border-radius: 8px;
        display: flex;
        flex-direction: column;
        padding: 10px;

        .listItem {
          display: flex;
          align-items: center;
          margin: 0px 10px;
          justify-content: space-between;
          padding: 16px 10px 11px;
          border-bottom: 1px solid rgba(123, 135, 147, .2);
          min-height: 52px;

          .name {
            @include title-1;
          }

          .iconBlcok {
            display: flex;
            align-items: center;

            .icon.lockIcon {
              height: auto;
              margin-right: 8px;
            }
          }

          &:last-child {
            border: unset;
          }

          &.connecting {
            .name {
              @include title-2;
            }

            .iconBlcok {
              .icon.loaderV3 {
                display: flex;
                align-items: center;
                height: auto;

                svg {
                  width: 18px;
                  height: 18px;
                }
              }
            }
          }
        }
      }

      .refreshWifiListButton{
        @include title-1;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 16px;
        color: #01B5DE;
        margin-top: 25px;
        font-weight: 500;
        
        .icon.refreshWifiList {
          display: flex;
          height: auto;
          margin-right: 8px;
        }
        .refreshButton_text{
          display: flex;
          align-items: center;
          padding-top: 2px;
        }
      }

      .bluetoothNetwork {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 16px;
        border-radius: 8px;
        background-color: #F5F5F8;

        .name {
          @include title-1;
        }

        .icon.bluetoothBlack {
          display: flex;
          height: auto;

          svg {
            width: 16px;
            height: 16px;
          }
        }
      }
    }

    .connectedSucc-wrapper {
      height: 450px;
      display: flex;
      flex-direction: column;
      justify-content: center;

      .connectedSucc {
        display: flex;
        flex-direction: column;

        .icon.greenTickOutline {
          display: flex;
          height: auto;

          svg {
            width: 66px;
            height: 66px;
          }
        }

        .title {
          @include title-2;
          text-align: center;
          margin-top: 20px;
          padding: 0px 30px;
        }

        .subtitle {
          @include body-1;
          text-align: center;
          margin-top: 16px;
          padding: 0px 30px;
        }

        .gotItBtn {
          display: flex;
          height: 40px;
          width: 80%;
          @include title-2;
          color: #FFFFFF;
          background-color: #01B5DE;
          align-items: center;
          border-radius: 20px;
          justify-content: center;
          margin: 30px auto 10px;
        }
      }
    }
  }

  .gatewayStatus {
    display: flex;
    flex-direction: column;
    padding: 20px 16px;
    background-color: #F5F5F8;
    border-radius: 8px;

    &.ntReachable {
      .statusCard .connectBtn {
        background-color: #A2ABB3;
      }
    }

    .statusCard {
      display: flex;
      align-items: center;

      .icon {
        display: flex;
        align-items: center;
        transform: translateY(-4px);
      }

      .textBlock {
        display: flex;
        flex-direction: column;
        margin-left: 12px;
        flex-grow: 1;

        .envName {
          @include title-1;
          color: #3F4041;
          line-height: normal
        }

        .statusInfo {
          @include body-1;
          color: rgba($color: #3F4041, $alpha: 0.5);
        }
      }

      .connectBtn {
        background-color: #01B5DE;
        height: 22px;
        border-radius: 11px;
        display: flex;
        align-items: center;

        .text {
          display: flex;
          align-items: center;
          margin: 4px 12px;
          @include body-2;
          line-height: normal;
          color: #FFFFFF;
        }

        .text_android {
          display: flex;
          align-items: center;
          margin: 4px 12px 2px;
          @include body-2;
          line-height: normal;
          color: #FFFFFF;
        }
      }
    }

    .help {
      display: grid;
      row-gap: 16px;
      padding-top: 18px;
      margin-top: 20px;
      border-top: 1px solid #D8D9D9D0;

      .title {
        @include body-2;
      }

      .instruction-item {
        display: flex;
        align-items: center;

        .icon.numberInCircle {
          display: flex;
          align-items: center;
        }

        .info {
          margin-left: 10px;
        }

        .learnHow {
          @include body-2;
          margin-left: 2px;
          text-decoration: underline;
          color: #01B5DE;
        }
      }
    }
  }

  .failed{
    .statusCard {
      .textBlock{
        .statusInfo {
          color: #F54040;
        }
      }
    }
  }
}
.om-popup__grid{
.popup__wrapper{
  &.cant-find-wifi-nw{
   .popup__header{
     padding-left: 16px;
   }
   .body-weak-signal{
    div:first-child{
      padding-bottom: 24px ;
    }
   }
  }
}
}

.showup{
  &.show-weak-signal{
    .showup__wrapper{
        .showup__inner-wrapper{
          // padding: 16px;
          .header{
            color:  #545456;
            @include title-2;
            padding: 16px;
            border-radius: 10px 10px 0 0;
            box-shadow: 0px 1px 0px 0px rgba(123, 135, 147, 0.30);
            }
            .icon{
              &.warningAlert{
                margin-top: 24px;
                svg{
                  height: 60px;
                  width: 60px;
                  g{
                    path{
                      &:first-child{
                        stroke: #6B7073;
                        fill:none;
                      }
                      fill:#6B7073;
                    }
                  }
                }
              }
          }
            .body-weak-signal{
              padding: 16px 16px 24px;
              color:  #545456;
              @include body-1;
              div:last-child{
                padding-top: 24px ;
              }
              div:first-child{
                padding-bottom: 24px ;
              }
            }
          }
        }
        .showup__footer{
          border-top: none;
          padding: 0 16px 16px;
          .showup__actions{
            display: flex;
            justify-content: space-between;
            flex-direction: row-reverse;
            column-gap: 16px;
            .showup__action{
              border-radius: 8px;
              border: 1px solid  #01B5DE;
              width: 100%;
              padding: 12px 16px;
              margin:0;
              &.weak-signal-ok{
                background: #01B5DE;
                color: #fff;
                border:none;
                display: flex;
                align-items: center;
                justify-content: center;
                span {
                  min-width: 0px;
                  padding: 0;
                }
              }
              &.weak-signal-cancel{
                display: flex;
                align-items: center;
                justify-content: center;
                span {
                  min-width: 0px;
                  padding: 0;
                }
              }
            }
          }
        } 
      }
    
  
}

.om-popup__grid .enterPassword {
  .popup__header {
    justify-content: unset;

    .close_popup {
      display: none;
    }

    .backButton {
      display: flex;
      padding-top: 10px;
      padding-bottom: 10px;

      .icon.arrowDown {
        display: flex;
        transform: rotate(90deg);

        svg {
          width: 16px;
          height: auto;
        }
      }

      .text {
        margin-left: 8px;
        color: #01B5DE;
      }
    }

    .title {
      display: flex;
      flex-grow: 0.7;
      justify-content: center;
      padding-top: 10px;
      padding-bottom: 10px;
    }
  }

  .popup__body-content {
    .title {
      @include title-2;
      margin-bottom: 20px;
    }

    .inputLabel {
      @include body-1;
    }

    .password-inputField {
      position: relative;

      .passwordInput {
        padding: 8px 0px;
      }

      .toggle-password {
        position: absolute;
        right: 0;
        top: 0;

        .icon.showPassword {
          display: flex;
        }
      }
    }

    .joinBtn {
      display: flex;
      height: 40px;
      width: 80%;
      @include title-2;
      color: #FFFFFF;
      background-color: #01B5DE;
      align-items: center;
      border-radius: 20px;
      justify-content: center;
      margin: 50px auto 40px;

      &.disable {
        filter: grayscale(1);
      }
    }

    .icon.loadingCircle {
      display: flex;
      margin: 50px auto 40px;
      height: auto;
      justify-content: center;

      svg {
        height: 40px;
      }
    }
  }

  &.inactive {
    .popup__body-content {
      color: #b1b1b6;

      .spinner {
        margin: 50px auto 40px;
        display: flex;
        justify-content: center;
        position: relative;
        width: 100%;
        height: 40px;

        &::before {
          position: unset;
          margin: unset;
          top: auto;
          left: auto;
          width: 40px;
          height: 40px;
          border: 2px solid #f2f4fa;
          border-top-color: #01B5DE;
        }
      }
    }
  }

  &.incorrect {
    .popup__body-content {
      .password-inputField .passwordInput {
        border-bottom: 1px solid #F26B6B;
      }

      .incorrectPassword {
        color: #F26B6B;
        margin-top: 7px;
      }
    }
  }
}

.night {
  .om-popup__grid{
  .popup__wrapper{
    &.cant-find-wifi-nw{
     .popup__header{
       color: #fff;
     }
    }
  }
}
  .showup{
    &.show-weak-signal{
      .showup__wrapper{
          .showup__inner-wrapper{
            
            .header , .body-weak-signal{
              color: #f8f8ff;
            }
            .icon{
              &.warningAlert{
               
                svg{
                  g{
                    path{
                      &:first-child{
                        stroke: #fff;
                        fill:none;
                      }
                      fill:#fff;
                    }
                  }
                }
              }
          }
          }
        }
      }
    }
  .om-popup__grid .om-configure-wifi-popUp {
    .popup__header {
      .step {
        color: #f8f8ff;
      }
    }

    .popup__body-content {
      .progressBar {
        #progressGradient {
          #remaining {
            stop-color: rgba(1, 181, 222, 0.1);
          }
        }
      }

      .step1 {
        .iconBlock {
          .icon.connectPhoneToEnv {
            svg circle {
              fill: rgba(1, 181, 222, 0.1);
            }
          }
        }

        .infoBlock {
          .title {
            color: #f8f8ff;
          }

          .subtitle {
            color: #8e9091;
          }
        }
      }

      .step2 {

        .first,
        .second {
          .text {
            color: #f8f8ff;
          }
        }

        .second {
          .icon.connectEnvToWifi {
            svg circle {
              fill: rgba(1, 181, 222, 0.1);

              &#Oval {
                fill: #01B4DE;
              }
            }
          }
        }

        .infoBlock {
          .title {
            color: #f8f8ff;
          }
        }
      }

      .actionPane {
        .gatewayStatus {
          background-color: #333;
          box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.25);

          .statusCard {
            .textBlock {
              .envName {
                color: #f8f8ff;
              }

              .statusInfo {
                color: #8e9091;

                &.android_style {
                  margin-top: 4px;
                }
              }

            }
          }

          .help {
            .instruction-item {
              .info {
                color: #8e9091;
              }
            }
          }
        }

        .failed{
          .statusCard {
            .textBlock{
              .statusInfo {
                color: #F54040;
              }
            }
          }
        }

        .loadingScreen {
          background-color: #333;
          box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.25);

          .title {
            color: #f8f8ff;
          }

          .subtitle {
            color: #8e9091;
          }
        }

        .connectActionScreen {
          background-color: #333;

          .subtitle {
            color: #8e9091;
          }
        }

        .bluetoothNetwork {
          background-color: #333;

          .name {
            color: #f8f8ff;
          }
        }

        .wifiList {
          background-color: #333;

          .listItem {
            .iconBlcok {
              .icon.lockIcon {
                svg path {
                  fill: #f8f8ff;
                }
              }

              .icon.activeWifi {
                svg g {
                  fill: #f8f8ff;
                }
              }
            }
          }
        }
      }
    }
  }

  .om-popup__grid .enterPassword {
    .popup__header {
      .title {
        color: #f8f8ff;
      }
    }

    .password-inputField {
      .passwordInput {
        background-color: transparent !important;
      }

      .icon.showPassword,
      .icon.hidePassword {
        svg path {
          &#eyeBg {
            fill: #262627;
          }
        }
      }
    }
  }
}