#offline-mode {
    .pes-popup {
        .wrapper {
            .container {
                .pes-popup-header {
                    text-align: left;

                    .header-technical-error {
                        padding: 24px 16.5px 22.45px;
                        border-bottom: 1px solid #C6C8CA;
                        font-size: 18px;
                        font-weight: 500;
                        color: #545456;
                        line-height: normal;
                    }
                }
                .pes-popup-content {
                    display: flex;
                    flex-direction: column;

                    &.turned-off-popup {
                        padding: 15px;
                        margin-bottom: 0;
                    }

                    .message-technical-error {
                        font-size: 14px;
                        text-align: center;
                        color: #545456;
                        margin-bottom: 33px;
                        width: 220px;
                        margin-left: auto;
                        margin-right: auto;
                        font-weight: 400;
                        line-height: normal;

                        &.turned-off-popup-message {
                            margin-bottom: 0px;
                        }
                    }
                }
                .pes-popup-action {
                    a {
                        text-decoration: none;
                    }

                    .footer-technical-error {
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        font-size: 16px;
                        font-weight: 500;
                        color: #fff;
                        padding: 12px 16px 12px 16px;
                        margin: 16px;
                        border-radius: 8px;
                        background: #01B5DE;
                        cursor: pointer;
                        line-height: normal;
                    }
                }
            }
        }
    }

    .night {
        .pes-popup {
            .wrapper {
                .container {
                    .pes-popup-header {
                        .header-technical-error {
                            border-bottom: 1px solid #545456;
                            color: #FFFFFF;
                        }
                    }
                    .pes-popup-content {
                        .message-technical-error {
                            color: #FFFFFF;
                        }
                    }
                }
            }
        }
    }
}
