.learn-more-about-profiles{
  .live-status__energy-flow{
    .live-status__energy-flow-wrapper{
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    .live-status__energy-flow-inner-wrapper{
      padding: 0;
      max-width: 350px;
      margin: auto;
    }

    .live-status__energy-flow__left{

    }

    .live-status__energy-flow__center{
      display: flex;
      justify-content: center;
      align-items: center;

      .live-status__system-grid-status{
        .energy-type{
          margin-top: 40px;
        }

        &.disconnected{
          .energy-type__icon{
            opacity: .5;
          }
        }
      }

      .live-status__generator{
        .energy-type{
          margin-top: 40px;
        }
      }

      .live-status__consumed{
        .energy-type{
          margin-top: 40px;
        }
      }

      .live-status__energy-flow__arrows{
        position: relative;
        width: 120px;
        height: 120px;

        .icon{
          height: 100%;
        }

        .live-status__energy-flow__arrows-top, .live-status__energy-flow__arrows-bottom{
          display: flex;
          justify-content:space-between;
          width: 100%;
          height: 50%;

          >div{
            width: 50%;
          }

          .icon{
            svg{
              width: 60px;
              height: 60px;
            }
          }
        }

        .live-status__energy-flow__arrows-horizontal, .live-status__energy-flow__arrows-vertical{
          display: flex;
          justify-content: center;
          align-items: center;
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
        }

        .live-status__energy-flow__arrows-horizontal{
          .icon{
            display: flex;

            svg{
              width: 120px;
            }
          }
        }

        .live-status__energy-flow__arrows-vertical{
          .icon{
            svg{
              height: 120px;
            }
          }
        }
      }
    }

    .live-status__energy-flow__bottom{

    }

    /* Overriding energy-type styles */
    .energy-type{
      width: 75px !important;
      text-align: center;
      margin: auto;

      .energy-type__icon-with-details{
        display: block;
        margin: auto;
        width: 75px !important;

        .energy-type__icon{
          margin: auto auto 7px;
          width: 40px;
          height: 40px;
        }

        .energy-type__value{
          font-size: 16px;
        }

        .energy-type__value-loading{
          .threeDotLoader {
            margin: 0;

            svg{
              width: 30px;
              height: 20px;
            }
          }
        }

        .energy-type__name,
        .energy-type__sub-title {
          font-size: 12px;
        }
      }
    }
  }
}