.browser {
	.header-menu-wrapper {
    position: fixed;
    top: 80px;
    height: calc(100% - 80px);
    width: 100%;
    z-index: 106;
		.header-menu-list {
			z-index: 105;
    //   transform: translateX(0px);
      width: 325px;
			right: 0;
			position: absolute;
			height: 100%;
      background-color: #ffffff;
      transition: all 0.3s ease-in-out;
			overflow-y: scroll;
			padding-bottom: 86px;
			.list-view__logout {
				background: none;
			}
      .header-menu-item {
        padding: 30px 0;
        margin: 0px 32px;
        border-bottom: solid 1px rgba(123, 135, 147, 0.3);
        &:last-child {
          border-bottom: none;
				}
				.list-view__wrapper {
					padding: 0px;
				}
				.list-view__title, .accordion-title {
					font-size: 20px;
        	color: #545456;
				}
				.menu-settings-wrapper, .system-statistics {
					.list-view {
						margin-top: 12px;
						border: none;
						cursor: pointer;
						.list-view__wrapper {
							padding: 10px;
							padding-right: 0px;
						}
						.list-view__title {
							font-size: 17px;
							color: #929699;
						}
						.list-view__subtitle {
							font-size: 11px;
							padding-top: 5px;
						}
						
						&:hover {
							background-color: rgba(216, 216, 216, 0.2);
							.list-view__title {
								color: #545456;
							}
						}

						&.card-guide:hover {
							background-color: #ffffff;
						}

						&:first-child {
							margin-top: 20px;
						}
          }
          
          .list-view__gridswitch{
            .toggle-switch{
              text-align: right;
        
              .toggle-switch__label{
                color: #7b8793;
                font-weight: 300;
              }
            }
					}
				}
      }
		}
		
		.mwg__guide-wrapper {
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			text-align: center;
			margin-top: 12px;
			color: #fff;

			.mwg__guide-note {
				margin: 12px 0;
				@include title-2;
			}

			.mwg__skip-btn,
			.mwg__got-it-btn {
				border-color: #fff;
				background: transparent;
				border-radius: 4px;
				width: 105px;
				height: 38px;
				border: 1px solid #fff;
				color: #fff;
				display: flex;
				justify-content: center;
				align-items: center;
				cursor: pointer;
				pointer-events: auto;
				@include title-2;
			}

			.mwg__got-it-btn {
				text-transform: uppercase;
			}
		}
    &.hideMenu {
      z-index: -1;
      .overlay {
        display: none;
      }
      .header-menu-list {
        transform: translateX(325px);
      }
    }
	}
}


.reorganisedMenu{
	.header-menu-list {
		width: 458px !important;
		&>div >div.header-menu-item{
			border-bottom: solid 1px rgba(123, 135, 147, 0.3) !important;
		}
		&>div >div.list-view__logout{
			border-bottom: none !important;
		}
		.list-view__icon{
			margin-right: 23px !important;
			height: 30px;
			width: 30px;
			.notificationIcon{
				svg {
					height: 20px;
					g {
						fill: #545456
					}
				}
			}
		}
		.header-menu-item{
			padding: 0 !important;
			margin: 0 !important;
			border-bottom: none !important;
			.accordion-title-wrapper{
				margin: 0px 32px;
				padding: 30px 0px;
				border-bottom: solid 1px rgba(123, 135, 147, 0.3);
			}
			.open{
				border-bottom: none;
				margin: 0;
				padding: 30px 32px;
				box-shadow: inset 0px -2px 0px #0000000D;
			}
			.menu-list__body-wrapper{
				background-color: #F6F7FB;
				.title-header{
					background-color: white;
					padding: 20px 30px;
				}
				.list-view{
					background-color: #F6F7FB;
					margin: 0px !important;
					padding: 18px 30px !important;
					.list-view__wrapper{
						padding: 0 !important;
						.list-view__title{
							font-size: 20px;
						}
						.list-view__subtitle{
							font-size: 16px;
						}
					}
				}
				& > div:not(:last-child) div.list-view{
					border-bottom: solid 0.5px rgba(123, 135, 147, 0.3) !important;
				}
				&  div.section-contents > div:last-child div.list-view{
					border-bottom: none !important;
				}
			}
		}
		.list-view{
			padding: 30px 0 !important;
    		margin: 0px 32px !important;
		}
	}
	.referFriend{
		position: fixed;
		bottom: 0;
		background-color: #F37320;
		color: white;
		width: 100%;
		height: 86px;
		display: flex;
		align-items: center;
		padding: 0 32px;
		cursor: pointer;

		.icon{
			margin-top: 15px;
		}
		.rf-title{
			align-self: stretch;
			margin: 10px;
			margin-left: 20px;
			border-left: 1px solid white;
			padding-left: 20px;
			display: flex;
			align-items: center;
		}
	}
	.accordion-left{
		display: flex;
		align-items: center;
	}
}