@import '../../../style/font-style.scss';

.night .settingsConnectionRetry-Container .contentPlaceholder p {
    color: #ffffff !important;
    // margin-top:0px !important;
}

.night .ojasPowerProfileSetting,
.ojasBrightnessSettings,
.ojasSurroundLightSettings,
.ojasAdvanceSettings {
    .settingsConnectionRetry-Container {
        background: #262627;
        padding-bottom: 10px;
    }
}

.night .pesSettingsMenu .pes-settings-list {
    background: none !important;
}

.night {
    .pesSettingsMenu .settingsConnectionRetry-Container {
        padding-top: 10px !important;
        padding-bottom: 10px !important;
        background: #262627;
    }
}

.night .pes-connection-ble {
    p,
    span,
    .reset-done-text {
        color: #ffffff !important;
    }

    .overlay {
        background: none;
    }

}

.settingsConnectionRetry-Container {
    // background-color: #FFFFFF;
    padding-left: 19px;
    padding-right: 19px;
    padding-top: 10px;
    position: relative;
    background: rgba(255, 255, 255, 0.5);

    .contentPlaceholder {
        align-items: center;
        justify-content: space-between;
        // background-color: #F9F9F9;
        font-size: 12px;
        line-height: normal;
        position: relative;
        z-index: 1;
        color: #7B8793;
        text-align: center;
    }

    p {
        // margin-top: 14px !important;
        margin-bottom: 8px !important;
        // margin-left: 13px;
        font-weight: normal;
    }

    a {
        font-size: 12px;
        line-height: 22px;
    }

    .links {
        color: #01b5de;
        text-decoration: none;
        margin-right: 14px;
        padding-left: 13px;
    }

}

.day {
    .ojasPowerProfileSetting,
    .ojasBrightnessSettings,
    .ojasSurroundLightSettings,
    .ojasAdvanceSettings {
        .settingsConnectionRetry-Container {
            background: #FFFFFF;
            padding-bottom: 10px;
        }
    }
}

.showRetryConnection {
    height: 96px !important;
}

.reset-connect-btn-bottom {
    position: absolute;
    padding-top: 2px;
    bottom: 0px;
}

.reset-connect-icon-bottom {
    position: absolute;
    bottom: 20%;
    height: 0px !important;
}

.reset-device-dashboard .reset-connect-btn {
    margin-bottom: 25% !important;
}

.icon-bluetooth {
    height: 39.13px;
    width: 49.13px;
}

.pes-connection-ble .pes-popup .wrapper .container .icon-bluetooth {
    margin-bottom: 31px;
    margin-top: 14px;
}

.error-ble {
    height: 40px;
    width: 40px;
    margin-bottom: 25px !important;
    margin-top: 30.5px !important;
}

.pes-connection-ble,
.settingsConnectionRetry-Container,
.pes-try-ltr .button-left {
    width: 100% !important;
    border-right: 0px !important;
    color: #01B4DE !important
}

.pes-connection-ble {
    .wrapper .container {
        display: flex !important;
    }

    .pes-popup .wrapper .container .header-bluetooth {
        text-align: left;
    }
}

// .ojasPowerProfileSetting .list-container {
//   height: 96px !important;
// }

// div.settingsConnectionRetry > div:not(.contentPlaceholder){
//     opacity: 50%;
// }

.ojas-status-screen .reset-connect-btn-bottom {
    bottom: 27px;
}

.settingsConnectionRetry-Container .pes-popup .wrapper .container {
    width: 285px;
    overflow-y: auto;
    font-weight: normal;
    height: 100%;
    display: flex;
}

.settingsConnectionRetry-Container .pes-popup .wrapper .container .icon-bluetooth {
    margin-bottom: 31px;
    margin-top: 14px;
}

.ble-connect .wrapper {
    height: auto !important;
    max-height: 100% !important;
    border-radius: 10px !important;
    overflow: hidden !important;
}

@media screen and (min-width:990px) {
    .reset-device-dashboard .reset-connect-btn {
        margin-bottom: 10% !important
    }
}

.ble-connect {
    .icon {
        max-width: none !important;
    }
}

.night {
    .settingsConnectionRetry-Container {
        background: #262627;
    }
}
