@import '../../../offline/styles/font-style.scss';

.connectivity-gateway {
  height: 100%;
  display: flex;
  flex-direction: column;

  .envoyCards-wrapper {
    overflow-y: scroll;
    flex-grow: 1;

    .wrapper-title {
      @include caption;
      color: #919CA9;
      margin: 26px 20px 16px;
    }

    .gatewayCard {
      margin: 20px;
      padding: 16px 16px 20px;
      background-color: #FFFFFF;
      border-radius: 8px;

      .name-wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;

        .name {
          @include title-2;
          line-height: normal;
          margin-bottom: 2px;
        }

        .serialNo,
        .sku {
          @include body-1;
          color: rgba($color: #3F4041, $alpha: 0.5)
        }

        .icon.loadingCircle {
          display: flex;
          align-items: center;
          height: auto;

          svg {
            width: 16px;
            height: 16px;
          }
        }
      }

      .connectionType {
        margin-top: 20px;
        @include title-2;
      }

      .currentStatus {
        display: flex;
        justify-content: space-between;
        margin-top: 13px;
        padding-bottom: 6px;
        border-bottom: 1px solid rgba(123, 135, 147, .2);

        .statusText {
          @include body-1;

          &.fail {
            color: #F54040;
          }

          &.success {
            color: #35C37D;
          }

          &.unknown{
            color: #7B8793;
          }
        }

        .icon.statusIcon {
          display: flex;
        }

      }

      .action-wrapper {
        display: flex;
        justify-content: space-between;
        margin-top: 16px;

        .text {
          .title {
            @include title-1;
            color: #01B4DE;
          }

          .subtitle {
            @include body-1;
            color: #7B8793;
          }
        }

        .icon.menuNext {
          display: flex;

          svg path {
            fill: #01B4DE;
          }
        }
      }

      .connection-type.connectivity {
        margin-bottom: 20px;

        .name {
          @include body-1;
        }

        .active {

          .name,
          .status {
            @include body-2;
          }
        }
      }
    }
  }

  .tutorialLink {
    margin: 16px 25px;
    @include body-1;
    color: #545456;
    display: flex;
    justify-content: center;

    .clickHere {
      margin-left: 5px;
      color: #01B4DE;
      @include body-2;
      text-decoration: underline;
      cursor: pointer;
    }
  }
}


.night {
  .connectivity-gateway {
    .envoyCards-wrapper {
      .wrapper-title {
        color: #8e9091;
      }
      .gatewayCard {
        background-color: #2a2a2b;
        box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.25);
        .name {
         color: #f8f8ff
        }
  
        .serialNo,
        .sku {
          color: #d8dcde
        }
        .currentStatus .statusText {
          &.fail {
            color: #F54040;
          }

          &.success {
            color: #35C37D;
          }
        }
        .action-wrapper .text .subtitle {
          color: #8e9091;
        }
      }
    }
    .tutorialLink {
      color: #8e9091;
    }
  }
}