.browser {
  .popup-container {

    .content-wrapper {
      padding: 32px;
      height: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;

      .cw__input-wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
        font-size: 17px;
        font-weight: normal;
        color: #545456;
        
        .cw__input-error-text {
          font-size: 14px;
          color: #ff5959;
          margin-top: 10px;
        }

        .cw__input-text {
          margin-top: 20px;
          font-size: 17px;
          color: #545456;

          &.with-error-text {
            margin-top: 12px;
          }

          .cw__input-resend-text {
            font-weight: 500;
            color: #01b4de;
            cursor: pointer;
          }
        }
      }

      .cw__content-text {
        font-size: 17px;
        line-height: 1.24;
        text-align: center;
        color: #545456;
      }
    }

    .cw__button-group {
      display: flex;
      flex-direction: column;

      button {
        width: 315px;
        height: 50px;
      }

      .bg__yes {
        border-radius: 25px;
        background-color: #00b6de;
        color: #fff;
        font-size: 17px;
        font-weight: 500;
        text-align: center;
        color: #ffffff;
        text-transform: uppercase;
        border-color: #00b6de;
        border: unset;
      }

      .bg__no {
        text-align: center;
        color: #01b4de;
        font-size: 17px;
        background: transparent;
        border: none;
        margin-top: 32px;
        height: auto;
        text-transform: uppercase;
      }

      &.single {
        margin: 30px;

        & button {
          color: #FFFFFF;
          border: none;
          background: #01B4DE 0% 0% no-repeat padding-box;
          border-radius: 20px;
          opacity: 1;

          &.bg__yes {
            text-align: center;
            letter-spacing: 0px;
            color: #FFFFFF;
            opacity: 1;
          }
        }
      }
    }
    
    &.off-grid-confirmation, &.try-again-confirmation, &.on-grid-confirmation, &.grid-contol-disabled-alert, &.warnProdltCons {
      .popup-body {
        height: 100%;

        .content-wrapper {
          .alertOutline,
          .alertOutline svg {
            width: 60px;
            height: 60px;
          }
        }
      }
    }

    &.grid-contol-disabled-alert, &.warnProdltCons {
      .popup-body {
        .content-wrapper {
          .cw__content-text {
            margin-top: 20px;
            font-weight: normal;
            .Obutton{
              font-size: inherit;
            }
          }
    
          .cw__button-group {
            margin: 30px 30px 0;
          }
        }
      }

      .popup-wrapper {
        z-index: 107;
      }
    }
    &.warnReduceLoadOnGrid .cw__content-text{
      padding: 10px 10px 20px;
      & .Obutton.link{
        font-size: inherit;
      }
    }
  }
}

.grid-overlay {
  position: absolute;
  left: 50%;
  top: 50%;
  background-color: #000000;
  opacity: 0.2;
  height: 100%;
  width: 100%;
  transform: translate(-50%, -50%);
  z-index: 107;

  &.grid-overlay__mobile {
    position: fixed;
  }
}

.grid-spin-container {
  position: absolute;
  width: 5em;
  height: 5em;
  border-radius: 50%;
  top: 50%;
  left: 50%;
  margin-top: -2.5em;
  margin-left: -2.5em;
  z-index: 107;
  opacity: 1;
  transition: opacity 600ms;

  &.grid-spin-container__mobile { position: fixed; }

  .spinner:before {
    content: '';
    box-sizing: border-box;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 1.2em;
    height: 1.2em;
    margin-top: -0.6em;
    margin-left: -0.6em;
    border-radius: 50%;
    border: 0.1em solid $super-light;
    border-top-color: $secondary;
    animation: spinner 700ms linear infinite;
  }
}

.verify-otp.grid-control__mobile .popup-wrapper {
  .popup-title-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .popup-title { line-height: 1; }
    .close-button {
      width: 14px;
      margin-right: 5px;
      .close { display: flex; }
    }
  }
  .popup-body .content-wrapper {
    .cw__content-text.cw__content-text--verify-otp {
      margin: 20px 15px 15px;
    }
    .cw__input-wrapper{ margin-bottom: 20px;}
    .cw__button-group.single {
      margin: 20px 30px 25px;
      align-items: center;
      height: 40px;
    }
  }
}
.grid-control__mobile {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 105;

  & .popup-wrapper {
    border-radius: 10px;
    background-color: #ffffff;
    position: relative;
    z-index: 106;

    & .popup-title-wrapper {
      padding: 20px 17px 17px;
      border-bottom: 1px solid #c6c8ca;
      font-size: 16px;
      font-weight: 500;
      color: #545456;

      &.no-title { display: none; }
    }

    & .popup-body {
      overflow: auto;

      & .content-wrapper {
        overflow: auto;

        & span.icon {
          width: 48px;
          height: 48px;
          margin: 30px auto 0;

          & svg {
            width: 48px;
            height: 48px;
          }

          &.alertOutline,
          &.alertOutline svg {
            width: 60px;
            height: 60px;
          }
        }
        &.warnProdltCons .cw__content-text{
          font-size: 12px;
          font-weight: normal;
          margin-top: 20px;
          line-height: 16px;
          .Obutton{
            font-size: inherit;
          }
        } 
        & .cw__content-text {
          margin: 30px;
          font-size: 16px;
          font-weight: 500;
          text-align: center;
          color: #545456;
          @include body-1;

          &.cw__content-text--verify-otp {
            margin: 20px;
            font-size: 12px;
            font-weight: normal;
            text-align: left;
          }
        }

        & .cw__input-wrapper {
          margin: 0 20px 40px;
          font-size: 14px;
          font-weight: normal;
          color: #545456;

          & .cw__input { margin-bottom: 10px; }

          & .cw__input-error-text {
            font-size: 12px;
            font-weight: normal;
            color: #ff5959;
          }

          & .cw__input-text {
            padding-top: 10px;
            font-size: 12px;
            font-weight: normal;
            color: #545456;

            & .cw__input-resend-text {
              font-weight: 500;
              color: #01b4de;
            }
          }
        }

        & .cw__button-group {
          display: flex;
          flex-direction: row-reverse;
          border-top: 1px solid #d8dcde;

          & button {
            width: 50%;
            padding: 18px 0;
            border: unset;
            font-size: 16px;
            font-weight: normal;
            color: #545456;
            text-transform: capitalize;
            background: unset;

            &.bg__yes {
              font-weight: 500;
              color: #01b4de;
              border-left: 1px solid #d8dcde;
            }
          }

          &.single {
            margin: 30px;
            border-top: none;
            background: #01B4DE 0% 0% no-repeat padding-box;
            border-radius: 20px;
            opacity: 1;

            & button {
              width: 100%;
              color: #FFFFFF;

              &.bg__yes {
                text-align: center;
                letter-spacing: 0px;
                color: #FFFFFF;
                opacity: 1;
              }
            }
          }
        }
      }
    }
  }

  &.grid-contol-disabled-alert {
    .cw__content-text {
      font-weight: normal !important;
    }

    .bg__ok-got-it {
      height: 40px;
      padding: 10px 0 !important;
      font-weight: 500 !important;
    }
  }
}

.cw__my-info-link{
  padding: 0;
  border: unset;
  color:  #00b6de;
  text-decoration: underline;
  background: unset;
}

.night {
  .verify-otp.grid-control__mobile {
    .popup-title-wrapper {
  
      .close-button .icon.close {
        fill: #ffffff;
      }
    }
  }
  .grid-control__mobile {
    & .popup-wrapper {
      background-color: #262627;
    
      & .popup-title-wrapper {
        border-color:rgba(123, 135, 147, 0.3);;
        color: #ffffff;
      }

      & .popup-body {
        & .content-wrapper {
          & span.icon path { fill: transparent; }

          & span.alertOutline path { fill: #ff5959; }

          & .cw__content-text { color: #ffffff; }

          & .cw__input-wrapper {
            color: #ffffff;

            & .cw__input-text { color: #ffffff; }
            & .cw__input-mobile-status{ color:  #ffffff; }
          }

          & .cw__button-group {
            border-color: rgba(123, 135, 147, 0.3);

            & button.bg__no { color: #d8dcde; }
            & button.bg__yes { border-color: rgba(123, 135, 147, 0.3); }
          }
        }
      }
    }
  }
}

@media (min-width:368px){
  .verify-otp.grid-control__mobile .popup-wrapper {
    width: 307px !important;
  }
}