.slider-rating {
    width: 290px;

    &.smaller {
        width: 242px;
    }
    &.hide-smiley {
        .rangeslider__handle {
            &::before {
                content: "" !important;
                background: none !important;
            }
        }
    }
  .slider-rating_range {
      display: flex;
      width: 100%;
      flex-direction: row;
      justify-content: space-between;
      opacity: 0.5;
      font-size: 12px;
      color: #545456;
      align-items: flex-end;

      .selected {
          visibility: hidden;
      }
  }
  .rangeslider-horizontal {
    height: 10px;
    margin-top: 5px;
    object-fit: contain;
    border-radius: 10px;
    border: solid 0.5px #e2e2e2;
    background: #f5f5f5; 

    &.slider-rating-0 {
        .rangeslider__handle {
            &::before {
                content: "0";
            }
        }
    }
    &.slider-rating-1 {
        .rangeslider__handle {
            &::before {
                content: "1";
            }
        }
    } 
    &.slider-rating-2 {
        .rangeslider__handle {
            &::before {
                content: "2";
            }
        }
    }
    &.slider-rating-0, &.slider-rating-1, &.slider-rating-2 {
        .rangeslider__fill{
            background-image: linear-gradient(90deg,rgb(225,13,18) 50%,rgb(225,13,18) 100%);
        }
        .rangeslider__handle {
            &::before {
                color: #e10d12;
                background-image: url(../../images/rating012.png),url(../../images/board.png);
            }
        }
    }
    &.slider-rating-3 {
        .rangeslider__fill{
            background-image: linear-gradient(90deg,rgb(225,13,18) 0%,rgb(225,13,18) 60%,rgb(262,118,61) 100%);
        }
        .rangeslider__handle {
            &::before {
                content: "3";
            }
        }
    }
    &.slider-rating-4 {
        .rangeslider__fill{
            background-image: linear-gradient(90deg,rgb(225,13,18) 0%,rgb(225,13,18) 39%,rgb(255,118,61) 71%,rgb(255,118,61) 100%);
        }
        .rangeslider__handle {
            &::before {
                content: "4";
            }
        }
    }
    &.slider-rating-3, &.slider-rating-4 {
        .rangeslider__handle {
            &::before {
                color: #ff763d;
                background-image: url(../../images/rating34.png),url(../../images/board.png);
            }
        }
    }
    &.slider-rating-5 {
        .rangeslider__fill{
            background-image: linear-gradient(90deg,rgb(225,13,18) 0%,rgb(225,13,18) 39%,rgb(255,118,61) 71%,rgb(255,118,61) 83%,rgb(255,165,52) 100%);
        }
        .rangeslider__handle {
            &::before {
                content: "5";
            }
        }
    }
    &.slider-rating-6 {
        .rangeslider__fill{
            background-image: linear-gradient(90deg,rgb(225,13,18) 0%,rgb(225,13,18) 25%,rgb(255,118,61) 48%,rgb(255,118,61) 63%,rgb(255,165,52) 78%,rgb(255,165,52) 100%);
        }
        .rangeslider__handle {
            &::before {
                content: "6";
            }
        }
    }
    &.slider-rating-5, &.slider-rating-6 {
        .rangeslider__handle {
            &::before {
                color: #fb940a;
                background-image: url(../../images/rating56.png),url(../../images/board.png);
            }
        }
    }
    &.slider-rating-7 {
        .rangeslider__fill{
            background-image: linear-gradient(90deg,rgb(225,13,18) 0%,rgb(225,13,18) 22%,rgb(255,118,61) 42%,rgb(255,118,61) 69%,rgb(255,165,52) 79%,rgb(255,165,52) 82%, rgb(255,211,1) 100%);
        }
        .rangeslider__handle {
            &::before {
                content: "7";
            }
        }
    }
    &.slider-rating-8 {
        .rangeslider__fill{
            background-image: linear-gradient(90deg,rgb(225,13,18) 0%,rgb(225,13,18) 29%,rgb(255,118,61) 44%,rgb(255,118,61) 11%,rgb(251,148,10) 70%,rgb(255,165,52) 76%,rgb(255,211,1) 87%,rgb(255,226,52) 100%);
        }
        .rangeslider__handle {
            &::before {
                content: "8";
            }
        }
    }
    &.slider-rating-7, &.slider-rating-8 {
        .rangeslider__handle {
            &::before {
                color: #ffd301;
                background-image: url(../../images/rating78.png),url(../../images/board.png);
            }
        }
    }
    &.slider-rating-9 {
        .rangeslider__fill{
            background-image: linear-gradient(90deg,rgb(225,13,18) 0%,rgb(225,13,18) 19%,rgb(255,118,61) 39%,rgb(255,118,61) 49%,rgb(251,148,10) 56%,rgb(255,165,52) 66%,rgb(255,211,1) 78%,rgb(255,226,52) 89%,rgb(184,238,0) 100%);
        }
        .rangeslider__handle {
            &::before {
                content: "9";
                color: #b8ee00;
                background-image: url(../../images/rating9.png),url(../../images/board.png);
            }
        }
    }
    &.slider-rating-10 {
        .rangeslider__fill{
            background-image: linear-gradient(90deg, rgb(225, 13, 18) 0%, rgb(225, 13, 18) 16%, rgb(255, 118, 61) 30%, rgb(255, 118, 61) 39%, rgb(251, 148, 10) 49%, rgb(255, 165, 52) 60%, rgb(255, 211, 1) 69%, rgb(255, 226, 52) 79%, rgb(184, 238, 0) 88%, rgb(29, 169, 47) 100%);
        }
        .rangeslider__handle {
            &::before {
                content: "10";
                color: #1da92f;
                padding-right: 5px;
                background-image: url(../../images/rating10.png),url(../../images/board.png);
            }
        }
    }

  }
  .rangeslider__handle {
    left: 114px;
    width: 20px;
    height: 20px;
    object-fit: contain;
    box-shadow: 0 2px 4px 0 rgba(153, 153, 153, 0.5);
    border: solid 1px #00b6de;
    background-color: #ffffff;
    
    &::before {
        content: "";
        position: absolute;
        height: 67px;
        width: 50px;
        background-size: 19px 19px,50px 73px;
        background-repeat: no-repeat;
        bottom: 0;
        background-position-x: 7px,0;
        background-position-y: 7px,0;
        right: 50%;
        text-align: end;
        display: flex;
        left: 50%;
        -webkit-transform: translate(-50%);
        transform: translate(-50%);
        justify-content: flex-end;
        padding-right: 8px;
        padding-top: 8px;
        font-size: 16px;
        font-weight: 700;
    }
  }
  .slider-rating_title {
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      opacity: 0.5;
      font-size: 12px;
      color: #545456;
      margin-top: 10px;
      text-transform: capitalize;

      div:last-child {
          text-align: end;
      }
  }
  .slider-rating_smiley-wrapper {
      position: absolute;
      bottom: 85%;
      pointer-events: none;
      cursor: none;

      &.smaller {
        bottom: 94%;
        left: -16px;
      }
      .slider-rating_smiley-board {
          position: absolute;
          top: 0;
          width: 49px;
          height: 36px;
          display: flex;
          justify-content: center;
          align-items: center;
          .icon {
              height: 17px;
              svg {
                  width: 17px!important;
                  height: 17px!important;
              }
          }
          .ratings-text {
              font-size: 16px;
              margin-left: 5px;
              font-weight: bold;

              &.rating0,&.rating1,&.rating2 {
                  color: #e10d12;
              }
              &.rating3,&.rating4 {
                  color: #ff763d;
              }
              &.rating5,&.rating6 {
                  color: #fb940a;
              }
              &.rating7,&.rating8 {
                  color: #ffd301;
              }
              &.rating9 {
                  color: #b8ee00;
              }
              &.rating10 {
                  color: #1da92f;
              }
          }
      }
  }
}

.night {
  .slider-rating {
    .css-1enyfh {
        border: none;
    }
    .slider-rating_title, .slider-rating_range {
        color: #d8dcde;
    }
    .rangeslider-horizontal {
        border: 1px solid #e2e2e2;
    }
  }
}