#offline-mode {
  .pesProfileUpdate {
    top: 70px !important;
  }

  .settingsConnectionRetry-Container {
    padding: 16px;

    .unable-to-connect {
      color: #545456;
      font-size: 12px;
      font-style: normal;
      font-weight: 500;
      text-align: left;
      margin-bottom: 16px;
    }
  }
}

.night {
  #offline-mode {
    .settingsConnectionRetry-Container {
      .unable-to-connect {
        color: #FFFFFF;
      }
    }
  }
}
