.date-picker {
  background-color: $background;
  /* transition: height 400ms ease-in-out, opacity 400ms ease-in-out; */
  position: absolute;
  overflow: hidden;
  z-index: 102;
  width: 100%;
  top: 15.5%;
  height: 0%;
  opacity: 0;

  &.open {
    opacity: 1; 
    height: 100%;
    .scale { opacity: 1 };
  }
  .disabled--override {
    width: 72%;
    height: 8%;
    position: absolute;
    z-index: 3;
    left: 14%;
  }

  .scale {
    bottom: #{$footer-height};
  }

  input {
    display: none;
  }

  .rdt {
    font-size: 1.3em;
    color: $super-dark;
    background-color: $background;
    
    .rdtPicker {
      width: 100%;
      border: none;
      padding: 0;
      margin: 0;
      font-weight: 300;
      @include title-1;
      text-transform: none;
      table {
        border-spacing: 0;
        border-collapse: collapse;
        border: none !important;
      }

      thead {
        background-color: $secondary;
        
        tr:first-child {
          height: 3.8em;
          th {
            font-size: 2.8vh;
            font-weight: 500;
            @include title-1;
            color: $background;
            border: none;
            &:hover { background-color: transparent; }
            span { display: none; }
          }
          .rdtPrev, 
          .rdtNext { width: 3em; }
          .rdtPrev { background: url('../images/datepicker/prev.svg') no-repeat 50% / 20%; }
          .rdtNext { background: url('../images/datepicker/next.svg') no-repeat 50% / 20%; }
        }
      }

      .dow {
        font-weight: 400;
        font-size: 0.7em;
        @include title-1;
        line-height: normal;
        color: $background;
        height: 3.5em;
        opacity: 0.7;
      }

      td {
        padding: 0;

        &.rdtDay {
          height: 3em;
        }
        &.rdtMonth,
        &.rdtYear {
          height: 5.4em;
        }
  
        &.rdtToday {
          font-weight: 800;
          color: $super-dark;
          &:before {
            border-width: 0;
          }
        }
  
        &.rdtActive {
          color: $background;
          font-weight: 600;
          span {
            width: 2.8em;
            height: 2.8em;
            display: inline-block;
            border-radius: 2.8em;
            line-height: 2.8em;
            background-color: $primary !important;
          }
        }
  
        &.rdtDisabled, 
        &.rdtDisabled:hover {
          opacity: 0.3;
        }
          
        &.rdtOld, 
        &.rdtNew {
          color: rgba(0, 0, 0, 0.15);
        }
      }

      tfoot {
        display: none;
      }

    }
  }
}

.night{
  .date-picker{
    background-color: #262627 !important;
    .rdt{
      background-color: #262627 !important;
      .rdtPicker{        
        border-style: none !important;
        border-color: none !important;
        border-width: 0 !important;
        border-radius: none;
        table, tr, td{
          border-collapse: collapse;
          border: none !important;
          thead{
            background-color: #333333;
            th{
              border:none;
            }
          }
          tbody{
            background-color: #262627 !important;
            td{
              &.rdtOld, 
              &.rdtNew {
                color: #636363 !important;
              }
              &.rdtToday {
                background-color: #262627 !important;
                color:#fff;
              }
            }
          }
        }
      }
    }
  }
  .cs-0{
    background-color: #262627 !important;
    color:#fff;
  }
}