

.rangeslider-horizontal .rangeslider__fill {
  background-color: #34c4e5;
}

.rangeslider-horizontal {
  height: 14px;
  border-radius: 10px;
  background: #fafafb;
  border: solid 1px #34c4e5;
  box-shadow: none;
}

.rangeslider__handle-tooltip {
  display: none;
}

.rangeslider .rangeslider__handle {
  border: solid 1px #01b5de;
  box-shadow: none;
  background-color: #01b5de;
}

.rangeslider-horizontal .rangeslider__handle:after {
  content: none;
}

.rangeslider__labels .rangeslider__label-item {
  font-size: 12px;
  font-weight: 300;
  color: #6f6f8c;
}

.rangeslider-horizontal .rangeslider__handle-tooltip {
  display: none;
}

.rangeslider .rangeslider__fill {
  box-shadow: none;
}

.rangeslider {
  box-sizing: content-box;
  margin: 7px 0;
}

.battery-reserve {
  height: 100%;
  overflow-y: auto;
  position: relative;
  overflow-x: hidden;
  .rangeslider__labels {
		margin-top: 6px;
    line-height: normal;
	}
  .failure-error-message {
    background-color: #ff5959;
    height: auto;
    max-height: 10%;
    font-size: 12px;
    color: #ffffff;
    padding: 1em 2em;
    width: 100%;
    display: flex;
    align-items: center;
    
    &.disabled {
      display: none;
    }
    &.enabled {
      position: absolute;
      z-index: 1;
    }
  
    .error-icon{
      margin-right: 10px;
    }
  }

  [data-value="20"],[data-value="30"],[data-value="40"],[data-value="60"],[data-value="70"],[data-value="80"],[data-value="90"]{
    visibility: hidden;
  
    &:after {
      content: "";
      visibility: visible;
      position: absolute;
      top: 5px;
      left: 0;
      background-image: url('../images/oval.png');
      background-size: 5px 5px;
      background-repeat: no-repeat;
      display: inline-block;
      width: 5px;
      height: 5px;
    }
  }
  .minSocChanged {
    [data-value="10"] {
      visibility: hidden;
  
      &:after {
        content: "";
        visibility: visible;
        position: absolute;
        top: 5px;
        left: 0;
        background-image: url('../images/oval.png');
        background-size: 5px 5px;
        background-repeat: no-repeat;
        display: inline-block;
        width: 5px;
        height: 5px;
      }
    }
    [data-value="5"] {
      padding-right: 5px;
    }
  }

  .reserve-container {
    font-size: 14px;
    font-weight: 500;
    color: #545456;
    padding: 20px;
    @include body-2;
  }



  .reserve-progress-slider{
    margin: 7px 20px;
    .rangeslider-horizontal {
      height: 7px;
      background-color: rgb(233, 233, 233);
      border: none;
    }
  
    .rangeslider .rangeslider__handle {
      border: 4px solid white;
      box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.3);
      
    }
  }

  .reserve-percent-container {
    margin: 12px 20px;
    font-size: 12px;
    font-weight: 300;
    color: #6f6f8c;
    @media screen and (min-width:320px) and (max-width:360px){
      margin: 12px 10px 12px 16px;
    }

    .right {
      padding-right: 30px;
    }

    .oval-icon {
      padding: 0 14px;
      display: inline-block;
      @media screen and (min-width:768px) and (max-width:1024px) and (orientation: portrait){
        padding: 0 30px;
      }
      @media screen and (min-width:768px) and (max-width:1024px) and (orientation: landscape){
        padding: 0 42px;
      }
    }
  }


  .line-break {
    opacity: 0.5;
    border: 0.7px solid #c6c8ca;
    margin: 40px 20px 20px 20px;
  }

	.save-button {
		box-shadow: 1px 2px 10px 0 rgba(63, 67, 71, 0.08);
		border: 1px solid #00b6de;
		padding: 10px 0;
		background: #00b6de;
		color: white;
		text-align: center;
		border-radius: 2rem;
		width: 15rem;
		margin: 18px auto 0;
		justify-content: center;
		display: flex;
		font-size: 16px;
		position: absolute;
		bottom: 3%;
		left: calc(50% - 7.5rem - 0px);
		&.buttonCancel {
		  background-color: #fff;
		  color: #01b5de;
		  border: solid 1px rgba(198, 200, 202, 0.5);
		}
		&.buttonDisable {
		  background: #a2a6a9;
		  border: none;
		}
	
		&.quiet-margin {
		  margin-top: 40px;
		}
	  }

  .reserve__disclaimer {
    padding: 0 20px 0;
    font-size: 14px;
    font-weight: normal;
    line-height: 1.33;
    color: #7b8793;
  }
  .disclaimer{
    color: #4A4A4A;
    font-size: 12px;
    opacity: 0.6;
    margin: 65px 20px 20px 20px;
  }
}
.deactive-button{
  pointer-events: none;
}
.night {
  .battery-reserve {
    .rangeslider__labels .rangeslider__label-item {
      color: #d8dcde
    }
    [data-value="20"],[data-value="30"],[data-value="40"],[data-value="60"],[data-value="70"],[data-value="80"],[data-value="90"]{
      &:after {
        background-image: url('../images/oval-dark.png');
      }
    }
    .line-break {
      opacity: 1;
      border: .7px solid rgba(123, 135, 147, 0.3);
    }
    .disclaimer{
      color: #c6c8ca;
    }
    .minSocChanged {
      [data-value="10"]:after {
        background-image: url('../images/oval-dark.png');
      }
    }
  }
}