/* Energy Heading */
.energy-heading {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 15px 10px;
  margin: 5px auto 0;

  .icon{
    display: inline-flex;
  }

  .notices {
    display: inline-block;
    color: $background;
    background-color: #ff5959;
    border-radius: 1em;
    font-size: 0.6em;
    font-weight: 500;
    min-width: 1.7em;
    text-align: center;
    min-height: 1.7em;
    line-height: 1.7em;
    margin: 0 0.6em;
    vertical-align: top;
  }

  .energy-summary-info{
    p{
      margin: 0;
    }

    .title{
      display: flex;
      align-items: center;
      font-size: 20px;
      font-weight: 500;
      color: #545456;
      @media screen and (min-width:768px) and (max-width:1024px){
        font-size: 24px;
      }

      .current-selected-time{
        span{
          margin-left: 5px;
        }
      }

      .refresh {
        padding-left: 4px;
        &.enabled {
          display: block;
        }
        &.disabled {
          display: none;
        }
      }
    }
    .updated-info {
      padding-top: 5px;
      .last-updated-wrapper {
        display: flex;
        flex-wrap: wrap;

        span:first-child{
          margin-right: 2px;
        }
      }
      .updated-time {
        display: flex;
        align-items: center;
        opacity: 0.5;

        .updated-time__info-icon{
          margin-left: 5px;
          cursor: pointer;

          .icon{
            display: flex;
            width: 14px;
            height: 14px;
    
            svg{
              width: inherit;
              height: inherit;

              path:first-child{
                fill: #6c7074;
              }
            }
          }
        }
      }
    }
    .updated-info,
    .system-status {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      font-size: 12px;
      font-weight: normal;
      line-height: normal;
      letter-spacing: normal;
      color: #6c7074;

      & span.icon {
        display: flex;
        justify-content: flex-start;
        align-items: center;

        svg {
          width: auto;
        }

        &.wifi {
          margin-right: 7px;

          svg {
            width: 15px;
            height: auto;
          }
        }

        &.cellular {
          margin-right: 7px;

          svg {
            height: 12px;
          }
        }

        &.ethernet {
          margin-right: 10px;

          svg {
            height: 13px;
          }
        }

        &.refresh
        &.refresh svg {
          width: 14px;
          height: 14px;
        }

        &.rightCheck,
        &.systemError { margin-right: 7px; }

        &.rightCheck,
        &.rightCheck svg,
        &.systemError,
        &.systemError svg {
          width: 15px;
          height: 15px;
        }

        &.statusWarning { 
          margin-right: 7px; 
          & svg path{ fill: #D69800; }
        }
        &.statusWarning svg {
          width: 15px;
          height: 15px;
        }



        &.systemError svg path { fill: #ff5959; }
      }
      
      & b {
        font-weight: 500;
        text-transform: uppercase;
      }

      & .refresh__icon { margin-left: 8px; }

      & .refresh-chart {
        &.enabled { display: block; }

        &.disabled { display: none; }
      }

      &.system-status {
        & .system-normal { color: #35c37d; }
  
        & .system-error { color: #ff5959; }

        & .system-warning { color: #D69800; }
      }

      @media screen and (min-width:768px) and (max-width:1024px){
        font-size: 16px;

        &.system-status { font-size: 19px; }

        &span.icon {
          &.wifi { margin-right: 10px; }

          &.wifi,
          &.wifi svg {
            width: 21px;
            height: 14px;
          }

          &.cellular { margin-right: 12px; }

          &.cellular,
          &.cellular svg {
            width: 19px;
            height: 14px;
          }

          &.ethernet {
            margin-right: 7px;
            
            svg {
              width: 24px;
              height: 15px;
            }
          }

          &.refresh
          &.refresh svg {
            width: 20px;
            height: 20px;
          }

          &.rightCheck,
          &.systemError { margin-right: 10px; }

          &.rightCheck,
          &.rightCheck svg,
          &.systemError,
          &.systemError svg {
            width: 21px;
            height: 21px;
          }
        }

        & .refresh__icon { margin-left: 11px; }
      }
    }
  }

  .weather-and-live-status{
    .weather-wrapper{
      text-align: right;
    }

    .live-status-wrapper{
      margin-top: 15px;

      .live-status {
        /* display: table-cell; */
        align-items: center;
        display: inline-flex;
        vertical-align: middle;
        padding: 3px 7px;
        border-radius: 10px;
        background-color: #01b4de;
      }

      .live-status-text {
        font-size: 12px;
        color: #ffffff;
        text-transform: uppercase;
        white-space: nowrap;
        @include caption;
        line-height: 12px;
        padding-top: 1px;
      }

      .live-status-icon {
        display: inline-flex;
        height: 14px;
        width: auto;
        float: left;
        margin-right: 5px;
      }
      .statusPageLiveStatus.connect-locally-button-container {
        padding-top: 0px;
        .connect-locally-btn{
          border-radius: initial;
          border: initial;
          background-color: initial;
          padding: initial;
          color: initial;
          font-size: initial;
          font-weight: initial;
          display: initial;
          margin: initial;
          white-space: initial;
          text-transform: uppercase;
        }
      }
    }
  }
}

.grid-status__wrapper {
  margin-top: 5px;
  display: flex;
  align-items: center;
  font-size: 12px;
  font-weight: normal;
  line-height: 1;
  color: #6c7074;
  text-transform: capitalize;

  .grid-status {
    width: 15px;
    margin-right: 7px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-right: 1px;
  }

  .loading, svg {
    width: 15px;
    height: 15px;
  }

  span{
    width: 10px;
    height: 10px;
    border-radius: 50%;

    &.onGrid { background-color: #35c37d; }

    &.offGrid { background-color: #ff6464; }

    &.systemError,
    &.systemError svg {
      width: 15px;
      height: 15px;
    }

    &.systemError svg path { fill: #FF8B0C; }
  }

  .grid-status__info {
    margin-left: 5px;
    cursor: pointer;

    .infoIcon{
      display: flex;
      align-items: center;
      width: 14px;
      height: 14px;
    }
  }

  & > button.grid-status__refresh {
    width: 15px;
    height: 15px;
    padding: 0;
    margin: 0 0 0 5px;
    border: unset;
    background: unset;

    & span.icon,
    & span.icon svg {
      width: 15px;
      height: 15px;
    }
  }
}

.night {
  .energy-heading {
    .updated-info {
      .updated-time {
        color: #9aa2a8;
        opacity: 1;
      }

      .wifi svg path,
      .cellular svg g{
        fill: #ffffff;
      }

      .ethernet{ 
        svg {
          g {
            stroke: #f8f8ff;
          }
          path {
            fill: none;
          }
        }
      }
    }
  }
  
  .grid-status__wrapper {
    color: #c6c7c9;
  }
}

@media screen and (max-width:360px) {
  .energy-heading {
    padding-top: 4px;
    padding-bottom: 4px;
  }
}
