.gen-card--parent {
  display: flex;
  flex-direction: column;
  background: white;
  border-radius: 10px;
  margin: 1rem;
  box-shadow: 1px 2px 10px 0 rgba(63, 67, 71, 0.08);


  &.disabled {
    opacity: 0.4;
    border: 1px solid;
  }

  &.desktop {
    width: 100%;
    margin: 0 0 15px 0;
    border: 1px solid #88888834;
    position: relative;
    box-shadow: none;

    .activate {
      position: absolute;
      right: 20px;
      width: auto;
      border-radius: 20px;
      padding: 10px 50px;
      top: calc(50% - 19px);
      &.buttonDown {
        position: static;
        width: 15rem;
        display: flex;
        justify-content: center;
        margin: 0 auto;
        margin-bottom: 20px;
        border: solid 1px rgba(198, 200, 202, 0.5);
      }
      &.buttonCancel {
        border: solid 1px rgba(198, 200, 202, 0.5);
        box-shadow: 1px 2px 10px 0 rgba(63, 67, 71, 0.08);
      }
    }

    .gen-card {
      .edit-btn {
        font-size: 14px;
      }
      &.addExtraPadding {
        padding-bottom: 20px;
      }
      &.showActivate {
        padding-bottom: 0;
      }
      .heading {
        font-size: 16px;
        .heading--active {
          font-size: 16px;
        }
      }
      .subheading {
        font-size: 12px;
      }
    }
  }

  .gen-card {
    padding: 20px 0 20px;
    &.addExtraPadding {
        padding-bottom: 50px;
    }
    .edit-btn {
      cursor: pointer;
      padding: 5px 0 10px 20px;
      font-size: 12px;
      color: #01b5de;
      text-align: right;
    }

    .heading {
      display: flex;
      color: #4dc37d;
      align-items: center;
      font-size: 16px;
      padding: 0 25px;
      font-weight: 500;
      line-height: normal;

      .heading--active {
        font-size: 12px;
        position: relative;
        bottom: 1px;
      }

      .icon {
        margin-right: 10px;
        margin-top: 2px;
        &.pend {
          svg {
            g {
              fill: #4dc37d;
            }
          }
        }
      }

      &.inactive {
        color: #545456;
      }
    }

    .subheading {
      font-size: 12px;
      font-weight: 300;
      padding: 0 25px;
      margin-top: 0.6rem;
      color: #6f6f8c;
      line-height: 1.25;
      &.limited{
        width: 84%;
      }
    }
  }
  .activate {
    align-items: center;
    padding: 10px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    text-align: center;
    display: none;
    justify-content: center;
    color: #fff;
    font-size: 16px;

    &.showActivation {
      display: inherit;
      background: rgb(0, 182, 222);
    }

    &.buttonActivate {
      background-color: #01b5de;
    }

    &.buttonInActive {
      background-color: #a2a6a9;
    }

    &.buttonCancel {
      font-size: 16px;
      background-color: #fff;
      color: #01b5de;
      border-top: solid 1px rgba(198, 200, 202, 0.5);

      &.buttonDisabled {
        color: #c7c8ca;
      }
    }
  }
}

.night {
  .gen-card--parent {
    .gen-card {
      background-color: #262627;
      box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.25);
      color: #f8f8ff;
      .heading {
        &.inactive {
          color: #fff;
        }
      }

      .subheading {
        color: #a2a6a9;
      }
    }
    .activate {
      &.buttonCancel {
        background-color: #262627;
      }
    }
  }
}

.itk {
  .gen-card--parent {
    .activate {
      background-color: #f37320;
    }
    .gen-card {
      .edit-btn {
        color: #f37320;
      }
    }
  }
}
