.feedback-survey {
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    bottom: env(safe-area-inset-bottom);
    position: fixed;
    border-radius: 0;
    overflow: hidden;
    background: linear-gradient(#fff 20%,#f2f4fa);

    .feedback-survey__header-wrapper {
        padding: 56px 24px 26px 20px;
        color: #fff;
        background-color: #00b6de;
        min-height: 107px;
        box-shadow: 0 0 2px rgba(0,0,0,.4);
        display: flex;

        .feedback-survey__header {
            display: flex;
            flex: 1 1 auto;
            align-items: center;
            justify-content: space-between
        }

        .feedback-survey__title {
            font-size: 19px;
            color: #ffffff;
        }

        .icon {
            width: 15px;
            height: 15px;
        }
    }
}

.night {
    .feedback-survey {
        background-color: #262627;
        .feedback-survey__header-wrapper {
            background-color: #333333;
        }

        .feedback-survey__content {
            background-color: #262627;
        }
    }
}