.browser {
  .feedback {
    .feedback_content {
      width: 712px;
      height: 725px;
      max-width: 90%;
      max-height: 95%;
      min-height: fit-content;
      z-index: 106;

      .overlay {
        position: absolute;
        background-color: $screen;
        height: 100%;
        width: 100%;
        z-index: 1;
        border-radius: 10px;
      }

      &.without-installer {
        height: 500px;
      }

      .feedback-ratings {
        margin: 0px 5px;
        .feedback-ratings_content {
          padding: 0;
          overflow-y: auto;
          scroll-behavior: smooth;
          height: calc(100% - 160px);
          &::-webkit-scrollbar {
            width: 6px;
          }
          &::-webkit-scrollbar-thumb {
            border-radius: 3px;
            background-color: #c3c3c3;
          }

          .feedback-ratings_site-details {
            font-size: 15px;
            line-height: normal;
            color: #8e9091;
            margin-bottom: 1px;
            padding: 25px 32px 0 32px;
          }

          .feedback-ratings_title {
            font-size: 20px;
            font-weight: 500;
            line-height: normal;  
            color: #565657;
            margin-bottom: 0;
            padding: 0 32px 25px 32px;
            border-bottom: 1px solid #d8dcd3;
          }

          .feedback-ratings_subtitle {
            margin: 32px 0 10px;
            padding: 0 32px;
            font-size: 20px;
            font-weight: 500;
            line-height: normal;
            color: #545456;
          }

          .feedback-ratings_description {
            padding: 0 32px;
            font-size: 17px;
            color: #545456;
          }

          .feedback-ratings_textarea {
            padding: 20px 32px 0 32px;
            textarea {
              height: 125px;
              font-size: 17px;
              line-height: normal;
              color: #545456;
            }
          }

          .feedback-ratings_slider-wrapper {
            padding: 60px 32px 10px;
            .slider-rating {
              width: 643px !important;

              .slider-rating_range {
                opacity: 0.5;
                font-size: 15px;
                color: #545456;
              }

              .rangeslider-horizontal {
                height: 16px;
                margin: 10px 0;
                &.slider-rating-0 {
                  .rangeslider__handle {
                    &::before {
                      content: "0";
                    }
                  }
                }

                &.slider-rating-0, &.slider-rating-1, &.slider-rating-2 {
                  .rangeslider__handle {
                    &::before {
                      background-image: url(../../images/rating012.png),url(../../images/desktopBoard.png);
                    }
                  }
                }
                &.slider-rating-3, &.slider-rating-4 {
                  .rangeslider__handle {
                    &::before {
                      background-image: url(../../images/rating34.png),url(../../images/desktopBoard.png);
                    }
                  }
                }
                &.slider-rating-5, &.slider-rating-6 {
                  .rangeslider__handle {
                    &::before {
                      background-image: url(../../images/rating56.png),url(../../images/desktopBoard.png);
                    }
                  }
                }
                &.slider-rating-7, &.slider-rating-8 {
                  .rangeslider__handle {
                    &::before {
                      color: #ffd301;
                      background-image: url(../../images/rating78.png),url(../../images/desktopBoard.png);
                    }
                  }
                }
                &.slider-rating-9 {
                  .rangeslider__handle {
                    &::before {
                      content: "9";
                      color: #b8ee00;
                      background-image: url(../../images/rating9.png),url(../../images/desktopBoard.png);
                    }
                  }
                }
                &.slider-rating-10 {
                  .rangeslider__handle {
                    &::before {
                      background-image: url(../../images/rating10.png),url(../../images/desktopBoard.png);
                    }
                  }
                }

                .rangeslider__handle {
                  width: 28px;
                  height: 28px;

                  &::before {
                    height: 83px;
                    width: 49px;
                    transform: translate(-50%, 6%);
                    background-size: 19px 19px,49px 84px;
                  }
                }
              }
              .slider-rating_title {
                opacity: 0.5;
                font-size: 15px;
                color: #545456;
              }
            }
          }

          .fr__checkbox-wrapper {
            padding: 0 32px;
            margin-top: 20px;
            margin-bottom: 0px;
            align-items: center;

            .fr__checkbox, .fr__checkbox input, .fr__checkbox input::before {
              width: 20px;
              height: 20px;
            }
            
            .fr__checkbox {
              margin-right: 10px;
              input {
                &:checked{
                  &::before{
                    margin-left: -1px;
                    margin-top: -1px;
                    zoom: 1;
                  }
                }
              }
            }

            .fr__checkbox-label {
              font-size: 17px;
            }
          }
        }
        .feedback-ratings_button-group {
          margin: 32px 0px;

          .feedback-ratings_submit-buttom, .feedback-ratings_ignore-buttom {
            text-transform: uppercase;
          }
          .feedback-ratings_ignore-buttom {
            margin-top: 30px;
          }
        }
      }
      &.feedback-ratings_comments {
        height: 770px;
        &.without-installer {
          height: 525px;
          max-height: 95%;
        }
      }
    }
  }

  .feedback_overlay {
    z-index: 105;
  }

  .feedbackAlertToast {
    width: 462px;
    height: 66px;
    margin: auto;
    left: 0;
    right: 0;
    top: 14px;

    .messageWrapper {
      padding: 21px 20px;
      .toastMessage {
        font-size: 15px;
      }
      .feedbackAlert {
        width: 24px !important;
        height: 24px !important;
        svg {
          width: 24px !important;
          height: 24px !important;
        }
      }
    }
  }
}