.referral-tnc-page{
  padding: 20px;

  .rtncp__heading{
    font-size: 16px;
    color:#545456;
  }

  .rtncp__tnc-list{
    margin-left: 15px;

    li{
      font-size: 14px;
      margin-top: 20px;
      line-height: 1.3;
      list-style-type: disc;
      color:#5A5A5C;
      @include body-1;
    }
  }

  .rtncp__tnc-fwm{
    font-weight: 500;
  }
}

.referral-tnc-popup{
  height: 70%;
  width: 70% !important;
  overflow: hidden;

  .popup__header p{
    font-size: 20px;
  }

  .popup__body{
    height: calc(100% - 50px);
    overflow: auto;

    .popup__body-content{
      padding-top: 5px;
    }
  }

  .referral-tnc-page{
    padding: 0;
  }
}

.night{
  .referral-tnc-page{
    .rtncp__heading{
      color:#f8f8ff;
    }
  
    .rtncp__tnc-list{
      li{
        color:#d8dcde;
      }
    }
  }
}