.browser {
    .footer-bar {
        width: 100%;
        height: 90px;
        min-width: 1024px;
        position: relative;

        .site-details-container {
            background-color: $secondary;
            height: 45px;
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            .site-details {
                font-size: 15px;
                font-weight: normal;
                font-style: normal;
                font-stretch: normal;
                line-height: normal;
                letter-spacing: normal;
                color: #ffffff;
            }
        
            .site-change {
                font-size: 13px;
                font-weight: normal;
                font-style: normal;
                font-stretch: normal;
                line-height: normal;
                letter-spacing: normal;
                color: #91f1ff;
                padding-left: 10px;
                text-decoration: underline;
                cursor: pointer;
            }
        }
        .footer-details {
            display: flex;
            padding: 0 32px;
            height: 45px;
            align-items: center;
            background-color: #ffffff;
            .copyright-wrapper {
                display: flex;
                flex-basis: 50%;
                align-items: center;
            }
            .copyrights-text {
                font-size: 15px;
                color: #545456;
                padding-right: 26px;
            }
            .privacy-terms {
                display: flex;
                a {
                    font-size: 15px;
                    color: #01b5de;
                }
                .divider {
                    width: 1px;
                    margin: 0 10px;
                    border: 0.5px solid rgba(84, 84, 86, 0.3);
                }
            }
            .weather-data {
                margin-left: auto;
                display: flex;
                font-size: 15px;
                color: #545456;
                span {
                    padding-right: 5px;
                }
                a {
                    color: #01b5de;
                }
            }
            @media only screen and (max-width: 1140px) {
                .copyright-wrapper {
                    flex-basis: 55%;
                }
                .copyrights-text {
                    font-size: 12px;
                }
                .privacy-terms {
                    a {
                        font-size: 12px;
                    }
                }
                .weather-data {
                    font-size: 12px;
                }
            }
        }
    }
    .site-option-list {
        height: 100%;
        display: flex;
        flex-direction: column;
        background-color: #fafafa;
        .loading-wrapper {
            height: 100%;
            .loading {
                display: flex;
                justify-content: center;
                height: 100%;
                svg {
                    width: 50px;
                    height: 50px;
                    margin-top: 2rem;
                    path {
                        fill: #01b5de;
                      }
                  }
            }
          }

        .MuiTabs-root {
            background: #fff;
            margin: 0 16px 0 32px;
            border-bottom: 1px solid #47B5DE;

            .MuiButtonBase-root {
                font-size: 20px;
                font-weight: 300;
                opacity: 1;
                letter-spacing: 0;
                color: #47B5DE !important;
                padding-left: 0;

                &.Mui-selected {
                    font-weight: bold;
                }

                .MuiTab-wrapper {
                    align-items: flex-start !important;
                }
            }

            .MuiTabs-indicator {
                width: 120px !important;
            }
        }

        ul {
            height: 100%;
            overflow: hidden;
            overflow-y: auto;
            margin-right: 5px;
            padding-right: 5px;
        }

        li {
            display: flex;
            min-height: 42px;
            padding-left: 32px;
            padding-right: 16px;
            align-items: center;
            cursor: pointer;
            background-color: #ffffff;
            justify-content: space-between;

            .site-with-icon {
                display: flex;
                width: 100%;

                .change-site-name {
                    width: 100%;
                }

                .star-icon {
                    margin-right: 10px;
                }
            }

            &.active {
                .change-site-name {
                    font-weight: 500;
                    color: $secondary;
                }

                .star-empty .icon svg g path {
                    stroke: #47B5DE;
                }

                .star-filled .icon svg g path {
                    stroke: #47B5DE;
                    fill: #47B5DE;
                }
            }
            &:hover {
                background-color: #d8d8d833;
            }

            .star-empty .icon svg g path {
                stroke: #545456;
            }

            .star-filled .icon svg g path {
                stroke: #545456;
                fill: #545456;
            }
        }

        .scroll::-webkit-scrollbar {
            width: 6px;
        }
        
        .scroll::-webkit-scrollbar-track {
           background-color: #fafafa;
        }
        
        .scroll::-webkit-scrollbar-thumb {
           border-radius: 3px;
           background-color: #c3c3c3;
        }
   
       .change-site-name {
           font-size: 17px;
           font-weight: normal;
           font-style: normal;
           font-stretch: normal;
           line-height: normal;
           letter-spacing: normal;
           color: #545456;
       }
   
       .icon {
           display: flex;
           height: 19px;
           cursor: pointer;
           svg {
               width: 19px;
               height: 19px;
               fill: #545456;
           }
       }

       .menu-search-wrapper {
            background: #fff;

            .msw__search-input {
                margin: 0 32px 14px;
                position: relative;
                border: 1px solid #545456;
                border-radius: 5px;
                display: flex;
                align-items: center;

                .close, .close svg {
                    height: 15px;
                    width: 15px;
                }
            }

            input {
                padding-left: 1em;
                border: none;
            }

            .icon {
                opacity: 1;
                margin: 0 0.5rem;
            }
        }

        .no-site-found {
            height: 100%;
            background: #fff;
            color: #545456;
            font-size: 17px;
            display: flex;
            justify-content: center;
            padding-top: 15px;
        }
    }

    .footer {
        &.menu-with-search {
            .popup-body {
                overflow: hidden;

                .search-site-list {
                    height: 287px;
                }

                .site-option-list {
                    background: #fff;
                }
            }
            .popup-title-border {
                display: none;
            }
        }
    }
}
