.browser {
    .latest-site {
        padding: 32px;
        height: max-content;

        .card__site-details{
          grid-column-start: 1;
          grid-row-start: 1;
          grid-row-end: 3;
        }
        
        .card {
            flex-basis: 49%;
            height: auto;
            border-radius: 3px;
            box-shadow: 0 2px 10px 0 rgba(63, 67, 71, 0.08);
            background-color: #ffffff;
            padding: 32px;
            height: max-content;
            header {
                opacity: 0.5;
                font-size: 17px;
                font-weight: 700;
                line-height: normal;
                letter-spacing: .5px;
                color: #7b8793;
                margin-bottom: 12px;
            }
            h3 {
                font-size: 20px;
                font-weight: 500;
                line-height: normal;
            }
            p {
                font-size: 17px;
                font-weight: normal;
                line-height: 1.41;
                margin: 0;
            }
            hr {
                border: 0.5px solid rgba(123,135,147,.2);
                margin: 0;
            }
            .site-description-title {
              margin-top: 30px;
              text-transform: capitalize;
            }
        }
        p.site-id, p.site-description {
            margin-top: 5px;
        }
        p.address {
            margin-top: 20px;
        }
        .bkp-config{
          margin-top: 30px;
          padding-top: 30px;
        }
        .hr{
          border-top: 1px solid rgba(123,135,147,.3);
          margin-top: 30px;
        }
        .inst-contact-info {
            a {
                display: flex;
                flex-direction: row;
                text-decoration: none;
                &:first-child {
                    margin-bottom: 13px;
                    margin-top: 32px;
                }             
            }
            span {
                color: #01b4de;
                font-weight: normal;
                font-size: 17px;
            }
            span.icon {
                margin-right: 15px;
                svg, path {
                    fill: #01b4de;
                }
            }
        }
        figure {
            margin-top: 20px;
            .carousel .slider-wrapper.axis-horizontal .slider .slide{
                @media screen and (min-width:768px) and (max-width:1024px){
                  min-width: 60% !important;
                }
                @media screen and (min-width:768px) and (max-width:1024px) and (orientation : landscape){
                  min-width: 60% !important;
                }
            }
            .slider {
                left: -20%;
            }
            .carousel-status-wrapper {
                display: flex;
                justify-content: center;
                align-items: center;
                margin-top: 20px;
                .carousel-button, .icon, svg {
                    margin: 0;
                    width: 12px;
                    height: 19px;
                }
                svg {
                    fill: #545456;
                    stroke: #545456;
                }
            }
            .sliding-img {
                height: 225px;
                object-fit: cover;
                width: 100%;
                border-radius: 0px;
            }
            .slide.selected {
                min-width: 350px;
                padding-right: 10px;
            }
            .carousel-status {
                margin: 0 18px;
                color: #3f4347;
                text-shadow: none;
                font-weight: 500;
            }
        }
    }
}