/* Energy Graph */
@import "../font-style";
.energy-graph{
  margin-bottom: 20px;

  .energy-graph__wrapper{
    position: relative;
    padding: 0;
    height: 100%;

    >div{
      margin: auto;
    }

    .rotate-energy-graph{
      position: absolute;
      width: 100%;
      height: 50px;
      display: flex;
      justify-content: flex-end;
      align-items: center;
      z-index: 1;

      .rotate-graph{
        margin-right: 15px;
        z-index: 2;
        
        .icon{
          height: 30px;
        }

        &.cordova-plugin{
          
        }
      }
    }

    .series{
      position: relative;

      .series-wrapper{
        position: relative;
      }

      .energy-graph-vertical-line{
        stroke-width: 2;
        stroke: #d5dadd
      }

      .rect-series{
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: transparent;
      }
    }

    .energy-graph-series{
      position: relative;
      /*height: calc(100% - 4em);*/
      height: calc(18vh + 50px);

      &.series-with-negative-values{
        margin-top: -80px;
      }

      .energy-series{
        width: 100vw;
        overflow-y: hidden;
        overflow-x: auto;
      }
    }

    .series-xaxis-line{
      background: #d5dadd;
      height: 1px;
      margin-left: 44px;
      margin-right: 15px;

      &.line-battery{
        margin-top: 5px;
        margin-bottom: -10px;
      }
    }

    .battery-graph-series{
      height: 12vh;
    }

    .energy-legends{
      position: relative;
      max-width: 375px;
      margin: auto;
      @include body-1;
    }

    .graph-contols{
      display: flex;
      align-items: flex-start;
      justify-content: space-around;
      margin: 10px 15px;

      .toggle-switch{
        text-align: center;
        max-width: initial;
      }

      &.more-then-three-switchs{
        justify-content: space-between;
      }
      .yesterday,
      .battery {
        background-color: inherit;
      }
    }

    .production-only, .consumption-only{
      .energy-graph-series{
        height: 36vh;
      }
    }
  }

  .expand-btn{
    position: absolute;
    z-index: 3;
    right: 10px;
    top: 10px;

    path{
      fill: #545456;
    }
  }

  /* React-Vis */
  .series-with-positive-values{
    .rv-xy-plot {
      position: inherit;
    }
    .rv-xy-plot__axis--vertical{
      /* transform: translate(0, 0); */

      .rv-xy-plot__axis__ticks{
        /* transform: translate(45px, 45px); */
      }

      .rv-xy-plot__axis__title{
        font-size: 10px;
        font-weight: 500;
        color: #7b8793;
        transform: translate(40px, -37.5px) rotate(90deg);
        /* transform: translate(35px, 10px) rotate(90deg); */
      }
    }
  }

  .rv-xy-plot__axis__line{
    stroke-width: 1px;
    stroke:#d5dadd;
  }
  .rv-xy-plot__axis__tick__line{
    stroke: none;
  }

  .rv-xy-plot__series--line{
    stroke-width: 1;
    &.energy-data-previous-positive{
      stroke-width: 1.5;
    }
  }
}

.ios-browser {
  .energy-graph .series-wrapper,
  .energy-graph .series-wrapper * {
    perspective: unset !important;
    backface-visibility: unset !important;
  }
}

/* Screen Size :: 320 */
@media (max-width: 320px) {
  .energy-graph {
    .energy-graph__wrapper {
      .graph-contols{
        margin: 10px
      }
    }
  }
}

/* Screen Size :: 480 and above (Tablet) */
@media (min-width: 480px) {
  .energy-graph {
    .energy-graph__wrapper {
      .energy-legends{
        max-width: 480px;
      }

      .graph-contols{
        margin-left: 15px;
        zoom: 1.2;

        .toggle-switch{
          max-width: 100%;
        }
      }
    }
  }
}
