
.list-view{
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  background-color: #fdfefe;

  &:not(:first-child){
    border-top: solid 1px #f4f3f4;
  }

  &:hover{
    cursor: pointer;
    background-color: #fdfefe;
  }

  .icon{
    height: 100%;

    svg{
      height: 100%;
      margin: 0;
    }
  }

  &.list-view__whatsnew, &.list-view__privacypolicy, &.list-view__tos {
    padding: 5px 10px;
    .list-view__wrapper {

      padding: 18px 10px;
      border-bottom: 1px solid #efefef;
    }
  }

  &.list-view__tos {
    &.web {
    .list-view__wrapper {

      border-bottom: none;
    }
  }
  }

  .list-view__wrapper{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 18px 20px;
  }

  .list-view__icon{
    width: auto;
    height: 12px;

    .deviceStorage {
      rect {
        fill: #545456;
      }

      path:last-child {
        fill: #545456;
      }
    }
  }

  .list-view__content{
    display: flex;
    align-items: initial;

    .list-view__icon{
      width: 20px;
      height: 20px;
      margin-right: 15px;

      & span.cellular svg,
      & span.ethernet svg {
        width: 20px;
        height: auto;
      }
      & .icon.evCharge{
        display: flex;
      }
    }

    .list-view__title{
      font-size: 16px;
      color: #545456;
      @include title-1;
      .icon.infoIcon{
        display: inline-block;
        margin-left: 4px;
        svg{
          width: 1.4ch;
        }
      }
    }

    .list-view__badge, .list-view__newIndicator {
      display: inline-block;
      color: $background;
      background-color: #F37320;
      border-radius: 1em;
      font-size: 0.6em;
      font-weight: 500;
      min-width: 1.7em;
      text-align: center;
      min-height: 1.7em;
      line-height: 1.7em;
      margin: 0 0.6em;
      vertical-align: top;
    }
    .list-view__newIndicator{
      font-size: 0.2em;
    }

    .list-view__newIndicator-icon {
      display: inline-block;
      margin: 0 0.6em;
    }

    .list-view__subtitle{
      font-size: 12px;
      font-weight: 300;
      color: #7b8793;
      margin-top: 2px;
      @include body-1;
    }
  }

  .list-view__action{
    display: flex;
    align-items: center;

    .list-view__info{
      width: auto;
      height: 12px;
      margin-right: 10px;
    }

    .list-view__cta-button {
      padding: 0;
      border: unset;
      background: unset;
      font-size: 12px;
      font-weight: 500;
      line-height: normal;
      letter-spacing: normal;
      color: #00b6de;
      cursor: pointer;
    }
  }

  &.list-view__logout{
    background: #f6f7fb;
  }

  .normal-status-color{
    color: #60d09b;
  }

  .error-status-color{
    color: #ff5959; 
  }

  .warning-status-color{
    color: #F37320; 
  }
  
  @media screen and (min-width:1024px) {
    &.list-view__devmode { display: none !important; }
  }
}

.se-menu-list {
  & .list-view__wrapper { line-height: 22px; }
}

.hidden {
  display: none;
}

.night {
  .list-view .list-view__icon .deviceStorage path:last-child, 
  .list-view .list-view__icon .deviceStorage rect,
  .list-view .list-view__icon span.cellular svg g {
      fill: #f8f8ff;
  }

  .list-view .list-view__icon span.ethernet{
    svg {
      g {
        stroke: #f8f8ff;
      }
      path {
        fill: none;
      }
    }
  }
  .list-view .list-view__icon .loadControl svg g circle{
    fill: #262627;
    stroke: #262627;
  }
  .list-view {
    &.list-view__whatsnew, &.list-view__privacypolicy, &.list-view__tos {
      .list-view__wrapper {
        border-bottom: 1px solid #555556;
      }
    }
  }
}