// Common CSS
@import './font-style.scss';
.ojas-status {
  background: #ffffff;

  .ojas-status__content {
    height: calc(100% - #{$footer-height} -  46px);
    
    .ojas-status__wrapper {
      width:100%;
      height: 100%;
      padding: 20px 16px;
      overflow-x: hidden;
      overflow-y: auto;

      .ojas-status-screen {
        .grid-container-ojas{
          border: 1px solid #D8DCDE;
          padding: calc(.9375em - 2px) 0;
          margin: 15px 0 20px;

          .grid-sub-container{
            height: inherit;
            display: flex;
            align-items: center;
            justify-content: flex-start;

            .grid-columns{
              display: flex;
              justify-content: flex-start;
              align-items: center;
              border-right: 1px solid #d8dcde;
              padding-right: 10px;
              
              .grid-icon{ 
                .icon{ 
                  svg{
                    height: 40px !important;
                    width: 36px !important;
                    margin: 8.6px 3px 0px 8px
                  }
                  float: unset;
                  margin-right: 0.625em;
                }
              }
  
              .grid-details{
                flex-grow: 1;
                display: flex;
                flex-direction: column;
                
                p { margin: 0;}
                
                .header{
                  display: flex;
                  color: #A2ABB3;
                  @include caption;
                  line-height: 18px !important;
                  span { margin-left: 5px; }
                  margin-top: 8px;
                  padding-left: 5px;
                }
                .description {
                  @include body-1;
                  color: #545456;
                  font-weight: 400;
                  padding-left: 5px;
                }
    
              }
  
              &.grid-columns-two{
                width: 50%;
                padding: 0 15px;

              &:last-child{ border-right: none;}
              }
            }
          }
          
        }
      }
    }

    .ojas-status__header {
      display: flex;
      justify-content: space-between;
      border-bottom: 1px solid #0000001A;
      .header-title {
        display: flex;
        align-items: center;
        padding-bottom: 10px;
        h3 {
          margin: 0;
          font-size: 20px;
          line-height: 24px;
          font-weight: 500;
          color: #545456;
        }
        .header-device-status{
          display: flex;
          flex-direction: column;
          font-size: 14px;
          .wifi{
            display: inline-flex;
            color: #01B5DE;
            p{
              margin: 0;
              padding: 5px 4px;
            }
           svg { path{fill: #01B5DE;} } 
          }
          .bluetooth{
            color: #01B5DE;
            display: inline-flex;
            p{
              margin: 0;
              padding: 5px 4px;
            }
            svg {fill: white; path{fill: #01B5DE;} } 
          }
          .icon {
            height: 16px;
  
            svg {
              width: auto;
              height: 100%;
            }
          }
        }
      }

      .system-status {
        margin: 8px 0 0 0;
        display: flex;
        align-items: center;
        font-size: 12px;
        line-height: 1;
        color: #6c7074;

        .system-status--icon {
          margin-right: 7px;

          .icon {
            svg { width: auto; }
  
            &.rightCheck,
            &.rightCheck svg,
            &.systemError,
            &.systemError svg {
              width: auto;
              height: 15px;
            }
  
            &.systemError svg path { fill: #ff5959; }
          }
        }

        .system-normal { color: #35c37d; }

        .system-error { color: #ff5959; }
      }
      .ojas-status__header-right{
        display: flex;
        flex-direction: column;
        .power-control{
          display: flex;
          justify-content: center;
          align-items: center;
          background: #EAF9FD;
          border: none;
          padding: 8px 20px 7px 16px;
          color: #01B5DE;
          .powerSaveIcon{
            svg{
              .a{fill:#6c7074;opacity:0;}.b{fill:#01B5DE;}
            }
          }
          .icon{
            margin: 0px 4px 4px 0px;
          }
        }
        .power-saving{
          display: flex;
          justify-content: center;
          align-items: center;
          background: #FEF3EC;
          border: none;
          padding: 8px 20px 7px 16px;
          color: #F37320;
          .powerSaveIcon{
            svg{
              .a{fill:#6c7074;opacity:0;}.b{fill:#f37320;}
            }
          }
          .icon{
            margin: 0px 4px 4px 0px;
          }
        }
      }
    }

    .ojas-status__charge {
      margin-top: 25px;
      padding: 14px 16px;
      border-radius: 3px;
      border: 1px solid #D8DCDE;
      display: grid;
      grid-template-columns: 50% 50%;

      & > div {
        display: flex;
        align-items: center;

        &:first-child { border-right: 1px solid #D8DCDE; }
        &:last-child { padding-left: 22px; }

        .icon {
          height: 24px;
          margin: 4px 10px 0 0;
          align-self: flex-start;

          &.batteryCharging{ margin: 1px 10px 0 0; }
          svg {
            width: auto;
            height: 100%;
          }
        }

        .ojas-status__charge-info {
          font-size: 14px;
          line-height: 16px;
          font-weight: bold;
          color: #545456;

          p {
            margin: 0 0 2px 0;
            font-size: 12px;
            line-height: 14px;
            color: #A2ABB3;
          }
        }
      }
    }

    .ojas-status__graph {
      height: calc(100% - 94px);
      margin: 0 auto;
      display: grid;
      grid-template-columns: 100%;
      align-items: center;

      .live-status__body {
        margin: 0 auto;
        overflow: unset;

        .energy-type {

          .energy-type__value,
          .energy-type__value .energy-type__unit {
            margin-top: 0;
            @include title-2;
          }

          .energy-type__name {
            font-size: 12px;
            line-height: 14px;
          }

          &.energy-produced .energy-type__icon-with-details .energy-type__icon { border-color: #01B4DE; }
          &.energy-imported .energy-type__icon-with-details .energy-type__icon { border-color: #6B7073; }
          &.energy-discharged {
            .energy-type__value { color: #7ACF38; }
            .energy-type__icon-with-details .energy-type__icon { border-color: #7ACF38; }
          }
          &.energy-consumed {
            .energy-type__value { color: #F37320; }
            .energy-type__icon-with-details .energy-type__icon { border-color: #F37320; }
          }
        }

        .live-status__energy-flow__arrows {
          .energyGridToBattery svg > g > path:last-child,
          .energyGridToBattery svg > g g#pointer-grid-battery { fill: #545456; }
          .energyGridToBattery svg > g > path#motion-path-grid-battery { stroke: #545456; }

          .energyBatteryToHome svg > g > g > path:first-child,
          .energyBatteryToHome svg > g g#pointer-battery-home,
          .energyBatteryToGrid svg > g > path:last-child,
          .energyBatteryToGrid svg > g g#pointer-battery-grid { fill: #7ACF38; }
          .energyBatteryToGrid svg > g > path#motion-path-battery-grid,
          .energyBatteryToHome svg > g > g > path#motion-path-battery-home { stroke: #7ACF38; }

          .energySolarToBattery svg > g > g > g > path:first-child,
          .energySolarToBattery svg > g g#pointer-solar-battery { fill: #01B4DE; }
          .energySolarToBattery svg > g > g > g > path#motion-path-solar-battery { stroke: #01B4DE; }
        }
      }
    }
  }

  .connecting-to-ojas { margin-top: 134px; }
}

.ojas-powercontrol__dialog {
  text-align: center;

  .data-area {
    padding: 0 !important;
    border-bottom: unset !important;
  }

  .ojas-powercontrol__content {
    padding: 20px;

    h5, h6 {
      margin: 0;
      line-height: 25px;
      font-weight: normal;
    }

    h5 {
      font-size: 18px;
      font-weight: 500;
      color: #131415;
    }

    h6 {
      font-size: 16px;
      color: #545455;
    }

    p {
      margin: 12px 0 0;
      font-size: 14px;
      line-height: 20px;
      color: #545455;
    }
  }

  .ojas-powercontrol__actions {
    display: grid;

    button {
      margin: 0;
      padding: 15px;
      border-width: 1px 0 0;
      border-color: #C6C8CA;
      border-style: solid;
      font-size: 15px;
      line-height: 18px;
      color: #01B4DE;
      background: #ffffff;

      &:last-child {
        border-bottom-left-radius: 10px;
        border-bottom-right-radius: 10px;
      }

      &.ojas-powercontrol__close { font-weight: 700; }
    }
  }
}

.ojasbox-scrim__dialog {
  width: 100% !important;
  height: 100% !important;
  border-radius: unset !important;
  text-align: center;
  color: unset !important;
  background: unset !important;
  position: unset !important;
  top: unset !important;
  transform: unset !important;

  .data-area {
    width: 100%;
    height: 100%;
    padding: 60px 40px !important;
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-bottom: unset !important;

    .spinner:before {
      width: 32px;
      height: 32px;
      margin-top: -16px;
      margin-left: -16px;
    }
  }

  .ojasbox-scrim__content {
    max-width: 300px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;

    .icon { height: 64px; }

    p {
      margin: 24px 0 12px;
      font-size: 20px;
      line-height: 28px;
      font-weight: 500;
      color: #FFFFFF;

      &.normal-para {
        margin: 0;
        font-size: 16px;
        line-height: 20px;
        font-weight: normal;
      }
    }

    span {
      font-size: 40px;
      color: #FFFFFF;
    }
  }

  .ojasbox-scrim__button {
    width: 236px;
    margin: 50px auto 0;
    padding: 10px;
    border: #FFFFFF 1px solid;
    border-radius: 20px;
    font-size: 16px;
    line-height: 20px;
    color: #FFFFFF;
    background: unset;
  }
}

.ojasbox-network__dialog {
  .data-area,
  .footer-area {
    text-align: center;
  }

  &.data-area_wob .data-area { border-bottom: unset; }

  .data-area {
    max-height: 60vh;
    overflow: auto;

    .spinner:before {
      width: 32px;
      height: 32px;
      margin-top: -16px;
      margin-left: -16px;
    }
    
    h3 {
      font-size: 18px;
      font-weight: 600;
      line-height: 19px;
      color: #545456;

      &.success { color: #35c37d; }

      &.failure { color: #ff5959; }

      &.failure-black-color { color: #131415; }
    }
    
    p {
      margin-bottom: 0;
      
      &.no-margin {
        margin: 0;
        text-align: left;
      }

      &.success { color: #35c37d; }

      &.failure { color: #ff5959; }

      a{ color: #01B4DE;}
    }
    .bluetoothErrorIcon{
      svg{
        .a{fill:rgba(0,0,0,0.7);opacity:0;}.b{fill:#c5c5c5;}.c{fill:#f26b6b;}
      }
    }
  }

  .footer-area {
    justify-content: space-around;

    button:first-child {
      border-left: unset !important;
      color: inherit !important;
    }
    button:last-child {
      border-left: unset !important;
      color: #01b4de !important;
      font-weight: 500;
    }
  }

  ul.ojasbox-network_list li {
    padding: 14px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #CFCFD2;
    font-size: 14px;
    line-height: 16px;
    color: #545456;
    text-align: left;

    .ojasbox-network_details {
      display: flex;
      align-items: center;

      .icon {
        width: 16px;
        height: 16px;
        display: flex;
        margin-left: 10px;

        &.bluetooth circle { fill: #545456; }

        svg {
          width: 100%;
          height: 100%;
        }
      }
    }
  }

  .form-container {
    padding: 20px 0 10px;

    .textinput { text-align: left; }
  }

  .dialogHeader {
    margin: 0 -20px;
    padding: 0 20px 20px;
    border-bottom: 1px solid #d8d8d8;
    text-align: left;
  }

  .comfirm-btn {
    width: 190px;
    height: 40px;
    border-radius: 20px;
    font-size: 16px;
    font-weight: 500;
    text-align: center;
    color: #ffffff;
    background-color: #01b4de;
    outline: none;
    border: none;
    margin-top: 30px;
  }
  .wifiInfoIcon{
    .a{fill:rgba(0,0,0,0.7);opacity:0;}.b,.c{fill:#6f6f6f;}.b{opacity:0.4;}.d{fill:#01b4de;}
  }
}

// Mobile browser CSS
.mobile-browser {
  .ojas-status {
    .ojas-status__content {
      height: calc(91% - 100px);

      .ojas-status__wrapper { height: calc(100% - 25px); }
    }
  }
}

// Dark Mode CSS
.night {
  .ojas-status {
    .ojas-status__content { background-color: #262627; 
      .ojas-status__header{
        .ojas-status__header-right{
          .power-control, .power-saving{
            background-color: transparent;
            border: 1px solid;
          }
        }
      }
    }
  }
}

