.browser {
    .reports {
        .comp-notification.comp-notification__failure {
            background-color: #ff5959;
        }
        .comp-notification.comp-notification__info {
            background-color: #545456;
        }
        height: 100%;
        margin-bottom: 32px;
        overflow: scroll;
        .card {
            background-color: #fff;
            max-width: 1051px;
            margin: 32px auto;
            border-radius: 3px;
            box-shadow: 0 2px 10px 0 rgba(63, 67, 71, 0.08);
            display: flex;
            &.header {
                height: 75px;
                padding: 15px 20px;
                align-items: center;
                &>div:last-child {
                    margin-left: 40px;
                }
                &>div:first-child {
                    margin-right: auto;
                    margin-left: 0;
                }
            }
            &.body {
                flex-direction: column;
                overflow-y: auto;
            }
        }
        .body-header {
            padding: 20px;
            border-bottom: solid 0.5px rgba(123, 135, 147, 0.3);
            display: flex;
            flex-direction: row;
            div > div {
                font-size: 17px;
                font-weight: 500;
                line-height: normal;
                &:last-child {
                    margin-top: 2px;
                }
            }
            &>span {
                margin-left: 24px;
                cursor: pointer;
                display: flex;
                &:nth-child(2) {
                    margin-left: auto;
                } 
            }    
        }
        .body-info {
            font-size: 17px;
            margin: 20px 32px;
        }
        .report-table {
            margin: 0 32px 32px 32px;
            border-collapse: separate;
            border-spacing: 0;
            width: calc(100% - 64px);
            tbody {
                border: solid 1px #d8dcde; 
            }
            th {
                border: solid 1px #01b5de;
                background-color: #01b5de;
                font-size: 20px;
                font-weight: 500;
                color: #ffffff;
                text-align: left;
                padding: 12px 35px;
            }
            td {
                border: solid 1px #d8dcde;
                border-left: none;
                border-top: none;
                font-size: 17px;
                padding: 12px 35px;
                &:first-child {
                    border-left: solid 1px #d8dcde;
                }
            }
            &.battery-report {
                border: solid 1px #d8dcde;
                td.totallabel {
                    border-bottom: none;
                }
            }
        }
    }
}

