.nav {
  position: fixed;
  bottom: 0;
  z-index: 100;
  width: 100%;
  height: $footer-height;
  font-weight: 300;
  letter-spacing: 0.03em;
  box-shadow: 0 0 1px $shadow;
  border-radius: 0;
  overflow: hidden;
  background-color: $background;
  @include caption;


  ul {
    height: 100%;
    display: flex;
    padding: 0.6em;
  }
  li {
    flex-grow: 1;
    text-align: center;
    color: $medium;
    font-size: 12px;
    max-width: 32%;
    display: flex;
    align-items: center;
    justify-content: center;
    white-space: nowrap;
    cursor: pointer;
    rect, path {
      fill: $light;
    }
    circle {
      fill: #ff5959;
      stroke: $background;
      stroke-width: 0.4em;
    }
    .group {
      height: 4.2em;
      &.active {
        font-weight: 500;
        color: $secondary;
        rect, path {
          fill: $secondary;
        }
      }
    }
  }
}