@import "../style/font-style.scss";

.gs_banner_popup {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 105;

  & .popup-wrapper {
    border-radius: 10px;
    background-color: #ffffff;
    position: relative;
    z-index: 106;
    padding: 0px !important;

    &::-webkit-scrollbar {
      width: 0;
    }

    & .popup-body {
      & .gsb_img_wrapper {
        position: relative;
      }

      & .gsb_img {
        width: 100%;
        padding: 4px 4px 0;
      }

      & .gsb_savings_img {
        height: 54px;
        width: 93px;
        position: absolute;
        right: 0;
        top: 0;
      }

      & .gsTransition {
        opacity: 1;
        position: absolute;
        bottom: 0;
        height: auto;
        width: 98%;
        left: 3px;

        & svg {
          width: inherit;
        }
      }

      & .gsb_content_title_wrapper {
        display: flex;
        align-items: center;

        img {
          width: 60px;
          height: 60px;
        }

        & .gsb_content_title {
          letter-spacing: 0px;
          color: #6f6f6f;
          opacity: 1;
          font-size: 16px;
          @include title-2;
        }
      }

      & .gsb_content_body {
        margin: 0 20px 17px;
        font-weight: 300;
        font-size: 14px;
        letter-spacing: 0px;
        color: #6f6f6f;
        @include body-1;

        & .gsb_content_highlights {
          font-size: 20px;
          font-weight: normal;
          color: #6f6f6f;
          opacity: 1;
          margin-top: 17px;
          @include title-2;
        }
      }

      & .gsb_button_group {
        font-size: 16px;
        font-weight: 500;
        color: #01b4de;
        letter-spacing: 0px;
        display: flex;
        border-top: 1px solid #d8dcde;
        @include title-2;

        & div {
          display: flex;
          flex-basis: 50%;
          height: 40px;
          justify-content: center;
          align-items: center;
          cursor: pointer;

          &:first-child {
            border-right: 1px solid #d8dcde;
          }
        }
      }
    }
  }
}

.gs_banner_overlay {
  position: fixed;
  left: 50%;
  top: 50%;
  background-color: #000000;
  opacity: 0.2;
  height: 100%;
  width: 100%;
  transform: translate(-50%, -50%);
  z-index: 104;
}

@media (min-width: 576px) {
  .gs_banner_popup {
    & .popup-wrapper {
      width: 450px !important;

      & .popup-body {
        & .gsb_img {
          width: 100%;
          padding: 4px 4px 0;
        }
      }
    }
  }
}

@media (max-width: 280px) {
  .gs_banner_popup {
    & .popup-wrapper {
      width: 260px !important;
    }
  }
}

@media (max-height: 280px) {
  .gs_banner_popup {
    & .popup-wrapper {
      width: 300px !important;
      height: 260px !important;
      overflow: scroll;
    }
  }
}

@media (min-height: 320px) {
  .gs_banner_popup {
    & .popup-wrapper {
      height: 300px !important;
      overflow: scroll;
    }
  }
}

@media (min-height: 470px) {
  .gs_banner_popup {
    & .popup-wrapper {
      height: auto !important;
      max-height: '450px';
    }
  }
}

.browser {
  .gs_banner_popup {
    .popup-wrapper {
      border-radius: 0px;

      .popup-body {
        & .gsb_content_title_wrapper {
          img {
            width: 80px;
            height: 80px;
          }

          & .gsb_content_title {
            font-size: 20px;
            color: #545456;
            @include title-2;
          }
        }

        & .gsb_content_body {
          font-size: '15px';
          color: #545456;
          opacity: 1;
          margin: '0 20px 27px';
          @include body-1;

          & .gsb_content_highlights {
            font-size: '24px';
            margin-top: '24px';
            @include title-2;
          }
        }
      }
    }
  }
}

.night {
  .gs_banner_popup .popup-wrapper {
    background-color: #262627;
    border: 1px solid #939598;

    .gsb_content_body {
      color: #ffffff;
    }

    .gsb_content_title, .gsb_content_body .gsb_content_highlights {
      color: #f8f8ff;
    }
  }

  .gs_banner_overlay {
    background-color: #000000;
    opacity: 0.6;
  }
}
