.OtpInputField {
  width: inherit;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  & input[type='text'] {
    border: 1px solid #c6c8ca;
    border-radius: 6px;
    font-size: 20px;
    text-align: center;
    width: 108px;
    padding: 10px 8px 8px;

    &:focus {
      border-color: #00b6de;
    }
  }
}