@import 'font-style';
.submenu.loadControl{
  .container {
    background-color: #FAFAFB;
    &.list-container{
      position: relative;
    }
    .comp-notification{
      z-index: 10;
      &.comp-notification__permissionDenied{
        background-color: #ff5959;
      }
    }
  }
}

.loadControl {
  .success-message {
    position: sticky;
    height: auto;
    max-height: 10%;
    box-shadow: 1px 2px 10px 0 rgba(63, 67, 71, 0.08);
    background-color: #545456;
    color: #fdfefe;
    padding: 1em 2em;
    width: 100%;
    display: flex;
    font-size: 12px;
    font-weight: normal;
    align-items: center;
    z-index: 201;
    top: 0;
    @include body-1;
    line-height: normal;

    &.disabled {
      display: none;
    }

    .icon {
      margin-right: 5px;

      svg {
        height: 20px;
        width: 20px;

        path {
          fill: #ffffff;
        }
      }

      &.tickWhiteSmall {
        svg {
          g {
            stroke: #ffffff;
            fill: none;
          }
        }
      }

      &.defaultTick{
        path{
          fill: #ffffff;
          stroke: #ffffff;
        }
      }
    }
  }
}
// Common CSS
.load-control__wrapper {
  padding: 20px;

  
  .load-control__header {
    color: #545456;

    .load-control__title {
      display: flex;
      align-items: flex-start;

      h3 {
        max-width: calc(100% - 20px);
        font-size: 16px;
        line-height: 19px;
        font-weight: 500;
      }

      & button {
        width: 16px;
        height: 19px;
        margin: 0 0 0 4px;
        padding: 0;
        border: unset;
        display: flex;
        align-items: center;
        align-self: unset;
        background: unset;

        span.icon.infoIcon,
        span.icon.infoIcon svg {
          width: 14px;
          height: 1em;
        }
      }
    }

    p.load-control__sub-title {
      margin: 4px 0 0;
      font-size: 14px;
      line-height: 16px;
      opacity: .5;
    }
  }

  .load-control__body {
    p.load-control__empty {
      margin-top: 20px;
      padding: 20px;
      border-radius: 5px;
      font-size: 16px;
      line-height: 20px;
      color: #545456;
      background: #FFFFFF;
      box-shadow: 0 0 15px #0000000D;
    }

    .load-control__card {
      margin-top: 20px;
      color: #545456;
      border-radius: 5px;
      background: #FFFFFF;
      box-shadow: 0 0 15px #0000000D;

      .card__header {
        padding: 15px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        opacity: .5;
        position: relative;
        z-index: 1;

        .card__header-info {
          display: flex;

          & > .icon {
            width: 40px;
            height: 40px;
            padding: 5px;
            background: #47B4DE22;
            border-radius: 4px;
            filter: grayscale(1);

            svg {
              width: 100%;
              height: 100%;
            }
          }

          .card__header-info-title {
            margin-left: 12px;

            h4 {
              margin: 0;
              display: flex;
              font-size: 16px;
              font-weight: 500;
              line-height: 40px;
            }
          }
        }

        .toggle-switch .toggle-switch__container { display: flex; }
      }

      .card__content {
        padding: 20px 15px 20px 54px;
        border-top: 1px solid #d8e1e4;
        opacity: .5;
        &.hide{
          display: none;
        }

        .card__content-header {
          padding-left: 13px;
          display: flex;
          justify-content: space-between;
          align-items: center;

          .leftPart{
            display: flex;
            align-items: center
          }
          .inLineRequestStatus{
            display: flex;
            align-items: center;
            font-size: 12px;
            color: #545456;
            opacity: 0.6;
            padding: 0 14px 0 5px;
            .icon{
              height: fit-content;
              display: flex;
              align-items: center;
              &.clockCircular svg g{
                fill : #01B4DE;
              }
            }
            label{
              padding-left: 5px;
            }
          }
          .card__content-header-mode {
            font-size: 14px;
            line-height: 16px;

            h5 {
              margin: 0;
              font-size: 14px;
              font-weight: 400;
              opacity: .5;
              @include body-1;
            }

            p {
              margin: 2px 0 0;
              font-weight: 500;
              @include body-2;
            }
          }

          button {
            width: 20px;
            height: 20px;
            padding: 0;
            margin: 0 9px 0 0;
            display: flex;
            border: unset;
            background: unset;

            .icon {
              width: 100%;
              height: 100%;
              align-self: center;

              svg { stroke: #6F6F6F; }
            }
          }
        }

        .card__content-body {
          margin-top: 20px;
          border-radius: 8px;
          display: none;
          background: #F5F5F8;

          form {
            min-height: unset;
            margin: 0;

            .form-group {
              display: flex;
              align-items: flex-start;
              padding: 15px 0 0 15px;

              &.disable{
                opacity: 0.5
              }

              .comp-radio-button{
                padding-top: 15px;

                input {
                  margin-right: 10.63px;
                  border: 2px solid #CBCBCB;
                }
              }

              .card__content-body-details {
                flex-grow: 1;
                padding: 15px 0 0;
                border-top: 1px solid #dedede;
                line-height: 16px;
                
                h6 {
                  font-size: 14px;
                  font-weight: 500;
                  line-height: 20px;
                  margin: 0;
                  padding-right: 15px;
                  @include body-2;
                }

                p {
                  font-size: 14px;
                  font-weight: 400;
                  line-height: 19px;
                  margin: 4px 0 0;
                  color: #A8ADAE;
                  padding-right: 15px;
                  @include body-1;

                  span{
                    font-weight: 500;
                  }
                }

                .card__content__action{
                  .edit-button{
                    font-size: 12px;
                    font-weight: 500;
                    min-width: 65px;
                    height: 24px;
                    padding: 4px 15px;
                    background-color: transparent;
                  }

                  .toggle-text-switch{
                    margin-top: 15px;
                  }
                }
                
                .card__content-body-slider {
                  padding: 25px 0;
                }
              }

              &:first-child{
                .comp-radio-button{
                  padding: 0;
                }
                .card__content-body-details {
                  padding: 0;
                  border-top: none;
                }
              }

              &:last-of-type{
                padding-bottom: 15px;
              }

              &.selected{
                .card__content-body-details {
                  h6{
                    color: #01B5DE;
                  }

                  p{
                    color: #545456;
                  }
                }

                .comp-radio-button{
                  input{
                    border-color: #01b5de;

                    &:before{
                      width: 9px;
                      height: 9px;
                      margin: 3px auto;
                    }
                  }
                }
              }
            }
          }
          .multi-slider {
            .error-text {
              font-size: 12px;
              position: relative;
              top: 40px;
              color: #E3655E;
            }
          }

          .action-wrapper{
            margin-top: 15px;
            border-top: 1px solid #dedede;
          }

          .save-button {
            box-shadow: 1px 2px 10px 0 rgba(63, 67, 71, 0.08);
            border: 1px solid #00b6de;
            padding: 10px 0;
            background: #00b6de;
            color: white;
            text-align: center;
            border-radius: 2rem;
            width: 11rem;
            margin: 15px auto;
            justify-content: center;
            display: flex;
            font-size: 16px;
            @include title-2;
            margin-left: auto;
            margin-right: auto;
            &.buttonCancel {
              background-color: transparent;
              color: #01b5de;
              border: 1px solid #01B5DE;
              font-size: 15px;
    
              &.buttonDisabled {
                color: #c7c8ca;
              }
            }
    
            &.quiet-margin {
              margin-top: 40px;
            }
          }
          .buttonInProgress{
            position: relative;
            justify-content: center;
            margin-left: auto;
            margin-right: auto;
            display: flex;
            margin: 25px 0 15px;
            .icon.loaderV2{
              display: flex;
              position: relative;
            }
          }
        }

        &.expanded {
          .card__content-header {
            .card__content-header-mode p {
              display: none;
            }

            button .icon {
              transform: rotate(180deg);
            }
          }

          .card__content-body {
            display: inline-block;
            width: 100%;
          }
        }
      }

      &.powered {
        .card__header {
          opacity: 1;
          
          .card__header-info{
            > .icon{
              filter: grayscale(0);
            }
          }
        }
        
        .card__content {
          opacity: 1;
        }
      }

      &.lc__processing{
        .card__content-body{
          pointer-events: none;
        }
      }
    }
  }
}

.load-control__info-popup {
  .popup__body .popup__body-content {
    p {
      margin: 0 0 10px;

      span { color: #F37320; }
    }

    a {
      display: flex;
      justify-content: flex-start;
      font-weight: 500;
      color: #01b5de;

      span.icon { margin-left: 5px; }
    }
  }
}

.lc-scheduled-mode, .cfg-scheduled-mode{
  p{
    font-size: 14px;
    font-weight: 400;
    margin: 5px 0;
    @include body-1;

    span{
      font-weight: 500;

      &.value{
        color: #01b5de;
      }
    }
  }

  .lsam__time-wrapper{
    display: flex;
    align-items: center;
    font-size: 14px;
    margin: 25px 0 0;
    color: #545456;
    line-height: 25px;
    @include body-1;

    .lsam__timer-icon{
      margin-right: 10px;

      .icon{
        height: 100%;
      }
    }

    .lsam__time{
      display: flex;
      align-items: flex-end;
      font-size: 12px;
      font-weight: 400;
      margin-right: 15px;
      color: #545456;
      line-height: inherit;

      >div{
        font-size: 16px;
        font-weight: 500;
        color: #47B4DE;
        margin-left: 5px;
      }
    }
  }

  .overlay{
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #000000;
    opacity: 0.6;
    height: 100%;
    width: 100%;
    z-index: 1;
  }

  .time-keeper{
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 2;

    .react-timekeeper__time-dropdown{
      display: none;
    }

    .done-btn{
      text-align: center;
      padding: 16px 0; 
      background-color:#F4F4F4; 
      color:#68687F;
      border-top: 1px solid #CCC;
    }
  }

  .time-error{
    font-size: 12px;
    line-height: 16px;
    margin: 15px 0 20px;
    color: #E3655E;
  }

  .save-button{
    color: #ffffff !important;

    &.disabled{
      pointer-events: none;
      background-color: #c6c8ca !important;
      border-color: #c6c8ca !important;
      color: #707070 !important;
    }
  }
}
.popup__grid{
  .lc_adv-mode{
    .popup__body{
      max-height: 80vh;
      overflow-y: scroll;
    }
  }
}

.lc-advanced-mode{
    .lc-advanced-mode__subtitle{
      color: #545456;
      font-size: 14px;
      margin: 11px 0px;
      @include title-1;
    }

    .lc-advanced-mode__cards{
      background: #F5F5F8;
      border: 1px solid #DFE5E9;
      border-radius: 10px;

      .line{
        width:calc(100% - 17px);
        border-top: 1px solid #D8E1E4;
        float: right;
      }

      .lc-advanced-mode-batteryCard, .lc-advanced-mode-generatorCard{
        padding: 17px 16px;

        .battery-title, .generator-title{
          display: flex;
          align-items: center;

          h3{
            color: #01B5DE;
            font-weight: 500;
            display: flex;
            align-items: center;
            @include title-2;
          }
        }

        p{
          span{
            margin-right: 5px;
          }
        }

        .comp-radio-button{
          font-weight: 500;
          input{
            border: 2px solid #01B5DE;

            &:before{
              width: 9px;
              height: 9px;
              display: block;
            }
          }
        }
        .selected{
          color: #01B5DE;
        }

      }
      .lc-advanced-mode-batteryCard{

        .battery-grid-status{
          padding: 16px 0px;
          font-weight: 400;

          p{
            margin: 1px 0px;
            span{
              font-weight: 600;
              margin-right: 5px;
              font-weight: 600;
            }
            .soc-value{
              color: #01B5DE;
              font-size: 16px;
            }
          }
        }

        .lsam__slider{
          margin-bottom: 30px;
          
          div.multi-slider > div:first-child{
            margin: 0px !important;
          }
        }
        .battery-soc-desc > p{
          margin: 0px;
        }

        &.battery-disabled{

          label{
            input{
              border-color: #CBCBCB;
            }
          }
          .battery-title{
            color: #545456;
          }

          .battery-status-disabled{
            p{
              color: #A8ADAE;
              font-weight: 400;
              span{
                color: #A8ADAE;
              }
            }
          }
        }
      }

      .lc-advanced-mode-generatorCard{
        .generator-grid-status{
          padding: 16px 0px;
          font-weight: 400;
          p{
            margin: 1px 0px;
            span{
              margin-right: 5px;
              font-weight: 600  ;
            }
          }
          &.disabled-generator-status{
            p{ 
              color: #A8ADAE;
              font-weight: 400;
            }
          }
        }

        &.disabled-generator{

          label{
            input{
              border-color: #CBCBCB;
            }
          }
          .generator-title{
            color: #545456;
          }
        }
      }
    }
    
  .save-button{
    color: #ffffff !important;

    &.disabled{
      pointer-events: none;
      background-color: #c6c8ca !important;
      border-color: #c6c8ca !important;
      color: #707070 !important;
    }
  }
}

// Dark Mode CSS
.night { 
  .submenu.loadControl{
    .container {
      &.list-container{
        background-color: #262627;
      }
    }
  }
  .load-control__wrapper{
    .load-control__header{
      .load-control__title{
        color: #f8f8ff;
      }
      .load-control__sub-title{
        color: #f8f8ff;
      }
    }
    .load-control__body{
      p.load-control__empty{
        color: #f8f8ff;
        background-color: #333333;
      }

      .load-control__card {
        background-color: #262627;
        border: 1px solid #5a5959;
        .card__header{
          .card__header-info{
            .card__header-info-title{
              h4{
                color: #f8f8ff;
              }
            }
          }
          
        }
        .card__content  {
          border-top: 1px solid #5a5959;
          .card__content-header{
            .card__content-header-mode{
              h5{
                color: #f8f8ff;
              }
              p{
                color:#f8f8ff;
              }
            }
            .leftPart{
              .inLineRequestStatus{
                label{
                  color: #f8f8ff;
                }
              }
            }
          }
          .card__content-body{
            background: #333333;

            form{
              .form-group{
                .card__content-body-details{
                  border-color: #5a5959;

                  h6{
                    color: #ffffff;
                  }
                  p{
                    color: #a2a6a9;
                  }
                }

                &.selected{
                  .card__content-body-details{
                    h6{
                      color: #01b5de;
                    }
                    p{
                      color: #cbced1;
                    }
                  }
                }
              }
            }

            .action-wrapper{
              border-color: #5a5959;
            }
          }
        }
      }
    }
  }

  .lc-scheduled-mode, .cfg-scheduled-mode{
    p{
      color: #cbced1;
    }
  
    .lsam__time-wrapper{
      color: #cbced1;
  
      .lsam__timer-icon{
        .icon{
          svg{
            path{
              fill: #ffffff;

              &:last-child{
                fill: #cbced1;
              }
            }
          }
        }
      }
  
      .lsam__time{
        color: #cbced1;
      }
    }
  }
  .lc-advanced-mode{
    .lc-advanced-mode__subtitle{
      color: #F5F5F8;
    }
    .lc-advanced-mode__cards{
      background: transparent;
      border-color: #4A4A4A;
      
      .lc-advanced-mode-generatorCard{
        &.disabled-generator{
          .generator-title{
            color: #A8ADAE;
          }
        }
      }
      .lc-advanced-mode-batteryCard{
        &.battery-disabled{
          .battery-title{
            color: #A8ADAE;
          }
        }
      }

      .line{
        border-top: 1px solid #545456;
      }
    }
    .save-button {
        &.disabled {
          background: #494a4b !important;
          color: #fff !important;
          border: 1px solid #494a4b !important;
      }
    }
  }
  .time-keeper{
    .done-btn{
      text-align: center;
      padding: 16px 0; 
      background-color:#333333; 
      color: #ffffff;
      border-color: #333333;
    }
  }
}

// Web browser CSS
.browser {
  .load-control__wrapper {
    width: 100%;
    max-width: 950px;
    margin: 20px auto;
  }

  .popup__web{
    position: relative;
  }

  .success-message {
    position: absolute;
    display: flex;
    align-items: center;
    font-size: 14px;
    font-weight: 300;
    background: #545456;
    color: #ffffff;
    padding: 20px;
    z-index: 3;

    .icon{
      &.clockCircular{
        g{
          fill: #ffffff;
        }
      }

      &.defaultTick{
        path{
          fill: #ffffff;
          stroke: #ffffff;
        }
      }
    }
  }
}

