@import "fonts";

.notification:not(.icon) {
  width: 100%;
  height: $alert-notification-height;
  position: fixed;
  bottom: #{$footer-height};
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px 20px;
  color: #fff;
  font-weight: 300;
  font-size: 12px;
  z-index: 199;
  opacity: 1;
  line-height: normal;

  .notification__wrapper{
    width: inherit;
    display: flex;
    justify-content: center;
  }
  
  &.notification__web{
    &.notification__has-icon{
      .notification__contents{
        margin-right: 15px;
      }
    }
  }

  &.notification__has-icon{
    height: $error-notification-height;
    animation: any 1s ease;

    &.notification__mobile{
      .notification__wrapper{
        justify-content: space-between;
      }
    }
  }

  .notification__contents{
    display: flex;
    align-items: center;

    .notification__title{
      font-family: 'Visuelt', Roboto;
      font-size: 12px;
      font-weight: normal;
      text-transform: capitalize;
      color: #fdfefe;
      &.no-internet{
        font-size: 14px;
        font-weight: normal;
        color: #FDFEFE;
      }
    }
  }

  .notification__actions{
    display: flex;
    align-items: center;

    .notification__action{
      display: flex;
      align-items: center;
      font-size: 12px;
      font-weight: bold;
      text-transform: uppercase;
      color: #fdfefe;
      cursor: pointer;
      &.no-internet{
        font-size: 14px;
        font-weight: normal;
        text-transform: none;
        color: #FFFFFF;
        border: 1px solid #FFFFFF;
        border-radius: 23px;
        padding: 2px 10px 3px 11px;
      }
    }
  }

  .icon{
    display: flex;
    align-items: center;
    width: 20px;
    height: 20px;

    &.infoIcon, &.deviceError{
      margin-right: 10px;

      svg{
        path{
          fill: #ffffff !important;

          &:last-child{
            fill: #ff5959;
          }
        }
      }
    }

    &.refresh{
      width: 14px;
      height: 14spx;
      margin-right: 7px;

      svg{
        path{
          stroke: #ffffff;
          fill: #ffffff;
        }
      }
    }

    svg{
      width: inherit;
      height: inherit;

      path{
        fill: #ffffff;
      }
    }
  }
}


.errorMessage {
  background-color: #ff5959;
}

.alertMessage {
  background-color: #ee7222;
}

.offlineInternetMessage {
  background-color: #ff5959;
}
