.custom-dropdown {
  &.disabled {
    filter: grayscale(1)
  }
  display: flex;
  position: relative;
  flex-direction: column;
  width: fit-content;
  max-height: auto;
  & .dropdown__button-wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    & .dropdown__infoText {
      font-size: 12px;
      color: #939598;
      font-weight: 300;
    }
    .dropdown__button {
      display: flex;
      align-items: center;
      justify-content: space-between;
      .icon{
        display: flex;
        align-items: center;
        &.collapse{
          transform: rotate(180deg);
        }
      }
    }
  }
  .dropdown__content {
    position: absolute;
    left: 0%;
    top: 100%;
    z-index: 100;
    border-radius: 6px;
    background-color: white;
    box-shadow: 0px 3px 6px 0px #C9C9C9;
    overflow-y: scroll;
    display: flex;
    max-height: 250px;
    flex-direction: column;
    width: 100%;
    .dropdown-item {
      display: flex;
      padding: 5px;
      justify-content: space-between;
      align-items: center;
    }
    .dropdown-item:hover {
      background-color: #f4f3f452;
    }
    .dropdown-item.selected {
      background-color: #f4f3f452;
      font-weight: 500;
    }
  }
  .overlay {
    z-index: 98;
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100vw;
    height: 100vh;
    background-color: rgba(147, 147, 147, 0.4);
  }
}
