.alertDialog {
  &.weather-alert{
    #positiveAction{
      border-right: none;
      border-bottom-right-radius: 10px;
    }
  }
  #negativeAction {
    display: none;
  }
  #positiveAction {
    width: 100%;
    border-left: none;
    font-weight: 500;
  }
  .cautionAlert {
    height: 78px;
    padding-top: 10px;
    padding-bottom: 20px;
    svg {
      width: 48px;
      height: 48px;
    }
    path {
      stroke: #fff;
      stroke-width: 1.2px;

      &:first-child {
        fill: none;
        stroke: rgb(255, 89, 89);
        stroke-width: 0.7px;
      }
    }
  }
  .alertContent {
    text-align: center;

    a { color: #00b6de; }
  }
}

.night{
  .cautionAlert{
    path{
      stroke: #262627;
    }
  }
}
