@import "fonts";
.registration {
  width: 100%;
  height: 84%;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}

.header.has-language-selector{
  .header-wrapper{
    display: flex;
    justify-content: space-between;
    align-items: center;

    div {
      display: flex;
      align-items: center;
      .icon {
        display: flex;
        align-items: center;
        svg {
          margin-top: 0px;
        }
      }
    }
  }
}

.new-user {

    transition: height 600ms;
    height: 100%;
    width: 90%;
    margin: 1em auto;
    font-size: 18px;
    .tnc{
      display: flex;
      margin-top: 20px;
      margin-left: 0 !important;

      .form-checkbox{
        margin-right: 10px;
      }
    }

    .form-input-file {
      display: none;
    }
    .form-input-file + label{
      width: 160px;
      height: 40px;
      border-radius: 20px;
      border: solid 1px #01b4de;
      display: inline-block;
      text-align: center;
      vertical-align: middle;
      line-height: 40px;
      color: #01b4de;
      font-size: 16px;
      font-weight: 500;
    }
  
    form {
      margin: 0 !important;
    }

    input, select {
      padding-left: 0;
    }

    .float-label {
      transform: translateY(-1.5em);
      font-size: 12px !important;
    }

    .input-container {
      position: relative;
      padding-bottom: 16px;
    }

    .select-container {
      padding-bottom: 20px;
    }
  
    .button {
      width: 100%;
      margin: 1em auto 1em;
      border-radius: 2em;
      background-color: $secondary;
      @media screen and (min-width:768px) and (max-width:1024px){
        width: 60%;
        height: 2.4em;
        padding: 0;
        float: left;
      }

    }

  .new-user-button{
    font-size: 16px;
  }
  
    .item {
      position: relative;
      &.error {
        label { color: #ff5959; }
      }
      &::placeholder {
        font-size: 12px;
        font-weight: 300;
        color: #939598;
      }

      .form-group-radio{
        display: flex;
      }
    }

    .form-label {
      display: inline;
      color: #939598;
      transition: height .3s;
      font-size: 14px;
      font-weight: 300;
      top: 0.25em;
      position: absolute;
      @include body-1;
    }

    .form-select {
      display: inline;
      color: #939598;
      transition: height .3s;
      font-size: 14px;
      font-weight: 300;
      @include body-1;
    }

    .personal-details {
      font-size: 20px;
      font-weight: 500;
      color: #545456;
      padding-bottom: 2em;
      @include title-2;
    }

    .item-header {
      font-size: 20px;
      font-weight: 500;
      color: #545456;
      padding-bottom: 1.2em;
    }

    .item-input, .input-select {
      font-size: 16px;
      color: #545456;
    }
    .company-logo {
      padding: 0;

      .title{
        font-size: 14px;
        color: #5a5a5c;
        margin: 0 0 10px;
        @include body-1;
      }

      .selected-company-logo{
        display: block;
        margin: 10px;
      }
    }

    .company-logo-details{
      border-radius: 5px;
      background-color: #5a5a5c;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 10px 15px;

      .icon{
        height: auto;
        display: flex;
      }

      .company-logo-details__info{
        display: inherit;

        p{
          margin: 0 0 0 10px;
          color: #ffffff;
        }
      }
    }

    .image-type-text {
      font-size: 12px;
      font-weight: 300;
      line-height: 1.25;
      color: #545456;
      margin-top: 10px;
      @include body-1;
    }

    .company-logo-header {
      font-size: 14px;
      line-height: 1.43;
      color: #5a5a5c;
    }

    .checkbox-label {
      font-size: 14px;
      line-height: 1.43;      
      color: #5a5a5c;      
      margin-left: .6em;
      @include body-1;
    }

    .settings-text {
      font-size: 14px;
      line-height: 1.43;      
      color: #5a5a5c; 
    }

    .disabled {
      display: none;
    }

    .enabled {
      display: block;
    }

    .adjust-size {
      padding: 0 6px;
    }

    .error-message {
      color: #ff5959;
      transition: height .3s;
      font-size: .7em;
      overflow: hidden;
      display: inline-block;
      margin-top: 5px;
      font-size: 12px;
      a {
        color: #00b6de;
      }
    }

    .error-message-policy {
      color: #ff5959;
      transition: height .3s;
      font-size: .7em;
      display: inline-block;
      padding-top: 2px;
      &.disabled {
        display: none;
      }
      &.enabled {
        display: block;
      }
    }

    .options {
      padding-bottom: 0;
      padding-top: 0;
    }

    .link-policy {
      color: #01b4de;
    }

    .logo-file-name {
      font-size: 0.7em;
    }

    .privacy-policy-container {
      padding-bottom: 1.3em;
    }

    .submit-container {
      height: 16% !important;
      @media screen and (max-width:360px) {
        height: 24% !important;
      }
    }

    .privacy-policy-label {
      padding-left: 4px;
    }
}

.failure-error-message {
  background-color: #ff5959;
  height: auto;
  max-height: 10%;
  font-size: 12px;
  color: #ffffff;
  padding: 1em 2em;
  width: 100%;
  display: flex;
  
  &.disabled {
    display: none;
  }
  &.enabled {
    position: absolute;
    z-index: 1;
  }

  .error-icon{
    margin-right: 10px;
  }
}

.extraMargin {
  margin-top: 1.8em;
}

.new-user-confirmation {
  transition: height .6s;
  padding: 8em 1em 1em 1em;
  height: 100%;
  .container-form {
    padding: 1em 6px;
    height: auto!important;
    margin: auto;
    .confirm-check {
      display: flex;
      justify-content: center;
      padding-bottom: 2em;
      .title-confirmation {
        font-size: 16px;
        margin-bottom: 2.5em;
        color: #5a5a5c;
        line-height: 1.38;
        text-align: center;
      }
    }
    .button {
      width: 55%;
      margin: 1em auto;
      border: solid 1px #01b4de;
      border-radius: 2em;
      background-color: transparent;
      color: #01b4de !important;

    }

  }
}

.hide {
  display: none;
}

.show {
  display: block;
}
.Offline {
  padding: .2em;
  background-color: #ee7222;
  color: #fff;
  height: 3%;
  text-align: center;
}
.dropdown-arw{
  position: absolute;
  top:50%;
  right: 0;
}
.arrowTop {
  top: 25%;
}

.camera-popup {
  z-index: 10;
  background-color: rgba(0,0,0,.7);
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  &.disabled {
    display: none;
  }
}

.camera-popup-wrapper {
  background-color: #fff;
  width: 60%;
  margin: auto;
  border-radius: 0.6em;
  >div{
    position: relative;
    height: 100%;
  }
  @media screen and (min-width:768px) and (max-width:1024px){
    width: 40%;
  }
}

.camera-info-header {
  color: #545456;
  font-size: 14px;
  font-weight: 300;
  justify-content: center;
  display: flex;
  line-height: 1.29;
  padding: 1em 1em;
  margin-bottom: 50px;
  max-height: 30%;
  overflow: auto;
}

.camera-info-content {
  display: flex;
  justify-content: center;
  background-color: #fff;
  color: #01b4de;
  border: none;
}

.camera-info-border {
  border-top: 1px solid #D3D3D3;
  border-width: thin;
  display: inline-flex;
  width: 100%;
  height: 50px;
  bottom: 1px;
  position: absolute;

  > div{
    height: inherit;
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;

    &:first-child{
      border-right: 1px solid silver;
    }
  }
}

.vl {
  border-left: 6px solid green;
  height: 500px;
}
.m-b-0 {
  .form-group{
    margin-bottom: 0 !important;
  }
}