.night{
  .energy-heading-widget{
    .energy-heading-widget__wrapper{
      .header-container{
        color: #9aa2a8;
      }
    }
  }
}


.energy-heading-widget{

  .energy-heading-widget__wrapper{
    .comp-heading{
      margin: 15px 17px;
    }
    .header-container{
      display: flex;
      justify-content: flex-end;
      font-size: 12px;
      color: #6C7074;
      margin: 18px 15px 22px 0px;
    }
  }

  &.production-consumption-storage{
    margin-top: 10px;
  }
}

@media screen and (max-width: 359px){
  .energy-heading-widget{

    .energy-heading-widget__wrapper{
      .comp-heading{
        .comp-heading__title{
          font-size: 16px;
        }
      }
    }
  }
}

.energy-revemp-wrapper{

  .comp-heading__icon{
    height: 28px!important;
    margin-right: 8px!important;
  }

  .prod-value{
    margin-top: -15px!important;
  }

  .comp-heading__sub-heading{
    margin-left: 36px;
    margin-top: 0px!important;
  }
}