
.production    { stroke: $secondary !important;   background-color: $secondary;   fill: $secondary; }
.consumption   { stroke: $primary !important;     background-color: $primary;     fill: $primary; }
.grid          { stroke: $medium-dark !important; background-color: $medium-dark; fill: $grid; }
.battery, .soc { stroke: $tertiary !important;    background-color: $tertiary;    fill: $tertiary; }
.bulbs         { stroke: $secondary !important;   background-color: $secondary;   fill: $secondary; }
.yesterday     { stroke: $medium-dark !important; background-color: $medium-dark; fill: $medium-dark; }
.lastyear      { stroke: $light !important;       background-color: $light;       fill: $light; }

.unavailable,
.unavailable .production,
.unavailable .consumption,
.unavailable .grid,
.unavailable .battery, 
.unavailable path {
  color: $light !important;
  fill: $light !important;
}

.disabledHide {
  color:  $background !important;
  /* graph fill */
  stroke: $background !important;
  fill:   $background !important;
  path, rect {
    stroke: $background !important;
    fill:   $background !important;
  }
}

.disabled,
.disabled .production,
.disabled .consumption,
.disabled .grid,
.disabled .battery {
  color:  $light !important;
  /* graph fill */
  stroke: $super-light !important;
  fill:   $super-light !important;
  /* icons */
  path, rect {
    stroke: $light !important;
    fill:   $light !important;
  }
}
.disabled.yesterday,
.disabled.lastyear {
  fill: none !important;
}

body {
  color: $dark;
  background-color: #000;
}

/* rect, path, polygon {
  fill: $dark;
} */

.page {
  background-color: $background;
}
