.energy-header {
  display: flex;
  justify-content: space-between;
  color: #6c7074;
  font-size: 12px;

  .energy-header__details {
    display: flex;
    flex-direction: column;

    .system-status {
      color: #35c37d;
    }

    .grid-status {
      .grid-status__text {
        display: flex;
        flex-direction: column;
