  .pes-energy-header-details-web{
    display: flex;
    align-items: center;
    flex-direction: row;
    width: 100%;
    padding-left: 32px;
    padding-top: 25px;
    padding-bottom: 25px;
    font-size: 16px;
    border-bottom: 1px solid #A9A9AA;

    .pes-system-status {
      display: inline-flex;
      align-items: center;
      height: 16px;
      margin-right: 32px;

      .pes-system-status-text {

        .pes-unit-status-normal{
          color: #35C37D;
        }

        .pes-unit-status-warning{
          color: #FFC400;
        }

        .pes-unit-status-error{
          color: #FF5959;
        }

        .pes-unit-status-unknown{
          color: #6C7074;
        }
      }

      .pes-system-status-icon{
        margin-right: 8px;

        .pes-status-icons{

          svg{
            height: 20px;
            width: 20px;
          }
        }

        .pesMenuOffGrid, .pesMenuOnGrid, .pesMenuUnknownGrid{

          svg{
            height: 20px;
            width: 20px;
          }
        }
      }

      .pes-system-status-text{
        display: flex;
        color: #6C7074;

        .pes-today-status-info-time-update{
          display: flex;
          color: rgba(108, 112, 116, 0.51);

          .web-info-icon{
            margin-left: 4px;
            cursor: pointer;
            
            .infoIcon.icon {
              svg {
                width: 16px;
                height: 16px;
              }
            }
          }
        }
      }
    }
  }
}

.pes-shimmer{

  .shimmer-effect {
    animation-duration: 1.2s;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
    animation-name: shimmer;
    animation-timing-function: linear;
    background: #ddd;
    background: linear-gradient(
      to right,
      #eeededbe 8%,
      #e6e4e4ea 18%,
      #ebe9e9f4 33%
    );
    background-size: 1200px 100%;
  }

  .icon-box {
    height: 24px;
    width: 24px;
    margin-right: 10px;
  }

  .text-box {
    width: 172px;
    height: 15px;
    margin-bottom: 6px;
  }

  .text-box-web {
    width: 100px;
    height: 15px;
    margin-bottom: 4px;
    margin-left: 8px;
  }

  .text-box-charge-web {
    display: inline-block;
    width: 100px;
    height: 15px;
    margin-bottom: -3px;
  }

  .web-icon-box {
    display: inline-block;
    height: 24px;
    width: 24px;
  }

  .web-text-box {
    display: inline-block;
    width: 100px;
    height: 15px;
    margin-bottom: 4px;
    margin-left: 8px;
  }

  .web-text-box-charge {
    display: inline-block;
    width: 100px;
    height: 15px;
    margin-bottom: -3px;
  }

  .icon-box-web {
    display: inline-block;
    height: 24px;
    width: 24px;
  }

  .battery-box {
    width: 100px;
    height: 15px;
    margin-bottom: 6px;
  }

  .battery-icon {
    width: 30px;
    height: 30px;
    margin-bottom: 6px;
    margin-right: 4px;
  }
  
  @keyframes shimmer {
    0% {
      background-position: 0 0;
    }
  
    100% {
      background-position: 1200px 0;
    }
  }  
}

