.connectionStatus {
  .popup__header {
    display: none;
    ::after{
        content: ' ';
        width: 30px;
        height: 4px;
        background: #6F7071;
    }
  }

  .popup__body > .popup__body-content > .wrapper {
      
    .connections {
      padding: 20px;
      border: 1px solid #d8dcde;
      border-radius: 10px;
      margin-bottom: 15px;

      display: grid;
      grid-template-columns: 16% 71% 13%;

      &:last-child {
        margin: 0;
      }

      .icon-container {
        .icon {
          svg {
            width: 30px;
            height: 30px;
          }

          &.bluetoothIcon{
              svg{
                  circle{
                      stroke: #ffd95c;
                      fill: transparent
                  }
                  path{ fill: #ffd95c; }
              }
          }
        }
      }
      .connections__content {
        .connections__content__title {
          @include title-2;
        }
        .connections__content__desc {
          @include body-1;
          color: #939598;
        }
      }
      .connections__action-button > .edit-button{
        background: transparent;
        border: none;
        color: #01B4DE;
        @include body-1;
      }
    }
  }
}

