.pes-popup{
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 250;
  background-color: rgba(0, 0, 0, 0.6);

    .wrapper{
    position: absolute;
    top: 35%;
    left:0;
    right:0;
    margin-left:auto;
    margin-right:auto;
    background-color: #FFFFFF;
    border-radius: 10px;
    border: 1px solid #C6C8CA;
    height: max-content;
    width: max-content;

    .pes-popup-pairing{
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      min-height: 290px;
      width: 285px;
      overflow-y: auto;
      z-index: 189;
      
      .icon{
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 70px;
        margin-bottom: 45px;
        max-height: 50px;
        max-width: 48px;
      }
    
      .message{
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 16px;
        color: #545456;
        margin-bottom: 105px;
        font-weight: 500;
      }
    }

      .container{
        display: flex;
        flex-direction: column;
        width: 285px;
        overflow-y: auto;
        
        .wifi-password{
          height: 224px;
          padding: 20px;
          font-size: 14px;
          color: #545456;

          .enter-pwd-text{
          }

          .selected-wifi-name{
            margin-top: 7px;
            font-weight: 500;
          }

          .wifi-incorrect-pwd{
            font-size: 12px;
            color: #F26B6B;
            margin-top: 9.5px;
          }

          .password-row{
            display: flex;
            justify-content: space-between;
            align-items: center;
            border-bottom: 1px solid #C6C8CA;
            margin-top: 30px;
            
            .password-col-1{
              display: flex;
              flex-direction: column;

              .pwd-header{
                color: #939598;
                height: 18px;
              }

              .enter-pwd{
                min-height: 18px;
                padding-left: 0;
                border-bottom: 0;
              }
            }

            .password-col-2{
              display: flex;
              height: 36px;
              cursor: pointer;
              margin-bottom: 13px;
            }
          }
        }

        .header{
          padding: 24px 16.5px 22.45px;
          border-bottom: 1px solid #C6C8CA;
          font-size: 18px;
          font-weight: 500;
          color: #131415;
        }

        .header-wifi{
          padding: 17px 16.5px 14px 16.5px;
