          font-size: 16px;
          font-weight: 500;
          color: #545456;
        }

        .pes-wifi-list{
          display: flex;
          flex-direction: column;
          padding: 19px 20px;
          height: 170px;
          overflow-y: scroll;
          
          .wifi-list-item{
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            padding-bottom: 13.55px;
            height: 30px;
            border-bottom: 1px solid #CFCFD2;
            margin-top: 13.45px;

            &:first-child{
              margin-top: 0;
            }

            &:last-child{
              border-bottom: 0;
            }

            .wifi-list-name{
              font-size: 14px;
