            }

            .wifi-list-icon{
              display: flex;
              justify-content: center;
              align-items: center;
            }
          }
        }

        .wifi-list-not-found{
          display: flex;
          width: 100%;
          height: 170px;
          align-items: center;
          justify-content: center;
          font-size: 12px;
          color: #545456;
        }

        .header-bluetooth{
          padding: 19px 16.5px 14px;
          border-bottom: 1px solid #C6C8CA;
          font-size: 16px;
          font-weight: 500;
          color: #545456;
        }
        
        .icon{
          display: flex;
          justify-content: center;
          align-items: center;
          margin-top: 30.5px;
          margin-bottom: 25px;
          max-height: 40px;
          max-width: 40px;
        }

        .icon-error{
          display: flex;
          justify-content: center;
          align-items: center;
          margin-top: 30.5px;
          margin-bottom: 12px;
          max-height: 40px;
