            cursor: pointer;
            padding-top: 19px;
            padding-bottom: 17px;
            border-right: 1px solid #C6C8CA;
            width: 50%;
            color: #545456;
          }

          .button-right{
            display: flex;
            justify-content: center;
            align-items: center;
            cursor: pointer;
            padding-top: 19px;
            padding-bottom: 17px;
            width: 50%;
            color: #01B4DE;
            font-weight: 500;
          }
        }
      }
    }


}

