.advance_settings_wrapper {
  margin: 15px;

  .dsc_wrapper {
    .dsc_main_slider {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: 20px;

      .storageBattery {
        margin-right: 10px;
      }

      .asw_slider {
        width: 100%;
        .multi-slider {
          display: flex;
          align-items: center;
          justify-content: center;
        }
      }

      .disable_asw_slider {
        pointer-events: none;
      }
    }

    .asw_info {
      .info-1 {
        margin-top: 30px;
        margin-bottom: 10px;
        font-weight: 400;
        font-size: 12px;
        line-height: 16px;
        color: #6f6f8c;

        .heading-span {
          color: #00b6de;
        }
      }

      .info-2 {
        @extend .info-1;
        margin-top: 10px;
        margin-bottom: 0;
        color: #b2b6b7;
      }
    }
    .error-msg {
      font-size: 12px;
      line-height: 16px;
      margin: 10px 0 0;
      color: #e3655e;
    }
  }

  .offline_mode_enabled-cfg .status__icon .icon svg g path {
    fill: #00b6de;
  }

  .disabled small {
    color: rgba(84, 84, 86, 0.5) !important;
  }

  .bsl__status-icon {
    display: flex;
    align-items: center;

    .offline__status-wrapper {
      margin-right: 14px;
    }
  }
}

.battery_header {
  .title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
}

.offline__cfg-dialog-checkbox {
  display: flex;
  align-items: center;

  input {
    margin-right: 5px;
  }
}

.cfg-settings-dialog {
  .dialogHeader {
    padding: 15px;
    border-bottom: 1px solid #d8d8d8;
    color: #131415;
    font-weight: 500;
    line-height: 21px;
    font-size: 18px;
  }
  .data-area {
    padding: 0%;
  }
  .dialogBody {
    padding: 20px 15px 25px 15px;
    .disclaimer {
      font-size: 14px;
      line-height: 16px;
      color: #545455;
    }
    .check {
      position: relative;
      padding-left: 25px;
      padding-top: 15px;
      input {
        position: absolute;
        top: 18px;
        left: 0px;
      }
      div {
        font-size: 14px;
        font-weight: 700;
        color: #545455;
        line-height: 16px;
      }
    }
  }
}

.night {
  .battery_header {
    .title .icon svg g rect {
      fill: #333 !important;
    }
  }

  .remove-border {
    background: #232323 !important;

    .storage-mgmt {
      background: #232323 !important;
      .text-blk {
        color: #f8f8ff !important;
      }
    }
  }

  .om__storm-alert-card {
    background: #232323 !important;
  }

  .om__battery_profile_card_night {
    box-shadow: 0 2px 10px 0 #00000040;
    background-color: #262627;
  }

  .cfg-settings-dialog {
    .dialogHeader {
      color: #f8f8ff;
    }

    .dialogBody {
      .disclaimer {
        color: #f8f8ff;
      }
      .check {
        div {
          color: #f8f8ff;
        }
      }
    }
  }

  .advance_settings_wrapper {
    .dsc_wrapper {
      .dsc_main_slider {
        .icon {
          svg {
            path {
              fill: #f8f8ff;
            }
          }
        }
      }

      .asw_info {
        .info-1 {
          color: #f8f8ff;
        }

        .info-2 {
          color: #b2b6b7;
        }
      }
    }
    .disabled small {
      color: #a2abb3 !important;
    }

    .offline_mode_enabled-cfg {
      color: #f8f8ff;
    }
  }

  .om_bsw_sm_ei-wrapper {
    .om__disabled small {
      color: #a2abb3 !important;
    }

    .om__bsw_sm_ei_title {
      color: #f8f8ff;
    }
  }

  .disabled small {
    color: #a2abb3 !important;
  }

  .bsw__battery_profile_card {
    .bsw__cards_title {
      .icon {
        svg {
          path {
            fill: #f8f8ff;
          }
        }
      }
    }
  }
}

.remove-border {
  border-bottom: 0px !important;
}

.bsw__cards_wrapper {
  margin: 20px;
  padding-bottom: 20px;
}

.bsw__battery_profile_card {
  .active {
    color: rgb(53, 195, 125);

    .icon {
      svg {
        path {
          fill: rgb(53, 195, 125) !important;
        }
      }
    }
  }

  .status {
    font-weight: 400;
  }

  .edit-button {
    font-size: 12px;
    font-weight: 500;
    border: none;
    color: #01b5de;
    background-color: transparent;
    cursor: pointer;
    padding: 0;
  }

  .bsw_active {
    margin: 20px 0 0;

    :last-child {
      margin-bottom: 0;
    }
  }

  .bsw__cards_title {
    display: flex;
    align-items: center;
    font-weight: 700;

    .icon {
      display: flex;
      margin: 0 10px 2px 0;
    }
  }
}

.battery-storage-wrapper {
  border-radius: 10px;
  box-shadow: none;
  position: relative;
  padding: 20px;
  display: flex;
  flex-direction: column;
  border-bottom: 0.5px solid #7b87934d;
  border-radius: 0;
  margin-bottom: 0;

  .storage-mgmt {
    display: flex;
    align-items: center;
    background: #ffffff;

    .img-blk {
      width: 100px;
      height: 67px;
      background-image: url("../../../images/storage.png");
      background-repeat: no-repeat;
      margin-right: 15px;
    }
    .text-blk {
      width: 60%;
      font-size: 14px;
      font-weight: 500;
      color: #545456;
      font-family: "Visuelt", "Roboto", "Helvectica Neue", Helvetica, Arial, sans-serif;
      font-size: 12px;
      line-height: 16px;
      font-weight: 500;

      .help-icon {
        margin-left: 5px;
        vertical-align: middle;

        .icon {
          display: inline;
        }
      }
    }
  }
}

.om__anchor {
  color: #01b5de;
}

.om__details-button {
  display: flex;
  align-items: center;
  margin: 15px 0 0;
  padding: 0;
  border: unset;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.29;
  color: #01b5de;
  background: unset;
  font-family: "Visuelt", "Roboto", "Helvectica Neue", Helvetica, Arial, sans-serif;
  font-size: 12px;
  line-height: 16px;
  font-weight: 400;

  .icon {
    margin-left: 5px;
  }
}

.disabled small {
  color: rgba(84, 84, 86, 0.5) !important;
}

.om_bsw_sm_ei-wrapper {
  margin-top: 20px;
  .om__disabled small {
    color: rgba(84, 84, 86, 0.5) !important;
  }

  .om__disabled-mt {
    margin-top: 5px;
  }

  .om__bsw_sm_ei_title {
    color: #545456;
    font-size: 12px;
    line-height: normal;
  }

  .om_icon {
    .icon {
      display: flex;
      margin-bottom: 2px;
    }
  }
}

.om_bsw_card_padding {
  padding: 20px;
}

.om__disable_content {
  opacity: 0.4;
}

.sm-loaderV2 {
  height: 1em;
}
