@import "../font-style";

.comp-calendar-weather{
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  @include caption;
  background-color: #f9f9f9;
  .comp-cw__wrapper{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 45px;
  }

  &.comp-cw__has-weather-info{
    .comp-cw__date-selector{
      width: auto;
      .date-select{
        .front{
          padding-right: 0;
        }
      }
    }
  }

  .icon{
    display: inline-flex;

    svg{
      width: 100%;
      height: 100%;
      margin: 0!important;
    }

    &.prev, &.next{
      width: 8px;
      height: 14px;
      opacity: 0.2!important;
      pointer-events: none;

      &.open{
        opacity: 1!important;
        pointer-events: all;
      }
    }

    &.calendar{
      width: 16px;
      height: 16px;
      margin-right: 10px;
    }
  }

  .weather {
    .icon{
      svg{
        width: auto;
        height: 18px;
      }
    }
  }

  .comp-cw__date-selector{
    width: 100%;

    .date-select{
      transform-style: preserve-3d;
      transform-origin: 50% 60% 0;
      transition: transform 400ms;
      overflow: hidden;

      .select{
        transform: rotateX(0deg)
      }

      .front{
        width: 100%;
        display: grid;
        grid-column-gap: 20px;
        grid-template-columns: auto auto auto;
        justify-content: space-between;
        align-items: center;
        padding: 13px 17px 13px 17px;

        .label{
          display: flex;
          align-items: center;
          font-weight: 500;
          color: #545456;
        }
        .date-displayed{
          display: flex;
          margin-top: 4px;
        }

        
        a {
          min-width: 20px;
          min-height: 20px;
          display: flex;

          &:first-child { justify-content: center; }
          &:last-child { justify-content: center; }
        }
      }

      .back{
        transform: rotateX(-180deg);
        display: none;
        padding: 2px 17px 19px 17px;
        /* width: 100vw;
        justify-content: flex-end !important; */

        .button{
          margin: 0;
          width: auto;
          padding: 7px 20px 5px;
          font-size: 12px;
          font-weight: 500;
          text-transform: uppercase;

          &.primary {
            background-color: $primary;
            margin-left: 10px;
          }
        }
      }

      &.flip{
        transform: rotateX(-180deg);

        .front{
          display: none;
        }

        .back{
          display: flex;
          align-items: center;
          justify-content: center;
        }
      }
    }
  }

  .comp-cw__weather-info{
    min-width: 85px;
    padding: 13px 17px 13px 0;

    .weather{

      img{
        max-height: 20px;
        margin-left: 10px;
      }
    }
  }

  .comp-cw__date-selector__with-touch{
    font-size: 16px;
    font-weight: 500;
    color: #545456;
    padding: 13px 0 13px 17px;
  }

  .date-picker{
    top: auto;
  }
}

.night{
  .comp-calendar-weather{
    background-color: #272728;

    .icon{
      &.prev, &.next{
        polygon{
          fill: #f8f8ff;
        }
      }
  
      &.calendar{
        fill: #f8f8ff;
      }
    }

    .comp-cw__date-selector{
      .date-select{
        .front{
          .label{
            color: #f8f8ff;
          }
        }
      }
    }

    .comp-cw__date-selector__with-touch{
      color: #a2a6a9;
    }
  }
}
@media screen and (max-height: 740px){
  .comp-calendar-weather{
    .date-picker {
      .rdt {
        height: calc(100vh - 47%);
        overflow-y: scroll;
      }
    }
  }
}

@media screen and (max-width: 359px){
  .comp-calendar-weather{
    .comp-cw__date-selector{
      .date-select{
        .front{
          grid-column-gap: 18px;

          .label{
            font-size: 12px;
          }
        }
      }

      .comp-cw__date-selector__with-touch{
        font-size: 12px;
      }
    }
  }
}
