.tariff-editor{
  .tariff-editor-web{
    width: 100%;

    .tew__header {
      display: none;
    }

    .tew__body{
      margin: 50px 0;
      width: 100%;
    }

    .rbl__radio-button{
      input{
        border-color: #565659;
      }
    }

    .charge-battery-grid {
      .cbg__wrapper {
        .cbg__radio-button-wrapper {
          .cbg__radio-button {
            input{
              &:before{
                width: 7px;
                height: 13px
              }
            }
          }
        }
      }
    }
  }

  .tariff-editor-mobile{
    bottom: #{$footer-height};

    .questionnaire-list{
      .ql__wrapper{
        .ql__contents{
          .radio-button-list{
            .rbl__wrapper{
              .rbl__radio-button{
                input{
                  border-color: #565659;

                  &:before{
                    left: 8px;
                    top: 4px;
                  }
                }
              }
            }
          }
        }
      }
    }

    .tariff-details-mobile{
      height: 100%;
    }

    .tariff-dialog{
      z-index: 100;
    }

    &+.tariff-dialog{
      .td__header{
        .td__header__title{
          font-weight: 500;
        }
      }
    }

    .charge-battery-grid {
      .cbg__wrapper {
        .cbg__radio-button-wrapper {
          .cbg__radio-button {
            input{
              border-color: #565659;
    
              &:before{
                left: 8px;
                top: 4px;
              }
            }
          }
        }
      }
    }
  }

  .te__loader{
    z-index: 105;
  }

  &.offline-background {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1;
  }

  &.web .te__notification {
    top: 100px;
  }

  .electricity-tutorial__dialog{
    .et__image{
      img{
        width: 100%;
      }
    }
    
    .et__contents__nav{
      height: 50px;

      .et__nav__active:before{
        top: -3px;
        left: -3px;
      }
    }
  }
}

.tariff-mobile__wrapper {
  .tariff-type {
    .tt__wrapper {
      .tt__content-wrapper {

        .tt__actions {
          margin: 20px 20px 10px !important;
          
          button {
            text-transform: capitalize;
            font-size: 16px;
            font-weight: 500;
          }
        }
      }
    }
  }

  .new-tariff-mobile {
    .ntm__wrapper {
      .ntm__content {
        height: 100%;
        min-height: max-content;
        &.single-tariff {
          min-height: auto;
        }
      }

      .autofill-tariff {
        .aft__wrapper {
          .aft__actions {
            height: 48px;
            
            button {
              height: 100%;
              text-transform: capitalize;
              font-size: 16px;
              font-weight: 500;
            }
          }
        }
      }
    }

    .tariff-type-kind {
      display: flex;
      flex-direction: column;
      height: calc(100% - 75px);
      min-height: max-content;
      &.single-tariff {
        min-height: auto;
        height: 100%;
        .ttk__actions {
          margin: 20px 20px 10px !important;
        }
      }

      &.tiered {
        height: calc(100% - 95px);
      }

      .ttk__actions {
        margin: 20px 20px 30px !important;

        button {
          text-transform: capitalize;
          font-size: 16px;
          font-weight: 500;
        }
      }
    }
  }
}

.tariff-dialog, .te__notification {
  z-index: 105;
}