
.layout {
  .comp-notification.comp-notification__failure {
    background-color: #545456;
  }
}

.array-viewer {
  height: 76.5%;
  width: 100%;
  position: relative;
  background-color: transparent;

  .zoom, .input {
    position: absolute;
    transition: top $transition_layout;
    box-shadow: 0.5px 0.5px 2px $shadow;
    border-radius: 0.1em;
    background-color: $background;
    color: $medium;
    z-index: 101;
  }

  .zoom {
    top: 0.8em;
    right: 18px;
    cursor: pointer;
    font-size: 2em;
    font-weight: 300;
    div {
      width: 1.6em;
      height: 1.6em;
      box-shadow: 0 0 1px $shadow;
      text-align: center;
      line-height: 1.6em;
      &:active { background-color: $super-light; }
    }
    .in { border-radius: 0.1em 0.1em 0 0; }
    .out { border-radius: 0 0 0.1em 0.1em; }
  }

  .input {
    top: 1.2em;
    border: none;
    width: initial;
    font-size: 1.2em;
    padding: 0.5em 1em;
    left: 1.3em;
  }

  /* Show / hide alarms */
  &.alarms {
    .input { top: 6.4em; }
    .zoom { top: 3.8em; }
    .jf-alarms { opacity: 1; }
    .message {
      margin-top: 0;
      opacity: 1;
      z-index: 101;
    }
  }

  .jf-alarms { 
    opacity: 0; 
    transition: opacity $transition_layout;
  }

  .messgeForMicro {
    position: inherit!important;
    padding-top: 14px!important;
  }

  .message {
    width: 100%;
    opacity: 0;
    margin-top: 0;
    transition: margin-top $transition_layout;
    background-color: $secondary-super-light;
    z-index: 101;
    position: absolute;
    padding: 1.3em 4.5em 1.3em 1.5em;
    line-height: 1.5em;
    font-size: 12px;
    font-weight: 300;
    color: $super-dark;

    span a {
      text-decoration: none;
      font-weight: 400;
      color: #01B4DE;
    }
    
    img {
      float: left;
      margin: 0.3em 1em 2em -0.2em;
      width: 1.4em;
    }

    .crossMark {
      position: absolute;
      right: 0;
      height: 4.4em;
      padding-right: 1.5em;
      display: block;
      fill: #939598;
      svg{
        width: 12px;
        height: 12px;
      }
    }

    .alarm-message {
      margin-left: 20px;
      display: table;
    }
  }
  .web-link{
    text-decoration: none;
    font-weight: 500;
    cursor: pointer;
  }
}

.scale {
  bottom: #{$footer-height};
  z-index: 101;
}
