.browser {
    .popup-container {
        .popup-wrapper {
            position: absolute;
            left: 50%;
            top: 50%;
            background-color: #ffffff;
            transform: translate(-50%, -50%);
            z-index: 107;
            max-height: 80%;
            max-width: 90%;
        }
        .popup-title-wrapper {
            display: flex;
            justify-content: space-between;
            padding: 26px 32px;
            height: 75px;

            &.no-title {
                padding: 0px;
                height: auto;
            }
        }
        .popup-title {
            font-size: 20px;
            font-weight: 500;
            font-style: normal;
            font-stretch: normal;
            line-height: normal;
            letter-spacing: normal;
            color: #545456;
        }
        .popup-title-border {
            opacity: 0.5;
            border: solid 0.5px rgba(123, 135, 147, 0.3);
        }
        .popup-body {
            overflow-y: auto;
            height: calc(100% - 76px);
            .comp-notification {
                position: absolute;
                top: 0;
                bottom: 0;
                left: 50%;
                right: 50%;
                width: 450px;
                height: 67px;
                transform: translate(-50%,-145%);
            }
        }
        .confirm-popup {
            position: absolute;
            top: 0;
            height: 100%;
            width: 100%;
            .confirm-popup-overlay {
                height: calc(100% - 200px);
                width: 100%;
                opacity: 0.8;
                background-color: #ffffff;
            }
            .confirm-popup-body {
                height: 200px;
                width: 100%;
                background-color: #ffffff;
                display: flex;
                box-shadow: 0 2px 34px 0 rgba(63, 67, 71, 0.28);
                flex-direction: column;
                align-items: center;
            }
            .confirm-popup-title {
                margin: 32px 0px;
                font-size: 18px;
                font-weight: 500;
            }
            .confirm-popup-button {
                width: 315px;
                height: 50px;
                display: flex;
                align-items: center;
                justify-content: center;
                font-size: 17px
            }
            .confirm-popup-yes {
                border-radius: 25px;
                font-weight: normal;
                background-color: #01b4de;
                color: #ffffff;
            }
            .confirm-popup-no {
                font-weight: 500;
                color: #01b4de;
            }
        }
        .close-button {
            display: flex;
            .icon {
                display: flex;
                height: 15px;
                cursor: pointer;
                svg {
                    width: 15px;
                    height: 15px;
                    fill: #545456;
                }
            }
        }
        @media (max-height: 500px) {
            &.battery-storage, &.footer {
                .popup-wrapper {
                    height: 90% !important;
                }
            }
        }
        @media (max-height: 700px) {
            &.currency-conversion, &.period_config_dialog, &.support, &.header-content {
                .popup-wrapper {
                    height: 90% !important;
                }
            }
        }
    }
}