.request-replacement-warrenty {
  position: absolute;
  font-weight: 400;
  width: 100%;
  height: 100%;
  background-color: #fafafb;

  .rr-header {
    justify-content: flex-start;
    min-height: 105px;
    background-color: #01b5de;
    display: flex;
    align-items: center;
    padding: 77px 20px 15px 10px;
    margin: 0 auto;
    color: #fff;
    font-size: 19px;
    font-weight: 400;

    .icon {
      display: inline-block;
      margin-left: 8px;
      margin-right: 15px;
      height: 32px;
      fill: #fff;
    }
  }

  .rr__dialog-wrapper {
    height: calc(100% - 170px);
    overflow-y: auto;
    scroll-behavior: smooth;
  }

  @media screen and (min-width:1024px) {
    position: relative;

    .rr__dialog-wrapper {
      height: calc(100% - 64px);
      margin: 32px;
      border-radius: 3px;
      box-shadow: 0 2px 10px 0 rgba(63,67,71,.08);
      background-color: #fff;
      position: relative;
      overflow: scroll;
    }
  }
}

.request-replacement {
  .line-break {
    border-bottom: solid 0.5px rgba(123, 135, 147, 0.3);
    margin-bottom: 20px;
  }

  .request-replacement-wrapper  {
    padding: 0 20px;

    .container section {
      padding: 20px 0;
      font-size: 16px;
      color: #545456;
      border-bottom: solid 0.5px rgba(123, 135, 147, 0.3);
    }

    .container section:last-child {
      border-bottom: unset;
    }

    .search-section-header {
      font-weight: 500;
      color: #545456;
      font-size: 16px;
    }

    .replacement-addressHeader {
      margin-bottom: 11px;
      overflow: auto;
      font-weight: 500;
      color: #545456;
      font-size: 16px;
      margin-top: 20px;
  }

    .search-section {
      margin-bottom: 4em;

      & .refresh-replacement-list {
        max-width: 257px;
        margin: 99px auto 0;

        & p {
          max-width: 230px;
          margin: 0 auto;
          font-size: 14px;
          line-height: 19px;
          text-align: center;
          color: #545456;
        }

        & button.refresh-button {
          min-width: 159px;
          height: 40px;
          padding: 0;
          margin: 15px auto 0;
          display: flex;
          justify-content: center;
          align-items: center;
          border: solid 1px #01b4de;
          border-radius: 20px;
          font-size: 16px;
          font-weight: 500;
          color: #00b6de;
          line-height: 21px;
          background: #ffffff;

          & > span {
            width: 13.3px;
            height: 13.4px;
            margin-right: 10px;

            & > svg {
              width: 13.3px;
              height: 13.4px;
              display: block;
            }
          }
        }
      }
    }

    .replacement-addressHeader span {
      display: inline-block;
    }

    .replacement-addressHeader span:first-child {
      float: left;
    }

    .replacement-addressHeader span:last-child {
      font-size: 14px;
      color: #01b4de;
      float: right;
    }

    .replacement-addressLabel {
      font-size: 12px;
      color: #7b8793;
      margin-bottom: 5px;
    }

    .replacement-address {
      color: #3f4347;
      font-size: 16px;
    }

    .listRow {
      position: relative;
      padding: 14px 0 12px 0;
    }

    .listRow:not(:last-child) {
      border-bottom: .5px solid rgba(123,135,147,.3);
    }

    .listRow > span {
      position: absolute;
      height: 8px;
      width: 8px;
      display: inline-block;
      top: 18px;
      left: -15px;
      border-radius: 50%;
    }
    .listRow .list-row-items{
      display: inline-block;
    }

    .listRow .list-row-items > * {
      color: #7b8793;
      font-size: 12px;
      padding-bottom: 5px;
    }

    .listRow .list-row-items > *:last-child {
      padding-bottom: unset;
    }

    .listRow .heading {
      font-size: 14px;
      font-weight: 500;
      color: #545456;
    }

    .listRow .status-normal {
      color: #7b8793;
    }

    .listRow .status-error {
      color: #ff5959;
    }

    .listRow .bullet-normal {
      background-color: #69dba5;
    }

    .listRow .bullet-error {
      background-color: #ff5959;
    }

    .checkboxWrapper {
      position: absolute;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
    }

    .searchInput {
      margin: 10px 0;
      position: relative;
    
      input {
        width: 100%;
        border-radius: 5px;
        border: solid 0.5px rgba(123, 135, 147, 0.3);
        padding: 7px 15px 7px 36px;
        font-size: 14px;
        font-weight: 300;
        opacity: 0.8;
        color: #545456;
        &.placeholder {
          padding: 7px 15px;
        }
      }
      
      .searchInput img {
        width: 17px;
        height: 17px;
        display: inline-block;
        top: 0;
        right: 0;
        position: absolute;
        margin: 7px 15px 0 0;
      }

      .prefix-text {
        opacity: 0.8;
        font-size: 14px;
        font-weight: 300;
        color: #545456;
        position: absolute;
        margin: 7px 15px 0 15px;
        &.disabled {
          display: none;
        }
      }
    }
  }

  .save-button {
    width: 100%;
    background-color: #02b6de;
    border: none;
    position: fixed;
    bottom: 0;
    bottom: env(safe-area-inset-bottom);
    height: 48px;
    display: block;
    color: #ffffff;
    &.disabled {
      background-color: #c8c9ca;
      pointer-events: none;
      cursor: default;
      color: #ffffff !important;
    }

    .text {
      font-size: 16px;     
      font-weight: normal;
    }

    .sub-text {
      font-family: Helvetica;
      font-size: 12px;
      text-align: center;
      display: block;
    }
  }

  @media screen and (min-width:1024px) {
    & .request-replacement-wrapper {
      padding: 20px 32px;

      & .replacement-addressHeader {
        margin: 12px 0;
        font-size: 20px;

        & > span:last-child {
          float: unset;
          margin-left: 16px;
        }
      }

      & .replacement-addressLabel {
        margin: 0 0 2px;
        font-size: 15px;
      }

      & .replacement-address {
        max-width: 325px;
        margin: 0;
        font-size: 17px;
      }

      & .search-section {
        margin-bottom: 44px;

        & .search-section-header { font-size: 20px; }

        & .replacement-search {
          display: flex;
          justify-content: space-between;
          align-items: center;

          & .searchInput {
            min-width: 333px;
            margin: 0;

            &  .prefix-text { margin: 9px 15px 8px 54px; }

            & input { padding: 9px 15px 8px 75px; }

            & input.placeholder { padding: 9px 15px 8px 54px; }

            & .icon.search {
              width: 20px;
              height: 20px;
              margin: 7px 20px 7px 14px;
              left: 0;

              & svg {
                width: 20px;
                height: 20px;
              }
            }
          }
        }

        & .refresh-replacement-list {
          max-width: 315px;
          margin: 138px auto 0;

          & p {
            max-width: 260px;
            font-size: 17px;
            line-height: 23px;
          }

          & button.refresh-button {
            min-width: 315px;
            height: 50px;
            margin-top: 20px 0 0 0;
            border-radius: 25px;
            font-size: 17px;
            line-height: 23px;
            text-transform: uppercase;

            & > span {
              width: 14px;
              height: 14px;
              margin-right: 12px;

              & > svg {
                width: 14px;
                height: 14px;
              }
            }
          }
        }

        & .listWrapper  {
          padding: 16px 0 0;
          display: grid;
          grid-template-columns: auto auto auto;

          &.fixed { grid-template-columns: 33.33% 33.33% 33.33%; }

          & .listRow {
            padding: 20px;
            margin: 0 32px 32px 0;
            display: flex;
            justify-content: flex-start;
            align-content: flex-start;
            border-radius: 10px;
            border: solid 1px rgba(136, 136, 136, 0.2);

            &:nth-of-type(3n) {
              margin-right: 0;
            }

            & > span.bullet-normal,
            & > span.bullet-error {
              width: 10px;
              height: 10px;
              margin-top: 6px;
              position: unset;
            }

            & .list-row-items {
              flex-grow: 1;
              margin: 0 10px;

              & > * {
                font-size: 15px;
                font-weight: 300;
                line-height: 20px;
              }

              & .heading {
                font-size: 17px;
                line-height: 23px;
              }
            }

            & .checkboxWrapper {
              margin: 2px 0;
              position: unset;
              transform: unset;
            }
          }
        }
      }
    }

    & .line-break { margin: 0; }

    & .deskto-submit-request {
      width: calc(100% - 64px);
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 15px 0;
      margin: 0 32px;
      border-radius: 3px;
      box-shadow: 0 2px 10px 0 rgba(63, 67, 71, 0.08);
      background-color: #ffffff;
      position: fixed;
      left: 0;
      bottom: 122px;
    }

    & .save-button {
      width: auto;
      min-width: 315px;
      height: 50px;
      padding: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 25px;
      text-transform: uppercase;
      position: unset;
      border: solid 1px #c8c9ca;
    }
  }

  @media screen and (min-width:1440px) {
    & .deskto-submit-request {
      width: 100%;
      max-width: 1376px;
      margin: 0 0 0 -688px;
      left: 50%;
    }
  }
}

.night {
  .request-replacement-warrenty {
    .rr-header {
      background-color: #333333;
    }

    .rr__dialog-wrapper {
      background-color: #262627;
    }
  }

  .request-replacement {
    .request-replacement-wrapper {
      .replacement-addressHeader span:first-child {
        color: #f8f8ff;
      }

      .replacement-addressLabel {
        color: #a2a6a9;
      }

      .replacement-address {
        color: #ffffff;
      }

      .search-section-header {
        color: #f8f8ff;
      }

      .searchInput input {
        color: #ffffff;
        opacity: 0.5;
      }

      .prefix-text {
        color: #ffffff;
        opacity: 0.5;
      }

      .searchInput .icon {
        svg path {
          fill: #fff;
        }
      }

      .listRow .list-row-items>* {
        color: #a2a6a9;
      }

      .listRow .heading {
        color: #f8f8ff;
      }
    }
    .save-button {
      &.disabled {
        background-color: #494a4b;
        color: #6e6e71!important;
      }
    }
  }
}
 