.connection-type {
  > div {
    display: flex;
    margin: 5px 0 0;
  }

  .icon {
    width: 15px;
    height: 14px !important;
    margin-right: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: unset;

    svg {
      width: 100%;
      height: auto !important;
    }

    &.ethernet svg {
      width: auto;
      height: 14px !important;
    }
  }

  p {
    margin: 0;
    font-size: 12px;
    font-weight: 300;
    line-height: 14px;
    color: #7b8794;
    @include body-1;
  }
  
  .active {
    .ethernet svg > g > g { stroke: #69dba5; }
    .wifi svg > path, .cellular svg > g { fill: #69dba5; }
    p { color: #545456; }
    .name { font-weight: 500; }
    .status {font-weight: 400; }
    .description { display: inline-block;
    font-weight: 400;
    }
  }

  .idle {
    .ethernet svg > g > g { stroke: #ffb026; }
    .wifi svg > path, .cellular svg > g { fill: #ffb026; }
    .name {font-weight: 500;}
    .status { color: #7b8793;
    font-weight: 400;
    }
  }

  .notAvailable {
    .ethernet svg > g > g { stroke: #bcc2c8; }
    .wifi svg > path, .cellular svg > g { fill: #bcc2c8; }
    .name {font-weight: 500;}
    .status { color: #7b8793;
    font-weight: 400;
    }
  }

  .connection-type__configure {
    margin: 10px 0 0;
    padding: 0;
    border: unset;
    outline: unset;
    font-size: 14px;
    font-weight: 500;
    line-height: 1;
    color: #00b6de;
    background: unset;
    cursor: pointer;
  }
}

.connection-type__devices {
  .icon {
    height: 16px !important;

    &.ethernet svg {
      height: 16px !important;
      padding-top: 2px;
    }
  }

  p {
    font-size: 12px;
    line-height: 16px;
  }
}

/* Dark mode CSS */
.night {
  .connection-type .active p { color: #d8dcde; }
}

/* Web version CSS */
.browser {
  .connection-type {
    > div {
      margin-top: 6px;

      &:first-child { margin-top: 3px; }
    }

    .icon {
      width: 18px;
      height: 16px !important;
      margin-right: 8px;

      &.ethernet svg {
        height: 16px !important;
        padding-top: 2px;
      }
    }

    p {
      font-size: 14px;
      line-height: 16px;
    }
  }

  .connection-type__devices {
    .icon { height: 19px !important; }

    p {
      font-size: 16px;
      font-weight: normal;
      line-height: 19px;

      .status, .description {
        font-size: 14px;
        font-weight: 300;
      }
    }
  }
}