.browser {
	.notices {
		margin: 0px 32px;
		.content li {
			display: flex;
			padding: 20px;
			background-color: #ffffff;
			border-radius: 10px;
  		box-shadow: 1px 2px 10px 0 rgba(63, 67, 71, 0.08);
			margin-top: 32px;
			position: relative;
			&:last-child {
				margin-bottom: 32px;
			}
			.alert {
				font-size: 18px;
				font-weight: 500;
				letter-spacing: normal;
				color: #ff4751;
				&.icon {
					position: absolute;
					left: 1.3em;
					svg {
						stroke: #ff4751;
					}
				}
			}
			h4 {
				padding-left: 50px;
				font-size: 19px;
				font-weight: 500;
				line-height: 1.21;
				color: #ff4751;
				margin-bottom: 12px;
			}
			p {
				margin: 0;
				padding: 0 30px 0 50px;
				font-size: 17px;
				line-height: 1.41;
				color: #7b8793;				
				a {
					font-size: 17px;
					line-height: normal;
					color: #01b5de;
					margin-top: 12px;
				}
			}
			.clear {
				position: absolute;
				margin-top: 4px;
				span {
					width: 24px;
					display: flex;
					justify-content: center;
					align-items: center;
				}
				svg {
					height: 16px;
					width: 16px;
					polygon {
						fill: #545456;
					}
				}
			}
		}
	}
}
.notices {
	.noNotice {
		text-align: center;
		height: calc(100vh - 134px - 9vh - 40px);
		display: flex;
		justify-content: center;
		align-items: center;
	}
}
