body {
  margin: 0;
  padding: 0;
  background-color: #000;
  top: constant(safe-area-inset-top);
  top: env(safe-area-inset-top);
}

@keyframes spinner {
  to { transform: rotate(360deg); }
}

.message {
  color: #ffffff;
  position: absolute;
  top: 50%;
  width: 100%;
  text-align: center;
}

