  width: 100%;
  text-align: center;
} */

.spinner:before {
  content: '';
  box-sizing: border-box;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 30px;
  height: 30px;
  margin-top: -15px;
  margin-left: -15px;
  border-radius: 50%;
  border: 2px solid #333;
  border-top-color: #f37321;
  animation: spinner .6s linear infinite;
}

.msg-background {
  font-family: 'Roboto', sans-serif;
  background-color: #ffffff;
  width: 100vw;
  height: 100vh;
}

.msg-background * {
  box-sizing: border-box;
  margin: 0;
}

.msg-background .msg-box {
  width: 100%;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  text-align: center;
  padding: 1px;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
}

.msg-box > svg {
  display: block;
  margin: 0 auto 30px auto;
}

.msg-box h1 {
  font-size: 18px;
  font-weight: bold;
  color: #545456;
}

.msg-box h3 {
  font-size: 14px;
  font-weight: normal;
  line-height: 1.29;
  color: #5a5a5c;
  margin: 10px 0 30px 0;
}

.msg-box .refresh-btn {
  border-radius: 20px;
  border: solid 1px #01b4de;
  background-color: #01b4de;
  text-decoration: none;
  padding: 10px 34px;
  color: #ffffff;
  font-size: 16px;
  font-weight: 500;
  display: block;
  margin: 0 auto;
  width: 159px;
  white-space: nowrap;
  display: flex;
  justify-content: space-evenly;

}

.msg-box .refresh-btn svg {
  margin-bottom: -5px;
  height: 1em;
}

.msg-box .refresh-btn.active svg {
  animation: spinner .6s linear 1;
}
