@import "../fonts";
.custom-config{
  height: calc(100% - 83px);
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  overflow-x: hidden;
  overflow-y: auto;
  
  .custom-config__wrapper{
    height: 100%;
    padding: 20px;
    display: flex;
    flex-direction: column;
    overflow-y: scroll;

    .custom-config__heading{
      font-size: 14px;
      font-weight: 500;
      @include caption;
      color: #545456;
      text-transform: uppercase;
    }

    .custom-config__type{
      border-radius: 10px;
      box-shadow: 1px 2px 10px 0 rgba(63, 67, 71, 0.08);
      background-color: #ffffff;
      margin-top: 15px;

      .custom-config__type__info{
        display: flex;
        padding: 25px 20px 15px 20px;
        pointer-events: all;
        cursor: pointer;

        .custom-config__type__icon{
          margin-right: 20px;

          .icon{
            width: 20px;
            height: 20px;

            svg{
              width: inherit;
              height: inherit;

              path{
                fill: #c8c9cb;
              }
            }
          }
        }

        .custom-config__type__heading{
          font-size: 16px;
          font-weight: 500;
          color: #545456;
        }

        .custom-config__type__sub-heading{
          font-size: 12px;
          font-weight: 300;
          color: #6f6f8c;
          margin-top: 5px;
        }
      } 

      .custom-config__type__details{
        border-top: 1px solid rgba(151, 151, 151, 0.2);
        display: none;

        .custom-config__type__details__wrapper{
          display: flex;
          padding:20px 0 30px 0;

          >div{
            display: flex;
            flex-direction: column;
            &:last-child {
              margin: 0 20px 0 10px;
            }
            width: 100%;
            margin: 0 10px 0 20px;
          }

          .cdp__dropdown { bottom: -134px; }
        }

        .custom-config__type__details__heading{
          flex-grow: 1;
          font-size: 14px;
          font-weight: 500;
          @include title-2;
          line-height: normal;
          color: #545456;

          &+.custom-config__type__details__sub-heading{
            margin-top: 10px;
          }
        }

        .custom-config__type__details__sub-heading{
          font-size: 12px;
          font-weight: 300;
          color: #6f6f8c;
        }

        .custom-config__type__details__value-selector{
          font-size: 14px;
          color: #545456;
          position: relative;

          input{
            padding: 6px 0;
            text-transform: capitalize;
          }
          .arrowDown {
            position: absolute;
            right: 0;
            top: 4px;
            path {
              fill: #000;
            }
          }
        }
      }

      &.custom-config__type__selected{
        .custom-config__type__info{
          pointer-events: none;

          .custom-config__type__icon{
            svg{
              path{
                fill: #01b4de;
              }
            }
          }
        }

        .custom-config__type__details{
          display: block;
        }
      }
    }
  }

  .custom-config__action{
    margin-top: auto;

    .custom-config__action__btn{
      width: 100%;
      font-size: 16px;
      text-align: center;
      color: #ffffff;
      background-color: #01b4de;
      padding: 14px;
      border-radius: 25px;
      max-width: 315px;
      margin: auto;
      margin-top: 15px;
      &.is-web {
        margin-bottom: 50px;
      }
      &.is-ios {
        margin-bottom: 3em;
      }
    }
  }
  .comp-date-picker {
    position: relative;
    .transparent-scrim {
      position: fixed;
      width: 100%;
      height: 100%;
      top: 0vh;
      left: 0vw;
      background-color: #ffffff00;
      z-index: 104;
    }
  }
  .cdpd__mobile {
    overflow: auto;
    display: none;
    position: absolute;
    border-radius: 3px;
    z-index: 105;

    &.active {
      display: block;
    }
    .rdtOpen .rdtPicker {
      height: auto;
      border-width: 6px;
    }
    .rdtPicker {
      height: 0;
      padding: 0;
      margin-top: 0;
      display: block;
      position: relative;
      border-style: solid;
      border-color: #f2f4fa;
      border-width: 0 6px;
      border-radius: 0 0 1em 1em;
      transition: all 300ms ease-in-out;
      overflow: hidden;
      table {
        border-spacing: 0;
      }
  
      th, td { line-height: 4em; }
      .rdtDays { th, td { line-height: 2em; }}
      .rdtSwitch,
      .dow {
        font-weight: 400;
      }
      .rdtDay:hover { background: #ffffff; }
      .rdtActive, .rdtActive:hover {
        background-color: #00b6de;
        font-weight: 400;
        text-shadow: none;
        span {
          min-width: 1.9em;
          padding: 0.05em 0.5em 0;
          display: inline-block;
          border-radius: 1.8em;
          line-height: 1.8em;
          background-color: #00b6de !important;
        }
      }
      .rdtDisabled {
        // background-color: #262627 !important;
        opacity: 0.3;
        background: none;
        color: #999999;
      }
      .rdtYears, .rdtMonths {
        .rdtPrev, .rdtNext {
          width: 20%;
        }
      }
      .between {
        background-color: #f2f4fa;
      }
    }
  }

  .comp-date-picker-dropdown{
    padding: 10px 5px;
    margin-bottom: 15px;
    overflow: auto;
    display: none;
    position: absolute;
    border-radius: 3px;
    box-shadow: 0 2px 10px 0 rgba(63, 67, 71, 0.08);
    background-color: #ffffff;
    z-index: 105;
    &.active {
        display: block;
    }
    .rdt {
        .rdtPicker {
            border: none;
            padding: 0;
            .rdtMonths,
            .rdtYears {
                table:first-child {
                    margin-bottom: 15px;
                }
                td.rdtMonth,
                td.rdtYear {
                    border-radius: 4px;
                }
            }
            th {
                border: none;
                padding-bottom: 10px;
            }
            .rdtPrev,
            .rdtNext {
                font-size: 36px;
                font-weight: 300;
                padding: 0;
                & span {
                    height: 28px;
                    transform: translateY(-0.8vh);
                }
            }
            .rdtSwitch {
                font-size: 20px;
                font-weight: 500;
                line-height: normal;
                letter-spacing: 0.79px;
                padding-bottom: 0;
            }
            .dow {
                font-size: 0;
                font-weight: 500;
                padding-bottom: 16px;
                padding-top: 30px;
            }
            .dow:first-letter {
                font-size: 2vh;
            }
            .rdtMonth,
            .rdtYear {
                font-weight: 300;
            }
            .highlight {
                background-color: #eee;
            }
            .rdtActive {
                background-color: #00b6de;
                font-weight: 400;
            }
            .rdtDay {
                border-radius: 50%;
                padding: 7px 0;
                height: unset;
                font-size: 15px;
                line-height: normal;
                font-weight: 300;
                &.rdtToday {
                    &::before {
                        content: none;
                    }
                    border: 1px solid #00b6de;
                }
            }
        }
    }
    .button-container {
        display: flex;
        .button {
            margin: 0;
            margin-top: 18px;
            padding: 12px 9px;
            font-size: 17px;
            line-height: 0.88;
            letter-spacing: normal;
            cursor: pointer;
            &:first-child {
                background-color: #fff;
                color: #00b6de!important;
            }
        }
    }
    .rdt .rdtPicker .dow {
      padding-top: 20px;
    }
  }
}

.ap-cont.highlight-label .ap-component-label {
  font-size: 20px;
  font-weight: 500;
  color: #545456;
  margin: 10px 0;
}

.night {
  .custom-period-selector .custom-period-daterange {
    color: #f8f8ff;
    .icon.settings {
      g {
        fill: #f8f8ff;
      }
    }
  }
  .custom-config {
    .custom-config__wrapper {
      .custom-config__heading {
        color: #787a7b;
      }
      .custom-config__type {
        background-color: #333333;
        .custom-config__type__info {
          .custom-config__type__icon svg path {
            fill: #ffffff48;
          }
          .custom-config__type__heading {
            color: #f8f8ff;
          }
          .custom-config__type__sub-heading {
            color: #a2a6a9;
          }
        }
        &.custom-config__type__selected {
          .custom-config__type__info {
            .custom-config__type__icon svg path {
              fill: #01b4de;
            }
          }
        }
        .custom-config__type__details {
          border-top: 1px solid rgba(123, 135, 147, 0.5);
          .custom-config__type__details__heading {
            color: #f8f8ff;
          }
          .custom-config__type__details__sub-heading {
            color: #a2a6a9;
          }
          .custom-config__type__details__value-selector {
            input {
              border-color: #7b8793;
            }
            .arrowDown path {
              fill: #f8f8ff;
            }
          }
        }
      }
    }
    .rdt {
      background-color: #262627 ;
      .rdtPicker {
        border-style: none ;
        border-color: none ;
        border-width: 0 ;
        border-radius: none;
        thead tr:first-child th{
          color: #fff;
          &:hover{
            background-color: transparent;
          }
        }
        .dow{
          color: #fff;
          font-weight: 400;
          opacity: 0.7;
        }
        table,
        tr,
        td {
          border-collapse: collapse;
          border: none !important;
          thead {
            background-color: #333333;
            th {
              border: none;
            }
          }
          tbody {
            background-color: #262627 ;
            td {
              &.rdtOld,
              &.rdtNew {
                color: #636363 !important;
              }
              &.rdtToday {
                background-color: #262627 ;
                color: #fff;
                &.rdtActive{
                  background-color: #00b6de;
                }
              }
              &.rdtDay,&.rdtMonth,&.rdtYears {
                color: #fff;
                &.rdtDisabled{
                  color: #999999;
                }
              }
            }
          }
        }
      }
    }
  }
}

.browser {
  .popup-container.period_config_dialog {
    .popup-wrapper {
      width: 716px;
      height: 686px;
    }
    .popup-body {
      position: relative;
    }
    .comp-notification.comp-notification__active {
      position: fixed;
    }
  }
  .custom-config {
    height: 100%;
    background-color: #eeeeee;
    .input-scrim {
      width: 100%;
      height: calc(100% + 75px);
      transform: translateY(-75px);
      background-color: #ffffff;
      opacity: 0;
      position: absolute;
      z-index: -1;
      transition: opacity 0.2s;
      &.active {
        opacity: 0.8;
        z-index: 2;
      }
    }
    .comp-date-picker{
      .button-container .button{
        margin: 12px 0 0;
      }
    }
    .custom-config__wrapper {
      padding: 20px 32px;
      padding-bottom: 134px;
    }
    .custom-config__type {
      margin-bottom: 20px;
      margin-top: 0px;
      .custom-config__type__info {
        padding: 18px 20px;
      }
      .custom-config__type__details .custom-config__type__details__wrapper>div{
          &:last-child {
            margin: 0 52px 0 31px;
            .comp-date-picker-dropdown {
              right: -52px !important;
              left: unset !important;
            }
          }
          margin: 0 31px 0 52px;
          .comp-date-picker-dropdown {
            left: -52px !important;
            padding: 17px 24px;
          }
      }
    }
    .custom-config__heading {
      opacity: 0.5;
      font-size: 17px;
      letter-spacing: 0.5px;
      color: #7b8793;
      font-size: 18px;
      font-weight: 500;
      margin-bottom: 20px;
    }
    .custom-config__action {
      min-height: 134px;
      background-color: #fff;
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      z-index: 5;
      .custom-config__action__btn {
        &.single-btn {
          margin-top: 43px;
        }
        &.cancel {
          margin-top: 0;
          background-color: #fff;
          color: #01b4de;
          font-weight: 500;
        }
        margin-top: 24px;
        text-transform: uppercase;
      }
    }
    .bill-option-header {
      box-shadow: 0px -7px 34px 0 rgba(63, 67, 71, 0.28);
      padding: 24px;
      border-bottom: solid 1px #c6c8ca;
      text-align: center;
    }
    .radio-btn-group {
      background-color: #fff;
      text-align: center;
      padding: 24px 0 15px 0;
    }
    .comp-radio-button {
      padding: 0 32px;
      display: inline-flex;

      .selected { color: #01b4de; }
    }
    .dropdown-label,
    .comp-dropdown {
      width: 213px;
      margin: 0 auto;
    }
    .dropdown-label {
      font-size: 15px;
      font-weight: 300;
      color: #6f6f8c;
    }
    .comp-dropdown {
      .option-container {
        position: fixed;
        width: 213px;
        height: 163px;
        border-radius: 0;
      }
      .input-field {
        height: 25px;
        font-size: 15px;
        font-weight: normal;
        margin-top: 3px;
        border-bottom: solid 1px #c6c8ca;
        .arrowDown {
          margin-left: auto;
          transform: translateY(-6px);
          svg path {
            fill: #000;
          }
        }
      }
    }
  }
}



#ap-component-0{
  .ap-component-data{
    .ap-row{
      text-transform: capitalize;
    }
  }
}
