.weather {
  font-weight: 300;
  font-size: 16px;
  transition: opacity 0.5s;
  color: #6c7074;
  display: flex;
  align-items: end;
  justify-content: flex-end;

  svg {
    width: auto;
    height: 18px;
    g, path {
      fill: #6c7074;
      stroke: #6c7074;
    }
  }

  .weather-text {
    margin-left: 5px;
    @include caption
  }

  &.loading {
    opacity: 0.2;
  }
  img { 
    height: 100%;
    vertical-align: middle;
    margin-top: -0.2em;
  }
}

.night{
  .weather {
    color: #929292;
    opacity: 1;
    svg {
      g, path {
        fill: #929292;
        stroke: #929292;
      }
    }
    img { filter: invert(.5); }
  }
}

@media screen and (min-width: 768px){
  .weather {
    font-size: 20px;
  }
}
