.OM_pesSubmenu .ojasProfileContainer {
  background-color: #ffffff;

  .ProfileStatus {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: auto;
    margin-bottom: auto;
    padding: 18px 0px;
    margin: 0 20px;

    .description {
      display: flex;
      flex-direction: column;

      .descSubTitle {
        font-size: 12px;
        color: #7B8793;
        font-weight: normal;
      }

      .descSubTitleEnabled {
        font-size: 12px;
        color: #F37320;
        font-weight: normal;
      }

      .descTitle {
        color: #545456;
        font-weight: normal;
        font-size: 16px;
      }
    }

    .next-icon {
      color: #555557;
    }

    .next {
      &.ojas-next {
        width: 24px;
        height: 24px;

        svg {
          polygon {
            fill: #555557;
          }
        }
      }
    }
  }

  .lightSettingItem__wrapper {
    width: 289px;
    margin-left: auto;
    margin-right: auto;
    padding-bottom: 43px;
  }

  .main-container {
    .content-container {
      .power-mode-container {
        display: flex;
        margin: 20px 20px 0 20px;

        .img {
          &.powerOff {
            svg {
              #Rectangle_1223 {
                fill: #fff;
                stroke: #cbcbcb;
                stroke-width: 2;
              }

              #Rectangle_1226 {
                fill: #fff;
                stroke: #cbcbcb;
                stroke-width: 2;
              }

              #Rectangle_1229 {
                fill: #fff;
                stroke: #cbcbcb;
                stroke-width: 2;
              }
            }
          }
        }

        .value-container {
          display: flex;
          flex-direction: column;
          gap: 36px;
          font-size: 12px;
          color: #545456;
          margin: 7px 0 0 15px;

          .dcPort-value {
            display: flex;
            flex-direction: column;
            line-height: 1.2;

            span {
              color: #7B8793;
            }
          }

          .acPort-value {
            line-height: 1.3;

            span {
              color: #7B8793;
            }
          }
        }
      }

      .desc-container {
        font-size: 14px;
        color: #545456;
        margin: 0 20px 20px 20px;
        padding-bottom: 17px;
        font-weight: normal;
      }
    }
  }
}

.ojasPower-container {
  margin-top: 25px;
  padding-bottom: 20px;
  border-bottom: 1px solid #e9e9e9;

  .ojasPowerHeader-container {
    display: flex;
    align-items: center;

    input[type='radio'] {
      display: none;
    }

    input[type='radio']+label {
      display: inline-block;
      height: 20px;
      width: 20px;
      border: 1px solid #a3a3a3;
      border-radius: 50%;
      position: relative;
      margin-right: 20px;
    }

    input[type='radio']:checked+label {
      background-color: #01b5de;
    }

    input[type='radio']:checked+label::after {
      position: absolute;
      content: '';
      height: 9px;
      width: 5px;
      border: 2px solid #ffffff;
      left: 0;
      right: 0;
      top: 0;
      bottom: 3px;
      margin: auto;
      transform: rotate(45deg);
      border-top-color: transparent;
      border-left-color: transparent;
    }

    .ojasPowerOn-radioButton {
      width: 20px;
      height: 20px;
      border: 1px solid #a3a3a3;
      border-radius: 50%;
      margin-right: 20px;
      outline: none;
    }

    .ojasPowerOn-Title {
      font-size: 16px;
      padding-top: 5px;
      margin-top: auto;
      margin-bottom: auto;
      color: #01b5de;
      font-weight: 500;
    }

    .ojasPowerOff-Title {
      font-size: 16px;
      padding-top: 5px;
      margin-top: auto;
      margin-bottom: auto;
      color: #131415;
      font-weight: 500;
    }

    .ojasPowerOff-icon {
      width: 20px;
      height: 20px;
      margin-left: 24px;
      outline: none;
    }
  }

  .ojasPowerContent {
    margin-left: 40px;

    .ojasContent {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 287px;
      height: 132px;
      border: 8px solid #f6f6f6;
      background-color: #f6f6f6;
      border-radius: 8px;
      margin-top: 10px;

      .serv {
        justify-content: center;
      }

      .serv ul {
        display: flex;
        flex-wrap: wrap;
        padding-left: 0;
        justify-content: center;
        align-items: center;
        gap: 15px 35px;
      }

      .serv ul li {
        box-sizing: border-box;
        list-style: none;
        flex: 0 0 20%;

        .ojasAcport {
          svg {
            height: 31px;
            width: 31px;
          }
        }

        .rightTick {
          svg {
            height: 23px;
            width: 23px;
          }
        }

        .ojasDcPort {
          svg {
            height: 24px;
            width: 24px;
          }
        }

        .redCross {
          svg {
            height: 23px;
            width: 23px;
          }
        }
      }
    }

    .ojasPowerContent-container {
      margin-top: 8px;
      width: 286px;
      font-size: 12px;
      color: #939598;
    }
  }

}

.ojasPower-container-Off {
  margin-top: 25px;
  padding-bottom: 20px;

  .ojasPowerHeader-container {
    display: flex;
    align-items: center;
    gap: 20px;

    .ojasPowerOff-Title {
      font-size: 16px;
      padding-top: 5px;
      margin-top: auto;
      margin-bottom: auto;
      color: #131415;
      font-weight: 500;
    }

    .ojasPowerOff-icon {
      width: 20px;
      height: 20px;
      outline: none;
    }
  }

  .ojasPowerContent-container {
    margin-left: 40px;
    margin-top: 8px;
    width: 286px;
    font-size: 12px;
    color: #939598;
  }
}

.overlay {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 29;
}

.ojasPopupBorder {
  border-radius: 10px 10px 0px 0px;
  max-height: 80%;
  top: unset !important;
  overflow-x: hidden;
  overflow-y: auto;

  .popup__body {
    position: relative;
    z-index: 41;
  }
}

@media screen and (min-width:768px) {

  .ojasPower-container {
    .ojasPowerContent {
      .ojasContent {
        width: auto;
      }

      .ojasPowerContent-container {
        width: auto;
      }
    }
  }
}

.powerOff-Overlay {
  position: absolute;
  left: 50%;
  top: 50%;
  background-color: #000000;
  opacity: 0.8;
  height: 100%;
  width: 100%;
  transform: translate(-50%, -50%);
  z-index: 107;

  &.overlay__mobile {
    position: fixed;
  }

  .power-off {
    height: 10% !important;
    margin: 285px 0 0 0;
  }

  .close-btn {
    //margin-top: 615px;
  }

  .icon {
    &.close-btn {
      height: 6% !important;
      width: 46px !important;
      margin: 0 auto;
    }
  }

  .power-off-desc-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #FFFFFF;
    margin-bottom: 164px;

    .power-off-description {
      @include title-2;
      text-align: center;
    }

    .poweroff-countdown {
      font-size: 40px;
      margin-top: 12px;
    }
  }
}

.overlay {
  position: absolute;
  height: calc(100vh - 15%);
  width: 100%;
  top: 210px;
  left: 0;
  z-index: 29;
  opacity: 0.4;
  background-color: #ffffff;
}

.spin-container {
  position: absolute;
  width: 5em;
  height: 5em;
  background: none;
  border-radius: 50%;
  top: 36%;
  left: 50%;
  margin-top: -2.5em;
  margin-left: -2.5em;
  margin-bottom: -2.5em;
  z-index: 103;
  opacity: 1;
  transition: opacity 600ms;
}

