.pesWebCardView {
  margin-top: 11px;
  /* UI Properties */
  background: #FFFFFF 0% 0% no-repeat padding-box;
  box-shadow: 0px 2px 10px #3F434714;
  border-radius: 10px;
  margin-left: 20px;
  margin-right: 20px;
  opacity: 1;
}

.slidecontainer.webViewSlide {
