.titlebar {
  /* padding: env(safe-area-inset-top) 0.4em 1.4em 0.4em; */
  padding-top: 2.7em;
  height: $header-height-energy; 
  color: $background;
  background-color: $secondary;
  perspective: 400px;
  position: relative;
  z-index: 110;

  &.isCircuitDetails-titlebar{
    background-color: unset;
    padding-top: 0;
    height: unset;
  }
  polygon {
    fill: $background;
  }

  .period-select {
    height: 45%;
    margin: 0;
    display: flex;
    position: relative;
    z-index: 1;
    font-family: "t-star", "Visuelt", "Roboto", "Helvectica Neue", Helvetica, Arial, sans-serif;
    text-transform: uppercase;

    &.isCircuitDetails-period-select{
   background: #EDEFF4;
   color:#545456;
   height: 48px;
   padding: 5px;
 }
    .focus {
      position: absolute;
      transition: left 0.3s;
      padding: 0.5em 0.2em;
      div {
        height: 2em;
        background-color: $secondary-dark;
        border-radius: 2em;
      }
    }

    li {
      flex-grow: 1;
      text-align: center;
      font-size: 12px;
      width: 20%;
      padding-top: 1em;
      letter-spacing: 0em;
      padding-right: 8px;
      padding-left: 8px;
      white-space: nowrap;
      overflow: hidden;
      cursor: pointer;
      &.active {
        padding: 4px 7px;
        color:#fff;
        opacity: 1;
        z-index: 1;
        -webkit-transform: translate3d(0,0,1px);
        transform: translate3d(0,0,1px);
        &.isCircuitDetails-active{
             padding: 12px ;
            
            }
        &.isCircuitDetails-active-month{
          padding:12px 15px;
        }
        &.isCircuitDetails-active-day{
          padding:12px 25px 12px 15px;
        }
        &.isCircuitDetails-active-week{
          padding:12px 25px 12px 18px;
        }
        &.isCircuitDetails-active-year{
          padding:12px 25px 12px 23px;
        }
        &.isCircuitDetails-active-lifetime{
          padding:12px 25px 12px 19px;
        }
      }
    }

    .extra-small-text-title {
        font-size: 0.8em;
    }

    .small-text-title {
      font-size: 0.8em;
    }

    @media only screen and (max-width: 375px) {
      .extra-small-text-title {
          font-size: 0.6em;
      }
      .small-text-title {
        font-size: 0.9em;
      }
    }

  }

  .date-select {
    height: 50%;
    width: 100%;
    position: relative;
    transform-style: preserve-3d;
    transform-origin: 50% 60% 0;
    transition: transform 400ms;

    &.isCircuitDetails-date-select{
      background:#F9F9F9;
      color: #545456;
      height: 40px;
      &.flip{
        background:unset;
      }
    }

   

    &.flip { transform: rotateX(-180deg); }
    .select {
      justify-content: space-between;
      align-items: center;
      backface-visibility: hidden;
      position: absolute;
      display: flex;
      width: 100%;
      height: 100%;
      transform: rotateX(0deg);

      
      .isCircuitDetails-back-button{
        svg {
          height: 14px;
          width: 14px;
          polygon{
            fill:#545456;
          }
        }
      }
      .isCircuitDetails-calendar-button{
      svg{
        height: 16px;
          width: 16px;
        path{
          fill:#545456;
        }
      }
      }
      .isCircuitDetails-next-button{
        svg {
          height: 14px;
          width: 14px;
          polygon{
            fill:#545456;
          }
        }
      }
    }

    .label {
      width: 100%;
      height: 100%;
      white-space: nowrap;
      display: flex;
      align-items: center;
      .calendar {
        height: 1.2em;
        margin-right: 0.5em;
        path { fill: $background; }
      }
    }
    .front {
      .label {
        font-size: 1.6em;
        justify-content: center;
        &.isCircuitDetails-label{
          font-size: 16px;
          color:#545456;
          font-weight: 500;
          }
      }
      a {
        width: 7em;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        &:first-child { padding-right: 1em; }
        &:last-child { padding-left: 1em; }
      }
    }

    .back {
      transform: rotateX(-180deg) translateY(-1.2em);
      .label {
        font-size: 1em;
        letter-spacing: 0.03em;
        padding: 0 1.2em;
      }
      .button {
        float: right;
        text-transform: uppercase;
        font-weight: 600;
        letter-spacing: 0.06em;
        padding: 1.4em 0.7em 0 0;
        margin: 0;
        width: initial;
        background-color: inherit;
        &.isCircuitDetails-select-button{
          font-size: 14px;
        }
        &:active { 
          background-color: $transparent; 
          span { border-color: $faint; }
        }
        
        span {
          border-radius: 2em;
          border: 0.2em solid transparent;
          padding: 0.2em 1.5em;
          background-color: $secondary-dark;
        }
        &.primary span {
          background-color: $primary;
        }
      }
    }
  }

  .more {
    left: 0;
    height: 1em;
    width: 100%;
    position: absolute;
    transform: rotateX(0deg);
    text-align: center;

    &.flip {
      background-color: $secondary;
      transform: rotateX(180deg);
      hr { display: inline-block; }
    }
    
    .icon {
      display: inline-block;
      svg {
        polygon {
          fill: $background;
          &.background {
            fill: $secondary;
          }
        }
      }
    }

    hr {
      width: 44%;
      display: none;
      position: absolute;
      left: 0;
      border: none;
      opacity: 0.3;
      border-bottom: 1px solid $background;
      &:last-child {
        right: 0;
        left: initial;
      }
    }
  }
}

.selection {
  display: flex;
  height: 6.5%;
  position: relative;
  justify-content: space-between;
  align-items: flex-end;
  .label {
    top: -2.2em;
    width: 100%;
    font-size: 1.5em;
    line-height: 2.2em;
    text-align: center;
    color: $background;
    position: absolute;
    background-color: $secondary-dark; 
    transition: top 100ms;
  }
  h3 {
    font-size: 1.4em;
    font-weight: 500;
    margin: 1em  0 0 .8em;
    opacity: 1;
    transition: opacity .1s;
  }
  &.expanded {
    .label { top: 0; }
    h3 { opacity: 0; }
  }
}
.night{
  .titlebar{
    .date-select {
    
     .select{
      .isCircuitDetails-back-button{
        svg {
         
          polygon{
            fill:#FFFFFF;
          }
        }
      }
      .isCircuitDetails-calendar-button{
      svg{
       
        path{
          fill:#FFFFFF;
        }
      }
      }
      .isCircuitDetails-next-button{
        svg {
         
          polygon{
            fill:#FFFFFF;
          }
        }
      }
    }
      &.isCircuitDetails-date-select{
        background:#272728;
        color: #FFFFFF;
        
      }
      .front{
        .label{
          &.isCircuitDetails-label{
            color:#FFFFFF;
          }

        }
      }
  }

}
}
