.custom-period-selector {
  width: 100%;
  display: grid;
  grid-template-columns: auto auto auto;
  justify-content: space-between;
  align-items: center;
  padding: 13px 17px;
  span{
    display: flex;
  }

  .disbale-select {
    pointer-events: none;
  }

  .custom-period-daterange {
    font-size: 16px;
    font-weight: 500;
    color: #545456;
    @include caption;
    display: flex;
    align-items: center;

    .custom-date{
      margin-top: 4px;
    }

    .custom-period-settings {
      display: flex;
      padding-right: 6px;

      .icon.settings {
        svg{
          width: 16px;
          height: 16px;
        }
      }
    }
  }
}
@media screen and (max-width: 359px){
  .comp-calendar-weather{
    .comp-cw__wrapper{
      .custom-period-selector{
        .custom-period-daterange{
            font-size: 12px;
        }
      }
      .comp-cw__date-selector__with-touch{
        font-size: 12px;
      }
    }
  }
}
