.currency-conversion-menu {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #ffffff;
  overflow-x: hidden;
  overflow-y: auto;

  .currency-conversion-menu-list {
    padding: 18px 20px;
    .currency-conversion-menu-list-wrapper {
      border-bottom: 1px solid #f4f3f4;

      .list-view__showcurrency {
        padding-bottom: 15px;

        .list-view__wrapper {
          padding: 0px;
        }
      }
    }

    .currency-factor-menu-list-wrapper {
      border-bottom: 1px solid #f4f3f4;
      margin: 18px 0px;
      font-size: 16px;
      color: #545456;

      .list-view__currencyFactor {
        .list-view__wrapper {
          padding: 0px;

          .list-view__title {
            justify-content: space-between;
            display: flex;
            .list-view__currency {
              opacity: 0.8;
              font-size: 12px;
              font-weight: 500;
              color: #00b8e3;
              .list-view__currency-text {
                padding-right: 6px;
              }
            }
          }

          .list-view__subtitle {
            padding-bottom: 15px;
          }
        }
      }
    }

    .currency-note-menu-list-wrapper {
      font-size: 12px;
      color: #7b8793;
    }

    input, .input {
      padding: 0px;
      font-size: 14px;
      height: 25px;
      &:focus~label, &.filled~label {
        transform: translateY(-100%);
        font-size: 12px;
        font-weight: 300;
      }
    }
    .conversion-factor-wrapper {
      position: relative;
      margin-top: 21px;
    }

    .conversion-factor-header {
      position: absolute;
      left: 0;
      transition: all .2s;
      opacity: 0.6;
      padding-top: 0px;
      font-size: 14px;
      color: #939598;
      margin-top: 3px;
    }

    .conversion-factor-unit {
      display: inline-flex;
      position: absolute;
      right: 1%;
      opacity: 0.6;
      font-size: 14px;
      text-align: right;
      color: #939598;
      margin-top: 3px;

      @media (min-width:768px) and (max-width:1024px) {
        right: 0.5%;
      }

      @media (min-width:768px) and (max-width:1024px) and (orientation : landscape) {
        right: 0.5%;
      }
    }
  }
  .currency-submit-button{
    width: 88%;
    margin: 0px 20px 20px 20px;
      .save-button {
        width: 100%;
        background-color: #02b6de;
        border: none;
        height: 48px;
        display: block;
        border-radius: 25px;
        &.disabled {
          opacity: 0.3;
          background-color: #545456;
          pointer-events: none;
          cursor: default;
          display: none;
        }

      .text {
        font-size: 16px;
        color: #ffffff;
        font-weight: normal;
      }
    }
  }
}
.currency-conversion-notification {
  position: fixed;
  top: 0px;
  width: 100%;
  z-index: 10;
  .comp-notification {
    margin-top: 0;
  }
  .comp-notification__success-tick {
    background-color: #35C37D;
    .icon.defaultTick svg circle {
        fill: #35C37D;
        stroke: #35C37D;
    }
    .icon.defaultTick svg path {
        stroke: #fff;
    }
}
}
.browser .currency-conversion-notification .comp-notification {
  /* resetting back for browser */
  margin-top: 15px;
}

.night {
  .currency-conversion-menu .currency-conversion-menu-list {
    .currency-conversion-menu-list-wrapper {
      border-bottom: solid 1px rgba(123, 135, 147, 0.3);
    }

    .currency-factor-menu-list-wrapper {
      border-bottom: solid 1px rgba(123, 135, 147, 0.3);

      .icon.menuNext{
        path {
          fill:#f8f8ff;
        }
      }
    }
  }

  .currency-conversion-menu {
    background-color: #262627;
  }
}
