.single-flash{
  .led-status__indicator{
    .led-status__indicator__led{
      &:before{
        content: '';
        display: block;
        height: 100%;
        width: 100%;
        border-radius: 50%;

        animation-name: single-flash;
        animation-delay: 0s;
        animation-duration: 1s;
        animation-direction: normal;
        animation-iteration-count: infinite;
        animation-timing-function: linear; /* cubic-bezier(n,n,n,n) */
      }
    }
  }
}

.double-flash{
  .led-status__indicator{
    .led-status__indicator__led{
      &:before{
        content: '';
        display: block;
        height: 100%;
        width: 100%;
        border-radius: 50%;

        animation-name: double-flash;
        animation-delay: 0s;
        animation-duration: 1s;
        animation-direction: normal;
        animation-iteration-count: infinite;
        animation-timing-function: linear;
      }
    }
  }
}

.triple-flash{
  .led-status__indicator{
    .led-status__indicator__led{
      &:before{
        content: '';
        display: block;
        height: 100%;
        width: 100%;
        border-radius: 50%;

        animation-name: triple-flash;
        animation-delay: 0s;
        animation-duration: 1s;
        animation-direction: normal;
        animation-iteration-count: infinite;
        animation-timing-function: linear;
      }
    }
  }
}

.one-second-flash{
  .led-status__indicator{
    .led-status__indicator__led{
      &:before{
        content: '';
        display: block;
        height: 100%;
        width: 100%;
        border-radius: 50%;

        animation-name: one-second-flash;
        animation-delay: 0s;
        animation-duration: 2s;
        animation-direction: normal;
        animation-iteration-count: infinite;
        animation-timing-function: step-start;
      }
    }
  }
}

.soft-pulse{
  .led-status__indicator{
    .led-status__indicator__led{
      animation-name: soft-pulse;
      animation-delay: 0s;
      animation-duration: 4s;
      animation-direction: normal;
      animation-iteration-count: infinite;
      animation-timing-function: linear;
    }
  }
}



.night{
  .single-flash{
    .led-status__indicator{
      .led-status__indicator__led{
        &:before{
          animation-name: single-flash-night;
        }
      }
    }
  }

  .double-flash{
    .led-status__indicator{
      .led-status__indicator__led{
        &:before{
          animation-name: double-flash-night;
        }
      }
    }
  }

  .triple-flash{
    .led-status__indicator{
      .led-status__indicator__led{
        &:before{
          animation-name: triple-flash-night;
        }
      }
    }
  }

  .one-second-flash{
    .led-status__indicator{
      .led-status__indicator__led{
        &:before{
          animation-name: one-second-flash-night;
        }
      }
    }
  }
}



@keyframes single-flash {
  /* 0 - 15 :: ON */
  0%{
    /* ON */
    background-color: inherit;
  }
  15%{
    /* OFF */
    background-color: $led-bg-color;
  }
}

@keyframes double-flash {
  /* 0 - 15, 30 - 45 :: ON */
  0%{
    /* ON */
    background-color: inherit;
  }
  15%{
    /* OFF */
    background-color: $led-bg-color;
  }
  30%{
    /* ON */
    background-color: inherit;
  }
  45%{
    /* OFF */
    background-color: $led-bg-color;
  }
}

@keyframes triple-flash {
  0%{
    /* ON */
    background-color: inherit;
  }
  15%{
    /* OFF */
    background-color: $led-bg-color;
  }
  30%{
    /* ON */
    background-color: inherit;
  }
  45%{
    /* OFF */
    background-color: $led-bg-color;
  }
  60%{
    /* ON */
    background-color: inherit;
  }
  75%{
    /* OFF */
    background-color: $led-bg-color;
  }
}

@keyframes one-second-flash {
  0% {
    /* ON */
    background-color: inherit;
  }
  50% {
    /* OFF */
    background-color: $led-bg-color;
  }
}

@keyframes soft-pulse {
  0% {
    width: 0;
    height: 0;
    opacity: 0;
  }
  50% {
    width: 10px;
    height: 10px;
    opacity: 1;
  }
  100% {
    width: 0;
    height: 0;
    opacity: 0;
  }
}



