@import '../../styles/font-style.scss';

.OL_Pes_SettingsMenu {
    .OL_Pes_SettingsMenu_SectionHeader {
        padding: 20px;
        font-size: 14px;
        text-transform: uppercase;
        color: #545456;
        opacity: 0.5;
        @include caption;
    }

    .overlay {
        height: 100%;
        top: 0px !important;
    }

    .OM_pesSubmenu {
        .overlay {
            top: 0px !important;
        }
    }
}

.OL_Pes_wrapper {
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    background-color: #fdfefe;

    :first-child {
        border-top: none !important;
    }

    .OL_Pes_listTile {
        padding: 18px 0px;
        margin: 0 20px;
        min-height: 78px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-top: 0.5px solid #7B87934D;

        .OL_Pes_listTile_content {
            display: flex;
            align-items: normal;

            .OL_Pes_listTile_content_listIcon {
                width: 24px;
                height: 24px;
                margin-right: 15px;
            }

            .OL_Pes_listTile_content_listText {
                .OL_Pes_listTile_content_listText_title {
                    color: #545456;
                    text-transform: capitalize;
                    word-break: break-word;
                    font-family: "Visuelt", "Roboto", "Helvectica Neue", Helvetica, Arial, sans-serif;
                    font-size: 16px;
                    font-weight: 400;
                    line-height: 24px;
                }

                .OL_Pes_listTile_content_listText_subText {
                    color: #7b8793;
                    margin-top: 2px;
                    font-family: "Visuelt", "Roboto", "Helvectica Neue", Helvetica, Arial, sans-serif;
                    font-size: 12px;
                    line-height: 16px;
                    font-weight: 400;
                }
            }
        }

        .OL_Pes_listTile_action {
            .OM_listTile_content_listIcon {
                height: 12px;

                .icon {
                    height: 100%;
                }
            }
        }
    }
}

.night {
    .OL_Pes_SettingsMenu {
        .OM_pesSubmenu {
            .contentArea {
                .componentBody {
                    background-color: #333333 !important;
                }
            }
        }

        .overlay {
            background: none;
        }
    }

    .OL_Pes_SettingsMenu_SectionHeader {
        color: #7b8793;
        opacity: 1;
        background-color: #2c2c2c;
    }

    .OL_Pes_wrapper {
        opacity: .99;
        background-color: #262627 !important;
        color: #fff;

        .OL_Pes_listTile {
            .OL_Pes_listTile_content {
                .OL_Pes_listTile_content_listText {
                    .OL_Pes_listTile_content_listText_title {
                        opacity: .99;
                        color: #f8f8ff !important;
                    }
                }

                .OL_Pes_listTile_content_listIcon {
                    .icon svg path {
                        fill: #f8f8ff;
                    }
                }
            }

            .OL_Pes_listTile_action {
                .OL_Pes_listTile_content_listIcon {
                    .icon .menuNext svg path {
                        fill: #f8f8ff !important;
                    }
                }
            }
        }
    }
}

#offline-mode {
    .OL_Pes_SettingsMenu {
        .OM_pesSubmenu {
            .overlay {
                z-index: 10 !important;
            }

            .spin-container {
                background: none;
            }
        }
    }
}
