.live-vitals-page{
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;

  &.lvp__web{
    width: 60%;
    height: 100%;
    overflow: auto;
    margin: 30px auto;
    border: 1px solid #ffffff;
    background-color: #ffffff;
    box-shadow: 0 2px 10px 0 rgba(63,67,71,.08);
  }


  .lvp__wrapper{
    height: inherit;
    padding: 0 20px 20px;
    background: #f5f5f5;
  }

  .lvp__no-live-vitals{
    height: 100%;
    display: flex;
    flex-flow: column;
    justify-content: center;
    font-size: 14px;
    color: #808080;
    text-align: center;
  }

  .lvp__try-connecting-envoy{
    height: inherit;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 400;

    .lvp__try-connecting-envoy__contents{
      text-align: center;

    }

    .lvp__try-connecting-envoy__refresh{
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }

  .lvp__contents{
  }

  .lvp__table{
    table-layout: fixed;

    tr{
      .lvp__item__parameter{
        width: 60%;
      }

      .lvp__item__icon{
        width: 12px;
      }
    }

    .lvp__table__header{
      border-top: none;
    }

    tbody{
      .lvp__item__parameter{
        font-weight: 400;
        text-transform: capitalize;
      }
      
      .lvp__item__state{
        >div{
          display: flex;

          span{
            line-height: normal;
            
            &:first-child{
              line-height: 1;
            }
          }
        }
      }
    }

    .enho__table__item{
      .lvp__item__icon{
        width: 12px;
        height: auto;

        .icon{
          transition: all 1s ease;
        }

        .icon, svg{
          width: inherit;
          height: inherit;

          path{
            fill: #919ca9;
          }
        }
      }
    }
    
    .enho__accordion__active{
      td{
        font-weight: 500;
      }

      .lvp__item__icon{
        .icon{
          transform: rotate(180deg);

          path{
            fill: #545456;
          }
        }
      }
    }

    .enho__accordion__contents{
      border-top: none;

      .enho__table__item{
        padding: 0 0 10px;
      }
    }

    .lvp__table__details{
      border-collapse: collapse;
    }
  }

  .lvp__card {
    padding: 20px;
    margin: 0 -20px 10px;
    box-shadow: 0 2px 10px 0 rgba(63, 67, 71, 0.08);
    background-color: #ffffff;

    &:last-child { margin-bottom: 0; }

    h4.lvp__card__title {
      display: flex;
      align-items: center;
      font-size: 16px;
      line-height: 19px;
      font-weight: 500;
      color: #545456;
      text-transform: capitalize;

      span { text-transform: lowercase; }

      &.lvp__card__site { margin-top: 20px; }

      .icon {
        margin-left: 3px;
        margin-bottom: 2px;
        display: flex;
        align-items: center;
      }
    }

    p.lvp__card__subtitle {
      margin: 2px 0 0;
      font-size: 12px;
      line-height: 14px;
      font-weight: 300;
      color: #7b8793;
      @include body-1;
      
      &.lvp__card__site {
        font-size: 12px;
        line-height: 16px;
        font-weight: normal;
        color: #545456;
      }
    }

    .lvp__card__header {
      display: flex;
      justify-content: space-between;
      align-items: flex-start;

      .lvp__card__header-details {
        display: flex;
        align-items: flex-end;

        & > div {
          display: flex;
          align-items: flex-end;

          &:last-child {
            margin-left: 7px;

            p{
              min-width: 38px;
            }
          }

          .icon {
            height: auto;
            display: flex;
            align-self: unset;
            margin-right: 5px;
            align-items: flex-end;

            &.energyGridConnected,
            &.energyGridDisconnected,
            &.energyGridUnknown { width: 35px; margin-right: 0; }
            &.communicating { width: 25px; }
            &.notCommunicating { width: 27px; }
            &.ensembleBatteryCharge,
            &.batteryDataCharging { width: 16px; }
            &.genConnected,
            &.genNotConnected { width: 35px; }
            &.infoIcon { margin-right: 0; }

            svg {
              width: 100%;
              height: auto;
            }
          }

          p {
            margin: 0;
            font-size: 14px;
            line-height: 1;
            font-weight: 500;
            color: #545456;
            @include body-2;

            span {
              display: block;
              font-size: 12px;
              line-height: 14px;
              color: #a2abb3;
              white-space: nowrap;
              @include body-2;

              & > span:before {
                content: '';
                width: 10px;
                height: 10px;
                display: inline-flex;
                margin-right: 5px;
                border-radius: 50%;
              }
              & > .comm-ok:before {background-color: #69dba5; }
              & > .no-comm:before { background-color: #ff5959; }
            }
          }

          button {
            width: 14px;
            height: 14px;
            margin-left: 3px;
            padding: 0;
            border: unset;
            display: inline-flex;
            justify-content: center;
            align-items: center;
            background: transparent;

            .icon {
              width: 10px;
              height: 10px;
      
              svg {
                width: auto;
                height: 100%;
              }
            }
          }
        }
      }
    }
    
    .lvp__card__item {
      margin-top: 5px;

      .lvp__card__item-block {
        &.ojasBlockView{
          ul > li { text-align: center;}
        }
        &.builtInBattery{
          li {
            p{ color: #3F4347;}
            .title{
              color: #7A8793;
              font-weight: 500;
            }
            .temperature{
              &::after{ content: "\2103"; }
            }
          }
          .built-in-battery-header{
            color: #7A8793;
            font-weight: 500;
            p { width: 100%;}
          }
        }
        margin-top: 10px;

        .lvp__card__item-heading {
          padding: 4px;
          display: flex;
          justify-content: center;
          align-items: center;
          border: solid 1px #d8dcde;
          font-size: 12px;
          font-weight: bold;
          color: #7b8793;
          background-color: rgba(226, 231, 233, 0.5);

          .icon {
            height: 14px;
            margin-left: 5px;

            svg {
              width: auto;
              height: 100%;
            }
          }
        }

        ul {
          display: flex;
          li {
            width: 33.33%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            padding: 15px 10px;
            border: 1px solid #d8dcde;
            font-size: 14px;
            font-weight: normal;
            line-height: 16px;
            letter-spacing: -0.5px;
            color: #7b8793;
            text-align: center;

            p {
              margin: 5px 0 0;
              color: #3f4347;
            }
          }
          &.two-block-column {
            &.ojasRelaylist{ 
              .relay-details:nth-child(odd) { border-right: 1px solid #d8dcde; }
              .relay-details {
                width: 100%;
                display: flex;
                justify-content: flex-end;
                
                p{
                  text-align: center;
                  font-weight: 500;
                }
              }
            }
          }
          &.two-block-column li { 
            width: 50%;
            text-align: center; 
          }
          &.four-block-column li {
            width: 25%;
            min-width: 25%;
            padding: 15px 0;
            text-align: center;

            .device-name{
              color: #3F4347;
              margin: 0;
              font-size: 14px;
              word-break: break-word;
            }
            .device-status{
              text-transform: capitalize;
            }
          }
          
          &.block-column {
            display: grid;
            grid-template-columns: 20% 20% 20% 20% 20%;

            li {
              width: 100%;
              text-align: center;
            }
          }
        }
        &.load-controls{
          ul li {
            padding: 2px 0 3px 0;

            p { color: #545456; }
            .label { color: #7A8793 }
            .value-with-colon {
              &::before{
                content: ':';
              }
            }
          }
        }
        &.builtInBattery {
          ul li { padding: 5px 0px 5px 0px;}
          ul {
            li{
              border-bottom: 1px solid #f0f0f0;
              
              &:first-child, &:last-child{
                border: none;
              }

              .label{
                color: #7A8793;
                font-weight: 500;
              }
            }
          }
        }
        &.IQ8D_MicroInverters,
        &.powerIn,
        &.powerOut {
          ul li { 
            border-top: 1px solid #f0f0f0;
            padding: 10px;

            .label{
              color: #7A8793;
              width: 100%;

              ul {
                margin: 0;

                li {
                  padding: 0;
                  margin-top: 3px;
                  border-top: none;
                }
              }

              span{
                color: #3F4347;

                &::before{
                  content: ':';
                  margin-right: 9px;
                  }
              }
            }

            .icon {
              height: 100%;

              &.arrowDown,
              &.arrowUp {
                width: 18px;
                height: 18px;
                padding: 0;
                display: flex;
                align-self: unset;
                justify-content: center;
                align-items: center;

                svg {
                  width: 7px;
                  height: auto;
                }
              }
            }
            &:first-child { border: none; }
          }
        }
        &.voltage-frequency {
          .four-block-column{
            li { padding: 0; }
          }
        }
      }

      ul.lvp__card__item-list {
        li {
          margin-top: 5px;
          font-size: 14px;
          font-weight: normal;
          line-height: 16px;
          color: #7b8793;
          @include body-1;

          &:first-child { margin-top: 0; }

          & > span {
            display: inline-flex;
            color: #3f4347;

            &.with-key::before {
              content: ': ';
              display: block;
              padding-right: 3px;
            }
          }
        }

        &.list-column {
          li {
            display: flex;
            justify-content: flex-start;

            span.with-key { white-space: normal; }
          }
        }

        .led-status {
          display: inline-flex;
          justify-content: flex-start;

          &::before {
            content: ': ';
            display: flex;
            align-items: center;
            padding-right: 3px;
          }

          .led-status__indicator {
            margin-right: 3px;

            .led-status__indicator__led {
              width: 10px;
              height: 10px;
            }
          }

          .led-status__info {
            font-weight: normal;
            color: #3f4347;
            white-space: normal;
          }
        }
        .enpower-led-block{
            display: flex;
            border-radius: 2px;
            margin-right: 5px;
            padding: 0px 2px;

            &::before {
              content: ': ';
              display: flex;
              align-items: center;
              padding-right: 3px;
            }
            
            .led-wrapper{
              border: 1px solid #D8DCDE;
              border-radius: 5px;
              display: flex;
              align-items: center;
              margin-right: 7px;
            }
          }
      }

      .lvp__card__item-desc {
        margin: 5px 0 0;
        font-size: 12px;
        font-weight: normal;
        line-height: 1.25;
        color: #7b8793;
      }

      .lvp__card__accordion {
        display: flex;
        
        & > li {
          width: 25%;
          border: solid 1px #d8dcde;

          & > p {
            margin: 0;
            padding: 14px 0;
            font-size: 14px;
            font-weight: normal;
            line-height: 1.43;
            color: #a2abb3;
            text-align: center;
            position: relative;

            & span {
              display: block;
              color: #3f4347;
              font-weight: normal;
            }

            & span.arrow-down {
              width: 0; 
              height: 0;
              margin-left: -5px;
              border-left: 5px solid transparent;
              border-right: 5px solid transparent;
              border-top: 7px solid #01b5de;
              position: absolute;
              bottom: -9px;
              left: 50%;
            }

            &.active-accordion {
              border: solid 2px #01b5de;
              margin: -1px;
            }
          }
        }
      }

      .lvp__card__accordion--panel {
        display: none;
        border: solid 1px #d8dcde;
        padding: 15px 20px;

        &.active-panel {
          display: block;
        }

        ul.lvp__card__item-list li { line-height: 1.43; }
      }

      .lvp__card__item-button {
        margin-top: 10px;
        padding: 0;
        border: unset;
        font-size: 12px;
        font-weight: 400;
        line-height: 16px;
        color: #01b5de;
        background: unset;
        cursor: pointer;
      }

      .lvp__card__item-error {
        color: #e3655e !important;
        align-items: center;

        .icon {
          height: 14px;
          margin-left: 5px;

          svg {
            width: auto;
            height: 100%;

            g path:first-child { fill: #e3655e; }
          }
        }
      }
    }

    &.lvp__card__encharge {
      .lvp__card__header {

        .lvp__card__header-title{
          .lvp__card__subtitle{
            min-width: 120px;
          }
        }

        .lvp__card__header-details {
          > div {
            .ensembleBatteryCharge { width: 12px; }
          }
        }
      }
    }
  }
}

.lvs__info-popup {
  max-height: 80%;
  top: unset !important;
  overflow-x: hidden;
  overflow-y: auto;

  .popup__header {
    background-color: #ffffff;
    position: sticky;
    top: -1px;
  }

  .popup__body-content {
    padding: 15px 20px !important;

    & > p {
      margin: 0;
      padding-bottom: 15px;
      border-bottom: 1px solid rgba(123, 135, 147, 0.3);
      font-size: 12px;
      font-weight: normal;
      line-height: 16px;
      letter-spacing: normal;
      color: #545456;
    }

    ul {

      li {
        padding: 15px 0;
        border-top: 1px solid rgba(123, 135, 147, 0.3);
        font-size: 14px;
        font-weight: normal;
        line-height: 1.29;
        color: #545456;
        @include body-1;

        &:first-child { padding-top: 10px; border-top: unset; }

        h4 {
          display: flex;
          align-items: center;
          margin: 0 0 5px;
          font-size: 14px;
          font-weight: bold;
          line-height: 1.29;
          color: #545456;
          @include title-1;

          &.communication-status{
            align-items: center;
            margin: 0px;
          }
          .icon {
            width: 25px;
            height: auto;
            margin-left: 5px;

            svg {
              width: 100%;
              height: auto;
            }
          }
        }
      }
    }

    .lvs__info-popup-diagram {
      padding-bottom: 20px;
      border-bottom: 1px solid solid 1px rgba(123, 135, 147, 0.3);

      img {
        width: 100%;
        max-width: 400px;
        height: auto;
        display: block;
        margin: 0 auto 10px;
      }

      p {
        margin: 0;
        font-size: 10px;
        font-weight: normal;
        color: #7b8793;
        text-align: center;
      }
    }

    .lvs__info-popup-subtitle {
      padding: 10px 20px;
      margin: -1px -20px 0;
      font-size: 15px;
      font-weight: bold;
      line-height: 1.2;
      color: #7b8793;
      text-transform: uppercase;
      background-color: #f0f3f4;
      position: sticky;
      top: 48px;
      @include title-2;
    }
    .windows_style{
      top: 44px;
    }
    .android_style {
      top: 44px;
    }
  }
}

