                  }
                }
              }
            }
          }
        }
        ul.lvp__card__accordion > li,
        .lvp__card__accordion--panel { border-color: rgba(123,135,147,.3); }
        ul.lvp__card__item-list li,
        ul.lvp__card__accordion > li > p {
          color: #a2a6a9;
        }

        ul.lvp__card__item-list li > span,
        ul.lvp__card__item-list li .led-status .led-status__info,
        .lvp__card__item-block ul li p,
        ul.lvp__card__accordion > li > p span { color: #d8dcde; }
      }
    }

    .lvp__table{
      .enho__accordion__active{
        .lvp__item__icon{
          .icon{
            path{
              fill:#f8f8ff;
            }
          }
        }
      }
    }
  }

  .lvs__info-popup {
    .popup__header { background-color: #262627; }

    .popup__body-content {
      & > p,
      ul li { color: #d8dcde; }
      ul li h4 { color: #a2a6a9; }

      .lvs__info-popup-subtitle {
        color: #a2a6a9;
        background-color: #333;
      }
    }
  }
  .lvp__card__header-details{
    div{
      .genNotConnected, .genConnected{
        svg{
          g{
            #Subtraction_17{
              fill: #f8f8ff;
            }
          }
        }
      }
      .energyGridConnected, .energyGridDisconnected, .energyGridUnknown{
        svg{
          g{
            path:nth-child(1){
              fill: #f8f8ff;
            }
          }
        }
      }
    }
  }
}

