.scrim {
  position: fixed;
  width: 100vw;
  height: 100%;
  top: 0;
  z-index: 100;
  -webkit-backdrop-filter: blur(2px);
  backdrop-filter: blur(2px);
  background-color: rgba(0, 0, 0, 0.75);
}

.scrim-pop-up {
  position: fixed;
  width: 100vw;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 199;
}


.header-support {
  &+.titlebar-wrapper{
    padding-top: 0;
  }
  
  .comp-notification.comp-notification__failure,
  .comp-notification.comp-notification__demo-user {
    background-color: #545456;
    margin-top: 80px;

    @media screen and (min-width:768px) and (max-width:1024px) {
      margin-top: 95px;
    }
  }

  .header-support-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 12px 15px;
    margin: 0 auto 0;

    .brandIcon {
      min-width: 99px;
      width: 99px;
      height: 18px;
      display: flex;
      path {
        fill: #fff;
      }

      .icon {
        width: inherit;
        height: inherit;

        svg {
          width: inherit;
          height: inherit;
        }
      }

      @media screen and (min-width:768px) and (max-width:1024px) {
        min-width: 162px;
        width: 162px;
        height: 30px;
      }
    }
    .header-share-support{
      display: inline-flex;
      align-items: center;
      .share-icons{
        width: 25px;
        height: 22px;
        margin-right: 25px;
        text-align: right;

        button {
          padding: 0;
          background: none;
          border: none;
        }
        span{
          .icon{
            height: 100%;
          }
        }
      }

      .close-enphase-icon {
        position: absolute;
        right: 1.625em;

        .icon.close {
          width: 18px;
          height: 22px;

          svg {
            width: 18px;
            height: 18px;
            margin-top: 2px;
          }
          
          polygon {
            fill: #ffffff;
          }
        }
      }

      .support-enphase-icon {
        z-index: 100;
        min-width: 22px;
        width: 22px;
        height: 22px;

        &.with-close { margin-right: 50px; }

        @media screen and (min-width:768px) and (max-width:1024px) {
          min-width: 26px;
          width: 26px;
          height: 26px;
          &.with-close {
            margin-right: 85px;
          }
        }

        .icon {
          width: inherit;
          height: inherit;

          svg {
            width: inherit;
            height: inherit;
          }
        }
      }
    }
  }
  .switch-to-classic {
    &.tablet {
      margin-right: 25px;
      font-size: 17px;
      font-weight: 500;
      color: #ffffff;
    }
    &.mobile {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 50px;
      font-size: 12px;
      font-weight: 500;
      color: #01b5de;
      background-color: #f9f9f9;
    }
  }
}

.header-title-bar-status {
  background-color: #00b6de;

  .comp-notification.comp-notification__failure,
  .comp-notification.comp-notification__demo-user {
    background-color: #545456;
    margin-top: 46px;
  }
}

.connect-info-bar {
  background-color: #3E3E3E;
  padding: 10px 20px;
  display: flex;
  align-items: center;
  .info {
    margin-left: 10px;
    color: #FFFFFF;
    font-size: 12px;
    letter-spacing: 0;
    line-height: 16px;
  }
}
.status-bar {
  padding-top: 2.3em;
  background-color: #00b6de;
}

.share-screen {
  .site-details {
    opacity: none !important;
    background-color: transparent !important;
    bottom: 2% !important;
  }
  .graph-contols, .energy-stats__wrapper, .carousel__container {
    zoom: 1 !important;
  }
}

.append-header {
  min-height: 50px;
  background-color: #ffffff;
  border-bottom: solid 1px rgba(123, 135, 147, 0.3);
  display: flex;
  align-items: center;
  justify-content: center;
}

.toggle-swtich__button-checked{
  .toggle-switch__handler{
    .react-switch-handle{
      transform: unset !important;
      right:0 !important;
    }
  }
}

.night {
  .status-bar {
    background-color: #333333;
  }

  .header-title-bar-status {
    background-color: #333333;
  }
  #capture.share-screen,
  #energy-stats-and-graph.share-screen,
  .append-header,
  .latest-status-page,
  .energy-stats-and-graph {
    background-color: #262627;
  }
}

@media screen and (max-width:320px) {
  .share-screen {
    .site-details {
      bottom: 0% !important;
    }
  }
}

/* iphone x  and xs*/ 
@media only screen and (device-width : 375px) and (device-height : 812px) {
  .share-screen {
    .site-details {
      bottom: 6% !important;
    }
  }
}

/* iphone xs-max*/ 
@media only screen and (device-width : 414px) and (device-height : 896px) {
  .share-screen {
    .site-details {
      bottom: 6% !important;
    }
  }
}