#login {
  height: 100%;
  display: flex;
  background-color: #F6F7FB;

  .login-wrapper {
    width: 100%;
    height: 100%;
    margin: 0 auto;
    display: flex;
    flex-flow: column;
    justify-content: center;
    position: relative;
    overflow: hidden;

    .login-form {
      width: 100%;
      height: 100%;
      padding: 40px 20px 30px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      position: relative;
    }

    .login__logo-menus {
      width: 100%;
      max-width: 450px;
      display: flex;
      justify-content: space-between;
      align-content: center;
      margin: 0 auto 35px;

      .icon.loginLogo  {
        width: 134px;
        height: auto;
  
        .logo {
          width: 100%;
          height: auto;
          display: block;
        }
      }
  
      .pre-login__top-menus {
        display: none;

        li {
          margin: 0 10px;
          color: #FFFFFF;

          &:first-child { margin-left: 0; }

          &:last-child { margin-right: 0; }

          .icon {
            height: 30px;

            svg {
              width: auto;
              height: 100%;
            }
          }
        }
      }
    }

    &.left-aligned .icon {
      margin-left: auto;
      margin-right: auto;
    }

    & h4 {
      width: 100%;
      max-width: 450px;
      margin: 0 auto 24px;
      font-size: 20px;
      font-weight: 500;
      color: #545456;
    }

    .login {
      width: 100%;
      max-width: 450px;
      height: 0;
      margin: 0 auto;
      border-radius: 10px;
      box-shadow: 1px 2px 5px #9898981A;
      font-size: 14px;
      background-color: #ffffff;
      transition: height 600ms;
      overflow: hidden;

      &.show {
        height: auto;
        padding: 38px 30px;
        transition: height 1s;
      }

      .item {
        min-height: 60px;
        position: relative;

        input {
          padding-right: 2em;
          margin-bottom: 5px;
          font-size: 14px;
          font-weight: normal;
          @include title-1;
          line-height: 24px;
          padding-bottom: 2px;
          vertical-align: middle;

          &::placeholder {
            font-weight: 300;
            color: #939598;
          }
        }

        &.error {
          input { border-bottom: 1px solid #ff5959; }
          label { 
            font-size: 12px;
            height: 15px;
           }
        }
  
        .icon.password,
        .icon.email {
          height: 1em;
          margin: .5em 0;
          position: absolute;
        }
        .icon.email {
          margin-top: calc( 0.5em + 5px );
        }
        
        .icon.password { 
          margin-left: 0.1em; 
          margin-top: 8px;
          margin-bottom: auto;
          svg{ 
            margin-top: auto;
          }
        }
        .icon.email svg { height: 100% }

        label {
          height: 0;
          display: inline-block;
          font-size: 13px;
          color: #ff5959;
          overflow: hidden;
          transition: height 300ms;
        }
        & .toggle-password {
          .icon.hidePassword{
            margin-top: 5px;
            margin-bottom: auto;
          }
          .icon.showPassword{
            margin-top: 5px;
          }
        }
      }

      &.invalid-input input { border-bottom: 1px solid #ff5959; }

      .toggle-password {
        position: absolute;
        right: 0;
        top: 0;
        cursor: pointer;
      }

      &:not(.show) .toggle-password { position: relative; }

      .message {
        margin: -17px 0 0 0;
        font-size: 12px;
        color: #ff5959;
      }
      .message-underline{
        color: #01b4de;
        text-decoration: underline;
      }
  
      .button {
        width: 100%;
        margin: 30px auto 20px;
        display: block;
        border-radius: 2em;
        font-size: 16px;
        text-align: center;
        background-color: $secondary;

        &.loading{ pointer-events: none; }
      }
  
      a {
        font-size: 12px;
        color: $medium;
        text-decoration: none;

        & + a {
          padding-left: 1em;
          margin-left: 1em;
          border-left: 1px solid $light;
        }
      }
  
      .text {
        height: inherit;
        padding-left: 10px;
        font-size: 14px;
        color: #545456;
        text-decoration: none;
        vertical-align: middle;
      }
  
      .rememberme {
        padding-left: 4px;
        margin-top: 10px;
        display: flex;
        text-align: left;
        align-items: center;
        .text{
          font-size: 16px;
        }
      }
      
      .forgotPwd { text-align: center; }
  
      .userLinks { cursor: pointer; }
  
      .userLinksDisabled {
        cursor: not-allowed;
        pointer-events: none;
      }
    }

    .login-header-button-wrapper {
      display: table;
      margin: 24px auto 0;

      .language-selector {
        display: none;
        font-size: 16px;
        position: relative;
    
        &.active {
          display: flex;
          align-items: center;
          justify-content: center;
        }
    
        .language-icon {
          margin: 0 5px 0 10px;
    
          .icon { height: 16px; }
        }
    
        .select-language {
          padding: 0;
          position: absolute;
          right: 0;
          opacity: 0;
        }
        
        .select-language-label { 
          color: #01b4de; 
          height: 16px;
        }
    
        .arrow-icon {
          margin: 0 10px 0 5px;
    
          .icon { height: auto; }
        }
      }
    }

    .pre-login__options {
      width: 100%;
      max-width: 490px;
      margin: 0 auto;
      padding: 20px;
      display: none;
      .newToEnphase{
        display: flex;
        margin-bottom: 16px;
        align-items: center;
        background: #ffffff;
        border-radius: 10px;
        padding: 15px;
        p {
          padding-left: 8px;
        }
      }
    }
  }

  &.add-login-menus {
    .login-wrapper {
      height: auto;
      flex-flow: wrap;
      justify-content: flex-start;
      overflow: auto;
      
      &::before {
        width: 100%;
        height: 25%;
        content: '';
        position: absolute;
        background: $secondary;
        top: 0;
        left: 0; 
      }

      .login-form {
        height: auto;
        background-color: $secondary;
      }

      .icon.loginLogo .logo path { fill: #ffffff; }

      .pre-login__top-menus { display: flex; }

      .pre-login__options { display: block; }
    }
  }

  &.add-app-demo {
    #appDemo-wrapper { display: flex; }

    .login-wrapper {
      width: 50%;

      .icon.loginLogo { display: none; }
    }
  }

  & .spin-container {
    width: 100%;
    height: 100%;
    margin: 0;
    border-radius: 0;
    background-color: #F6F7FB;
    left: 0;
    top: 0;
  }

  .pre-login__menu-comp {
    width: 100%;
    height: 100%;
    background-color: #F6F7FB;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 11;

    .pre-login__menu-comp-header {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      padding: 43px 20px 15px;
      background-color: $secondary;

      & button {
          width: 22px;
          height: 20px;
          padding: 0;
          margin: 1px 35px 1px 0;
          border: unset;
          background: unset;
          outline: unset;

          & span.icon {
              height: 20px;

              & svg {
                  height: 20px;
                  margin: 0;
                  fill: #ffffff;
              }
          }
      }

      & p {
          margin: 0;
          font-size: 19px;
          font-weight: normal;
          line-height: 22px;
          letter-spacing: normal;
          color: #ffffff;
      }
    }

    .pre-login__menu-comp-content {
      width: 100%;
      height: calc(100% - 80px);
      overflow: auto;
    }
  }

  .pre-login__loader {
    width: 100%;
    height: 100%;
    position: relative;

    .loginLogo {
      width: 150px;
      height: auto;
      margin: 0 0 0 -75px;
      position: absolute;
      top: 125px;
      left: 50%;
      z-index: 104;

      svg {
        width: 100%;
        height: auto;
      }
    }
  }

  .pre-login__demo-video {
    height: 100% !important;
    background-color: transparent !important;

    .spin-container {
      background-color: unset !important;
    }
  }

  &.pre-login__web {
    .login-wrapper {
      .login-form {
        .login__logo-menus { justify-content: center; }

        background-color: unset;
      }
    }
  }

  @media screen and (max-width: 768px) {
    &.add-app-demo {
      height: auto;
      flex-flow: wrap;
      overflow: auto;

      #appDemo-wrapper {
        width: 100%;
        height: auto;
      }

      .login-wrapper {
        width: 100%;
        height: auto;

        .login.show { padding: 24px 20px; }
      }
    }
  }

  @media screen and (min-width: 768px) {
    &.add-login-menus {
      .login-wrapper {
        flex-flow: column;
        justify-content: center;
      }
    }
  }
}

#content > .comp-notification {
  top: 0;
  z-index: 10000;
  width: 100vw;
  font-size: 12px;
}

.pre-login__card-menus {
  width: calc(100% - 15px);
  display: grid;
  grid-template-columns: 50% 50%;
  grid-gap: 15px;

  .pre-login__menu-card {
    padding: 24px 14px 14px;
    box-shadow: 0px 2px 10px #3F434714;
    border-radius: 10px;
    text-align: center;
    background: #FFFFFF;
    cursor: pointer;

    .icon {
      height: 50px;
      align-self: start;
      text-align: left;
      display: inline-flex;

      &.batteryAccount{
        rect, path:last-child{
          fill: #7acf38 !important;
        }
      }

      svg {
        width: auto;
        height: 100%;
      }
    }

    p {
      margin: 10px 0 0;
      font-size: 14px;
      font-weight: 500;
      line-height: 19px;
      color: #545456;
      @include title-1;
    }
  }

  &.support__options {
    width: calc(100% - 20px);
    padding: 20px;
    grid-template-columns: 33.33% 33.33% 33.33%;
    grid-gap: 10px;

    .pre-login__menu-card {
      padding: 17px 9px 6px;
      text-align: left;

      .icon {
        height: 20px;

        > svg { margin: 0; }

        &.enphaseLogo svg > path,
        &.layout svg > rect,
        &.batteryAccount svg > g > rect,
        &.batteryAccount svg > g > path:last-child { fill: $secondary; }
      }

      p {
        max-width: 80px;
        margin-top: 5px;
        color: #707070;
        word-break: break-word;
      }
    }
  }
}
