
.submenu {
    width: 100%;
    left: 110%;
    top: 0;
    bottom: 0;
    position: fixed;
    border-radius: $app-border-radius-top;
    overflow: hidden;
    background-color: $background;
    z-index: 100;
    transition: left 300ms ease-in-out;
    display: flex;
    flex-direction: column;
    &.generator {
      .header {
        padding: 77px 15px 20px 15px;
      }
    }

    .pageContent{
      overflow: scroll;
      flex-grow: 1;
    }
    
    &.open { left: 0; }
  
    .menu-name__searchInput {
      width: 100%;
      .searchInput {
        position: relative;
        margin: auto;
        .icon.search {
          position: absolute;
          cursor: pointer;
          right: 4%;
          @media screen and (min-width:768px) and (max-width:1024px) {
            right: 3%;
          }
          @media (min-width:768px) and (max-width:1024px) and (orientation : landscape) {
            right: 0%;
          }
          @media screen and (min-width:320px) and (max-width:360px) {
            right: 2%;
          }
          path {
            fill: #545456;
          }
        }
  
        input {
          width: 95%;
          border-radius: 5px;
          border: solid 1px #f4f3f4;
          padding: 7px 15px;
          font-size: 14px;
          font-weight: 300;
          color: #545456;
          background-color: #fff;
          &::placeholder {
            opacity: 0.8;
          }
          @media screen and (min-width:768px) and (max-width:1024px) {
            width: 98%;
          }
        }
      }
    }
  
    &.microinverters.open {
      & > div:first-child {
        width: 100%;
        position: fixed;
        left: 0;
        top: 0;
      }
  
      .header {
        padding: 77px 27px 20px 15px;
        &.microinverters-dialog {
          display: flex;
        }
      }
  
      & .container {
        margin-top: 134px;
      }
    }
  
    .header {
      padding: 77px 50px 20px 15px;
      color: $background;
      background-color: $secondary;
      min-height: $header-height;
      box-shadow: 0 0 2px rgba(0,0,0,.4);
      display: flex;
      align-items: center;
      position: relative;
      z-index: 100;
  
      &:active { background-color: $secondary-dark; }
      .menu-name {
        bottom: initial;
        display: inline-block;
        color: #fff;
        word-break: break-word;
       &.generator, &.batteryStorage {
        display: flex;
        justify-content: space-between;
        width: 100%;
        align-items: center;
       }
        &.supportHistory {
          display: flex;
          justify-content: space-between;
          width: 100%;
          align-items: center;
        }

          .search-icon {
            .icon.search {
              width: 17px;
              height: 17px;
              padding-left: 17px;
              path {
                fill: #FFFFFF;
              }
            }
        }


        &.eventsHistory{
          margin-left: 5px;
        }
  
        @media screen and (min-width:320px) and (max-width:360px) {
          font-size: 16px;
        }
      }
      .menu-name-history{
        bottom: initial;
        line-height: 21px;
        font-size: 16px;
        display: inline-block;
        color: #fff;
        margin-right: -140px;
        padding-left:8px;
      }
      .icon {
        display: inline-block;
        margin-left: 8px;
        margin-right: 5px;
        height: 32px;
  
        &.prev {
          display: flex;
          align-items: center;
          svg {
            margin-top: 0px; 
          } 
        }
  
        polygon{
          fill: #ffffff;
        }
      }
    }
  
    .menu-name__search {
      cursor: pointer;
      float: right;
      font-size: 14px;
      display: flex;
      margin: auto;
      align-items: center;
      justify-content: center;
      margin-right: -8px;
      float: right;
  
      .menu-search {
        display: flex;
        align-items: center;
        justify-content: center;
        .icon {
          height: 18px;
          path {
            fill: #ffffff;
          }
        }
      }
    }
  
    .menu-name__sort {
      cursor: pointer;
      float: right;
      font-size: 14px;
      padding-right: 56px;
      margin-top: -48px;
    }
  
    .menu-name__close {
      cursor: pointer;
      float: right;
      font-size: 14px;
      width: 20px;
      float: right;
      margin: -17px 20px -3px 0px;
      height: 20px;

      .icon.close {
        polygon {
          fill: #ffffff;
        }
      }
    }
  
    &.settings {
      .overlay {
        position: fixed;
        z-index: 102;
        background-color: $screen;
        width: 100%;
        height: calc(100% - 134px);
        top: 134px;
      }
      .spinner {
        font-size: 2.5em;
        &:before { z-index: -1; }
      }
    }
    &.reports {
      .comp-notification {
        font-size: 12px;
  
        &.reports-notify {
          font-size: 1em;
          background-color: #545456;
        }
      }
      .content {
        line-height: unset;
        font-size: unset;
        & form {
          margin: 0;
          margin-top: 15px;
          &>p {
            font-size: 16px;
          }
        }
        & .button {
          border-radius: 32px;
          background-color: $secondary;
          font-size: 16px;
          font-weight: 500;
          letter-spacing: normal;
          margin-top: 32px;
          padding: 12px 32px;
        }
        & .email {
          font-size: 12px;
          line-height: 1.25;
          padding-top: 20px;
        }
        & .icon,
        & .input {
          font-size: 16px;
        }
        & select.input {
          padding-left: 0;
        }
        & .date-inline-picker{
          margin: 16px 0;
          .input {
            padding-left: 25px;
          }
        }
        & .icon.dropdown {
          right: 0;
          margin: 9px 0 3px 0;
        }
        & .icon.calendar {
          margin: 7px 0 3px 0;
        }
        & .info {
          font-size: 12px;
          line-height: normal;
          color: #7b8793;
          margin-bottom: 19px;
        } 
      }
    }
    &.batteryStorage {
      .header {
        padding: 77px 20px 20px 15px;
      }
      & > .comp-notification__failure,
      & > .comp-notification__demo-user {
        background-color: #545456;
        z-index: 2;
      }
      .close-enphase-icon {
        position: absolute;
        right: 1.625em;
        top: 3em;
  
        .icon.close,
        .icon.close svg {
          width: 18px;
          height: 18px;
  
          polygon {
            fill: #ffffff;
          }
        }
      }
    }
    &.batteryStorage,
    &.batteryReserve {
      height: 91%;
      .container {
        background-color: #fafafb;
        height: calc(100% - 134px) !important;
        .overlay {
          position: fixed;
          z-index: 102;
          background-color: $screen;
          width: 100%;
          height: calc(100% - 134px);
        }
        .spin-container {
          background: none;
        }
      }
    }

    &.gridServices {
      .container {
        height: 95% !important;

        form {
          margin: 0
        }

        .MuiFormLabel-root.Mui-focused { 
          color: #6F6F6F
        }

        .Mui-focused {
          &.MuiInput-underline:after {
            border: none
          }
        }

        #grid_services_root {
          height: calc(100% - 107px);

          div {
            box-sizing: content-box;
          }

          .MuiSnackbar-root {
            bottom: #{$footer-height};
          }

          #gs__signup {
            bottom: #{$footer-height};
            border-radius: 0
          }

          .MuiLinearProgress-root {
            background-color: #fff;
          }
        }

        #grid_services_container {
          height: 100%;
        }

        #grid_Services_wrapper {
          top: 0;
          z-index: 1;
        }
      }
    }
    
    &.support {
      .container {
        background-color: #f6f7fb;
      }
    }
    &.devices {
      .header {
        padding: 77px 20px 20px 15px;
  
        .menu-name__devices{
          display: flex;
          align-items: center;
          margin-left: auto;
          float: right;
          font-size: 14px;
          cursor: pointer;
        }
      }
  
      .comp-notification.comp-notification__failure {
        background-color: #545456;
      }
    }
    &.devices .content,
    &.configure .content {
      padding: 1.7em 1.6em 1.1em 1.6em;
      border-bottom: 1px solid $light;
      &.connected {
        .indicator { background-color: $tertiary-dark; }
        .indicator-title { color: $tertiary-dark; }
      }
      h5, p {
        margin: 0 0 0 5em;
      }
      img {
        margin-top: -1.3em;
        width: 4em;
        float: left;
      }
      hr {
        margin: 1.1em 0;
      }
      .indicator {
        float: left;
        border-radius: 50%;
        width: 1em;
        height: 1em;
        margin: 0.2em 1.6em;
        background-color: $primary-dark;
      }
      .indicator-title {
        color: $primary-dark;
      }
      .menu-item {
        padding: 1.1em 0.4em 1.1em 5em;
        margin: -0.9em 0;
        display: block;
        .icon {
          height: 1.8em;
          float: right;
          margin-top: -0.3em;
        }
      }
      .label { font-weight: 500; }
      .button { background-color: $super-light; }
      &.connected .label { color: $tertiary; }
      &.disconnected .label { color: $primary; }
      &.connected .button { background-color: $tertiary; }
    }
  
    &.configure .content {
      h5 { margin: 1em 0 1em 5em; }
      img { margin-top: 0; }
    }
  
    &.details .content {
      .ec2 [class^="ec-system-icon-"],
      .ec2 [class*=" ec-system-icon-"] {
        background-image: url("../images/ec2_spr_system_icons_888.png");
        -moz-background-size:auto 26px;
        -webkit-background-size:auto 26px;
        background-size:auto 26px;
        width: 26px;
        height: 26px;
      }
      .stat-box {
        margin:1em 0 2em 0;
        display:block;
        padding-left:45px;
        .icon {
          margin-left: -44px;
          position: absolute;
        }
        .value{
          color: #666 !important;
          display: inline-block;
          font-size: 14px;
          font-weight: normal;
          margin-left: 0.5em;
          max-width: 80%;
          vertical-align: bottom;
        }
        .ec-system-icon-install {
          background-position: -312px 0;
        }
        .ec-system-icon-address {
          background-position: -286px 0;
        }
        .ec-system-icon-micros {
          background-position: -260px 0;
        }
        .ec-system-icon-acbs {
          background-position: -651px 0;
        }
        .ec-system-icon-arrays {
          background-position: -364px 0;
        }
        .ec-system-icon-connection {
          -moz-background-size:auto 26px;
          -webkit-background-size:auto 26px;
          background-size:auto 26px;
          width: 26px;
          height: 26px;
        }
      }
      h4 { margin: 0; }
      h5 { margin: 0; }
    }
  
    .container {
      height: calc(91vh - 134px);
      overflow: scroll;
      scroll-behavior: smooth;
      -webkit-overflow-scrolling: touch; /* momentum scrolling */
  
      &.container--addstorage {
        height: calc(91% - 46px) !important;
  
        &.iOS-addstorage { height: calc(91% - 80px) }
  
        & .system-estimator-iframe {
          height: 100%;
          position: relative;
  
          iframe { height: 100%; }
  
          .exit-system-estimator {
            height: calc(91% - 46px);
            top: 46px;
          }
        }
      }
    }
  
    .container-form {
      height: 84%;
      -webkit-overflow-scrolling: touch; /* momentum scrolling */
  
      iframe {
        height: 100% !important;
      }
    }
  
    .content {
      position: relative;
      padding: 1em 1.6em 3em;
      line-height: 1.5em;
      font-weight: 300;
      font-size: 1.2em;
      @include title-1;
      
      & > p, h5, ul {
        margin: 0 0 0 1.9em;
        li { padding: 1.2em 0; }
        li > * { margin-left: 0; margin-top: 0; }
        li + li { border-top: 1px solid $faint; }
      }
      h3 {
        margin: 1.6em 0 0.6em 1.1em;
        font-size: 1.5em;
        font-weight: 400;
        color: $super-dark;
      }
      h4 {
        margin: 1.6em 0 0.6em 1.4em;
        font-size: 1.3em;
        font-weight: 400;
      }
      h5 {
        margin-top: 1.2em;
        font-size: 1em;
        font-weight: 400;
      }
      a {
        text-decoration: none;
        font-weight: 400;
      }
  
      &.disconnected{
        .button{
          background-color: gray;
        }
      }
  
      hr {
        margin: 1.9em;
        margin-right: -1.6em;
        border: none;
        border-bottom: 1px solid $faint;
      }
  
      .alert { color: $primary; }
  
      .button {
        margin: 1em 0 0 0;
        background-color: $tertiary;
        width: auto;
        border-radius: .2em;
      }
  
      .alert.icon {
        position: absolute;
        left: 1.3em;
        svg { stroke: $primary; }
      }
  
      .clear {
        position: absolute;
        left: 0;
        height: 4.1em;
        padding: 1.4em;
        margin-top: -1em;
      }
  
      .logo {
        margin: 2em 1.9em -1em;
      }
  
      .group {
        display: block;
        margin-top: 0.5em;
      }
  
      .photos {
        line-height: 0;
        .photo {
          width: 200px;
          height: 200px;
          margin: 0.5em 0.5em 0 0;
          cursor: pointer;
          display: inline-block;
          overflow: hidden;
          border: 1px solid $light;
          border-radius: 0.5em;
          &.expanded {
            top: 0;
            left: 0;
            height: 91%;
            width: 100%;
            margin: 0;
            border: none;
            z-index: 100;
            position: fixed;
            overflow-x: scroll;
            overflow-y: hidden;
            border-radius: 0;
          }
          img {
            height: 100%;
          }
        }
      }
    }
  
    form {
      margin: 1em;
      position: relative;
      min-height: 30em; /* for touch overflow scrolling bug */
  
      .input, .icon {
        font-size: 1.2em;
      }
  
      .icon {
        position: absolute;
        &.dropdown {
          margin: 0.4em 0.6em;
          height: 1em;
        }
        &.calendar {
          margin: 0.4em 0.3em;
          height: 1.2em;
        }
      }
      /* .button { font-size: 0.9em; } */
    }
  
    .result {
      color: $primary;
      font-weight: 500;
      div {
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }
  }
  
  iframe {
    /* height is set dynamically via js based on iframe size */
    position: relative;
    width: 100%;
    border: 0;
  }
  
  .frame {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: none;
    background: white;
    transform: translateZ(0);
  }
  .menu-name__storage{
    display: flex;
    margin-left: auto;
    cursor: pointer;
    float: right;
    font-size: 14px;
    .menu-backupHistory{
      max-width: 140px;
      display: flex;
      align-items: center;
    }
    .backupHistory{
      height: auto !important;
      svg{
        padding-top: 2px;
        width: 20px;
        height: 18px;
      }
    }
    .backup-text{
      color: #fff;
      padding-left: 5px;
      word-break: break-all;
    }
  }
  .close__menu{
    width: 20px;
    height: 20px;
    float: right;
    margin: -45px 20px 0  0;
    .close{
      svg{
        width: 15px;
        height: 15px !important;
        fill: #fff;
      }
    }
  }
  
  .night{
    .backup-text{
      color: #01b5de;
    }
  
    .menu-name__searchInput {
      .searchInput {
        .icon.search {
          path {
            fill: #FFF;
            opacity: 0.6;
          }
        }
  
        input {
          width: 100%;
          border-radius: 5px;
          border: solid 1px rgba(123, 135, 147, 0.3);
          padding: 7px 15px;
          font-size: 14px;
          font-weight: 300;
          color: #ffffff;
          background-color: #333;
          &::placeholder {
            opacity: 0.5;
            color: #ffffff;
          }
          @media screen and (min-width:768px) and (max-width:1024px) {
            width: 92%;
          }
        }
      }
    }
      
    .menu-name__storage{
      .backupHistory{
        svg{
          path{
            fill: #01b5de;
          }      
        }
      }
    }
    .batteryStorage, .batteryReserve {
      .container {
        .overlay {
          background-color: $dark-screen;
        }
      }
    }
    .settings {
      .overlay {
        background-color: $dark-screen;
      }
    }
  }
  