.appUpdate {
    .update-info-container {
        padding: 20px;
        margin-bottom: auto;
        color: #545456;
        height: 100%;
        padding-bottom: 52px;
        overflow: auto;

        &.updated {
            height: initial;
            text-align: center;
            position: relative;
            top: 50%;
            transform: translateY(-50%);
            opacity: 0.6;
            h3 {
                font-size: 16px;
                font-weight: 400;
            }
            p {
                margin: 0;
                margin-top: 3px;
                font-size: 12px;
                font-weight: normal;
            }
        }
    }
 
    .update-info {
        display: flex;
        .icon.download svg {
            height: 40px;
            width: 40px;
        }
        &> div {
            display: inline-block;
            margin-left: 10px;
        }
    }
    
    .update-version {
        font-size: 16px;
        font-weight: 500;
        color: #545456;
    }
    
    .update-tag {
        background-color: #35C37D;
        border-radius: 4px;
        color: #ffffff;
        font-size: 12px;
        margin-left: 5px;
        padding: 2px 4px;
        transform: translateY(-2px);
        display: inline-block;
    }
    
    .update-date {
        font-size: 12px;
        color: #8e9091;
        margin-top: 5px;
    }
    
    .update-info-container p {
        margin: 15px 0 6px 0;
        font-size: 14px;
        font-weight: 500;
        line-height: 1.57;
    }
    
    .update-info-container ul {
        margin: 0;
        padding-left: 18px;
        font-size: 14px;
    }
    
    .update-info-container li {
        list-style-type: disc;
        margin-bottom: 6px;
    }

    .update-info-container li:last-child {
        margin-bottom: 15px;
    }
    
    .read-more {
        font-size: 14px;
        color: #00b6de;
        cursor: pointer;
    }
    .button.update-btn {
        position: absolute;
        bottom: #{$footer-height};
        width: 100%;
        margin: 0;
        border-radius: 0;
        font-size: 16px;
        padding: 17px 0;
    }
}

.night {
    .appUpdate {
        .icon.download {
            path {
                fill: #f8f8ff;
            }
        }
        
        .update-version, .update-info-container {
            color: #f8f8ff;
        }
        .update-info-container p {
            color: #f8f8ff;
        }
    }
} 
