.heatmap-series {
    position: relative;
    border-bottom: solid 1px #d8dcde;
    height: 344px;
    width: 100%;

    .heatmap-series_wrapper {
            max-width: 1200px;
        &.months {
            max-width: 900px;
        }
        .rv-xy-plot__series--heatmap rect {
            cursor: pointer;

            &.grid-disabled {
                cursor: unset !important;
            }
        }
    }

    .y-next-icon, .x-next-icon, .y-prev-icon, .x-prev-icon {
        width: 10px;
        height: 15px;
        margin-left: 55.5px;
        cursor: pointer;
    }

    .y-next-icon, .y-prev-icon {
        transform: rotate(90deg);
    }

    .icon {
        opacity: 1;
        display: flex;
        width: 7.7px;
        height: 12.7px;

        svg {
            width: 7.7px;
            height: 12.7px;
            margin-top: 0px;
        }
    }
    .y-next-icon, .y-prev-icon {
        margin: 0;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .y-prev-icon {
        margin-top: auto;
    }

    .energy-heatmap__yaxis-buttongroup, .energy-heatmap__xaxis-buttongroup {
        position: absolute;
        z-index: 1;
    }

    .energy-heatmap__yaxis-buttongroup {
        display: flex;
        flex-direction: column;
        height: 100%;
        padding: 40px 0 1px 20px;
    }

    .energy-heatmap__xaxis-buttongroup {
        display: flex;
        width: 100%;
        top: 17px;
        padding: 0 32px;
        border-bottom: solid 1px #d8dcde;
        height: 18px;

        .x-next-icon, .x-prev-icon {
            width: 7.7px;
            height: 12.7px;
            margin: 0px;
        }

        .x-next-icon {
            margin-left: auto;
        }

        .x-prev-icon {
            margin-left: 65px;
        }
    }
    .hovered-energy {
        position: absolute;
        top: 0;
        width: auto;
        text-align: center;
        display: flex;
        align-items: center;
        max-width: 100px;
        font-size: 12px;
        font-weight: 700;
        z-index: 1;
        opacity: 1;
        border: 4px solid #fff;
        box-shadow: 0px 1px 5px 0px #0000009c;
        cursor: pointer;
        pointer-events: none;
    }
    .tooltip-yaxis, .tooltip-xaxis {
        position: absolute;
        width: auto;
        text-align: center;
        display: flex;
        right: calc(100% - 120px);
        align-items: center;
        max-width: 100px;
        font-size: 12px;
        font-weight: bold;
        color: #ffffff;
        z-index: 2;
        span {
            justify-content: center;
            display: flex;
            margin: auto;
            padding: 5px;
            z-index: 2;
            background-color: #01DE29;
        }
        .tooltip-arrow {
            position: absolute;
            background-color: #01DE29;
            -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
            z-index: 1;
            pointer-events: none;
            cursor: pointer;
        }
    }
    .tooltip-yaxis {
        .tooltip-arrow {
            width: 15px;
            height: 15px;
            top: 0;
            right: 0;
            bottom: 0;
            margin: auto;
        }
    }
    .tooltip-xaxis {
        top: 0px;
        &.days, &.hours {
            span {
                width: 100%;
            }
        }
        .tooltip-arrow {
            width: 10px;
            height: 10px;
            top: 16px;
            right: 0;
            bottom: 0;
            left: 0;
            margin: auto;
        }
        &.hours {
            .tooltip-arrow {
                right: auto;
            }
        }
    }
}

.energy-heatmap__verticalbar-series {
    height: 334px;
}

.heatmapHovered {
    stroke-width: 3px !important;
    stroke: #ffffff !important;
}