.browser {
  .scrim-pop-up {
		.popup__grid {
			#popUpWrapper{
				&.currency-popup {
					width: 450px;
          height: max-content;
          
          .popup__header {
            height: 82px;
            padding: 32px 32px 26px;
            p {
              font-size: 20px;
              font-weight: 500;
              color: #545456;
            }
          }
          .popup__body-content {
            padding: 32px;
            font-size: 15px;
            line-height: 1.4;
            color: #545456;
          }
					.close_popup {
						.close {
              display: flex;
              svg {
                width: 15px !important;
                height: 15px !important;
              }
						}
						svg polygon {
							fill: #545456;
						}
					}
				}
			}
		}
	}
  .energy {
    .energy-stats-and-graph-browser {
      display: flex;
      height: calc(100% - 70px);
      .energy-left-panel {
        height: max-content;
        width: calc(100% - 335px);
        
        &.lifetime {
          width: 100%;
          border: none;
        }
      }
      .energy-right-panel {
        width: 335px;
        height: max-content;
        min-height: 100%;
        border-left: solid 1px rgba(123, 135, 147, 0.3);

        &.lifetime {
          display: none;
        }

        .heatmap-color-indicator {
          padding: 34px 25px;
          display: flex;
          align-items: center;

          .heatmap-values {
            font-size: 15px;
            color: #545456;
          }
          .heatmap-production-view {
            width: 165px;
            height: 10px;
            background-image: url('../../images/energyProductionScale.svg');
            background-size: cover;
            margin: 0 10px;
          }

          .heatmap-consumption-view {
            width: 190px;
            height: 10px;
            background-size: cover;
            background-image: url('../../images/energyConsumptionScale.svg');
            margin: 0 8px;
          }
        }

        .energy-stats-wrapper {
          height: 119px;
          border-bottom: 1px solid rgba(123,135,147,.3);
          padding: 30px 36px;
          vertical-align: ce;
          display: flex;
          align-items: center;

          .energy-solar-produced {
            .energy-type__details .energy-type__value {
             color: #01b5de;
           }
           .energy-type__icon {
            width: 28px;
            height: 47px;
            min-width: 28px;
            margin-right: 21px;
            span {
              width: 28px;
              height: 47px;
              svg {
                width: 28px !important;
                height: 47px !important;
              }
            }
          }
          }

          .energy-solar-consumed {
            .energy-type__details .energy-type__value {
              color: #f37320;
            }
            .energy-type__icon {
              width: 40px;
              height: 35px;
              min-width: 40px;
              margin-right: 35px;
              span {
                width: 50px;
                height: 35px;
                svg {
                  width: 40px !important;
                  height: 35px !important;
                }
              }
            }
          }

          .energy-type__details {
            .energy-type__value {
              font-size: 30px;
              font-weight: bold;
              margin-right: 2px;
  
              span {
                font-size: 12px;
                font-weight: 500;
                text-align: right;
              }
            }
            .energy-type__name {
              font-size: 15px;
              color: #7a8692;
            }
          }

          .energy-type__icon {
            width: 28px;
            height: 47px;
            min-width: 28px;
            margin-right: 21px;
            span {
              width: 28px;
              height: 47px;
              svg {
                width: 28px !important;
                height: 47px !important;
              }
            }
          }
        }

        .currency-conversion-carousel {
          border: none;
          border-bottom: solid 1px rgba(123, 135, 147, 0.3);
          border-radius: 0;
          min-height: 170px;
          height: auto;
          margin: 0;
          padding: 29.5px 25px;
          max-width: 375px;
          width: -webkit-fill-available;

          .currency-conversion-container .currency-conversion-wrapper {
            padding: 0;

            .setcurrency-conversion-text {
              padding: 0;
              margin-top: 40px;
            }
            .currency-conversion-icon {
              .icon {
                display: none;
              }
              .currency-conversion-text {
                padding: 0;
                font-size: 25px;
                font-weight: 500;
                color: #545456;
              }
            }
            .currency-conversion-rightIcon {
              .icon {
                display: flex;
              }
              .icon svg {
                height: 19.2px !important;
                width: 18px !important;
              }
            }
          }
          .carousel-type-1 {
            border: 0;
            .carousel__container {
              zoom: 0;
              margin: 0;

              .solar_heading {
                padding: 0;
              }
  
              .carousel__wrapper {
                border: 0;
                padding: 0;
                margin-top: 30px;

                .carousel__arrow .icon svg {
                  width: 13px !important;
                  height: 21px !important;
                }

                .carousel__item {
                  margin: auto 10px;
                  .carousel__item-wrapper {
                    display: flex;
                    align-items: center;

                    .energy-type {
                      min-width: 100px;
                      margin-bottom: 0px;
                    }
                    .energy-type .energy-type__icon-with-details .energy-type__details {
                      .energy-type__name {
                        text-align: left;
                        margin-top: 5px;
                        font-size: 12px;
                        color: #7a8692;
                      }
                      .energy-type__value {
                        text-align: left;
                        font-size: 20px;
                        font-weight: 500;
                        height: auto;

                        span {
                          font-size: 12px;
                          font-weight: 500;
                        }
                      }
                    }
                    .carousel-equal-currency {
                      margin: 0 13px;
                    }
                  }
                }
              }
            }
          }
        }
      }
      .energy-graph {
        padding: 0 17px 0 32px;
        margin-bottom: 0px;
      }
      .energy-stats {
        height: auto;
        margin: 0 32px;
        padding: 24px 0;
      }
      .production-consumption, .production-consumption-storage {
        .energy-stats__wrapper {
          padding: 0;
          height: 100%;
          width: auto;
          justify-content: center;
  
          .energy-type__value {
            font-size: 20px;
            span {
              font-size: 12px;
            }
          }
          .energy-type__name {
            font-size: 15px;
          }
        }
      }
      .consumption-only, .production-only {
        .energy-stats__wrapper, .energy-stats__item {
          border: none;
        }
        .energy-stats__wrapper {
          margin: 0;
          padding: 0 32px;
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
        }
        .energy-type__icon-with-details {
          width: 100%;
        }
        .energy-type__icon {
          height: 55px;
          width: 55px;
        }
        .energy-type__value {
          font-size: 30px;
          font-weight: bold;
          font-stretch: normal;
          font-style: normal;
          line-height: 1;
          letter-spacing: normal;
          span {
            font-size: 15px;
            font-weight: normal;
            line-height: normal;
            text-align: right;
          }
        }
        .energy-type__name {
          font-size: 15px;
          font-weight: normal;
          font-stretch: normal;
          font-style: normal;
          line-height: 1.4;
          letter-spacing: normal;
          color: #7a8692;
        }
        .energy-stats__item {
          padding: 0;
          .energy-last-year {
            .energy-type__icon {
              path {
                fill: #6c7073;
              }
              circle {
                stroke: #6c7073;
              }
            }
          }
        }
      }
  
      @media (min-width: 480px) {
        .energy-stats__wrapper {
          zoom: 1;
        }
      }
  
      .production-only {
        .energy-type__value {
          color: #01b4de;
        }
        .energy-stats__item {
          .energy-previous {
            .energy-type__icon {
              path {
                fill: #008cab;
              }
              circle {
                stroke: #008cab;
              }
            }
          }
          .energy-current {
            .energy-type__icon {
              path {
                fill: #01b4de;
              }
              circle {
                stroke: #01b4de;
              }
            }
          }
        }
      }
      .consumption-only {
        .energy-type__value {
          color: #f37320;
        }
        .energy-stats__item {
          .energy-previous {
            .energy-type__icon {
              path {
                fill: #a6490b;
              }
              circle {
                stroke: #a6490b;
              }
            }
          }
          .energy-current {
            .energy-type__icon {
              path {
                fill: #f37320;
              }
              circle {
                stroke: #f37320;
              }
            }
          }
        }
      }
      
      .energy-legends {
        padding: 32px 32px 50px;
        .legend-heading {
          font-size: 25px;
          font-weight: 500;
          color: #545456;
          padding-bottom: 32px;
        }
        .graph-contols  {
          .toggle-switch {
            display: flex;
            align-items: center;
            padding-bottom: 40px;
            width: auto;
            &:last-child {
              padding-bottom: 0px;
            }
          }
          .toggle-switch__label {
            font-size: 15px;
            font-weight: 500;
            padding-left: 20px;
            margin-top: 0px;
          }
        }
      }
      .carousel__radio-button-group {
        display: flex;
        justify-content: space-between;
        padding: 31px 32px 30.5px;
        border-bottom: 1px solid rgba(123, 135, 147, 0.3);
        .carousel__radio-button-wrapper {
          display: flex;
          flex-basis: 45%;
          .icon {
            display: flex;
            width: 20.8px;
            height: 20.8px;
          }
          .carousel__radio-battery-type {

            font-size: 17px;
            font-weight: 500;
            color: #545456;
            margin-left: 6.2px;
          }
        }
      }
      .carousel-type-1 {
        margin: 0px !important;
        border-bottom: solid 1px rgba(123, 135, 147, 0.3);
        .carousel__container {
          border: none;
          flex-direction: column;

          &.solar-equivalent {
            margin: 0;
            padding: 29.5px 25px;
            min-height: 170px;
          }
        
          .solar_heading {
            font-size: 25px;
            font-weight: 500;
            color: #545456;
            padding-top: 0;
          }
          .carousel__static-info{
            .carousel__static-info__wrapper{
              margin-left: 20px;
            }
          }
          .carousel__static-info, .carousel__wrapper {
            border: none;
            padding: 39px 0;
          }
          .solar-equivalent {
            padding: 0;
            padding-top: 30px;
            border: none !important;
            .carousel__item .carousel__item-wrapper {
              display: flex;
              align-items: center;
              
              .energy-type {
                margin: 0;
                min-width: 100px;

                &.energy-solar-produced {
                  .energy-type__value {
                    color: #01b4de;
                  }
                }

                .energy-type__icon-with-details .energy-type__details {
                  .energy-type__value:first-child {
                    text-align: left;
                  }
                  .energy-type__name {
                    text-align: left;
                    margin-top: 5px;
                    font-size: 12px;
                    color: #7a8692;
                  }
                }
              }

              .carousel-equal-solar {
                margin: 0 13px;
              }
            }
          }
          .carousel__static-info__value {
            font-size: 30px;
            font-weight: bold;
            font-stretch: normal;
            font-style: normal;
            line-height: 1;
            letter-spacing: normal;
            color: #01b4de;
            span {
              font-size: 15px;
              font-weight: normal;
              line-height: normal;
              text-align: right;
            }
          }
          .carousel__static-info__name {
            font-size: 15px;
            font-weight: normal;
            font-stretch: normal;
            font-style: normal;
            line-height: 1.4;
            letter-spacing: normal;
            color: #7a8692;
          }
          .carousel__wrapper {
            border-bottom: solid 1px rgba(123, 135, 147, 0.3);
            .carousel__arrow{
              position: initial;
            }
            .energy-type__icon {
              height: 50px;
              width: 50px;
            }
            .energy-type__value {
              font-size: 20px;
              font-weight: 500;
              font-stretch: normal;
              font-style: normal;
              line-height: normal;
              letter-spacing: normal;
              color: #545456;
            }
            .energy-type__name {
              font-size: 15px;
              font-weight: 500;
              font-stretch: normal;
              font-style: normal;
              line-height: normal;
              letter-spacing: normal;
              color: #7a8692;
            }
          }
          .carousel__static-info__value {
            span {
              font-size: 12px;
            }
          }
        }
      }
  
      .lifetime-consumed-wrapper {
        display: flex;
        flex-direction: column;
        padding: 26px 32px;
        border-bottom: 1px solid #d8dcde;
        .lifetime-consumed-name {
          font-size: 19px;
          font-weight: 500;
          font-stretch: normal;
          font-style: normal;
          line-height: normal;
          letter-spacing: normal;
          color: #545456;
          margin: 0;
        }
        .lifetime-consumed-value {
          font-size: 30px;
          font-weight: bold;
          font-stretch: normal;
          font-style: normal;
          line-height: normal;
          letter-spacing: normal;
          color: #f37320;
          margin: 0;
          margin-top: 15px;
          span {
            font-size: 14px;
            font-weight: 500;
            font-stretch: normal;
            font-style: normal;
            line-height: normal;
            letter-spacing: normal;
            text-align: right;
            color: #f37320;
            vertical-align: text-top;
            margin-left: 2px;
          }
        }
      }
      
      @media (min-width: 480px) {
        .carousel-type-1 {
          .carousel__container {
            zoom: 1;
          }
        }
      }
    }
  }
}