.offline {

  background-color: #000;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  position: fixed;
  
  transition: opacity 2s;
  z-index: 1000;
  border-radius: $app-border-radius;
  overflow: hidden;
  
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  list-style: none;
}

.offline-message {
  color: #ffffff;
  position: absolute;
  top: 50%;
  width: 100%;
  text-align: center;
}    
