.showup {
  &.inactive-micros-details-popup {
    .showup__body {
      overflow: scroll;
      border-radius: 10px;
    }
  }
}

.device-details-popup {
  &.inactive-micros-details-popup {
    width: 450px;

    .popup__header {
      display: flex;
      justify-content: space-between;
      padding: 26px 32px;
      height: 75px;
      align-items: center;

      .close_popup .close svg {
        width: 15px;
        height: 15px;

        polygon {
        fill: #545456;
        }
      }
    }

    .popup__body {
      .popup__body-content {
        max-height: 500px;
        overflow: scroll;
      }
    }
  }
}


.inactive-micros-details {
  padding: 20px 20px 30px;
  background: #ffff;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;

  .imd__header-title {
    font-size: 14px;
    line-height: 1.29;
    color: #545456;
  }

  .imd__detail-view {
    .imd__content {
      margin: 20px 0;
      padding: 20px;
      border: solid 1px #d8dcde;
      border-radius: 10px;

      &.imd__single-microinvertor {
        border: none;
        padding: 0 0 12px;

        .imd__microinvertors-detail {
          margin-top: 15px;
          max-height: 105px;
          overflow: auto;
          
          &::-webkit-scrollbar {
            width: 6px;
          }
          
          &::-webkit-scrollbar-track {
            background-color: #fafafa;
          }
          
          &::-webkit-scrollbar-thumb {
            border-radius: 3px;
            background-color: #c3c3c3;
          }
        }
      }

      .imd__encharge-detail, .imd__microinvertors-detail {
        .imd__title {
          font-size: 12px;
          color: #a2a6a9;
        }
        .imd__value {
          font-size: 14px;
          font-weight: 300;
          color: #3f4347;
          margin-top: 4px;
        }
      }

      .imd__microinvertors-detail {
        margin-top: 15px;
      }
    }
  }

  .imd__footer {
    font-size: 14px;
    line-height: 1.29;
    color: #545456;

    span {
      color: #01b4de;
      font-weight: 500;
      cursor: pointer;
    }
  }
}

.browser {
  .inactive-micros-details {
    padding: 0 32px 32px;

    .imd__header-title {
      display: none;
    }

    .imd__detail-view {
      .imd__content {
        .imd__encharge-detail, .imd__microinvertors-detail {
          .imd__title {
            font-size: 15px;
          }
          .imd__value {
            font-size: 17px;
            margin-top: 0;
          }
        }
  
        .imd__microinvertors-detail {
          margin-top: 20px;
        }
      }
    }

    .imd__footer {
      font-size: 15px;
      line-height: 1.4;
    }
  }
}

.night {
  .inactive-micros-details {
    background: #262627;
    .imd__header-title {
      color: #f8f8ff;
    }

    .imd__detail-view {
      .imd__content {
        border-color: rgba(123,135,147,.3);

        .imd__encharge-detail, .imd__microinvertors-detail {
          .imd__title {
            color: #a2a6a9;
          }
          .imd__value {
            color: #f8f8ff;
          }
        }
      }
    }

    .imd__footer {
      color: #f8f8ff;
    }

  }
}