/*
  --- --- --- --- --- ---
  How to use Mixin
  --- --- --- --- --- ---
  .selector{
    @include transform(rotate(30deg));
  }
*/

@mixin transform($property) {
  transform: $property;
}

@mixin placeholder {
  &::placeholder { @content; }
  &:placeholder { @content; }
}

@mixin save-button {
  @include title-2;
  box-shadow: 1px 2px 10px 0 rgba(63, 67, 71, 0.08);
  border: 1px solid #00b6de;
  padding: 10px 0;
  background: #00b6de;
  color: white;
  text-align: center;
  border-radius: 2rem;
  width: 11rem;
  margin: 20px auto 0;
  justify-content: center;
  display: flex;

  &.buttonCancel {
    background-color: transparent;
    color: #01b5de;
    border: 1px solid #01B5DE;
    font-size: 15px;

    &.buttonDisabled {
      color: #c7c8ca;
    }
  }

  &.quiet-margin {
    margin-top: 40px;
  }

  &.disabled{
    pointer-events: none;
    background-color: #c6c8ca;
    border-color: #c6c8ca;
    color: #707070;
  }
}
