.comp-radio-button {
  display: flex;
  align-items: center;
  font-size: 16px;
  font-weight: normal;
  line-height: 19px;
  color: #545456;

  input {
    width: 19px;
    height: 19px;
    min-width: 19px;
    margin-right: 15px;
    border-width: 1px;

    &::before {
      width: 11px;
      height: 11px;
      margin: 3px auto;
    }
  }

  .icon {
    width: auto;
    height: 22px;
    margin-right: 10px;
  }
}

.night {
  .comp-radio-button {
    color: #d8dcde;

    input { border-color: #00b6de; }

    .icon svg > g > path { fill: #d8dcde; }
  }
}