.splash {
  background-color: $background;
  bottom: 0;
  position: fixed;
  top: 0;
  // transition: opacity 2s;
  width: 100%;
  z-index: 10000;
  border-radius: 0;
  overflow: hidden;
  overflow-y: scroll;

  .progress {
    background-color: $light;
    height: 0.6%;
    position: absolute;
    top: 0;
    width: 100%;
    .bar {
      background-color: $secondary;
      height: 100%;
      transition: width 1s;
    }
  }

  .container {
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
  }

  .close {
    position: absolute;
    top: 1.8em;
    right: 1.5em;
    height: 1em;
    polygon { fill: $medium; }
  }

  .notices {
    opacity: 0;
    transition: 300ms opacity;

    .message {
      position: absolute;
      top: 12.6%;
      left: 21%;
      path { 
        fill: none !important;
        stroke: $medium;
      }
    }

    &:after {
      content: '';
      width: 100%;
      height: 30%;
      background: linear-gradient($background, $transparent 5%, $transparent 95%, $background);
      display: block;
      position: absolute;
      top: 10%;
      pointer-events: none;
    }

    &.show { opacity: 1; }
    ul {
      top: 10%;
      padding: 0 20%;
      width: 100%;
      height: 30%;
      position: absolute;
      overflow: scroll;
      -webkit-overflow-scrolling: touch; /* momentum scrolling */

      li { 
        width: 100%;
        padding: 1.2em 0 1.2em 3.4em; 
        line-height: 1.4em;
        color: $medium;
      }
      li + li { border-top: 1px solid $faint; }
    }
  }

  .version {
    bottom: 3em;
    color: $light;
    font-size: 0.6em;
    letter-spacing: 0.2em;
    position: absolute;
    text-align: center;
    text-transform: uppercase;
    width: 100%;
    display: none;
  }

  .success-message {
    background-color: rgb(92, 212, 130);
    align-items: center;
    justify-content: center;
    display: flex;
    padding: 15px;
    color: #ffffff;
    font-size: 14px;

    .success-icon{
      margin-right: 10px;
    }
  }
}
