@import './font-style.scss';

.live-status {
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;

  &.multi-envoy.enlm {
    height: 100%;

    .live-status__body .multiEnvoy-root {
      padding: unset;
    }
  }

  .storm-status-container{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-top: 16px !important;
    width: 130px;
    height: 15px;
  }

  .storm-status-container-wb{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-top: 6px !important;
    width: 130px;
    height: 15px;
    margin-left: 40px;
  }

  .stormwrapper-ev-ct-pv{
    width: 120px;
  }

  .storm-alert-status{
    line-height: 12px;
    font-size: 12px;
    padding-top: 4px;
  }

  .profile-container{
    font-size: 12px;
  }

  .profile-container-live {
    font-size: 12px;
    color: #6C7074;
    margin-top: 4px;
    padding-left: 2px;
  }

  .storm-live-status-container-ev{
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-top: 16px !important;
    width: 130px;
    height: 15px;
    // position: absolute;
    // top: 85px;
  }

  .storm-live-status-container{
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-top: 16px !important;
    width: 130px;
    height: 15px;
  }

  .pes-ble-connected-status {
    margin-top: -56px;
    margin-left: -15.5px;
  }

  &.pvOnly,
  &.pvOnlyWeb,
  &.pvOnlyEnlm,
  &.ls__web .content-body-wrapper {
    justify-content: center;
    .live-status__wrapper {
      display: flex;
      flex-direction: column;

      .pvOnly-multienvoy.custom-dropdown {
        display: flex;
        background-color: #F6F7FB;
        @include caption;
        text-transform: capitalize;
        border-radius: 6px;
        margin-bottom: 20px;
        margin-left: 15px;
        width: max-content;

        &.disabled {
          .dropdown__button {
            cursor: unset;
          }
        }

        .dropdown__button {
          padding: 2px 3px 2px 8px;
          cursor: pointer;

          .icon.envoyDropDown {
            margin-left: 5px;

            rect {
              opacity: 0;
            }
          }

          .dropdown__buttonText {
            display: flex;

            .gateway_num {
              margin-left: 10px;
              color: #01B4DE;
            }
          }
        }

        .dropdown__content {
          display: flex;
          flex-direction: column;
          align-items: center;
          background-color: #F6F7FB;
          box-shadow: none;
          max-height: 100px;

          .dropdown-item {
            width: 100%;
            justify-content: center;
            cursor: pointer;

            &.selected {
              color: #01B4DE;
            }
          }

          .icon.SelectedIcon {
            display: none;
          }

          li {
            border-bottom: 0.5px solid #B4B6B8;
            border-left: 0.5px solid #B4B6B8;
            border-right: 0.5px solid #B4B6B8;
          }

          li:first-child {
            border-radius: 6px 6px 0px 0px;
            border-top: 0.5px solid #B4B6B8;
          }

          li:last-child {
            border-radius: 0px 0px 6px 6px;
          }
        }

        .overlay {
          background-color: transparent;
        }
      }

      .live-status__body {
        margin: 0px 15px 15px 15px;

        .pvOnly-multienvoy.custom-dropdown {
          margin-left: unset;
        }

        .live-status__try-connecting-envoy {
          height: auto;
          margin: auto;
        }

        .live-status__energy-flow__production-only {
          .energy-type.energy-produced {
            width: auto !important;
          }

          .energy-type__icon-with-details {
            width: auto !important;

            .energy-type__icon {
              width: auto;
              height: auto;
              display: flex;
              justify-content: center;
              margin-bottom: 15px;
            }
          }
        }

        .cta {
          display: flex;
          align-items: center;
          padding: 20px 0px 23px;
          margin: 0px auto;
          @include body-1;
          cursor: pointer;

          .learn {
            color: #01B4DE;
            @include body-2;
          }

          .text {
            margin-left: 5px;
          }

          .icon.consumptionMeter {
            display: flex;
            height: auto;
          }
        }
      }
    }
  }

  &.pvOnly {
    background-color: rgba(0, 0, 0, 0.4);

    .live-status__wrapper {
      background-color: white;
      border-radius: 6px;
      border: 1px solid #F4F4F5;
      position: relative;
      height: 368px;
      width: auto;
      margin: 0px 17px;
      top: 26%;
      left: auto;

      .live-status__heading-wrapper {
        .live-status__timer-icon {
          display: flex;
          height: auto;
        }

        .live-status__timer-text {
          height: auto;

          .countdown__text {
            line-height: normal;
          }
        }
      }

      .live-status__body {
        overflow-y: scroll;
        justify-content: center;
      }
    }
  }

  &.pvOnlyEnlm {
    height: 100%;

    .live-status__wrapper {
      margin: auto;

      .pvOnly-multienvoy.custom-dropdown {
        margin: 20px 0px 20px 35px;
        border: 0.5px solid #B4B6B8;
      }
    }

    .live-status__body {
      padding: 20px;

      .pvOnly-multienvoy.custom-dropdown {
        border: 0.5px solid #B4B6B8;
      }
    }
  }

  .popup__grid {
    .popup__wrapper {
      .popup__body {
        .popup__body-content {
          .loads-InfoPopUp {
            span {
              color: #01B5DE;
              text-decoration: underline;
              cursor: pointer;
            }
          }
        }
      }

      &.popup__web {
        height: 150px;
      }
    }
  }

  &.ls__web {
    height: auto !important;

    .title {
      display: flex;

      .live-status__timer {
        display: flex;
        margin-left: 15px;

        .live-status__timer-icon .icon svg {
          width: 17px;
          height: 17px;
        }

        .live-status__timer-text {
          margin-left: 5px;
        }

        .live-status__timer-text .countdown .countdown__text {
          font-size: 15px;
          font-weight: 500;
          line-height: 1.21;
          text-transform: initial;
        }
      }
    }

    .content-body-wrapper {
      width: 60%;
      height: 100%;
      overflow: auto;
      margin: 30px auto !important;
      padding-top: 20px;
      border: 1px solid #ffffff;
      background-color: #ffffff;
      box-shadow: 0 2px 10px 0 rgba(63, 67, 71, .08);

      .live-status__wrapper {
        .live-status__body {
          margin: 0;
          border-top: none;
          height: 100%;
          overflow: hidden;

          .live-status__grid-connectivity {
            padding: 20px 32px;
            height: 92px;
          }

          .live-status__energy-flow {
            margin: 0 15px;

            .live-status__energy-flow-wrapper {
              height: 100%;
              padding: 32px 0;
            }
          }
        }
      }
    }
  }

  .comp-notification.comp-notification__failure {
    background-color: #545456;
  }

  .live-status__wrapper {
    height: 100%;

    .live-status__header,
    .live-status__grid-connectivity {
      display: flex;
      justify-content: space-between;
      align-items: center;

      p {
        margin: 3px 0 0 0;
      }
    }

    .live-status__header {
      height: $live-status-header;
      padding: 20px 15px 15px;
      box-shadow: 0 1px 1px 0 #00000010;
      display: flex;
      align-items: start;

      .live-status__close-live-status {
        margin-top: 3px;

        svg polygon {
          fill: #6C7073;
          display: block;
          height: 14.89px;
          width: 14.9px;
        }
      }

      .live-status__heading {
        font-size: 16px;
      }
    }

    .live-status__body {
      height: calc(100% - #{$live-status-header});
      overflow: auto;
      display: flex;
      flex-direction: column;

      &.external_source {
        height: 100%;
      }

      &.withDryContacts {
        .connected-devices-section {
          padding-bottom: 14px;
        }
      }

      .main__liveStatus__container {
        display: flex;
        margin: 0 !important;
        flex-grow: 1;
        &.web_view {
          flex-direction: row;
          justify-content: flex-start;
          .live_status_grid {
            display: flex;
            width: 100%;
            flex-direction: column;
            justify-content: center;
            height: 100%;
          }

          .live_status_tables {
            min-width: 483px;
            overflow-y: scroll;
            width: 483px;
            background-color: #F6F7FB;
          }
        }
        &.mobile_view {
          flex-direction: column;
          justify-content: flex-start;
          .live_status_grid:only-child {
            display: flex;
          }
          .live_status_grid {
            flex-direction: column;
            flex-grow: 1;
            width: 100%;
            justify-content: flex-start;
            &.multi_envoy_grid {
              padding-bottom: 44px;
            }

            .live-status__energy-flow {
              display: flex;
              flex-grow: 1;
              flex-direction: column;
              justify-content: center;
            }
          }

          .live_status_tables {
            width: 100%;
          }
        }
      }

      .grid-container__ensemble {
        margin-bottom: 0px;
      }

      .live-status__grid-wrapper {
        padding: 0 15px;

        >div:first-child {
          display: grid;
          grid-template-rows: auto auto;
          grid-template-columns: auto auto;
          justify-content: space-between;
        }

        .live-status_relays .toggle-switch__container {
          display: flex;
        }

        .live-status_relays {
          margin-top: 6px;
          margin-left: 2px;

          .live-status__timer {
            margin: 5px 0;
          }
        }

      }

      .grid-status__wrapper {
        display: flex;
        align-items: center;
        font-size: 12px;
        font-weight: normal;
        line-height: 1;
        color: #6c7074;
        text-transform: capitalize;

        &.phase__wrapper {
          justify-self: end;
          align-self: flex-start;
          grid-row: 1 /span 2;
          grid-column: 2 /span 1;

          & .choosePhase-old.custom-dropdown {
            justify-content: center;
            height: 24px;
            background-color: #E37938;
            border-radius: 12px;
            color: #FFFFFF;
            padding: 5px 10px 5px 10px;

            .dropdown__button .icon {
              margin-left: 3px;

              &.dropdownArrowFilled {
                width: 12px;
                height: 12px;
                fill: #ffffff;
              }
            }

            .dropdown__content {
              padding: 5px 0px;
              right: 0%;
              left: auto;
              font-size: 16px;
              width: max-content;
              color: #545456;
              border-radius: 15px;
              box-shadow: -5px 5px 15px -5px #b7b5b5;

              & li:not(:first-child) {
                border-top: 1px solid #d8dcde;
              }

              .dropdown-item {
                padding: 12px 12px 12px 12px;

                &.selected {
                  color: #E37938
                }

                .SelectedIcon {
                  display: none;
                }
              }
            }

            .overlay {
              background-color: rgba(0, 0, 0, 0.1);
            }
          }
        }

        .grid-status {
          width: 15px;
          margin-right: 7px;
          display: flex;
          align-items: center;
          justify-content: center;
          padding-right: 1px;
        }

        .loading,
        svg {
          width: 15px;
          height: 15px;
        }

        span {
          width: 10px;
          height: 10px;
          border-radius: 50%;
          margin: 1px 0;

          &.onGrid{ background-color: #35c37d; }
    
          &.offGrid { background-color: #ff6464; }
    
          &.icon {
            width: 15px;
            height: 15px;

            &.mqtt-status-icon {
              height: 18px;
              width: 18px;
            }

            svg {
              width: inherit;
              height: inherit;
            }
          }

          &.systemError svg path {
            fill: #FF8B0C;
          }
        }

        .mqtt-status-text {
          margin-top: 3px;
          text-transform: capitalize;
        }
      }

      .live-status-dryContactBlock {
        padding: 20px;
        background-color: #F6F7FB;
        margin-top: 20px;

        &.hemsEVEnabled__dryContacts {
          padding-top: 0;
          margin: 0;
        }

        .live-status-dryContactBlock__heading {
          display: flex;
          @include caption;
          align-items: center;
          font-size: 12px;
          font-weight: 500;
          padding: 10px 0px 12px 0px;
          color: #A2ABB3;

          .icon.infoIcon {
            display: flex;
            margin-left: 4px;
            margin-top: -3px;
          }
        }

        ul {
          display: flex;
          flex-direction: column;
          padding: 0px 16px;
          background-color: #FFF;
          border-radius: 6px;
          border: 1px solid #F4F4F5;

          li {
            border-bottom: 1px solid #d8dcde;
            font-size: 12px;
            font-weight: normal;
            width: 100%;
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 16px 0;
            color: #545456;

            .leftBlock {
              display: flex;

              &>.icon {
                width: 24px;
                height: 24px;

                svg {
                  fill: none;
                }

                &.medicalDevice svg>g>g>g>path {
                  fill: none;
                }
              }

              p {
                margin: 3px 0 0 0;
              }

              .device-name {
                padding-left: 16px;
                font-weight: 500;
                word-break: break-all;
                margin-right: 5px;
                font-size: 16px;
                color: #545456;
                margin-top: 5px;
              }

              .name__status__block {
                display: flex;
                flex-direction: column;
              }
            }

            .device-status {
              //width: 25%;
              padding-left: 16px;
              font-weight: 400;
            }
          }

          li:last-child {
            border-bottom: none !important;
            padding-bottom: 16px;
          }
        }
      }

      .cfgmsg {
        display: flex;
        @include body-1;
        padding: 35px 10px 30px 10px;

        .msgtext {
          margin: 5px;
          color: #7B8793;

          .linkText {
            @include body-2;
            color: #01B4DE;
          }
        }

        .icon.infoIconGrey {
          display: flex;

          & svg {
            width: 15px;
            height: 15px;

            path {
              fill: #545456;
            }
          }
        }
      }
    }

    .production_only_live_status {
      justify-content: center;

      .cta {
        display: flex;
        padding: 20px;

        .text {
          font-size: 12px;

          .learn {
            color: #01B4DE;
            cursor: pointer;
          }
        }

        .consumptionMeter {
          margin-right: 8px;

          svg {
            width: 20px;
            height: 20px;
          }
        }
      }
    }

    .live-status__try-connecting-envoy {
      height: calc(100% - (#{$live-status-header } + 25px));
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 20px 0;

      .live-status__try-connecting-envoy__contents {
        text-align: center;

        p {
          margin-top: 0;
        }

        .live-status__try-connecting-envoy__refresh {
          cursor: pointer;
          display: flex;
          flex-direction: column;
          align-items: center;

          .refresh {
            width: 50px;
            height: 50px;
            margin-bottom: 10px;

            svg {
              width: 100%;
              height: 100%;
            }
          }
        }

        .loadingCircle {
          margin-bottom: 20px;
        }

        p {
          font-size: 16px;
          color: #545456;
          margin: 0;
        }
      }
    }

    .live-status__grid-connectivity {
      height: $live-status-gid-connectivity;
      margin: 0;
      padding: 10px 0;
      border-bottom: 1px solid #d8dcde;

      &.waiting-for-message .live-status__icon-with-text {
        filter: grayscale(1);
        pointer-events: none;
      }

      &.loading .live-status__icon-with-text {
        filter: grayscale(1);
      }
    }

    .hide-grid-connectivity {
      .live-status__grid-connectivity {
        .live-status__icon-with-text {
          height: 0;
          padding: 0;
          border-bottom: none;
        }
      }

      .live-status__actions {
        width: 100%;
      }
    }

    .live-status__power-table {
      background: #f2f2f2;
      text-align: center;
      padding: 25px;
    }

    .live-status__select_phase {
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: #E37938;
      color: #ffffff;
      border-radius: 20px;

      &:before,
      &:after {
        content: none;
      }

      .MuiSelect-root {
        padding: 3px 22px 3px 10px;
        font-size: 12px;

        &:focus {
          background-color: transparent;
        }
      }

      .MuiSelect-icon {
        top: auto;
        width: 23px !important;
        height: 23px !important;

        path {
          fill: #ffffff;
        }
      }
    }

    .live-status__select_phase__disabled {
      padding: 1px 2px 1px 9px;
      background-color: #C0BFC0;
      cursor: pointer;
    }

    .live-status__energy-flow {
      height: calc(100% - #{$live-status-gid-connectivity} - 32px);

      &.live-status__with-gen {
        height: calc(100% - #{$live-status-gen-connectivity});
      }

      &.live-status__with-storm {
        padding-top: 10px !important;
      }

      .live-status__energy-flow-wrapper {
        height: 100%;
        // padding: 20px 0;
        display: flex;
        justify-content: center;
        align-items: center;
      }

      .live-status__energy-flow-inner-wrapper {
        padding: 0;
        max-width: 360px;
        margin: auto;

        .energy-type__value {
          .energy-type__unit {
            margin-left: 4px;
          }
        }
      }

      .live-status__energy-flow__left {}

      .live-status__energy-flow__center {
        display: flex;
        justify-content: center;
        align-items: center;

        .live-status__system-grid-status {
          .energy-type {
            margin-top: 48px;

            .energy-type__icon-with-details {
              margin-right: 4px;
            }
          }

          &.disconnected {
            .energy-type__icon {
              opacity: .5;
            }
          }

          .subtitle {
            margin: 0;
            text-align: center;
            font-size: 12px;
          }
        }

        .live-status__generator {
          .energy-type {
            margin-top: 48px;
          }
        }

        .live-status__consumed {
          position: relative;

          .energy-type {
            margin-top: 53px;
          }

          &.live-status__ojasConsumed {
            position: relative;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            .energy-consumednomargin {
              // margin: 0 !important;
            }

            .live-status__energy-flow__arrows-vertial-acPort,
            .live-status__energy-flow__arrows-vertial-dcPort {
              position: relative;
              height: 40px;
            }

            .energy-acport-hidden,
            .live-status__energy-flow__arrows-vertial-acPort,
            .live-status__energy-flow__arrows-vertial-dcPort,
            .energy-dcport-hidden,
            .energy-dcport,
            .energy-acport {
              position: absolute;
            }

            .energy-acport {
              bottom: 124px;
              left: -2px;

              .energy-type__icon-with-details {
                .energy-type__icon {
                  margin-bottom: 0;
                }
              }
            }

            .live-status__energy-flow__arrows-vertial-acPort {
              bottom: 80px;
              left: 30px;

              @media screen and (max-width: 370px) {
                left: 37px;
              }
            }

            .live-status__energy-flow__arrows-vertial-dcPort {
              top: 132.5px;
              left: 30px;

              @media screen and (max-width: 370px) {
                left: 37px;
              }
            }

            .energy-dcport {
              top: 164px;
              left: -2px;

              .energy-type__icon-with-details {
                .energy-type__icon {
                  margin-bottom: 0;
                }
              }
            }

            &.is-ios {
              .energy-acport{
                bottom: 122px;
              }
              .live-status__energy-flow__arrows-vertial-acPort{
                bottom: 86px;
                transform: rotate(180deg);
              }
            }
          }
        }

        .live-status__energy-flow__arrows {
          position: relative;
          /* width: calc(100% - 150px); */
          width: 190px;
          height: 194px;
          margin: 0 4px;

          .icon {
            height: 100%;

            &.energyGridToBattery,
            &.energyBatteryToHome,
            &.energySolarToGrid,
            &.energySolarToHome {
              height: auto;
            }
          }

          .live-status__energy-flow__arrows-top,
          .live-status__energy-flow__arrows-bottom {
            display: flex;
            justify-content: space-between;
            width: 100%;
            height: 50%;

            >div {
              width: 50%;
            }
          }

          .live-status__energy-flow__arrows-horizontal,
          .live-status__energy-flow__arrows-vertical {
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
          }

          .live-status__energy-flow__arrows-horizontal {
            .icon {
              display: flex;
            }

            .energyGridToHomeConnected {
              position: absolute;
              right: -11px;
              top: 97px;
              height: auto;
              width: 212px;
            }

            .energyGeneratorToHome {
              position: absolute;
              right: -11px;
              top: 97px;
              height: auto;
              width: 212px;
            }

            .energyGridToHomeDisconnected {
              position: absolute;
              right: -11px;
              top: 97px;
              height: auto;
              width: 212px;
            }
          }

          .live-status__energy-flow__arrows-vertical {
            .energySolarToBattery {
              position: absolute;
              top: 5px;
              right: 89px;
            }
          }
        }
      }

      .live-status__energy-flow__arrows-top {
        .energySolarToGrid {
          position: absolute;
          top: 5px;
          left: -11px;
        }

        .energySolarToHome {
          position: absolute;
          top: 5px;
          right: -11px;
        }
      }

      .live-status__energy-flow__arrows-bottom {
        .energyGridToBattery {
          position: absolute;
          top: 109px;
          left: -11px;
        }

        .energyGeneratorToBattery {
          position: absolute;
          top: 109px;
          left: -11px;
        }

        .energyBatteryToHome,
        .energyGenToHomeWoEncharge {
          position: absolute;
          top: 109px;
