.feedback-ratings {
  height: 100%;

    &:after,
    &:before {
        content: "";
        position: absolute;
        pointer-events: none;
        width: 100%;
        height: 0;
        transition: height 0.15s;
        background-image: linear-gradient(0deg,#ffffff00,#ffffffd0);
    }

    &:after {
        transform: translateY(-112px);
        background-image: linear-gradient(180deg,#ffffff00,#ffffffd0);
        bottom: 0;
    }

    &:before {
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
    }

    &.fade-bottom:after {
        height: 55px;
    }

    &.fade-top:before {
        height: 55px;
    }

    .feedback-ratings_content {
        padding: 24px 20px 0;
        height: calc(100% - 112px);
        overflow: scroll;
        -webkit-overflow-scrolling: touch;

        .fr__checkbox-wrapper {
            display: flex;
            align-items: flex-start;
            margin-bottom: 20px;

            .fr__checkbox, .fr__checkbox input, .fr__checkbox input::before {
                width: 15px;
                height: 15px;
            }

            .fr__checkbox {
                margin-right: 5px;

                input {
                    &:checked{
                        background-color: #01b4de;
                        color: #ffffff;
                        border: 0;
                  
                        &::before{
                          content: "\02143";
                          transform: rotate(40deg);
                          margin-left: 2px;
                          zoom: 0.8;
                        }
                    }
                }
            }

            .fr__checkbox-label {
                font-size: 14px;
                color: #545456;
            }
        }
    }

    .feedback-ratings_site-details {
        font-size: 11px;
        color: #545456;
        margin-bottom: 4px;
        text-transform: capitalize;
    }
    .feedback-ratings_title {
        font-size: 20px;
        font-weight: 500;
        color: #545456;
        margin-bottom: 40px;
    }
    .feedback-ratings_subtitle {
        font-size: 16px;
        font-weight: 500;
        color: #545456;
        margin: 14px 0 10px;
    }
    .feedback-ratings_description {
        font-size: 14px;
        color: #545456;
    }
    .feedback-ratings_slider-wrapper {
        padding: 48px 0 26px;
        display: flex;
        justify-content: center;
    }
    .feedback-ratings_textarea {
        padding: 15px 0px 30px;
        textarea {
            width: 100%;
            height: 110px;
            resize: none;
            border-radius: 4px;
            border: solid 1px #c3c6ca;
            padding: 15px;
            font-size: 14px;
            line-height: 1.29;
            color: #545456;
        }
        .feedback-ratings_count {
            font-size: 10px;
            text-align: right;
            color: #939598;
        }
    }
}

.night {
    .feedback-ratings {
        &:after {
            background-image: linear-gradient(180deg,#26262700,#262627ff);
        }

        &:before {
            background-image: linear-gradient(0deg,#26262700,#262627ff);
        }

        .feedback-ratings_title, .feedback-ratings_site-details, .feedback-ratings_subtitle {
            color: #f8f8ff;
        }

        .feedback-ratings_textarea {
            textarea {
                color: #f8f8ff;
                background-color: #262627;
            }
        }
        
        .feedback-ratings_description {
            color: #d8dcde;
        }

        .fr__checkbox-wrapper {
            input {
                border: none;

                &::before {
                    border-color: #f8f8ff;
                }
            }

            .fr__checkbox-label {
                color: #d8dcde;
            }
        }
    }
}