@import "../fonts";

@keyframes icon_spinner {
    to { transform: rotate(360deg); }
}

.offline-background {
    background-color: #ffffff;
    padding-bottom: #{$footer-height};

    .offline-container {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        width: 100%;
        text-align: center;

        h1 {
            font-size: 18px;
            font-weight: bold;
            color: #545456;
            font-weight: bold;
        }
        h3 {
            font-size: 14px;
            line-height: 1.29;
            color: #5a5a5c;
            margin: 10px 0 30px 0;
            font-weight: normal;
        }
        .button {
            width: 159px;
            margin: 0 auto;
            font-weight: 500;
            font-size: 16px;
        }
    }

    .icon.wifiErrorIcon {
        margin-bottom: 30px;
    }

    .icon.refresh {
        margin-right: 9.5px;
        svg {
            height: 15px;
            width: 15px;
            path {
                stroke: #ffffff;
            }
        }
    }

    .button.active .icon.refresh svg {
        animation: icon_spinner .6s linear 1;
    }
}

.night .offline-background {
    background-color: #262627;
    .icon.wifiErrorIcon {
        svg {
            g {
                path[stroke-width="2"] {
                    stroke: #262627;
                }
            }
        }
    }
    .offline-container {
        h1 {
            color: #ffffff;
        }
        h3 {
            color: #ffffff;
        }
    }
}


.page.layout .offline-background {
    height: 76.5%;
}