.settings{
  .list-view__gridswitch{
    .list-view__icon{
      height: 25px;

      .energyGrid {
        padding-top: 5px;
      }
    }

    .toggle-switch{
      text-align: right;

      .toggle-switch__label{
        color: #7b8793;
        font-weight: 300;
      }
    }
  }

  .list-view__wifi {
    .list-view__icon {
      .systemError svg {
        width: auto;

        path { fill: #545456; }
      }
    }
  }
}

.night {
  .settings {
    .list-view__gridswitch {
      .list-view__icon {
        .menuUnknownGrid > svg > g > g > path:first-child {
          fill: #ff8b0d;
        }
      }
    }

    .list-view__wifi {
      .list-view__icon {
        .systemError svg path { fill: #f8f8ff; }
      }
    }
  }

  .list-view__icon {
    .menuUnknownGrid > svg > g > g > path:first-child {
      fill: #ff8b0d;
    }
  }
}