.container-form {
    padding: 1em 6px 1em 6px;
    height: auto !important;
    margin: auto;
  
    .form-group {
      margin-bottom: 1em;
    }

    .form-group-radio, .form-group-checkbox {
      margin-bottom: 1em;
    }

    .form-checkbox {
      margin-right: 6px;
    }

    .form-group-file {
      margin: 0;
    }

    .form-input-file {
      border-bottom: none;
    }

    .form-label-file {
      font-size: 14px;
      line-height: 1.43;
      color: #5a5a5c;
    }

    .form-input {
      font-size: 14px;
      color: #545456;
      @include title-1;
    }
  
    .title {
      font-size: 14px;
      margin-bottom: 2.5em;
      color: #5a5a5c;
      line-height: 1.43;
      @media (min-width:768px) and (max-width:1024px) {
        width: 70%;
      }
    }

    .title-confirmation {
        font-size: 16px;
        margin-bottom: 2.5em;
        color: #5a5a5c;
        line-height: 1.38;
        text-align: center;
    }
}
