.toggle-switch{
  position: relative;
  font-size: 12px;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;

  .toggle-switch__isloading{
    .icon{
      width: 20px;
      height: 20px;
    }
  }

  .toggle-switch__label{
    font-size: 12px;
    margin-top: 5px;

    &.toggle-switch__label-checked{
      color: #545456;
      font-weight: 500;
    }

    &.toggle-switch__label-unchecked{
      color: #7a8692;
      font-weight: normal;
    }
  }
}