@import "fonts";
.backup-history{
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  letter-spacing: normal;
  height: 100%;
  overflow: hidden;

  &.bh__web {
    width: 60%;
    height: 100%;
    overflow: auto;
    margin: 30px auto;
    border: 1px solid #ffffff;
    background-color: #ffffff;
    box-shadow: 0 2px 10px 0 rgba(63,67,71,.08);

    .bh__wrapper {
      width: 100%;

      .bh__total-backup {
        box-shadow: none;
        text-align: left;
        border-bottom: solid 1px #f4f3f4;

        .bh__total-backup__title {
          font-size: 17px;
        }

        .bh__total-backup__duration {
          font-size: 25px;
          justify-content: flex-start;
        }
      }

      .bh__duration__lists {
        padding: 0 32px;

        .bh__total-backup-events {
          display: none;
        }

        .enho__table thead tr {
          border-bottom: 1px solid #f4f3f4;
        }

        .enho__table thead td {
          width: 33.33%;
        }
      }
    }
  }
  
  .bh__wrapper{
    width: inherit;
    height: inherit;
  }

  .bh__total-backup{
    background: #ffffff;
    text-align: center;
    padding: 20px;
    box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.08);

    .bh__total-backup__title{
      font-size: 16px;
      color: #545456;
    }

    .bh__total-backup__duration{
      display: flex;
      justify-content: center;
      font-size: 30px;
      font-weight: bold;
      color: #545456;
      margin-top: 5px;

      >div{
        padding: 0 2px;
      }

      p{
        margin: 0;

        &:first-child{
          letter-spacing: 2px;
        }

        &:last-child{
          font-size: 12px;
          font-weight: bold;
          text-align: center;
          color: rgba(123, 135, 147, 0.6);
          margin-top: 5px;
        }
      }
    }
  }

  .bh__duration__lists{
    overflow: auto;
    height: calc(100% - 120px);
    padding: 10px 20px 20px;
  }

  .bh__no-backup-events{
    height: 100%;
    display: flex;
    flex-flow: column;
    justify-content: center;
    font-size: 14px;
    color: #808080;
    text-align: center;
  }

  .bh__total-backup-events{
    font-size: 16px;
    font-weight: 500;
    color: #545456;
    padding: 10px 0 20px 0;
  }

  .bh__table{
    thead{
      tbody{
        .bh__duration{
          width: 65px;
        }
      }
    }

    tbody{
      .bh__duration{
        width: 65px;
        font-weight: 500;
        color: #3f4347;
        vertical-align: middle;
      }
    }
  }
}

.night{
  .backup-history{
    background: #333333;

    .bh__total-backup{
      background: #262627;
      box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.08);
  
      .bh__total-backup__title{
        color: #d8dcde;
      }
  
      .bh__total-backup__duration{
        color: #f8f8ff;

        p{
          color: inherit;

          &:last-child{
            color: rgba(123, 135, 147, 0.6);
          }
        }
      }
    }

    .bh__total-backup-events{
      color: #d8dcde;
    }

    .enho__table{
      tbody{
        th,td{
          color: #d8dcde;
        }

        .bh__duration{
          color: #d8dcde;
        }
      }
    }
  }
}
