@import "../../../style/font-style.scss";

.night .OM_pesSubmenu .ojasSurroundLightContainer {
  background-color: #262727;

  .lightStatus .description .descrContent .descrTitle {
    color: #ffffff;
  }

  .descrSubTitle {
    color: #A9A9AA !important;
  }

  .lightStatus .description .img {
    path {
      fill: #ffffff;
    }

  }

  .lightSettingItem__wrapper .greyBackground {
    background-color: #1b1a1a;
  }
}

.night .overlay {
  background-color: #262627 !important;
}

.OM_pesSubmenu .ojasSurroundLightContainer {
  background-color: #ffffff;

  .lightStatus {
    border-bottom: 0.5px solid #7B87934D;
    display: flex;
    align-items: initial;
    padding: 16px 0px;
    margin: 0 20px;

    .description {
      display: flex;
      width: 100%;
      justify-content: space-between;
      align-items: center;

      .img {
        width: 24px;
        height: 24px;
        margin-right: 15px;
      }

      .descrContent {
        .descrTitle {
          font-size: 16px;
          color: #545456;
          font-weight: normal;
          @include title-1;
        }

        .descrSubTitle {
          font-size: 12px;
          font-weight: 300;
          color: #7b8793;
          @include body-1;
        }
      }
    }
  }

  .lightSettingItem__wrapper {
    display: flex;
    justify-content: space-between;
    margin-left: 32px;
    margin-right: 32px;
    margin: 23px 32px 0 32px;
    padding-top: 6px;
    padding-bottom: 24px;
    position: relative;

    .greyBackground {
      position: absolute;
      width: 100%;
      height: 49%;
      border-radius: 25px;
      background-color: #F4F4F4;
    }
  }
}
