.today {
  .title {
    margin: 0.4em auto 0;
    font-size: 1.6em;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .notices {
    display: inline-block;
    color: $background;
    background-color: $primary;
    border-radius: 1em;
    font-size: 0.6em;
    font-weight: 500;
    min-width: 1.7em;
    text-align: center;
    min-height: 1.7em;
    line-height: 1.7em;
    margin: 0 0.6em;
    vertical-align: top;
  }
}

.shim {
  position: relative;
  margin: auto;
  width: 100%;
  .last-update {
    color: $medium;
    line-height: 2em;
    font-size: 1.1em;
    margin-right: 0.5em;
  }
  .weather {
    font-size: 1.2em;
    float: right;
  }
}

.rating-popup {
  position: fixed;
  z-index: 1;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  &.disabled {
    display: none;
  }
  &.enabled {
    display: block;
  }
}

.rating-popup-wrapper {
  background-color: #fff;
  width: 80%;
  height: auto;
  padding-bottom: 1em;
  margin: auto;
}

.ratingHeader {
  color: #545456;
  font-size: 20px;
  font-weight: 500;
  justify-content: center;
  display: flex;
  line-height: 1.29;
}

.ratingContent {
  padding-top: 10px;
  display: flex;
  justify-content: center;
}

.ratingSubContent {
  display: flex;
  justify-content: center;
}

.logoRating {
  display: flex;
  justify-content: center;
  padding: 2em;
}

.button {
  width: 80%;
  margin: 1.5em auto 1em;
  border-radius: 2em;
  background-color: $secondary;
  color: #fff;
  display: flex;
  justify-content: center;
}

.ratingCloseButton {
  color: #939598;
  border: none;
  align-items: center;
  width: 100%;
  background-color: transparent;
  padding-top: 1em;
  padding-bottom: 1em;
}

.expander {
  position: absolute;
  overflow: hidden;
  border-radius: 0.75em;
  padding: 1.4em;
  color: $background;
  z-index: 200;
  transition: all $transition ease-in-out,
              z-index $transition ease-in-out $transition;
  
  & > *, 
  & > * > * { 
    transition: all $transition ease-in-out; 
  }

  section { position: relative; }
  
  .primary {
    height: 100%;
  }

  .lifetime {
    position: absolute;
    right: 0.5em;
    top: 120%;
    font-weight: 500;
    transform: rotate(270deg);
    transform-origin: right;
    letter-spacing: 0.05em;
    opacity: 0.5;
    label {
      font-weight: 300;
    }
  }

  .breakdown {
    opacity: 0;
    height: 25%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;

    h5 {
      font-size: 0.8em;
      font-weight: 500;
      letter-spacing: 0.2em;
      text-transform: uppercase;
      padding-top: 1em;
      margin: 0;
      border-top: 1px solid $screen;
      .progress {
        border: 1px solid $background;
        position: absolute;
        margin-top: -1.1em;
        transition: width 5s;
        .blip, .blip:after {
          background-color: $background;
          position: absolute;
          display: block;
          height: 6px;
          width: 6px;
          border-radius: 50%;
        }
        .blip {
          right: -1px;
          top: -3px;
          &:after {
            content: '';
            animation: blip 3s linear infinite;
          }
        }
      }
    }
    ul {
      display: flex;
      align-items: flex-end;
    }
    li { 
      flex-grow: 1;
      flex-basis: 0;
      position: relative;
      padding-left: 1.4em;
      font-size: 1.0em;
      font-weight: 300;
      & > span {
        width: 1em;
        height: 1em;
        border-radius: 0.5em;
        position: absolute;
        left: 0;
      }
    }
    label {
      display: block;
      font-size: 5.3vw;
      font-weight: 400;
      & > sup {
        font-size: .5em;
      }
    }
    div {
      display: block;
      font-size: 0.75em;
      font-weight: 200;
    }
  }

  h4 {
    font-size: 0.7em;
    font-weight: 500;
    letter-spacing: 0.5em;
    text-transform: uppercase;
    overflow-wrap: break-word;
    margin-right: 10px;
  }

  h3 {
    width: 100%;
    -webkit-box-orient: vertical;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    position: absolute;
    bottom: 0;
    font-weight: 300;
    font-size: 1.2em;
  }

  h2 {
    position: absolute;
    top: 40%;
    font-weight: 300;
    font-size: 2.7em;
    text-align: center;
    white-space: nowrap;
    display: flex;
    align-items: center;
    transition: all $transition ease-in-out;
    span { width: 100% }
  }

  sup {
    font-weight: 500;
    font-size: 0.3em;
    line-height: 3em;
    margin-left: 0.2em;
    vertical-align: top;
  }

  .donut {
    position: absolute;
    top: -4em;
    svg {
      opacity: 0;
      transform: rotate(180deg);
      transition: all $transition ease-in-out;      
    }
  }

  .icon.enlarge,
  .icon.close {
    position: absolute;
    top: -3%;
    right: 0;
    height: 1em;
    svg { width: 0.5em; }
    path, polygon { fill: $background; }
  }

  .icon.battery {
    display: inline;
    margin-right: 0.2em;
    svg { 
      width: 28%;
      transition: all $transition ease-in-out;
    }
    path, .soc  { fill: $background !important; stroke: $background !important }
  }

  &.open {
    top: 0 !important;
    left: 0 !important;
    height: 100%;
    width: 100%;
    padding: 2.5em;
    padding-top: 4.6em;
    z-index: 210;
    border-radius: 0;
    transition: all $transition ease-in-out;
    .lifetime {
      top: 11%;
      opacity: 1;
    }
    h4 { 
      font-weight: 400;
      font-size: 1.3em;
      letter-spacing: 0.1em;
   }
    h3 { 
      font-size: 3.0vh;
    }
    h2 {
      font-size: 6vh;
      top: 17%;
    }
    .donut {
      top: 17%;
      svg {
        opacity: 1;
      }
    }
    .primary {
      height: 75%;
    }
    .breakdown {
      opacity: 1;
    }
    .icon.battery {
      svg { width: 7%; }
      background-color: transparent !important;
    }
    .icon.close {
      top: 0;
      svg {
        width: 1.2em;
        height: 1.2em;
      }
    }
  }

  &.single {
    &.open .primary {
      height: 68%;
    }
    .title {
      font-size: 1em;
      letter-spacing: 0;
      text-transform: initial;
      margin-top: 0;
    }
    .weather {
      float: right;
      font-size: 1.2em;
      img {
        mix-blend-mode: screen;
      }
    }
    .breakdown li {
      padding-left: 0;
      padding-right: 1em;
    }
  }
  
  .production   { background-color: $secondary !important; fill: $secondary !important; stroke: $secondary !important }
  .consumption  { background-color: $primary !important; fill: $primary !important; stroke: $primary !important }
  .grid         { background-color: $background !important; fill: $background !important; stroke: $background !important }
  .battery      { background-color: $tertiary !important; fill: $tertiary !important; stroke: $tertiary !important }
  .soc          { background-color: $medium-dark !important; fill: $medium-dark !important; stroke: $medium-dark !important }
  .total        { background-color: $background !important; }

  &.unavailable { background-color: $super-light }
}