.reconnect-options {
  .main-conatainer-header {
    display: inline-flex;
    padding-top: 20px;
    align-items: center;
    .icon {
      height: 27px;
    }
  }

  .content-header {
    font-size: 16px;
    font-weight: bold;
    color: #545456;
    padding-left: 10px;
    @include title-2;
  }

  .content-text {
    font-size: 12px;
    font-weight: 300;
    line-height: 1.25;
    color: #6f6f8c;
    padding-left: 34px;
    @include body-1;
  }
}

.night{
  .reconnect-options {
    .main-conatainer-header {
      .blueCircleWhite{
        svg{
          text{
            fill: #262627;
          }
        }
      }
    }
    .content-header {
      color: #f8f8ff;
    }
    .content-text {
      color: #d8dcde;
    }
  }
}