.storage-gen {
  &.desktop {
    margin: 20px auto;
    border-radius: 31px;
    width: 1051px;
    .pending {
      font-size: inherit;
      color: inherit;
    }
    .weather-watch {
      .generator-status--wrapper {
        display: flex;
        align-items: center;
        .generator-status {
          margin: 0 15px 5px;
          font-size: 17px;
          color: #f37320;
          &.suspend {
            color: #a7a7a8;
          }
        }
      }
      .weather-watch-wrapper
        .weather-watch-title-status
        .weather-watch-title-wrapper {
        .weather-watch-title {
          font-size: 20px;
        }
        .weather-watch-infoIcon {
          margin-top: 3px;
        }
      }
      .weather-watch-description {
        font-size: 14px;
      }
    }
  }
  .overlay-gen {
    position: absolute;
    left: 50%;
    top: 50%;
    background-color: #000000;
    opacity: 0.3;
    height: 100%;
    width: 100%;
    transform: translate(-50%, -50%);
    z-index: 105;
  }
  .save-button {
    box-shadow: 1px 2px 10px 0 rgba(63, 67, 71, 0.08);
    border: 1px solid #00b6de;
    padding: 10px 0;
    background: #00b6de;
    color: white;
    text-align: center;
    border-radius: 2rem;
    width: 15rem;
    margin: 18px auto 0;
    justify-content: center;
    display: flex;
    font-size: 16px;
    &.buttonCancel {
      background-color: #fff;
      color: #01b5de;
      border: solid 1px rgba(198, 200, 202, 0.5);
    }
    &.buttonDisable {
      background: #a2a6a9;
      border: none;
    }

    &.desktop {
      margin: 18px 0 0;
    }

    &.quiet-margin {
      margin-top: 40px;
    }
  }
  .exercise-desktop {
    padding: 20px 0;
    margin: 25px 0;
    background: white;
    box-shadow: 0px 2px 10px #3F434714;
    .icon {
      &.open {
        transform: rotate(180deg);
        transition-duration: 0.8s;
      }
    }
    .advance {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 0 20px;
      &.showAdvanceSettingsInDesktop {
        padding-bottom: 20px;
        border-bottom: 1px solid #88888834;
      }
    }
    .desktop-advance--settings {
      overflow: hidden;
      opacity: 0;
      // "height: 0" not work with css transitions
      max-height: 0;
      transition: all 0.3s ease-out;
      transition-property: max-height;
      &.show {
        opacity: 1;
        margin: 20px 20px 0;
        max-height: 300px;
      }
    }
    .MuiInputLabel-outlined.MuiInputLabel-shrink {
      background: white;
      @include title-1;
      line-height: normal;
    }

    select.MuiOutlinedInput-input, input.MuiOutlinedInput-input {
      color: #00b6de;
      padding-top: 15px;
      padding-bottom: 15px;
      border-bottom: unset;
      @include title-1;
      line-height: normal;
    }

    .exercise-heading {
      font-size: 15px;
      @include title-2;
    }

    .exercise-subheading {
      font-size: 15px;
    }

    .setting-parent {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      width: 50%;
      .individual-setting {
        flex: 0 48%;
        [class*='makeStyles-formControl-'] {
          margin: 8px 0;
        }
      }
    }
  }

  .pb-20 {
    padding-bottom: 20px !important;
  }

  .mr-3 {
    margin-right: 3px;
  }

  .ml-3 {
    margin-left: 3px;
  }

  .dark {
    color: #000000;
  }

  .light {
    font-weight: 300;
  }

  .pending {
    color: #a2a6a9;
    margin: 0 5px;
    font-size: 12px;
    &.buttonInActive {
      color: #ffffff;
    }
  }

  .exercise {
    box-shadow: rgba(63, 67, 71, 0.08) 1px 2px 10px 0px;
    padding: 0.5rem;
    background: white;
    color: rgb(0, 182, 222);
    position: absolute;
    bottom: 0;
    border: 1px solid rgb(0, 182, 222);
    text-align: center;
    height: 46px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;

    &.buttonInActive {
      background-color: #a2a6a9;
      color: #fff;
      border: 1px solid #a2a6a9;
      .icon {
        fill: #fff;
      }
    }

    .icon {
      fill: rgb(0, 182, 222);
    }
  }

  .generator {
    height: calc(100% - 46px);
    overflow: auto;
    box-shadow: 0px 2px 10px #3F434714;
    .settings-card {
      margin-bottom: 3rem;
    }
    &.desktop {
      padding-bottom: 0;
      background-color: inherit;
      position: relative;
      height: auto;
      background: #ffffff;
      .success-message {
        top: 50px;
        font-size: 14px;
      }
      .settings-card {
        margin: 0 2.5%;
        flex: 1 1 0;
      }
      .storage-mgmter {
        padding: 20px;
        .text-blk {
          font-size: 25px;
          width: 100%;
        }
      }
      .generator-state-loading-toast { top: 0 !important; }
    }
    background-color: #fafafb;

    div[disabled] {
      pointer-events: none;
      opacity: 0.4;
    }

    .weather-watch {
      .grid-status {
        align-self: flex-end;
        display: flex;
        align-items: center;
        font-size: 12px;

        .grid-status--text {
          width: 3.7rem;
        }
      }
    }

    .storage-mgmter {
      display: flex;
      padding: 20px 20px 10px;
      align-items: center;

      .img-blk {
        width: 100px;
        height: 67px;
        background-image: url('../images/generator.png');
        background-repeat: no-repeat;
        margin-right: 15px;
        background-position: center;
        background-size: cover;
      }

      .text-blk {
        width: 60%;
        font-size: 14px;
        font-weight: 500;
        color: #545456;
        @include body-2;

        &.unavailableContent {
          color: #6f6f8c;
        }

        .help-icon {
          margin-left: 5px;
          vertical-align: middle;

          .icon {
            display: inline;
          }
        }
      }
    }

    .success-message {
      position: absolute;
      height: auto;
      max-height: 10%;
      box-shadow: 1px 2px 10px 0 rgba(63, 67, 71, 0.08);
      background-color: #545456;
      color: #fdfefe;
      padding: 1em 2em;
      width: 100%;
      display: flex;
      font-size: 12px;
      font-weight: normal;
      align-items: center;
      z-index: 201;

      &.disabled {
        display: none;
      }

      .icon {
        margin-right: 5px;

        svg {
          height: 20px;
          width: 20px;

          g {
            fill: #ffffff;
          }

          path {
            fill: #ffffff;
          }
        }

        &.tickWhiteSmall {
          svg {
            g {
              stroke: #ffffff;
              fill: none;
            }
          }
        }
      }
    }
  }
  .generator-state-loading-toast{
    svg{
      g{
        circle{
          fill: #fff;
        }
        path{
          fill: #545456;
        }
      }
    }
  }

  .permission-denied-toast{
   .icon{
     svg{
       g{
         path:first-child {
          fill: white;
         }
       }
     }
   }
  }

  .weather-watch {
    padding: 20px 0px;
    margin: 0 20px;
    border-bottom: solid 0.5px rgba(123, 135, 147, 0.3);

    .weather-watch-wrapper {
      display: flex;
      justify-content: space-between;
      align-items: center;

      .weather-watch-title-status {
        width: 75%;

        .weather-watch-title-wrapper {
          display: flex;
          align-items: center;

          .weather-watch-title {
            line-height: 24px;
            font-size: 16px;
            font-weight: 500;
            margin-right: 7.5px;
          }

          .infoIcon {
            align-self: flex-start;
            margin-bottom: 1px;
          }

          .weather-watch-infoIcon,
          .infoIcon {
            display: flex;

            svg {
              width: 14px;
              height: 14px;
            }
          }
        }
      }
    }

    .weather-watch-description {
      font-size: 12px;
      font-weight: 300;
      line-height: normal;
      color: #f37320;
      &.suspend {
        color: #a7a7a8;
      }
    }

    .weather-watch-status {
      font-size: 12px;
      font-weight: 500;

      &.disabled {
        color: rgba(84, 84, 86, 0.5) !important;
      }

      &.enabled,
      &.active {
        color: #f37320;
      }
    }
  }

  .toggleblk__wrapper {
    padding: 20px;
  }

  .storage-mgmt {
    display: flex;
    padding: 20px;
    align-items: center;

    .img-blk {
      width: 100px;
      height: 67px;
      background-image: url('../images/generator.png');
      background-repeat: no-repeat;
      margin-right: 15px;
    }

    .text-blk {
      width: 60%;
      font-size: 14px;
      font-weight: 500;
      color: #545456;

      .help-icon {
        margin-left: 5px;
        vertical-align: middle;

        .icon {
          display: inline;
        }
      }
    }
  }

  .storage-blk {
    border-radius: 10px;
    margin: 0 20px 15px 20px;
    box-shadow: 1px 2px 10px 0 rgba(63, 67, 71, 0.08);
    background-color: #ffffff;

    .toggleblk {
      .edit-block {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 6px;

        div {
          display: flex !important;

          svg {
            width: 19px;
            height: 19px;
            margin-right: 10px;
          }

          .clockCircular {
            g {
              fill: #545456;
            }
          }

          &.active {
            .storage-title {
              color: #35c37d;
              margin-bottom: 0;
            }

            .clockCircular {
              g {
                fill: #35c37d;
              }
            }
          }
        }
      }

      .storage-title {
        font-size: 16px;
        font-weight: bold;
        margin-bottom: 5px;
        line-height: normal;
        display: flex;
        flex-wrap: wrap;

        span {
          font-weight: normal;
        }
      }

      .storage-text {
        font-size: 12px;
        font-weight: 300;
        line-height: 1.25;
        color: #6f6f8c;
        margin: 0;
      }
    }

    &:last-child {
      margin-bottom: 45px;
    }
  }

  .m-b {
    margin-bottom: 20px;
  }
}

.night {
  .submenu {
    .icon {
      &.advanceSettings {
        svg {
          rect {
            fill: #333333 !important;
          }
        }
      }
    }
  }
  .exercise-wrapper {
  .exercise-popup {
    .exercise-heading {
      .main-heading {
        color: #fff;
      }
    }
    .MuiInputBase-root .MuiSelect-iconOutlined {
      color: #f8f8ff;
    }
    .MuiInputBase-root {
      svg {
        color: #f8f8ff;
      }
    }
    .popup__header {
      color: #fff;
    }
    .MuiInputLabel-outlined.MuiInputLabel-shrink {
      background: #262627;
      color: #f8f8ff;
    }
    select.MuiOutlinedInput-input, input.MuiOutlinedInput-input {
      color: #f8f8ff;
      padding-top: 15px;
