      border: 1px solid #f8f8ff;
    }
    .MuiInputLabel-outlined {
      background: #262627;
      color: #f8f8ff;
    }
  }

  .MuiSelect-select:not([multiple]){
    option, optgroup{
      background-color: #333333;
    }
  }
}
  .submenu {
    &.generator {
      .header {
        .menu-name {
          background-color: #333333;
        }
      }
    }
  }
  .scrim-pop-up {
    .popup__grid {
      #popUpWrapper {
        &.thresold-popup {
          .add-new--wrapper {
            background: #262627;
            box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.25);
          }
          .battery-threshold--title {
            background: #333333;
            .battery-threshold--title-text {
            color: #ffffff;
            }
          }
          .queit-setting--parent {
            .icon {
              svg {
                fill: white;
              }
            }
          }
          .battery-threshold {

            .charge-rate {
              .charge {
                color: #f8f8ff;
                font-size: 0.9rem;
              }
              .limit .lower.edit span {
                color: #01b5de;
