              }
            }

            svg {
              g {
                fill: #f8f8ff;
              }
            }
          }
        }
      }
    }
  }

  .scrim-pop-up
    .popup__grid
    #popUpWrapper.exercise-popup
    .MuiInputBase-root
    .MuiSelect-iconOutlined {
    color: #f8f8ff;
  }

  .scrim-pop-up .popup__grid #popUpWrapper.exercise-popup .MuiInputBase-root {
    svg {
      color: #f8f8ff;
    }
  }

  .generator {
    background-color: #262627;

    .storage-gen {
      .storage-mgmter {
        .text-blk {
          color: #fff;
        }
      }
      .weather-watch {
        .weather-watch-title {
          color: #f8f8ff;
        }
      }
      .permission-denied-toast{
        .icon{
          svg{
            g{
              path:first-child{
               stroke: white !important;
               fill: none;
              }
              path:nth-child(2){
                fill: #f8f8ff !important;
                stroke: none;
              }
            }
          }
        }
       }
    }
    svg {
      fill: #f8f8ff;

      g {
        fill: #f8f8ff;

        path {
          fill: #f8f8ff;
        }
      }
    }

    .infoIcon {
      svg {
        g {
          path:nth-child(1) {
            fill: darkgray;
          }
        }
      }
    }

    .edit-btn {
      color: #a2a6a9 !important;
    }

    .exercise {
      background-color: #262627;
      border: 1px solid #f8f8ff;
      color: #f8f8ff;
      &.buttonInActive {
        background-color: #a2a6a9;
