          fill: #fff;
        }
      }

      svg {
        fill: white;
      }
    }

    .quiet-time {
      .add-new {
        color: #a2a6a9 !important;

        svg {
          path {
            fill: #a2a6a9 !important;
          }
        }
      }

      .quiet-time--parent {
        svg {
          fill: white;
        }
      }
    }
  }
  .generator-state-loading-toast{
    svg{
      g{
        circle{
          fill: #545456;
        }
        path{
          fill: #fff;
        }
      }
    }
  }
  .MuiOutlinedInput-root:hover .MuiOutlinedInput-notchedOutline { border: unset; }
  .MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline { border: unset; }
}

.itk {
