.scrim-pop-up {
  .popup__grid {
    #popUpWrapper {
      .success-message {
        height: auto;
        max-height: 10%;
        box-shadow: 1px 2px 10px 0 rgba(63, 67, 71, 0.08);
        background-color: #545456;
        color: #fdfefe;
        padding: 1em 2em;
        width: 100%;
        display: flex;
        font-size: 12px;
