          display: none;
        }

        .icon {
          margin-right: 5px;

          svg {
            height: 20px;
            width: 20px;

            g {
              fill: #ffffff;
            }

            path {
              fill: #ffffff;
            }
          }

          &.tickWhiteSmall {
            svg {
              g {
                stroke: #ffffff;
                fill: none;
              }
            }
          }
        }
      }
