        color: white;
        text-align: center;
        border-radius: 2rem;
        width: 15rem;
        margin: 18px auto 0;
        justify-content: center;
        display: flex;
        font-size: 16px;
        &.buttonCancel {
          background-color: #fff;
          color: #01b5de;
          border: solid 1px rgba(198, 200, 202, 0.5);
        }
        &.buttonDisable {
          background: #c7c8ca;
          border: none;
        }

        &.quiet-margin {
          margin-top: 40px;
        }
        &.grey {
          background-color: #c6c8ca;
          border: transparent;
        }
      }

      &.thresold-popup {
        &.desktop {
          height: fit-content;
          max-width: 470px;
          width: 40%;
