            }
          }
        }
        .add-new--wrapper {
          padding: 15px;
          margin: 0 20px 20px;
          background: aqua;
          border-radius: 10px;
          background-color: #f6f7fb;
          .add-new-subheading {
            line-height: 1.2;
            font-size: 12px;
            color: #7b8793;
            opacity: 0.65;
          }
          .add-new {
            align-items: center;
            cursor: pointer;
            display: flex;
            color: #47b5de;
            margin-bottom: 5px;

            .icon {
              line-height: 0;
            }

            & > div {
              margin: 0 10px;
            }
          }
        }

        .queit-setting--parent {
          display: flex;

          .icon {
            margin-right: 15px;
            svg {
              fill: #545456;
            }
          }
        }

        .edit-queit-time {
          display: flex;
          justify-content: space-between;
          width: 100%;
          font-weight: 500;

          .edit-timer {
            display: flex;
            align-items: center;

            .start-time {
              font-size: 12px;

              span {
                color: #01b5de;
                font-size: 16px;
              }
            }
          }

          .overlay {
            position: absolute;
            left: 50%;
            top: 50%;
            background-color: #000000;
            opacity: 0.6;
            height: 100%;
            width: 100%;
            transform: translate(-50%, -50%);
            z-index: 105;
          }

          .time-wrapper {
            position: absolute;
            bottom: 40%;
            left: 15%;
            z-index: 106;
            opacity: 1;
            transition: opacity 600ms;
            width: min-content;

            &.timeWrapperAvailable {
              left: 115%;
            }
          }
        }
        .minTimeError{
          font-size: 12px;
          position: relative;
          top: 10px;
          color: #E3655E;
        }

        .battery-threshold {
          display: flex;
          flex-direction: column;
          .battery-data--wrapper {

          padding: 20px 30px;
          .battery-data {
            display: flex;
            margin-bottom: 20px;
          }
        }

          &.quiet-timer {
            padding-bottom: 0;
            padding: 0;
            margin: 20px 0;
            flex-direction: row;

            .slider {
              padding: 0px 2rem;
              margin-bottom: 1rem;
            }
          }

          .icon {
            margin-right: 20px;
            &.storageBattery {
              position: relative;
              left: 4px;
            }
          }

          .charge-rate {
            display: flex;
            flex-direction: column;
            width: 100%;
            font-size: 12px;

            .edit-battery-optimzation {
              .slider {
                width: 95%;
                align-self: center;
              }
            }

            .charge {
              color: #545456;
            }

            .limit {
              display: flex;
              justify-content: space-between;
              font-weight: 300;
              margin-top: 5px;

              .upper {
                font-weight: 500;
                &.edit {
                  span {
                    color: #01b5de;
                    font-size: 16px;
                  }
                }
              }

              .lower {
                font-weight: 500;
                &.edit {
                  span {
                    color: #01b5de;
                    font-size: 16px;
                  }
                }
              }
            }
          }
        }
      }

      &.exercise-popup {
        .popup__body-content {
          padding: 25px;
        }

        .MuiInputLabel-outlined.MuiInputLabel-shrink {
          background: white;
        }

        select.MuiOutlinedInput-input, input.MuiOutlinedInput-input {
          color: #00b6de;
          padding-top: 15px;
          padding-bottom: 15px;
        }

        top: auto;
        .setting-parent {
          display: flex;
          flex-wrap: wrap;
          .individual-setting {
            flex: 0 50%;
            &:nth-child(2n-1) {
              padding-right: 10px;
            }
            &:nth-child(2n) {
              padding-left: 10px;
            }
          }
        }
        .makeStyles-formControl-5 {
          margin: 8px 0;
        }
      }

      &.generator-info {
        &.desktop {
          height: fit-content;
          width: 40%;
          .close_popup {
            position: relative;
            bottom: 4px;
          }
          .popup__body {
            .popup__body-content {
              margin: 0 30px 20px;
            }
          }
        }
        top: auto;

        .popup__header {
          min-height: 50px;
          height: auto;
        }

        .popup__body {
          .popup__body-content {
            padding: 20px;
          }
        }

        .info-popup {
          a {
            color: #01b5de;
          }
        }

        .details-button {
          display: flex;
          align-items: center;
          margin: 15px 0 0;
          padding: 0;
          border: unset;
          font-size: 12px;
          font-weight: 500;
          line-height: 1.29;
          color: #01b5de;
          background: unset;
          font-size: 14px;

          .icon {
            width: auto;
            height: 16px;
            margin-left: 5px;
          }
        }
      }
    }
  }
}

@media only screen and (device-width: 375px) and (device-height: 812px) {
  .menu-list {
    &.desktop {
      .submenu {
        &.generator {
          .container {
            height: calc(100% - 134px - #{$footer-height}) !important;
          }
        }
      }
    }
  }
}

