.exercise-wrapper {
  height: 100%;
  .success-message {
    position: absolute;
    height: auto;
    max-height: 10%;
    box-shadow: 1px 2px 10px 0 rgba(63, 67, 71, 0.08);
    background-color: #545456;
    color: #fdfefe;
    padding: 1em 2em;
    width: 100%;
    display: flex;
    font-size: 12px;
    font-weight: normal;
    align-items: center;
    z-index: 201;

    &.disabled {
      display: none;
    }

    .icon {
      margin-right: 5px;

      svg {
        height: 20px;
        width: 20px;

        g {
          fill: #ffffff;
        }

        path {
          fill: #ffffff;
        }
      }

      &.tickWhiteSmall {
        svg {
          g {
            stroke: #ffffff;
            fill: none;
          }
        }
      }
    }
  }
.exercise-popup {
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;
  .pending {
    color: #a2a6a9;
    margin: 0 5px;
    font-size: 12px;
    &.buttonInActive {
      color: #ffffff;
    }
  }
  .light {
    font-weight: 300;
  }
  .exercise-heading {
    font-size: 16px;
    margin-bottom: 18px;
  }

  .exercise-subheading {
    font-size: 14px;
    @include body-1;
  }
  .save-button {
    box-shadow: 1px 2px 10px 0 rgba(63, 67, 71, 0.08);
    border: 1px solid #00b6de;
    padding: 10px 0;
    background: #00b6de;
    color: white;
    text-align: center;
    border-radius: 2rem;
    width: 15rem;
    margin: 18px auto 0;
    justify-content: center;
    display: flex;
    font-size: 16px;
    position: absolute;
    bottom: 8%;
    left: calc(50% - 7.5rem - 0px);
    &.buttonCancel {
      background-color: #fff;
      color: #01b5de;
      border: solid 1px rgba(198, 200, 202, 0.5);
    }
    &.buttonDisable {
      background: #a2a6a9;
      border: none;
    }

    &.quiet-margin {
      margin-top: 40px;
    }

  }
  padding: 20px;
  .popup__body-content {
    padding: 25px;
  }

  .MuiInputLabel-outlined.MuiInputLabel-shrink {
    @include title-1;
    background: white;
  }

  select.MuiOutlinedInput-input, input.MuiOutlinedInput-input {
    color: #00b6de;
    padding-top: 15px;
    padding-bottom: 15px;
    border-bottom: unset;
    @include title-1;
  }

  top: auto;
  .setting-parent {
    display: flex;
    flex-wrap: wrap;
    .individual-setting {
      flex: 0 50%;
      position: relative;
      &:nth-child(2n-1) {
        padding-right: 10px;
      }
      &:nth-child(2n) {
        padding-left: 10px;
      }

      .time-wrapper {
        position: absolute;
        right: 0;
        top: 100%;
        z-index: 2;
      }
    }
  }
  .MuiFormControl-root{
    margin: 8px 0;
    width: 100%;
  }
  .MuiOutlinedInput-root {
    border-radius: 10px;
}
}
}

