        .data-area {
          border-bottom: none;
          font-size: 15px;
          text-align: center;
          padding: 20px;
          .enable-disable--parent {
            text-align: center;
            padding: 0;
          }
        }
        .title-area {
          text-align: center;
          padding: 20px;
        }
      }
    }
  }

  .time-wrapper-overlay {
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    top: 0;
    -webkit-backdrop-filter: blur(2px);
    backdrop-filter: blur(2px);
    background-color: rgba(0, 0, 0, 0.75);
    z-index: 2;
  }

  .time-wrapper {
    position: fixed;
    left: 50%;
    top: 50%;
    z-index: 3;
    transform: translate(-50%, -50%);
  }
