@import "../fonts";
.custom-header {
  position: absolute;
  font-weight: 400;
  width: 100%;
  height: 100%;
  background-color: #fafafb;

  .ch-header {
    justify-content: flex-start;
    height: 105px;
    background-color: #01b5de;
    display: flex;
    align-items: center;
    padding: 60px 20px 15px 10px;
    margin: 0 auto;
    color: #fff;
    font-size: 19px;
    font-weight: 400;

    .icon {
      display: inline-block;
      margin-left: 8px;
      margin-right: 15px;
      height: 32px;
      fill: #fff;
    }
  }

  .ch__dialog-wrapper {
    background-color: #fff;
    height: calc(91% - 105px);
    overflow-y: auto;
    scroll-behavior: smooth;
    position: relative;
  }
}

.night {
  .custom-header {
    .ch-header {
      background-color: #333;
    }

    .ch__dialog-wrapper {
      background-color: #262627;
    }
  }
}