@import "fonts";
.device-card-inverters {
  height: calc(100% - 16px);
  overflow: auto;

  .loading-info {
    font-size: 12px;
    color: #7b8793;
    margin-bottom: 21px;
    div {
      text-align: center;
    }
    .icon.loading  {
      margin-top: 10px;
      g {
        path {
          fill: #7B8793;
        }
      }
    }
  }
}

.device-card-storage {
  box-shadow: 0 2px 10px 0 rgba(63, 67, 71, 0.08);
  background-color: #ffffff;
  padding: 12px 20px 20px;

  .device-card-storage__wrapper {
    display: flex;
    line-height: normal;
    align-items: center;
  }

  .device-card-storage__overview {
    margin-left: 30px;
  }

  .device-card-view__info{
    width: 45px; 
  }

  .device-card-storage__chart {
    position: relative;
  }

  .device-card-storage__wrapper {
    &.energy-details, &.power-details {
      flex-direction: column;
      align-items: initial;
    }
  }

  .device-card-storage__label {
    display: flex;
    align-items: center;
    font-size: 12px;
    font-weight: normal;
    margin-top: 4px;
    color: #8e9091;
  }

  .device-card-storage__details {
    margin-bottom: 12px;
    margin-top: 2px;
    font-size: 14px;
    font-weight: 500;
    color: #545456;

    &.power-details {
      margin-bottom: 0;
    }

    .device-card-storage__value {
      font-size: 20px;
      font-weight: bold;
      @include title-2;
    }

    .device-card-storage__total {
      font-size: 20px;
      font-weight: 300;
      @include title-2;
    }
  }

  .device-card-storage__backup-time {
    margin-bottom: 12px;

    .battery-backup-time {
      font-size: 14px;
      font-weight: 300;
      color: #545454;
      @include title-2;
    }

    .battery-backup-time-info {
      cursor: pointer;
    }

    .infoIcon {
      margin-left: 5px;
    }

    .infoIcon, .infoIcon svg {
      height: 14px;
      width: 14px;
    }
  }

  .device-card-storage__active-encharge-details {
    font-size: 12px;
    font-weight: 300;
    letter-spacing: -0.1px;
    color: #545456;
    margin-top: 3px;

    .infoIcon {
      display: none;
    }

    &.device-card-storage__error {
      display: flex;
      color: #ff6464;

      .infoIcon {
        display: block;
        margin-left: 5px;

        svg {
          path:first-child {
            fill: #ff6464;
          }
        }
      }

      .infoIcon, .infoIcon svg {
        width: 14px;
        height: 14px;
      }
    }
  }
}

.device__details {
  display: block;
  overflow: auto;
  .device-card-view{
    .device-card-view__wrapper {
      .device-card-view__body{
        .device-card-view__content{
          .device-card-view__content-wrapper{
            .device-card-view__title{
              font-size: 15px;
              font-weight: 400;
              @include title-2;
            }
          }
        }
      }
    }
  }
}
.device-loads__try-connecting-envoy__refresh{
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}

.device-card-view{
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  background-color: #fdfefe;

  &:hover{
    cursor: pointer;
    background-color: #fdfefe;
  }

  .icon{
    height: 100%;

    svg{
      height: 100%;
      margin: 0;
    }
  }

  .device-card-view__wrapper{
    margin: 20px;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 2px 10px 0 rgba(63, 67, 71, 0.08);

    &.device-card-view__show-status{
      .device-card-view__body{
        &>div:not(.device-card-view__content){
          margin-left: 20px;
        }
      }

      &:before{
        content: '';
        width: 10px;
        height: 10px;
        display: block;
        float: left;
        margin-right: 10px;
        margin-top: 4px;
        border-radius: 50%;
      }
    }
  }

  .device-card-view__body{
  }

  .device-card-view__icon{
    width: auto;
    height: 12px;
  }

  .device-card-view__content{
    display: flex;
    justify-content: space-between;
    word-break: break-all;
    margin-bottom: 15px;
    &.dryContactLoad{
      margin-bottom: 0px;
    }

    .device-card-view__title{
      font-size: 14px;
      color: #545456;
      margin-bottom: 6px;
    }

    .device-card-view__serial-number,
    .device-card-view__sku-number,
    .device-card-view__model{
      font-size: 14px;
      font-weight: 300;
      color: #3f4347;
      margin-top: 2px;
      @include body-1;
    }

    .device-card-view__available-energy {
      margin-top: 15px;

      .device-card-view__available-energy__header {
        font-size: 12px;
        font-weight: normal;
        color: #a2a6a9;
      }

      .device-card-view__available-energy__body {
        margin-top: 2px;
        font-size: 14px;
        font-weight: 300;
        color: #545456;
        @include body-1;
      }
    }
  }

  .device-card-view__available-power {
    margin: 15px 0;

    .device-card-storage__label {
      font-size: 12px;
      font-weight: normal;
      color: #a2a6a9;
    }

    .device-card-view__available-energy__body {
      margin-top: 2px;
    }

    .device-card-storage__total,
    .device-card-storage__value,
    .device-card-storage__active-encharge-details {
      font-size: 14px;
      font-weight: 300;
      color: #545456;
      @include body-1;
    }

    .device-card-storage__active-encharge-details {
      .infoIcon {
        display: none;
      }
    }
  }

  .device-card-view__action{
    border-top: 1px solid rgba(123, 135, 147, 0.3);
    margin-top: 15px;
    padding-top: 15px;

    .device-card-view__action__connect-locally{
      align-items: center;
      display: flex;
      justify-content: space-between;
     .device-card-view__connect-locally{
      font-size: 16px;
      color: #01b4de;
      .device-card-view__subTitle{
        font-size: 12px;
        color: #7b8793;
      }
     }
      span{
        margin-left: 10px;

        &.icon{
          display: flex;

          path{
            fill: #01b4de;
          }
        }
      }
    }

    .device-card-view__configure_wifi{
      display: flex;
      justify-content: space-between;
      align-items: center;
      .device-card-view__action__configure_wifi{
        font-size: 16px;
        color: #01b4de;
        .device-card-view__subTitle{
          font-size: 12px;
          color: #7b8793;
        }
      }
      span{
        margin-left: 10px;

        &.icon{
          display: flex;

          path{
            fill: #01b4de;
          }
        }
      }
    }
  }

  .device-card-view__info{
    min-width: 50px;
    margin-left: 10px;
    text-align: right;

    >div:not(:first-child){
      margin-top: 10px;
    }

    .device-card-view__zigbee-signal{
      display: flex;
      align-items: center;

      .icon{
        &.communicating{
          width: 25px;
          margin-right: 5px;
          display: flex;
          align-self: flex-end;
        }
      }

      .dcv__zigbee-signal__title{
        display: block;
        font-size: 12px;
        line-height: 16px;
        color: #a2abb3;
        white-space: nowrap;
      }

      .dcv__zigbee-signal__value{
        margin: 0;
        font-size: 14px;
        line-height: 1;
        font-weight: 500;
        color: #545456;
      }

      .dcv__zigbee-signal__info{
        height: 14px;
        width: 14px;
        padding: 0;
        border: unset;
        display: inline-block;
        background: transparent;
        margin-left: 5px;

        .infoIcon svg{
          width: inherit;
          height: inherit;
        }
      }
    }

    .device-card-view__battery{
      display: inline-flex;
      height: 12px;
      font-weight: 500;
      color: #7b8793;
      font-size: 12px;
      padding-top: 1px;

      span.icon{
        margin-right: 2px;
      }
    }

    .device-card-view__help{
      display: inline-flex;
      height: 14px;
      font-weight: 500;
      color: #01b4de;
      cursor: pointer;

      span.icon{
        margin-right: 5px;
      }
    }
  }

  .device-card-view__device-connection,
  .device-card-view__status,
  .device-card-view__profile-status,
  .device-card-view__encharge-error-wrapper,
  .device-card-view__last-reported-date,
  .device-card-view__firmware-version,
  .device-card-view__cycle-count,
  .device-card-view__load-control-status,
  .device-card-view__mode,
  .device-card-view__auxillary-contact,
  .device-card-view__battery-mode,
  .device-card-view__array{
    font-size: 12px;
    font-weight: 400;
    color: #7b8793;
    margin-top: 2px;
    word-break: break-word;

    span{
      margin-left: 2px;

      &.noMargin{
        margin: 0px;
      }
    }
  }
  .device-card-view__mode{
    text-transform: capitalize;
  }
  .device-card-view__firmware-version-heading{
    font-size: 12px;
    font-weight: 500;
    padding: 5px 0px 1px 0px;
  }
  .device-card-view__status{
    display: flex;
    align-items: center;
    column-gap: 5px;
    span:first-child{
      margin-left: 0;
    }
    .loading{
      height: 14px;
    }
  }

  .device-card-view__connection-type {
    .device-card-view__title {
      font-size: 16px;
      color: #545456;
      margin-bottom: 8px;
      font-weight: 400;
    }
  }

  .device-card-view__power-error-wrapper {
    margin-top: 2px;

    .device-card-view__power-error-text {
      display: flex;
      align-items: center;
      color: #ff5959;
      font-size: 12px;
      font-weight: 300;

      .infoIcon {
        margin-left: 5px;

        svg {
          path:first-child {
            fill: #ff6464;
          }
        }
      }

      .infoIcon, .infoIcon svg {
        width: 14px;
        height: 14px;
      }
    }
  }

  .device-card-view__status__encharge-error{
    margin: 15px 0 0;

    .device-card-view__label{
      display: flex;
      align-items: center;
      color: #a2a6a9;
      font-weight: 400;
    }

    .device-card-view__value{
      margin-top: 2px;
    }

    .infoIcon {
      width: 14px;
      height: 14px;
      display: inline-block;
      vertical-align: sub;
      margin-left: 5px !important;

      svg {
        width: inherit;
        height: inherit;

        path:first-child {
          fill: #ff6464;
        }
      }
    }
  }
  .device-card-view__cycle-count {
    .icon{
      display: inline-flex;
      vertical-align: bottom;
      margin-left: 5px;

      svg{
        width: 14px;
      }
    }
  }
  .device-card-view__led-status{
    margin-top: 15px;

    .device-card-view__led-status__header{
      font-size: 12px;
      color: #a2a6a9;

      .icon{
        display: inline-flex;
        vertical-align: bottom;
        margin-left: 5px;

        svg{
          width: 14px;
        }
      }
    }

    .device-card-view__led-status__body{
      margin-top: 5px;

      .device-card-view__led-status__item{
        display: flex;
        align-items: flex-start;
      }

      .led-status__info{
        font-size: 14px;
        font-weight: 300;
        color: #3f4347;
        @include body-1;
      }
    }
  }

  &.device-card-view__status-normal{
    .device-card-view__show-status{
      &:before{
        content: '';
        background-color: #69dba5;
      }

      .device-card-view__action{
        margin-left: 20px;
      }
    }
  }

  &.device-card-view__status-warning{
    .device-card-view__show-status{
      &:before{
        content: '';
        background-color: #F37320;
      }

      .device-card-view__action{
        margin-left: 20px;
      }
    }

    .device-card-view__status{
      .device-card-view__value{
        color: #F37320;
      }
    }

    .device-card-view__status__encharge-error{
      .infoIcon{
        svg{
          path:first-child{
            fill: #F37320;
          }
        }
      }
    }
  }

  &.device-card-view__status-error{
    .device-card-view__show-status{
      &:before{
        content: '';
        background-color: #ff5959;
      }

      .device-card-view__action{
        margin-left: 20px;
      }
    }

    .device-card-view__status{
      .device-card-view__value{
        color: #ff5959;
      }
    }
  }
}

.device-details-popup {
  &.encharge-cycle-count{
    .popup__body {
      .popup__body-content {
        padding: 20px;
      }
    }
    .details-button {
      display: flex;
      align-items: center;
      margin: 15px 0 0;
      padding: 0;
      border: unset;
      font-size: 14px;
      font-weight: 500;
      line-height: 1.29;
      color: #01b5de;
      background: unset;
      @include body-1;
      .icon {
        width: auto;
        height: 16px;
        margin-left: 5px;
      }
    }
  }
  &.popup__mobile {

    &.encharge-error, &.power-error {
      height: 300px;
      position: fixed;
      left: 0;
      top: 0;
      bottom: 0;
      width: 300px;
      z-index: 105;
      right: 0;
      margin: auto;
      border-radius: 10px;
  
      .popup__header {
        display: none;
      }

      .popup__body {
        height: 100%;
        
        .popup__body-content {
          height: 100%;
        }
      }
    }
    
    &.power-error {
      border-radius: 10px;
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
      
      .inactive-micros-details {
        .imd__detail-view {
          .imd__content {
            padding: 20px 0;
            border: none;
          }
        }
      }
      .pec__close {
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 16px;
        font-weight: 500;
        height: 56px;
        border-bottom-left-radius: 10px;
        border-bottom-right-radius: 10px;
        border-top: solid 1px #d8dcde;
        background-color: #ffffff;
        color: #01b4de;
        cursor: pointer;
      }
    }
  }
}

.browser{
  .device-card-storage {
    .device-card-storage__details {
      .device-card-storage__backup-time {
        display: flex;
        align-items: center;
        margin-top: 7px;

        .battery-backup-time {
          font-size: 17px;
          font-weight: 300;
          color: #545456;
        }

        .infoIcon {
          margin-left: 5px;
        }

        .infoIcon, .infoIcon svg {
          height: 16px;
          width: 16px;
        }
      }
    }

    .device-card-storage__active-encharge-details {
      font-size: 15px;
      line-height: 1.4;
      color: #545456;
      margin-top: 4px;

      .infoIcon {
        margin-left: 5px;
        cursor: pointer;
      }

      .infoIcon, .infoIcon svg {
        height: 16px;
        width: 16px;
      }
    }
  }
}

.night {
  .device-card-storage {
    background-color: #262627;
    box-shadow: 0 2px 10px 0 rgba(0,0,0,.25);

    .device-card-storage__details  {
      color: #f6f6fc;
    }

    .device-card-storage__backup-time {
      .battery-backup-time {
        color: #f6f6fc;
      }
    }

    .device-card-storage__active-encharge-details {
      color: #8e9091;

      &.device-card-storage__error {
        color: #ff6464;
      }
    }
  }

  .device-card-view {
    background-color: #262627;
    .device-card-view__wrapper{ 
      box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.25);
    }

    .device-card-view__content {
      .device-card-view__title {
        color: #f8f8ff;
      }

      .device-card-view__serial-number,
      .device-card-view__sku-number,
      .device-card-view__model{
        color: #d8dcde;
      }

      .device-card-view__available-energy {
        .device-card-view__available-energy__body {
          color: #d8dcde;
        }
      }
    }

    .device-card-view__available-power {
      .device-card-storage__value, .device-card-storage__total, .device-card-storage__active-encharge-details {
        color: #d8dcde;
      }
    }

    .device-card-view__device-connection,
    .device-card-view__status,
    .device-card-view__profile-status,
    .device-card-view__last-reported-date,
    .device-card-view__encharge-error-wrapper,
    .device-card-view__firmware-version,
    .device-card-view__cycle-count,
    .device-card-view__load-control-status,
    .device-card-view__mode,
    .device-card-view__auxillary-contact,
    .device-card-view__battery-mode,
    .device-card-view__array{
      color: #8e9091;
    }
    .device-card-view__firmware-version-heading{
      color: #d8dcde; 
    }

    .device-card-view__led-status{
      .led-status__info{
        color: #8e9091;
      }
    }

    .device-card-view__zigbee-signal{
      .dcv__zigbee-signal__value{
        color: #f6f6fc;
      }
    }
  } 

  .device-card-view__battery {
    color: #8e9091!important;

    .icon svg g path:nth-last-child(2) {
      fill: #262627;
      stroke: #262627;
    }

    .icon svg g path:last-child {
      fill: #8E9091;
    }
  }

  .device-details-popup {
    &.popup__mobile {
      &.power-error {
        .popup__body .popup__body-content .power-error-content {
          color: #d8dcde;

          .pec__close {
            background-color: #262627;
            border-color: #333537;
          }
        } 
      }
    }
  }
}


/* Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) {
  .device-details-popup {
    &.popup__mobile {
  
      &.encharge-error, &.power-error {
        width: 335px;
      }
    }
  }
}

