.postlogin__status {
    width: 100%;
    height: calc(100% - #{$footer-height});
    position: relative;

    .postlogin__status-page {
        width: 100%;
        height: calc(100% - 50px);
        background: #FFFFFF;
        position: relative;
    }

    .postlogin__status-header {
        padding: 25px 30px;
        background: #ffffff;
        position: relative;
        z-index: 1;
        padding-bottom: 30px;

        .postlogin__status-actions--wrapper {
            display: flex;
            justify-content: space-between;
        }

        .postlogin__status-actions {
            margin-top: 16px;
            display: flex;
            justify-content: space-between;

            button {
                padding: 0;
                display: flex;
                border: unset;
                font-size: 16px;
                font-weight: 500;
                line-height: 21px;
                color: #01B4DE;
                background: unset;
                align-items: center;

                .icon {
                    height: 21px;
                    margin-right: 3px;

                    &.mapLocation {
                        svg path:first-child { fill : #01B4DE; }
                    }

                    &.smartPhonePlain {
                        svg {
                            width: 24px;
                            height: 27px;
                            position: relative;
                            bottom: 2px;
                            g {
                                g {
                                    path {
                                        fill : #01B4DE;
                                    }
                                    g {
                                        path {
                                            fill : #fff;
                                        }
                                        
                                    }
                                }
                            }
                        }
                    }

                    &.playVideoPlain,
                    &.playVideoPlain svg {
                        width: 24px;
                        height: 27px;
                    }
                }
            }
        }
    }

    .postlogin__status-bg {
        width: 100%;
        height: calc(100% - 150px);
        position: absolute;
        left: 0;
        bottom: 0;
        overflow: hidden;
        background: center center url('../images/post-login-bg.png') no-repeat;
        background-size: cover;

        .postlogin__demo-btn {
            width: calc(100% - 60px);
            margin: 0 30px;
            padding: 15px 15px 14px;
            border: unset;
            border-radius: 25px;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 16px;
            font-weight: 500;
            line-height: 21px;
            color: #fff;
            background: #00b6de;
            position: absolute;
            bottom: 30px;

            &:disabled {
                opacity: .75;
                cursor: not-allowed;
            }

            .icon {
                height: 21px;
                margin-right: 3px;
            }
        }
    }

    .postlogin__status-ios { height: calc(100% - 84px - #{$footer-height}); }
}

h4.postlogin__title {
    font-size: 16px;
    font-weight: 500;
    line-height: normal;
    letter-spacing: 0.3px;
    color: #5F6062;
}

.postlogin__menu ul li { max-width: 50%; }

.menu-list__card-view {
    margin: 10px 10px 0;
    padding: 0 !important;
    display: flex;
    flex-wrap: wrap;
    box-sizing: border-box;

    & > div:last-child { width: 100%; }

    .list-view__icon {
        width: 32px !important;
        height: 25px !important;
        margin: 0 !important;

        .icon { text-align: left !important; }

        svg { width: auto !important; }
    }

    .cardMenuItem {
        margin: 10px;
        width: calc(50% - 20px);

        .card-view {
            border-radius: 10px;
            box-shadow: 0px 2px 10px #3F434714;

            .card-view__wrapper {
                padding: 23px 20px 20px;

                .card-view__content {
                    flex-direction: column;

                    .list-view__content-wrapper {
                        margin-top: 5px;

                        .card-view__title {
                            font-size: 16px;
                            font-weight: bold;
                            @include title-1;


                        }
                    }
                }
            }
        }
    }

    .list-view__logout {
        background: transparent;

        .list-view__wrapper {
            padding: 15px 10px;

            .list-view__content {
                align-items: center;

                .list-view__icon {
                    width: 20px !important;
                    height: 20px !important;
                    margin-right: 8px !important;
                }
            }
        }
    }
}

.system-estimator .postlogin__status {
    height: 100%;
    overflow: hidden;
    &.postlogin__status-scroll{
        overflow: auto;
    }

    .postlogin__status-ios { height: calc(100% - 84px) !important; }

    .header-support-wrapper {
        padding: 14px 15px;

        .support-logo .icon.supportMenu,
        .support-logo .icon.supportMenu svg {
            height: 22px;

            path {
                &:first-child {
                    stroke: #ffffff;
                    fill: #fff;
                }
                &:nth-child(2),
                &:last-child {
                    stroke: #00b6de;
                    fill: #00b6de;
                }
            }
        }
    }
}

.se-menu-list {
    .se-menu-list__body {
        .list-view__wrapper {
            padding: 24px 0;
            margin: 0 32px;
            font-size: 17px;
            border-bottom: 1px solid #D7DBDE;

            &.card-view__wrapper {
                padding: 18px 20px;
                margin: 0;
            }

            .list-view__content {
                align-items: flex-start;

                .list-view__icon {
                    width: 20px !important;
                    height: 20px !important;
                    margin-top: 3px;

                    .icon {
                        width: 100%;
                        height: 100%;
                    }
                    .icon svg {
                        width: auto;
                        height: 100%;
                    }
                }

                .list-view__title { line-height: 21px; }

                .list-view__subtitle {
                    margin: 0 !important;
                    line-height: 16px;
                }
            }
        }

        & > div:last-child .list-view__wrapper { border-bottom: unset; }

        &.se-menu-list__body-support {
            height: 100% !important;
            max-width: 100% !important;
            margin: 0 !important;
            background: #f6f7fb !important;
            box-shadow: unset !important;

            .ls__dialog-wrapper {
                display: flex;
                height: 100%;

                & > div:last-child { flex-grow: 1; }
            }

            .support-side-panel {
                width: 25%;
                min-width: 325px;
                height: 100%;
                padding: 0 32px;
                background: #FFFFFF;

                & > div {
                    padding: 28px 0;
                    border-bottom: 0.5px solid rgba(215, 219, 222, 0.5);
                    font-size: 20px;
                    line-height: 1;
                    color: #545456;

                    &.active {
                        font-weight: 500;
                        color: #01B5DE;
                    }

                    &:last-child { border-bottom: unset; }
                }
            }

            .support-new__frequentlyAsked {
                margin: 12px 20px 0;
                border-radius: 3px;
                background: #FFFFFF;
                box-shadow: 0px 2px 10px #3f434714;

                .support-new__title {
                    margin: 0 32px;
                    padding: 25px 0;
                    font-size: 20px;
                    font-weight: 500;
                    border-bottom: 0.5px solid #7B87934D;
                }

                .list-view__title {
                    color: #01B4DE;
                }
            }

            .wosite_popup-container {
                width: 100%;
                height: 100%;
                background-color: rgba(0, 0, 0, 0.75);
                -webkit-backdrop-filter: blur(4px);
                backdrop-filter: blur(4px);
                position: fixed;
                left: 0;
                bottom: 0;
                z-index: 999;

                .popup-wrapper {
                    padding: 20px 30px;
                    border-radius: 10px;
                    background: #fff;
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    transform: translate(-50%, -50%);

                    .popup-title-wrapper {
                        display: flex;
                        justify-content: space-between;
                        margin-bottom: 12px;
                        font-size: 18px;
                        line-height: 1;
                        font-weight: 500;
                    
                        .close-button {
                            width: 20px;
                            height: 20px;
                            display: block;

                            .icon.close {
                                height: 20px;
                                position: relative;
                                right: 0;
                                top: 0;

                                svg {
                                    width: auto;
                                    height: 100%;
                                }
                            }
                        }
                    }

                    .fcs__wrapper { padding: 0; }
                }
            }
        }

        .notices {
            height: 100%;
            opacity: 1;

            &::after { display: none; }

            .content,
            .noNotice { height: 100%; }
        }
    }
}

.browser {
    h4.postlogin__title {
        font-size: 26px;
        line-height: 34px;
    }

    .postlogin__demo-block {
        .postlogin__installet-site {
            width: 25%;
            height: 100%;
            padding: 0 32px;
            border-right: solid 1px rgba(123, 135, 147, 0.3);

            & > div {
                padding: 32px 10px;
                border-bottom: 1px solid #7B87934D;

                &:last-child { border-bottom: unset; }
            }

            h6 {
                margin: 0;
                font-size: 17px;
                font-weight: 700;
                line-height: 23px;
                color: #7B8793;
            }

            p {
                margin: 16px 0 0;
                font-size: 17px;
                font-weight: normal;
                line-height: 24px;
                color: #545456;
                word-break: break-all;

                &.site-id { margin-top: 5px; }

                &.with-icon {
                    display: flex;
                    align-items: flex-start;
                    margin-top: 11px;

                    .icon {
                        height: 24px;
                        margin-right: 15px;
                        align-self: flex-start;

                        svg {
                            width: 16px;
                            height: auto;

                            & > path { fill: #545456; }
                        }
                    }
                }

                &.first-child { margin-top: 35px; }
            }
        }

        h5 {
            margin: 12px 0 0;
            font-size: 25px;
            font-weight: 500;
            line-height: 33px;
            color: #545456;
        }

        .energy-layout {
            width: 50% !important;
            padding: 32px;
            left: 25% !important;
            justify-content: flex-start !important;

            .postlogin__demo-web {
                max-width: 600px;
                margin: 0 auto;

                h5 {
                    margin-top: 35px;
                    text-align: center;
                }
            }
        }

        .device-carousel-wrapper { width: 25% !important; }
    }
}

.se-menu-list__webnav {
    .list-view__wrapper {
        padding: 25px 0;
        margin: 0 30px;
        border-bottom: 1px solid #7B87934D;
        font-size: 20px;
        line-height: 1;
        font-weight: 500;
        color: #6F6F6F;

        .list-view__content {
            align-items: center;

            .list-view__icon {
                width: 32px;
                height: 32px !important;

                .icon svg { max-width: 100%; }
            }

            .list-view__title {
                font-size: 20px;
                color: #6F6F6F;
            }
        }
    }

    .menu-list__body-wrapper {
        padding-bottom: 86px !important;

        & > div:last-child .list-view__wrapper { border-bottom: unset; }

        .accordion-wrapper {
            .accordion-title-wrapper {
                padding: 25px 0;
                margin: 0 30px;
                display: grid;
                grid-template-columns: auto auto;
                border-bottom: 1px solid #7B87934D;
                font-size: 20px;
                line-height: 1;

                .accordion-left {
                    display: flex;
                    align-items: center;
                    font-weight: 500;
                    color: #6F6F6F;

                    .list-view__icon {
                        width: 32px;
                        height: 32px !important;
                        margin-right: 15px;
        
                        .icon,
                        .icon svg { height: 100%; }
                    }
                }

                .icon.dropdown {
                    height: auto;
                    display: flex;
                    justify-content: flex-end;
                    align-self: center;
                    text-align: right;

                    & svg {
                        width: 24px;
                        height: auto;
                    }
                }

                &.open {
                    border-bottom: unset;

                    .icon.dropdown {
                        justify-content: flex-start;
                        transform: rotate(-180deg);
                    }
                }
            }

            .menu-settings-wrapper {
                border-top: 1px solid #7B87934D;
                box-shadow: inset 0px 0px 10px #3F434714;
                background: #F6F7FB 0% 0% no-repeat padding-box;

                .menu-list__body-wrapper {
                    padding-bottom: 0 !important;

                    .list-view {
                        background: transparent;

                        .list-view__wrapper {
                            padding: 24px 0;
                            font-weight: normal;

                            .list-view__content {
                                align-items: flex-start;

                                .list-view__icon {
                                    width: 20px;
                                    height: auto !important;
                                    margin: 4px 15px 0 0;
                    
                                    .icon svg { max-width: 100%; }
                                }

                                .list-view__title { line-height: 26px; }

                                .list-view__subtitle {
                                    font-size: 16px;
                                    line-height: 21px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    .web_refer-friends {
        width: 325px;
        position: fixed;
        bottom: 0;
        right: 0;

        .list-view__wrapper {
            padding: 7px 22.5px;
            margin: 0;
            border-bottom: unset;
            /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#f37421+0,f9a441+100 */
            background: #f37421; /* Old browsers */
            background: -moz-linear-gradient(left,  #f37421 0%, #f9a441 100%); /* FF3.6-15 */
            background: -webkit-linear-gradient(left,  #f37421 0%,#f9a441 100%); /* Chrome10-25,Safari5.1-6 */
            background: linear-gradient(to right,  #f37421 0%,#f9a441 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
            filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f37421', endColorstr='#f9a441',GradientType=1 ); /* IE6-9 */

            .list-view__icon {
                width: 67px;
                height: 72px !important;
                margin: 0;
                padding: 15px 25px 15px 0;
                border-right: 1px solid #FFFFFF;
            }

            .list-view__content-wrapper {
                margin-left: 20px;
                
                .card-view__title { color: #FFFFFF; }
            }
        }
    }
}
.se-menu-list__webcontent {
    .explore-enphase-page .eep__list__wrapper {
        height: auto;
        display: grid;
        grid-template-columns: repeat(2, 1fr);

        .search-input { grid-column: 1 / -1; }
    }

    .pre-login__card-menus.support__options {
        width: calc(100% - 64px);
        grid-gap: 32px;

        .pre-login__menu-card {
            padding: 32px;

            .icon { height: 40px; }

            p {
                max-width: 100%;
                margin-top: 15px;
                font-size: 20px;
                line-height: 1;
            }
        }
    }
}

.night{
    .postlogin__status {
        .postlogin__status-page,  .postlogin__status-header{
            background: #272728;
        }

        .header-support-wrapper {
            .support-logo .icon.supportMenu,
            .support-logo .icon.supportMenu svg {
                path {
                    &:nth-child(2),
                    &:last-child {
                        stroke: #333333;
                        fill: #333333;
                    }
                }
            }
        }
    }
    h4.postlogin__title{
        color: white;
    }
    .menu-list__card-view {
        .cardMenuItem {
            .card-view {
                background-color: #333;
                box-shadow: 0px 2px 10px rgba(0,0,0,0.05);

                .card-view__wrapper .card-view__content {
                    .list-view__icon {
                        .icon.supportMenu {
                            path:first-child {
                                fill: none;
                                stroke: #f8f8ff;
                            }
                        }

                        .icon.exploreEnphase {
                            ellipse { stroke: #f8f8ff; }
                        }

                        .icon.community {
                            path {
                                fill: none;
                                stroke: #f8f8ff;
                            }
                            line { stroke: #f8f8ff; }
                        }

                        .icon.systemAccess {
                            path {
                                fill: #f8f8ff;
                                stroke: #f8f8ff;
                            }
                        }
                    }

                    .list-view__content-wrapper .card-view__title {
                        color: #ffffff;
                    }
                }
            }
        }
    }
    .se-menu-list {
        .se-menu-list__body {
            .list-view__wrapper {
                border-bottom-color: rgba(123,135,147,.30196078431372547);
            }

            .notices .content,
            .notices .noNotice { background: #262627; }
        }
    }
    .pre-login__menu-card {
        background-color: #333;

        p { color: #ffffff !important; }
    }
    .siteempty { background-color: #262627; }

    .menu-list__brand-icon-and-app-version span:last-child { color: #ffffff !important; }
}

