@media (min-width: 576px) { }

/* 768px and up */
@media (min-width: 768px) {
    .postlogin__status-ios { height: calc(100% - 94px - #{$footer-height}); }
    .system-estimator .postlogin__status .postlogin__status-ios { height: calc(100% - 94px) !important; }
}

