@import "fonts";
.events-history-page{
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;

  &.ehp__web {
    width: 60%;
    height: 100%;
    overflow: auto;
    margin: 30px auto;
    border: 1px solid #ffffff;
    background-color: #ffffff;
    box-shadow: 0 2px 10px 0 rgba(63,67,71,.08);
  }

  .ehp__wrapper{
    padding: 20px;
  }

  .ehp__no-events{
    height: 100%;
    display: flex;
    flex-flow: column;
    justify-content: center;
    font-size: 14px;
    color: #808080;
    text-align: center;
  }

  .ehp__total-events{
    font-size: 16px;
    font-weight: 500;
    color: #545456;
    @include title-2;

    span:last-child{
      margin-left: 5px;
    }
  }

  .ehp__contents{
    margin-top: 20px;

    .ehp__pagination{
      text-align: center;
      font-size: 12px;
      color: #7b8793;
      margin-top: 10px;
    }

    .ehp__table{
      border-collapse: collapse;
      padding: 10px 0;

      tr{
        border-top: 1px solid #f4f3f4;
      }

      th, td{
        padding: 10px;
        vertical-align: top;
      }

      .ehp__events__header{
        .ehp__item__start-date,
        .ehp__item__end-date,
        .ehp__item__device-impacted,
        .ehp__item__event,
        .ehp__item__recomm-action{
          font-size: 13px;
          font-weight: bold;
          line-height: 1.23;
          color: #919ca9;
          text-transform: uppercase;
          word-break: unset;
          @include caption;
        }

        th, td{
          padding: 15px 10px;

          span{
            width: 60px;
            display: block;
          }
        }

        .ehp__item__event{
          max-width: 50px;
        }
      }

      .ehp__item__icon{
        width: 20px;
        min-width: 20px;
        padding-top: 12px;
        padding-left: 0;

        .icon{
          svg{
            width: 20px;
            height: 20px;
          }
        }

        .defaultTick{
          svg{
            circle,path{
              stroke: #35C37D;
            }
          }
        }

        &.ehp__item__has-sorting{
          vertical-align: middle;
          padding: 5px;
        }
      }

      .ehp__item__sorting{
        width: 10px !important;
      }

      .ehp__item__start-date,
      .ehp__item__end-date,
      .ehp__item__device-impacted,
      .ehp__item__event,
      .ehp__item__recomm-action{
        font-size: 14px;
        font-weight: 300;
        line-height: 1.43;
        color: #3f4347;
        @include body-1;

        a, .link{
          cursor: pointer;
          color: #00b6de;
          text-decoration: none;
        }
      }

      .ehp__item__event{
        padding-right: 0;
        word-break: break-word;

        p{
          margin: 0;

          a{
            color: #00b6de;
          }
        }

        .ehp__item__status{
          font-size: 14px;
          margin-top: 5px;
          line-height: 1;
          color: #00b6de;
          text-transform: capitalize;
          @include body-1;
        }
      }
    }

    .ehp__events{
      .ehp__item{
        word-break: break-word;
      }
    }
  
  }
}

.tariff-mode-popup{
  top: auto;
  height: auto;
}

.tariff-mode-popup{
  .popup__body{
    background: #FAFAFB;
    overflow-y: scroll;
    max-height: 85vh;
  }
}

.tariff-mode-popup{
  .popup__body_dark{
    overflow-y: scroll;
    max-height: 85vh;
  }
}

.gen-eco-mode-popup{
  .popup__body{
    background: #FAFAFB;
  }
}

.gen-exercise-mode-popup{
.popup__body{
  background: #FAFAFB;
}
}

.eco-mode-heading{
  @include caption;
  color: #A7A7A8;

}
.eco-mode-heading-mid{
  @include caption;
  color: #A7A7A8;
  padding-top: 20px;
}

.exercise-mode-wrapper {

  .st__contents {
    .st__col {
      padding-bottom: 20px;
      padding-top: 20px;
      .new_wrapper {
        display: block;

        .rbl__title {
          @include body-1;
          padding-left: 3px;
        }

        .half-width {
          width: 50%;
        }
      }
    }
  }
}

.tariff-mode-wrapper{
  .st__contents {
    .st__col {
      padding-bottom: 0px;
      padding-top: 15px;
      .new_wrapper {
        display: block;
        border-radius: 10px;
        .rbl__title {
          @include body-1;
          padding-left: 3px;
        }
        .dark_text{
          color: #FFFFFF; // tariff modal text
        }
        .dark_sub_text{
          color: black;  // tariff modal sub text
        }
        .half-width {
          width: 50%;
        }
      }
      .dark{
        background-color: #303032;  // tariff modal item background color
      }
    }
  }
}
.events-history__mobile{
  height: 100%;
  
  .events-history-page{
    height: inherit;

    .ehp__wrapper{
      height: inherit;
      overflow: auto;
    }

    .ehp__contents{
      .ehp__table{
        .ehp__item__start-date,
        .ehp__item__end-date{
          >div{
            width: 47px;
            display: block;
          }
        }
        
        .ehp__item__end-date,
        .ehp__item__device-impacted,
        .ehp__item__recomm-action{
          display: none;
        }
      }
    }
  }
}

.events-history-popup{
  .popup__header{
    height: 70px;
  }

  .popup__body{
    .popup__body-content{
      padding: 0 !important;
    }
  }

  .events-history-page{
    .ehp__wrapper{
      height: 500px;
      overflow: auto;
      padding: 0;
    }
  
    .ehp__total-events{
      display: none;
    }
  
    .ehp__contents{
      margin: 0 0 20px;

      .ehp__table{
        th, td{
          padding: 15px;
        }
        
        .ehp__events__header{
          tr{
            border-top: 1px solid #f4f3f4;
          }

          th, td{
            span{
              width: 50px;
            }
          }
        }

        .ehp__item__icon{
          padding-left: 30px;
        }

        .ehp__item__event{
          padding-right: 10px;

          .ehp__item__status{
            display: none;
          }
        }

        .ehp__item__recomm-action{
          padding-right: 30px;
          width: 150px;
          word-break: break-word;

          span{
            width: 100px !important;
          }
        }
      }
    }
  }
}

.night{
  .events-history-page{
    .ehp__total-events{
      color:#f8f8ff;
    }

    .ehp__contents{
      .ehp__table{
        tr{
          border-color: rgba(123,135,147,.3);
        }

        .ehp__events__header{
          .ehp__item__start-date,
          .ehp__item__end-date,
          .ehp__item__device-impacted,
          .ehp__item__event,
          .ehp__item__event p,
          .ehp__item__recomm-action{
            color:#8e9091;
          }
        }

        .ehp__item__start-date,
        .ehp__item__end-date,
        .ehp__item__device-impacted,
        .ehp__item__event,
        .ehp__item__event p,
        .ehp__item__recomm-action{
          color:#f8f8ff;
        }
      }
    }
  }
}


/* Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) {
  .events-history-page{
    .ehp__contents{

      .ehp__table{
        .ehp__item__start-date,
        .ehp__item__end-date{
          >div{
            width: 100px !important;
          }
        }
      }
    }
  }
}

