.scrim .with-notch {
  background-color: #00b6de;
  height: $status-bar-notch-height;
}

.replacement-confirm-container {
  min-height: 100vh;
  height: 100%;
  display: flex;
  display: -ms-flexbox;
  display: -webkit-flex;
  align-items: center;
  justify-content: center;
  overflow: auto;
  z-index: 200;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: #fff;
  &.disabled {
    display: none;
  }

  .confirm-content {
    text-align: center;
    width: 100%;
    padding: 30px;
    color: #7b8793;
    font-size: 14px;
    max-width: 480px;
  }
  
  .confirm-content p {
    margin: 0px;
  }
  
  .confirm-img {
    width: 60px;
    height: 60px;
    text-align: center;
  }
  
  .confirm-label {
    font-size: 16px;
    font-weight: bold;
    color: #545456;
    padding: 20px 10%;
    border-bottom: solid 0.5px rgba(123, 135, 147, 0.3);
  }

  .error-label {
    font-size: 16px;
    font-weight: bold;
    color: #545456;
    padding: 20px 10% 0 10%;
  }

  .error-label-sub {
    font-size: 16px;
    font-weight: bold;
    color: #545456;
    padding-top: 2px;
  }
  
  .confirm-info {
    line-height: 1.57;
    color: #5a5a5c;
    padding: 20px 10% 25px;
  }
  
  .comfirm-btn {
    width: 190px;
    height: 40px;
    border-radius: 20px;
    font-size: 16px;
    font-weight: 500;
    text-align: center;
    color: #ffffff;
    background-color: #01b4de;
    outline: none;
    border: none;
    margin-top: 30px;
  }
  
  .confirmId-box {
    word-break: break-word;
    border-radius: 10px;
    border: dashed 0.5px rgba(123, 135, 147, 0.93);
    border-style: dashed;
    padding: 15px 15px;
  }
  
  .confirmId-box p {
    font-size: 24px;
    font-weight: bold;
    letter-spacing: 2px;
    text-align: center;
    color: #545456;
    margin: 10px 0 0 0;
  }

  @media screen and (min-width:1024px) {
    width: 450px;
    height: auto;
    min-height: 1px;
    margin-left: -225px;
    position: relative;
    top: 50%;
    left: 50%;
    transform: translateY(-50%);

    & .confirm-content {
      padding: 32px 67px 32px 68px;

      & p.confirm-label,
      & p.error-label,
      & p.error-label-sub {
        font-size: 17px;
        font-weight: bold;
        line-height: 23px;
      }

      & p.confirm-info {
        padding: 17.5px 0;
        font-size: 17px;
        line-height: 1.29;
      }

      & .confirmId-box {
        font-size: 15px;
        line-height: 20px;
        color: #7b8793;

        & > p {
          margin-top: 4px;
          font-size: 30px;
          font-weight: bold;
          line-height: 39px;
          letter-spacing: 2.5px;
        }
      }

      & .comfirm-btn {
        width: 100%;
        height: 50px;
        margin-top: 32px;
        border-radius: 25px;
        font-size: 17px;
        line-height: normal;
        text-transform: uppercase;
      }
    }
  }
}

.night {
  .scrim.with-notch {
    background-color: #333333;
  }
  .replacement-confirm-container {
    background-color: #262627;
    .confirm-label {
      color: #f8f8ff;
    }

    .confirm-info {
      color: #d8dcde;
    }

    .confirmId-box {
      color: #a2a6a9;
      border: dashed 0.5px rgba(123, 135, 147, 0.93);
    }

    .confirmId-box p {
      color: #f8f8ff;
    }

    .error-label {
      color: #f8f8ff;
    }

    .error-label-sub {
      color: #f8f8ff;
    }
  }
}
