
.menu-list{
  position: fixed;
  top: 0;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  width: 100vw;
  height: calc(100vh - #{$footer-height});
  background: #f6f7fb;
  @include title-2;

  .menu-list__wrapper{
    height: inherit;
    
    .menu-list__header{
      position: relative;
      z-index: 100;
      background-color: $secondary;
      min-height: $header-height;
      box-shadow: 0 0 2px rgba(0,0,0,.4);
      border-radius: $app-border-radius-top;

      .menu-list__header-wrapper{
        padding: 77px 20px 26px;
      }

      .menu-list__header__selected{
        display: inline-block;

        .menu-list__header__selected-site-name{
          display: flex;
          align-items: flex-start;
          color: #ffffff;
          font-weight: 400;
        }
        .menu-list__header__selected-dropdown{
          width: auto;
          margin-left: 10px;

          .icon{
            display: inline-flex;
            height: 100%;
            transform: rotate(0deg);
            transition-duration: 0.8s;
            transition-property: transform;

            &.dropdown.open {
              transform: rotate(180deg);
              transition-duration: 0.8s;
            }
        
            svg{
              width: 1em;
              height: 100%;
              margin: 0;
            }
          }
        }

        .menu-list__header__selected-site-id{
          font-size: 15px;
          font-weight: 300;
          letter-spacing: -0.2px;
          color: #caf0f8;
          @include body-1;
        }
      }
    }

    .menu-list__body{
      position: relative;
      height: calc(91% - #{$header-height});
      overflow-y: auto;
      scroll-behavior: smooth;

      .loading-wrapper {
        svg {
          width: 50px;
          height: 50px;
          margin-top: 2rem;
          path {
            fill: #fff;
          }
        }
      }

      .MuiTabs-root{
        border-bottom: 1px solid white !important;
        .MuiTabs-indicator {
          background-color: white !important;
        }
      }
      .MuiTab-textColorInherit.Mui-selected {
        color: white !important;
      }

      &.se-menu-list__body {
        .MuiTabs-root {
            .Mui-selected { color: #01b5de !important; }
            .MuiTabs-indicator { background-color: #01b5de !important; }
        }
      }

      .menu-list__body-wrapper{
       padding-bottom: 50px;

       .comp-notification {
        position: fixed;
       }

       .comp-notification.comp-notification__failure {
        background-color: #545456;
       }

       .comp-notification.comp-notification__active {
        padding: 10px 15px;
       }
      }

      .menu-list__sites-wrapper{
        visibility: hidden;
        height: inherit;
        position: fixed;
        left: 0;
        right: 0;
        top: $header-height;
        z-index: 10;
        background-color: $secondary;

        .search__site__list__input{
          display: flex;
          border: 1px solid #ffffff;
          border-radius: 5px;
          margin: 10px 20px;
          padding: 0 15px;
  
          input{
            border: 0;
            padding-left: 0;
            min-height: 30px;
            font-size: 14px;
            color: #ffffff;
            @include body-1;

            &::placeholder{
              color: #ffffff;
            }
          }
  
          .icon{
            display: flex;

            svg{
              path, polygon{
                fill: #ffffff;
              }
            }
          }

          >div{
            display: flex;

            .icon{
              height: 15px;
              width: 15px;

              svg{
                height: inherit;
                width: inherit;
              }
            }
          }
        }

        &.menu-list__sites__search__enabled{
          .unordered-list__open{
            border-top: 1px solid #fff;
            height: calc(100%-1px);
          }

          .no-site-found{
            font-size: 16px;
            font-weight: 300;
            color: #ffffff;
            display: flex;
            justify-content: center;
            margin-top: 1rem;
          }
        }

        &.menu-list__sites__show{
          display: block;
          visibility: visible;

          .react-swipeable-view-container > div[aria-hidden="false"] {
            height: 100%;
        }
        
        .react-swipeable-view-container > div[aria-hidden="true"] {
          height: 0;
        }
        }
      }

      &.menu-list__grid-switch {
        & .menu-list__body-wrapper{
          & .comp-notification {
            padding: 14px 20px;
            font-size: 12px;
            position: relative;
          }
        }
      }
    }

    .menu-list__footer{
      width: 100%;
      position: fixed;
      bottom: #{$footer-height};
      padding: 15px 20px;
      background: #f6f7fb;
      display: flex;
      justify-content: space-between;

      .menu-list__brand-icon-and-app-version{
        display: flex;
        align-items: center;

        .icon{
          height: 18px;
          width: auto;
          @media screen and (min-width:768px) and (max-width:1024px) and (orientation : portrait){
            height: 25px;
          }
        }

        span:last-child{
          font-size: 12px;
          color: #545456;
          opacity: 0.6;
          margin-left: 10px;
        }
      }

      .menu-list__footer-build-details{
        font-size: 10px;
        padding: 0 10px;
        margin-bottom: 5px;
        height: 20px;
        line-height: 20px;
        color: #ffffff;
        background-color: #00a215;
        text-transform: capitalize;
      }
    }
    .menu-list__adjustHeight{
      bottom: calc(#{$footer-height} + 22px);
    }
    .menu-list__sites-available{
      .unordered-list{
        li{
          cursor: pointer;
          &:first-child{
            border-top: none;
          }
          
          &.unordered-list__selected{
            background-color: #00b6de;
          }
          :active { 
            background-color: $secondary-dark;              
          }
          &:hover:not(.unordered-list__selected){
            background-color: #00b6de;
          }
        }

        .unordered-list__title{
          color: #ffffff;
        }

        .unordered-list__normal {
          display: flex;
          align-items: center;
          justify-content: space-between;

          .loading {
            svg {
              path {
                fill: #fff;
              }
            }
          }
        }

        .unordered-list__normal--sites {
          display: flex;
          align-items: center;
          padding: 0.75rem 0;
          width: 100%;
          svg {
            margin: 0 1rem;
          }
          a{
            flex: 1;
          }
        }

        .unordered-list__sub-title{
          color: #ffffff;
        }
      }
    }
  }

  .close-enphase-icon,
  .menu-close-enphase-icon {
    position: absolute;
    top: 50px;
    right: 1.625em;
    .icon.close {
      width: 18px;
      height: 18px;

      svg {
        width: 18px;
        height: 18px;
      }
      polygon {
        fill: #ffffff;
      }
    }
  }

  .submenu{
    .container{
      display: inline-block;
      height: calc(100% - #{$header-height} - #{$footer-height});
      width: 100%;
    }

    &.envoy .container { position: relative; }

    &.sgipIncentive .close-enphase-icon {
      top: 14px;

      &.ios-demo-logout { top: 50px; }
    }
  }

  .device__error{
    height: 100%;
    display: flex;
    flex-flow: column;
    justify-content: center;
    font-size: 14px;
    color: #808080;
    text-align: center;
  }

  .disable-envoy-device-confirmation{
    display: none;
  }
  .list-view__settings {
    position: relative;
    .list-view__badge {
      position: absolute;
      left: 6px;
      min-height: 1em;
      max-height: 1em;
      min-width: 1em;
      margin: auto 0;
      top: 0;
      bottom: 0;
    }
  }


  &.se-menu-list {
    z-index: 11;

    & .menu-list__wrapper {
      height: 100%;

      & .menu-list__header {
        min-height: 80px;

        & .menu-list__header-wrapper {
          display: flex;
          padding-top: 39px;
          font-size: 19px;
          font-weight: normal;
          line-height: 22px;
          letter-spacing: normal;
          color: #ffffff;
          @include title-2;

          &.menu-list__header-wrapper-empty {
            justify-content: space-between;
            align-items: center;

            button {
              width: 22px;
              padding: 0;
              border: unset;
              background: unset;

              .icon.notificationIcon {
                height: 22px;

                svg {
                  width: auto;
                  height: 22px;
                }
              }
            }
          }

          & > span {
            width: 20px;
            height: 20px;
            margin-right: 20px;
            display: flex;
            justify-content: center;

            & span {
              height: 100%;

              & svg {
                height: 100%;
                margin: 0;
                fill: #ffffff;
              }
            }
          }
        }   
      }

      & .menu-list__body {
        height: calc(100% - 128px);

        .list-view__icon {
          height: auto;

          .notificationIcon svg g { fill: #545456; }
        }
      }
    }

    &.se-menu-list__webnav {
      top: #{$footer-height};
      background: rgba(0,0,0,.6);
      .backdrop{
        width: 100%;
        height: 100%;
        opacity: 0;
      }

      & .menu-list__wrapper {
        width: 458px;
        height: 100%;
        max-width: 477px;
        background: #ffffff;
        position: absolute;
        right: 0;
        top: 0;

        & .menu-list__header { display: none; }

        & .menu-list__body { height: 100%; color: #00DCB5}
      }
    }

    &.se-menu-list__webcontent{
      top: #{$footer-height};

      & .menu-list__wrapper {
        & .menu-list__header {
          min-height: 52px;
          box-shadow: unset;
          border: 1px solid #D8DCDE;
          background: #ffffff;

          & .menu-list__header-wrapper {
            max-width: 1088px;
            margin: 0 auto;
            padding: 28px 32px;
            flex-direction: row-reverse;
            justify-content: space-between;
            background: #ffffff;
            font-size: 30px;
            line-height: 1;
            font-weight: bold;
            color: #545456;

            &.menu-list__header-wrapper-support { max-width: 100%; }

            & span {
              margin-right: 0;
    
              & span.icon {
                height: 20px;
                position: relative;
                top: 0;
                right: 0;
  
                & svg {
                  height: 20px;
                  margin: 0;
                  fill: #545456;

                  & polygon { fill: #545456; }
                }
              }
            }
          }
        }

        & .se-menu-list__body {
          width: 100%;
          max-width: 876px;
          height: calc(100% - 146px);
          margin: 32px auto;
          padding-bottom: 32px;
          box-shadow: 0px 2px 10px #3f434714;
        }
      }
    }
  }
}

.menu-list + .comp-notification__mobile { top: $header-height !important; }
.menu-list__cardview + .comp-notification__mobile { top: 67px !important; }
.menu-list__cardview + .comp-notification__ios { top: 102px !important; }

.night{
  .menu-list{
    .menu-list__wrapper{
      .menu-list__body{
        .menu-list__sites-wrapper{
          background-color: #262627;
        }

        .MuiTabs-root { border-bottom-color: rgba(123,135,147,.30196078431372547) !important; }
      }
    }
  }
}

@media screen and (min-width:768px) and (max-width:1024px){
}

/* Screen Size :: 480 and above (Tablet) */
@media (min-width: 480px) {
  .menu-list{
    .menu-list__wrapper{
      zoom: 1.2;
    }
  }
}
/* iphone x  and xs*/ 
@media only screen and (device-width : 375px) and (device-height : 812px) {
  .submenu{
    .container{
      height: calc(91vh - 134px) !important;
    }
  }
}

/* iphone xs-max*/ 
@media only screen and (device-width : 414px) and (device-height : 896px) {
  .submenu{
    .container{
      height: calc(91vh - 134px) !important;
    }
  }
}


