$tracking: 50;
$fallbackFontsSeq: 'Visuelt', 'Roboto', 'Helvectica Neue', Helvetica, Arial, sans-serif;

/* 
  mixinname-2 --> medium  
  mixinname-1 --> regular 
*/

@mixin title-2 {
  font-family: $fallbackFontsSeq;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
}
@mixin title-1 {
  font-family: $fallbackFontsSeq;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
}
@mixin body-2 {
  font-family: $fallbackFontsSeq;
  font-size: 12px;
  line-height: 16px;
  font-weight: 500;
}
@mixin body-1 {
  font-family: $fallbackFontsSeq;
  font-size: 12px;
  line-height: 16px;
  font-weight: 400;
}
@mixin caption {
  font-family: 't-star', $fallbackFontsSeq;
  font-size: 12px;
  line-height: 16px;
  font-weight: normal;
  letter-spacing: ($tracking/1000) * 1em;
  text-transform: uppercase;
}
@mixin graph-label-2 {
  font-family: $fallbackFontsSeq;
  font-size: 10px;
  line-height: 16px;
  font-weight: 500;
}
@mixin graph-label-1 {
  font-family: $fallbackFontsSeq;
  font-size: 10px;
  line-height: 16px;
  font-weight: 400;
}
