/* Crosshair */
@import "../fonts";
.rv-crosshair{
  z-index: 2;

  .rv-crosshair__line{
    background: rgba(123, 135, 147, 0.3);

    &::before{
      content: "";
      position: absolute;
      top: 0;
      left: -6px;
      border-color: #f7f8fc transparent transparent;
      border-style: solid;
      border-width: 8px 6px 0;
      filter: drop-shadow(0 0 1px #9d9d9d);
    }

    &::after{
      content: "";
      position: absolute;
      bottom: 0;
      left: -6px;
      border-color: transparent transparent #f7f8fc;
      border-style: solid;
      border-width: 0 6px 8px;
      filter: drop-shadow(0 0 1px #9d9d9d);
    }
  }

  .rv-crosshair__inner{
    margin-right: 2px;
    z-index: 2;

    &::before{
      content: "";
      position: absolute;
      top: 10px;
      right: -5px;
      border-color: transparent transparent transparent #d5dadd;
      border-style: solid;
      border-width: 5px 0 5px 5px;
    }

    &::after{
      content: "";
      position: absolute;
      top: 10px;
      right: -4px;
      border-color: transparent transparent transparent #ffffff;
      border-style: solid;
      border-width: 5px 0 5px 5px;
    }

    &.rv-crosshair__inner--right{
      margin-left: 2px;

      &::before{
        left: -5px;
        right: auto;
        border-width: 5px 5px 5px 0;
        border-color: transparent #d5dadd transparent transparent;
      }

      &::after{
        left: -4px;
        right: auto;
        border-width: 5px 5px 5px 0;
        border-color: transparent #ffffff transparent transparent;
      }
    }
  }
}

.energy-crosshair{
  &.energy-crosshair__positive{
    .rv-crosshair__line{
      &::before{
        content: none;
      }
    }

    .rv-crosshair__inner{
      background: #ffffff;
      color: #000000;
      padding: 8px;
      box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.09);
      border: 1px solid #d7dbde;

      .energy-crosshair__col-power{
        padding-left: 10px;

        &.production-only, &.consumption-only {
          text-align: right;
        }
      }

      .energy-crosshair__col-energy{
        padding-left: 10px;
      }

      .energy-crosshair__heading{
        font-size: 8px;
        font-weight: bold;
        font-style: normal;
        font-stretch: normal;
        line-height: normal;
        letter-spacing: 0.5px;
        color: rgba(123, 135, 147, 0.6);
        margin-bottom: 2px;
        text-transform: uppercase;
      }

      .energy-crosshair__row{
        margin-top: 0;
        font-size: 11px;
        margin-bottom: 2px;
        line-height: 1;
        
        &:last-child{
          margin-bottom: 0px;
        }
      }

      table{
        width: 100%;
        white-space: nowrap;
        margin-bottom: 5px;
      }
    }

    ul.energy-battery-status {
      min-width: 125px;
      border-top: solid 1px rgba(123, 135, 147, 0.3);

      &.border-top-none {
        border-top: none;
      }

      & li {
        margin: 5px 0;

        div {
          color: #7ecd40;
          display: flex;

          &.low-battery {
            color: #ff6464;
          }

          .ebs__title {
            padding: 3px;
          }

          .ebs__value {
            padding: 3px 0px 3px 14px;
          }
        }
      }
    }

    ul.energy-grid-battery-details {
      min-width: 125px;
      border-top: solid 1px rgba(123, 135, 147, 0.3);
      padding-top: 5px;

      &.border-top-none {
        border-top: none;
        padding: 0;
      }

      & li {
        margin-top: 0.3125em;
        display: grid;
        grid-template-columns: 20px auto;

        & > span {
          width: 15px;
          height: 15px;
          display: flex;
          justify-content: center;
          align-items: center;
          margin-right: 5px;
          border: 1px solid #6c7073;
          border-radius: 50%;
          font-size: 10px;
          font-weight: bold;
          line-height: normal;
          letter-spacing: normal;
          color: #6c7073;
          background-color: #fff;

          &.grid-on {
            color: #ffffff;
            background-color: #6c7073;
          }

          &.grid-off { border-color: #6c7073; }

          &.battery-change {
            color: #7ecd40;
            border-color: #7ecd40;
          }

          &.sg-active {
            color: #ffffff;
            border-color: #f37320;
            background-color: #f37320;
          }

          &.sg-other {
            color: #f37320;
            border-color: #f37320;
          }
        }

        & > p {
          margin: 0;
          font-size: 10px;
          font-weight: normal;
          line-height: normal;
          letter-spacing: normal;
          color: #6c7073;

          & > span {
            display: block;
            margin-top: 2px;
            font-size: 9px;
            font-weight: 500;
            color: rgba(123, 135, 147, 0.6);
          }
        }
      }
    }
  }

  &.energy-crosshair__negative, &.energy-crosshair__battery-status, &.energy-crosshair__hide-content{
    .rv-crosshair__inner__content, .rv-crosshair__inner{
      display: none !important;
    }
  }

  .energy-crosshair__value{
  }
  .energy-crosshair__unit{
  }

  .energy-crosshair__production{
    color: #01b4de
  }
  .energy-crosshair__consumption{
    color: #f37320
  }
  .energy-crosshair__import, .energy-crosshair__export{
    color: #545456
  }
  .energy-crosshair__charge, .energy-crosshair__discharge{
    color: #7acf38
  }
  .energy-crosshair__generator {
    color: #9062AC
  }
}

.series{
  position: relative;

  .energy-graph-vertical-line{
    stroke-width: 2;
    stroke: #d5dadd
  }

  &.production-only{
    .energy-crosshair{
      .energy-crosshair__today, .energy-crosshair__selected_day{
        color: #01b4de
      }
      .energy-crosshair__yesterday, .energy-crosshair__previous_day{
        color: #008cab
      }
      .energy-crosshair__last_year{
        color: #6c7073
      }
    }
  }

  &.consumption-only{
    .energy-crosshair{
      .energy-crosshair__today, .energy-crosshair__selected_day{
        color: #f37320
      }
      .energy-crosshair__yesterday, .energy-crosshair__previous_day{
        color: #a6490b
      }
      .energy-crosshair__last_year{
        color: #6c7073
      }
    }
  }

  &.production-consumption-battery{
    .energy-crosshair{
      &.energy-crosshair__positive{
        .rv-crosshair__line{
          &::after{
            content: none;
          }
        }
      }
      
      &.energy-crosshair__negative{
        .rv-crosshair__line{
          &::before{
            content: none;
          }
        }
      }
    }
  }
}

.portrait{
  .energy-crosshair__col-energy{
    display: none;
  }
}

.night {
  .energy-crosshair{
    .energy-crosshair__generator {
      color: #dca2ff
    }
  }
}

.browser {
  .energy-crosshair__row{
    font-size: 12px;
  }
}
