@import '../../../src/style/variables.scss';
$statusPagePadding: 16px;

.browser {

  #popUpWrapper.popup__web.solarForecast-info {
    width: 450px;
  }

  .forecast-wrapper {
    height: 100%;

    .spin-container {
      background-color: transparent;
    }

    .overlay {
      position: fixed;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      height: 100%;
      width: 100%;
      z-index: 10;
    }

    .solarforecastPage.fromStatusPage {
      padding-bottom: $statusPagePadding;

      .sf-wrapper {
        padding: 16px 16px 0px 16px;
      }

      .today-wrapper {
        margin: unset;
      }
    }

    .solarforecastPage {
      height: 100%;
      overflow-y: scroll;
      background-color: #FAFAFB;
      border: 1px solid #F4F4F5;
      border-radius: 6px;
      padding-bottom: 20px;

      .forecast-grid {
        display: grid;
        grid-template-columns: 50% 50%;
        grid-auto-columns: 1fr;

        .selected-graph-view {
          display: flex;
          background-color: white;
          flex-direction: column;
        }
      }

      .sf-wrapper {
        padding: 22px 20px 10px 20px;

        .title-bar {
          @include title-2;
          display: flex;
          align-items: center;

          & .icon.infoIcon {
            display: flex;
            margin-left: 5px;
            cursor: pointer;
          }

          & .icon.infoIcon,
          .infoIcon svg {
            width: 14px;
            height: 14px;
          }
        }
      }

      .viewMoreLess {
        @include body-2;
        color: #01B4DE;
        text-decoration: underline;
        padding: 8px 0px;
        margin-left: 20px;
        margin-top: 5px;
      }

      .forecast-not-availble {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        margin: 10px 20px 20px;
        height: calc(100% - 54px - #{$footer-height});
        border-radius: 10px;
        background-color: #FFFFFF;

        .title {
          @include title-2;
          padding: 10px 10px 5px;
          text-align: center
        }

        .subtitle {
          @include body-1;
          padding: 10px 10px 5px;
          text-align: center;
        }

        &.fromstatus {
          background-color: transparent;
          display: flex;
          flex-direction: row;
          padding: 10px 10px 10px 20px;
          margin: unset;

          .icon.alertOutline {
            display: flex;
            align-self: flex-start;
            margin-right: 5px;

            & svg {
              width: 20px;
              height: 20px;
            }
          }

          .text-wrapper {
            display: flex;
            flex-direction: column;

            .title {
              @include body-2;
              text-align: left;
              padding: 0px 5px 0px 5px;
            }

            .subtitle {
              @include body-1;
              text-align: left;
              margin-top: 5px;
              padding: 0px 5px 0px 5px;
            }
          }
        }
      }

      .today-graph,
      .next-day-graph {
        .sun-pointer {

          .sunrisePath,
          .sunsetPath {
            transform: translate(-7px, 3px);
          }

          foreignobject.bgrnd {
            border-radius: 50%;
            transform: translate(-1px, -1px);
            background-color: #CBCED1;
            box-shadow: 0px 1px 8px #53559B2D;
          }
        }
      }

    }
  }

  .accuracy-cta {
    & .topnote {
      margin-bottom: 20px;
    }

    & .reasons {
      @include body-2;
      margin-bottom: 15px;
    }

    & .reasons-list {
      & li {
        list-style: disc;
      }

      .reason-item {
        padding: 6px 6px 6px 0px;
        margin-left: 12px;
      }
    }
  }

  .energy-wrapper {
    display: flex;
    align-items: center;
    color: #6B7073;

    .energy-value {
      @include title-2;
      margin-left: 0px;
    }

    .energy-unit {
      margin: 0px 10px 0px 5px;
      @include body-1;
    }

    .icon.dropdown {
      display: flex;
      align-items: center;

      svg {
        width: 16px;
        height: 16px;
      }
    }
  }

  .today-wrapper {
    background-color: white;
    // border-radius: 6px;
    // margin: 5px 20px 5px 20px;
    // padding: 15px 15px 15px;
    // box-shadow: 0px 2px 10px #3f434714;

    // .today-date {
    //   display: flex;
    //   align-items: center;
    //   @include body-2;
    //   margin: 0px 0px 5px;

    //   .today-weather-icon {
    //     display: flex;
    //     margin-right: 5px;
    //   }
    // }


    .today-data {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-left: 7px;

      .left-part {
        display: flex;
        align-items: center;

        .today-weather-icon {
          display: flex;
          margin-right: 10px;
          align-items: center;

          svg {
            height: 24px;
            width: 24px;
          }
        }

        .text-wrapper {
          display: flex;
          align-items: center;

          .today-text {
            @include title-2;
          }

          .today-weather-label {
            @include body-1;
            color: #B4B6B8;
            margin-left: 5px;
          }
        }
      }

      .energy-wrapper {
        display: flex;
        justify-content: center;
        flex-direction: row;
        // padding-right: 20px; // add if all energy values to be aligned

        .energy-value {
          @include title-2;
          line-height: 20px;

          &.NA {
            margin-right: 26px;
          }
        }

        .energy-unit {
          margin-left: 5px;
          @include body-1;
          line-height: 20px;
          margin-right: calc(10px + 16px + 5px);
        }
      }

    }

    // .collapsed {
    //   display: flex;
    //   justify-content: space-between;

    //   .subtitle {
    //     @include body-2;
    //     display: flex;
    //     align-items: center;
    //     color: #6B7073;
    //   }

    //   & .icon.dropdown {
    //     &.invert {
    //       transform: rotate(180deg);
    //     }
    //   }

    // }
    .graph-wrapper {
      background-color: unset;
      border: unset;
      border-radius: unset;
      padding: unset;
      margin: unset;

      .today-graph {
        display: flex;

        .custom-hint {
          display: flex;
          flex-direction: column;
          // margin: 5px 10px 5px 10px;
          transform: translate(0px, -66px);
          // position: relative;
          // top: -90px;
          border-radius: 4px;
          padding: 8px;
          box-shadow: 0px 1px 8px #53559B2D;
          background-color: #FFFFFF;

          .data-1 {
            @include body-1;
            color: black;

            .value {
              @include body-2;
              margin-left: 2px;
              color: #01B4DE;
            }

            .kWh {
              margin-left: 2px;
              color: #01B4DE
            }
          }

          .data-2 {
            @include caption;
            color: #B4B6B8;
          }
        }



        .live-pointer-crosshair {


          .rv-crosshair__inner {

            &.rv-crosshair__inner--right,
            &.rv-crosshair__inner--left {
              .rv-crosshair__inner__content {
                display: none;
              }

              margin: unset;
              left: unset;
              transform: translate(-8px, 60px);

              &::before,
              &::after {
                display: none;
              }
            }

            .error-icon {
              display: flex;
              height: 16px;
              width: 16px;
              align-items: center;
              justify-content: center;
              background-color: #FFFFFF;
              box-shadow: 0px 1px 8px rgba(0, 0, 0, 0.25);
              border-radius: 50%;

              .icon.statusWarning {
                display: flex;
                transform: translateY(-1px);

                svg {
                  width: 12px;
                  height: 12px;

                  path {
                    fill: #D69800;
                  }
                }
              }
            }
          }
        }
      }



      .accuracyCTA {
        margin: 15px 15px 5px 15px;
        @include body-1;
        color: #01B4DE;
        text-decoration: underline;
      }

      .notReporting-msg {
        display: flex;
        color: #D69800;
        align-items: center;
        margin: 15px 15px 5px 15px;

        .icon.statusWarning {
          display: flex;
          width: 16px;
          height: 16px;

          svg {
            width: 16px;
            height: 16px;

            path {
              fill: #D69800;
            }
          }
        }

        .msg {
          padding: 0px 0px 0px 10px;
          @include body-1;
        }
      }

      .legends {
        margin: unset;
      }

    }

    .act-forecast-wrapper {
      display: flex;
      margin: 12px 7px;
      justify-content: space-between;

      .act-prod,
      .forecast-val {
        background-color: #FAFAFB;
        flex-grow: 1;
        padding: 8px;
        border-radius: 4px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: space-between;


        .subtitle {
          color: #545456;
        }

        .duration {
          @include body-1;
          color: #B4B6B8;
          margin-left: 0px;
        }


        .energy-wrapper {
          display: flex;
          justify-content: center;
          flex-direction: row;

          .energy-value {
            @include title-2;
            line-height: 20px;

            &.NA {
              margin-right: 26px;
            }
          }

          .energy-unit {
            margin-left: 5px;
            @include body-1;
            line-height: 20px;
            margin-right: calc(10px + 16px);
          }
        }

      }

      .act-prod {
        margin-left: 4px;
      }

      .forecast-val {
        margin-right: 4px;
      }
    }
  }

  .next-forecast-title {
    margin: 17px 20px 15px;
    @include caption;
    color: rgba(84, 84, 86, 0.5);
  }

  .selected-graph-view {
    padding: 20px;

    .weather-data {
      display: flex;
      align-items: center;
      margin-left: 7px;

      .next-weather-icon {
        display: flex;
        margin-right: 10px;
        align-items: center;

        svg {
          height: 24px;
          width: 24px;
        }
      }

      .text-wrapper {
        display: flex;
        align-items: center;

        .day-text {
          @include title-2;
        }

        .next-weather-label {
          @include body-1;
          color: #B4B6B8;
          margin-left: 5px;
        }
      }

    }

    .next-forecast-val {
      margin: 12px 7px;
      background-color: #FAFAFB;
      min-width: 46%;
      padding: 8px;
      border-radius: 4px;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: space-between;


      .subtitle {
        @include body-2;
        color: #545456;
      }

      .duration {
        @include body-1;
        color: #B4B6B8;
        margin-left: 0px;
      }


      .energy-wrapper {
        display: flex;
        justify-content: center;
        flex-direction: row;

        .energy-value {
          @include title-2;
          line-height: 20px;
        }

        .energy-unit {
          margin-left: 5px;
          @include body-1;
          line-height: 20px;
          margin-right: calc(10px + 16px);
        }
      }

    }

    .legends {
      display: flex;
      justify-content: space-between;
      padding: 2px 15px 10px;

      .sunrise,
      .sunset {
        display: flex;
        align-items: center;
        @include body-1;
        color: #7B8793;

        .text {
          margin-left: 3px;
        }
      }
    }

    .kwh-unit {
      line-height: normal;
      font-size: 10px;
      color: #a2aab3;
      padding: 3px 0px 0px 15px;
    }

    .top-wrapper {
      display: flex;
      justify-content: space-between;

      .legends {
        margin: unset;
        padding: unset;

        .text {
          @include body-1;
          color: #7B8793;
          margin-left: 3px;
        }

        .actualproduction,
        .forecastproduciton {
          display: flex;
          align-items: center;
        }

        .forecastproduciton {
          margin: 0px 12px 0px 15px;
        }

        .lightblue,
        .darkblue {
          display: flex;
          align-items: center;
          width: 10px;
          height: 10px;
        }

        .lightblue {
          background-color: #DFF3F9;
        }

        .darkblue {
          background-color: #01B4DE;
        }
      }
    }
  }

  .next-days-forecast {
    display: grid;
    align-items: center;

    :first-child {
      border-radius: unset;
    }

    :last-child {
      border-radius: unset;
    }

    .next-day {
      margin: 2px 0px 0px 15px;
      padding: 6px 7px 6px;
      height: 98%;
      box-shadow: 0px 1px 0px #0000000A;

      &.inactive {
        background-color: #F5F5F5;
      }

      &.active {
        background-color: white;

        .next-day-wrapper {

          .day-text,
          .energy-wrapper {
            color: #01B4DE;
          }

          .icon.forecast svg path {
            fill: #01B4DE;
          }
        }
      }

      .next-day-wrapper {
        padding: 0px 10px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 100%;
        cursor: pointer;

        .day-text {
          @include body-2;
          text-transform: capitalize;
          color: #545456;
        }

        .day-otherdata {
          display: grid;
          grid-template-columns: 30px 100px;
          justify-items: end;
          @include title-1;
          color: #6C7074;

          & .icon {
            display: flex;
            margin-right: 5px;
            align-items: center;
            justify-content: center;

            &.dropdown {
              transform: rotate(-90deg);

              &.invert {
                transform: rotate(90deg);
              }
            }

            &.forecast {
              display: inline-flex;

              svg {
                width: 18px;
                height: 18px;
              }
            }
          }

          & .energy-value,
          .energy-unit {
            @include body-1;
          }
        }
      }

    }
  }

  .today-graph,
  .next-day-graph {

    .rv-xy-plot__axis__title text {
      font-weight: 500;
      transform: translate(9px, -20px) rotate(90deg);
    }
  }

  .night {
    .forecast-wrapper {
      .solarforecastPage {
        border: unset;
        background-color: transparent;

        &.fromStatusPage {
          background-color: #FFFFFF0D;
          box-shadow: 0 1px 1px rgba(0, 0, 0, 0.25);
          color: #CBCED1;
          margin-bottom: 20px;
          border: 1px solid #F4F4F50D;
        }

        .icon.today-weather-icon,
        .icon.forecast {
          svg path {
            fill: #CBCED1;
          }
        }
      }
    }

    .title-text,
    .today-date,
    .next-forecast-title {
      color: #CBCED1;
    }

    .forecast-not-availble {
      background-color: #262627;
      box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.25);
      color: #CBCED1;
    }

    .energy-wrapper {
      color: #CBCED1;
    }

    .graph-wrapper {
      border: 1px solid #F4F4F50D;
    }

    .today-graph,
    .next-day-graph {
      .rv-xy-plot__axis__line {
        stroke: rgba(123, 135, 147, 0.3);
      }
    }

    .next-day-graph {
      padding: 0px 2px;
    }

    .today-graph {
      .error-icon {
        background-color: #e3e3e6;
      }
    }

    .today-wrapper .act-prod .content .energy-wrapper {
      color: #CBCED1;
    }

    .today-wrapper {
      color: #CBCED1;

      .act-forecast-wrapper .text-wrapper {
        .subtitle {
          color: #CBCED1;
        }

        .duration {
          color: #9AA2A8;
        }
      }
    }

    .today-wrapper .graph-wrapper,
    .next-day {
      background-color: #2C2C2C;

      .legends {
        margin: unset;

        .icon.sunrise,
        .icon.sunset {
          svg path {
            fill: #CBCED1;
          }
        }

        .text {
          color: #CBCED1;
        }

        .forecastproduciton .lightblue {
          background-color: #8AADB733;
        }
      }

      .sun-pointer {
        foreignobject.bgrnd {
          background-color: #2a2a2a;
          box-shadow: 0px 1px 8px rgba(0, 0, 0, 0.25);
        }

        path {
          fill: #CBCED1;
        }
      }
    }


    .next-day {
      background-color: #2C2C2C;
      color: #CBCED1;
    }

    .next-day .next-day-wrapper {

      .day-text,
      .day-otherdata {
        color: #CBCED1;
      }
    }

    .rv-xy-plot__grid-lines__line {
      stroke: #424649;
    }

    .rv-crosshair {
      .rv-crosshair__line {
        &::before {
          border-color: #2C2C2C transparent transparent;
        }

        &::after {
          border-color: transparent transparent #2C2C2C;
        }
      }
    }
  }
}