.unordered-list-container{
  display: none;
  height: 0;
  transition: height .5s ease-out;

  &.unordered-list__open{
    display: block;
    height: 100%;
    overflow: auto;
  }

  .unordered-list{
    height: 100%;
    overflow: auto;
    margin-bottom: 3rem;
    &.mlr-1 {
      margin: 0 2rem 3rem;
    }
    li{
      word-break: break-all;

      a{
        display: block;
        cursor: pointer;
      }
    
      &.unordered-list__selected{
        background: #eaeaea;
      }

      &:hover:not(.unordered-list__selected){
        background: #f2f2f2;
      }
    }

    .unordered-list__title{
      font-size: 16px;
      font-weight: 300;
      margin-right: 5px;
      &.bold {
        font-weight: 500;
      }
    }
  
    .unordered-list__sub-title{
      display: inline-block;
      font-size: 12px;
      font-weight: lighter;
    }
  }
}