.splashmessage {

  background-color: $background;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 20px;
  position: fixed;
  
  transition: opacity 2s;
  z-index: 1000;
  overflow: hidden;
  
  margin: 0;
  box-sizing: border-box;
  list-style: none;
  background: #fff;

  .title {
    font-weight: bold;
    font-size: 1.2em;
    margin-bottom: 7px;
  }

  .box {
    margin-top: 20px;
    margin-bottom: 20px;
  }

  .detail {
    font-size: 1.1em;
  }

  .button {
    background-color:#7ecd40;
  }

  .center {
    margin: auto;
    text-align: center;
    
    a {
      text-decoration: none;
    }
  }

  .tryagain {
    margin-top: 75px;
  }

}


