/* Energy Stats Status */
.energy-stats-status {
    height: 70px;
    border-radius: 3px;
    border: 1px solid #d8dcde;
    background-color: #fff;
    margin-bottom: 20px;

  .energy-stats__wrapper{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 0;
    margin: auto;
  }

  .energy-stats-header {
    display: inline-flex;
    align-items: center;
    margin: 15px 20px 0 15px;
  }

  /* Production Only */
  &.production-only, &.consumption-only{
    min-height: 112px;
    height: auto;

    div{
      h4{
        font-size: 16px;
        font-weight: 500;
        color: #545456;
        padding-left: 4px;
      }
    }
   .energy-stats__wrapper{
    justify-content: flex-start;
    .energy-stats__left{
      width: 33.3%;
      padding: 0 15px;
      &.elongate{
        width: 50%;
      }
    }

    .energy-stats__right{
      padding: 0 15px;
      width: 33.3%;
      &.elongate{
        width: 50%;
      }
      circle{
        fill: #6c7073
      }
    }

    .grid-seperator {
      border-left: solid 1px #d8dcde;
      height: 45px;
    }

    .energy-type{
      width: auto;
    }

    .energy-type__name {
      color: #7b8793;
      @media screen and (min-width:768px) and (max-width:1024px){
        font-size: 16px !important;
      }
    }

    .energy-type__icon {
      padding-left: 4px;
    }

    .energy-type__details {
    }

    .energy-type__name {
      font-size: 12px;
    }

    .energy-type.energy-previous .energy-type__value {
      color: #008cab;
    }

    .energy-type.energy-last .energy-type__value {
      color: #6c7073;
    }

    .energy-type__value {
      font-size: 16px;
      @media screen and (min-width:768px) and (max-width:1024px){
        font-size: 22px;
      }

      span {
        @media screen and (min-width:768px) and (max-width:1024px){
          font-size: 15px;
        }
      }
    }
   } 
  }

  &.consumption-only {
    .energy-type.energy-current .energy-type__value {
      color: #f37320;
    }

    .energy-type.energy-previous .energy-type__value {
      color: #a6490b!important;
    }
  }
}

/* Screen Size :: 320 */
@media (max-width: 374px) {
  .energy-stats-status {
    .energy-stats__wrapper{
      width: 100%;
    }
  }
}

.night{
  .production-only, .consumption-only{
    div{
      h4{
        color: #f8f8ff;
      }
    } 
    .energy-stats__wrapper {
      .energy-type__name {
        color: #a2a6a9 !important;
      }
    }    
  }
}
