.comp-notification {
  position: absolute;
  z-index: 201;
  color: #fff;
  opacity: 0;
  height: 0;
  text-align: center;
  width: 100%;
  margin: 0 auto;
  font-weight: 300;
  font-size: 14px;
  line-height: 16px;
  display: flex;
  flex-direction: row;
  align-items: center;
  text-align: left;
  background-color: #545456;
  @include body-1;

  .icon {
    margin-right: 10px;
    height: 20px;

    svg{
      width: inherit;
      height: inherit;
    }
    
    &.defaultTick svg circle { fill: #fff; }
  }

  &.comp-notification__demo-user {
    background-color: #545456;
  }

  &.comp-notification__success {
    background-color: #5cd482;
  }
  
  &.comp-notification__failure {
    background-color: #ff5959;
    &.permission-error {
      text-transform: capitalize;
      background-color: #ff5959 !important;
      z-index: 201;
    }
  }
  &.comp-notification__active {
    opacity: 1;
    height: auto;
    padding: 15px;
  }
  &.comp-notification__icon.tickWhiteSmall {
    transform: scale(0.8);
  }
  .button {
    margin: 0;
    background-color: #fff;
    color: #01b5de !important;
    padding: 6px 15px;
    width: unset;
    flex-shrink: 0;
    margin-left: auto;
    font-weight: 500;
    max-width: 110px;
    text-overflow: ellipsis;
    display: block;
    overflow: hidden;
  }
}
.browser {
  #content .comp-notification {
    width: 450px;
    height: 66px;
    top: 80px;
    font-size: 15px;
    display: none;

    &.comp-notification__active {
      display: flex;
    }
  }
  .success-message, .comp-notification {
    left: 0;
    top: 80px;
    bottom: 0;
    right: 0;
    width: 450px !important;
    height: 70px !important;
    margin: 15px auto !important;
  }
  .success-message {
    .icon, svg {
      width: 22px;
      height: 22px;
    }
    .icon {
      margin-right: 15px;
    }
  }
}

.night {
  .comp-notification{
    .cautionAlert{
      path{
        stroke: transparent;
      }
    }
  }
}

/* Support for IOS, avoid overlap in toolbar */
#content > .comp-notification,
#content > div > div > div > .comp-notification {
  margin-top: env(safe-area-inset-top, 0px);
}

.comp-notification__submenu { top: $header-height !important; }
