@import "../font-style";
.energy-independence{
  max-width: 450px;
  width: calc(100% - 30px);

  &.ei__slider-view{
    border: 1px solid rgba(123,135,147,.3);
    border-radius: 5px;
    padding: 10px 15px;
    margin: 15px auto;
  }

  &.ei__icon-view{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 0 !important;

    .energy-type{
      width: auto !important;

      .energy-type__icon-and-value{
        .energy-type__icon{
          min-width: 17px !important;
          width: 17px !important;
          height: 17px !important;
        }

        .energy-type__value{
          font-size: 16px !important;
        }
      }
    }
  }

  .ei__heading{
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 16px;
    font-weight: 500;
    color: #545456;
    margin-bottom: 12px;
    @include title-2;

    .ei__value{
      margin-left: 5px;
    }

    .icon{
      display: flex;
      cursor: pointer;
    }
  }

  .ei__body{
    .rangeslider{
      height: 5px;
      border: none;
      background-image: linear-gradient(to right, $ei_red, $ei_orange, $ei_green);

      .rangeslider__handle{
        width: 20px;
        height: 20px;
        background-color: #ffffff;
        box-shadow: 0 0px 1px rgba(0,0,0,.4), 0 0px 1px rgba(0,0,0,.4);

        .rangeslider__handle-label{
          content: "";
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%,-50%);
          width: 10px;
          height: 10px;
          border-radius: 50%;
          background-color: #dadada;
        }
      }

      .rangeslider__fill{
        background-color: transparent;
      }
    }
  }

  .ei__footer{
    font-size: 10px;
    color: #7B8793;
    margin-top: 12px;
    @include body-1;
  }

  &.ei__unknown{
    .ei__value{
      color: $ei_unknown;
    }

    .ei__body{
      .rangeslider{
        background-image: none;
        background-color: $ei_unknown;

        .rangeslider__handle{
          background-color: #e3e3e6;
          border-color: #e3e3e6;
          display: none;

          .rangeslider__handle-label{
            background-color: $ei_unknown;
          }
        }
      }
    }
  }

  &.ei__red{
    .ei__value{
      color: $ei_red;
    }

    .ei__body{
      .rangeslider__handle{
        background-color: #F8DADA;
        border-color: #F8DADA;

        .rangeslider__handle-label{
          background-color: $ei_red;
        }
      }
    }
  }

  &.ei__red_orange{
    .ei__value{
      color: $ei_red_orange;
    }

    .ei__body{
      .rangeslider__handle{
        background-color: #F8DADA;
        border-color: #F8DADA;

        .rangeslider__handle-label{
          background-color: $ei_red_orange;
        }
      }
    }
  }

  &.ei__orange{
    .ei__value{
      color: $ei_orange;
    }

    .ei__body{
      .rangeslider__handle{
        background-color: #FFECCE;
        border-color: #FFECCE;

        .rangeslider__handle-label{
          background-color: $ei_orange;
        }
      }
    }
  }

  &.ei__orange_green{
    .ei__value{
      color: $ei_orange_green;
    }

    .ei__body{
      .rangeslider__handle{
        background-color: #C8F2C9;
        border-color: #C8F2C9;

        .rangeslider__handle-label{
          background-color: $ei_orange_green;
        }
      }
    }
  }

  &.ei__green{
    .ei__value{
      color: $ei_green;
    }

    .ei__body{
      .rangeslider__handle{
        background-color: #C8F2C9;
        border-color: #C8F2C9;

        .rangeslider__handle-label{
          background-color: $ei_green;
        }
      }
    }
  }
}

.energy-independence-popup{
  top: auto !important;

  &.popup__web{
    width: 500px;
    height: fit-content;
  }

  .ei__popup__content{

    >div:not(:last-child){
      margin-bottom: 20px;
    }

    .ei__formula_title{
      color: #01B4DE;
    }

    .ei__add_storage {
      .ei__add_storage_title {
        color: #01B4DE;
        text-decoration: underline;
      }
    }
    .box {
      border: none;
      border-radius: 10px;
      background-color: rgba(230, 229, 235, 0.6);
      margin-top: 20px;
      .ei-description-wrapper {
          padding: 14px;
          display: flex;
          border-radius: 8px;
        .icon-class {
          margin-right: 10px;

        }
        .text-class{
          text-align: left;
        }
      }
    }
  }
}

.browser{
  .energy-independence{
    width: 100%;
    margin: 0;
    border: none;
    border-bottom: 1px solid rgba(123,135,147,.3);
    border-radius: 0;

    &.ei__icon-view{
      border: none;

      .energy-type{
        .energy-type__icon{
          width: 24px !important;
          height: 24px !important;

          .icon, svg{
            width: inherit !important;
            height: inherit !important;
          }
        }

        .energy-type__value{
          font-size: 24px !important;
        }
      }
    }

    .ei__heading{
      font-size: 25px;
      align-items: baseline;

      >div:first-child{
        display: inline-grid;
      }

      .ei__colon{
        display: none;
      }

      .ei__value{
        margin: 0;
      }

      .icon{
        svg{
          width: 18px;
          height: 18px;
        }
      }
    }

    .ei__footer{
      font-size: 12px;
      margin-top: 15px;
    }
  }
}

.night{
  .energy-independence{
    .ei__heading{
      color: #ffffff;
    }

    .ei__footer{
      color: #a2a6a9;
    }
  }
  .energy-independence-popup{
    .ei-description-wrapper{
      background-color: #333333;
      .infoIconGrey svg path{ fill: white;}
    }
  }
}

