// Common CSS
.live-vitals__main {
  .lvp__card .lvp__card__header {
    .lvp__card__header-details {
      
      .icon {
        width: auto !important;
        // height: 28px !important;
        margin-bottom: 3px;

        &.pes-site-info-grid-icon {
          margin-left: 15px;
        }

        &.batteryDataCharging,
        &.ensembleBatteryCharge {
          height: 20px;
          width: 20px;
          margin-bottom: 4px;
        }
        
        &.ensembleBatteryCharge svg{
          width: 11px !important;
        }

        svg {
          width: auto !important;
          height: 100% !important;
        }

        &.communicating {
          width: 25px !important;
          height: auto !important;

          svg {
            width: 100% !important;
            height: auto !important;
          }
        }

        &.infoIcon {
          width: 10px !important;
          height: 10px !important;
        }
      }
      .energyGridConnected, .energyGridDisconnected{
        margin-bottom:0px;
      }
    }
  }

  .lvp__card .lvp__card__item {
    margin-top: 10px;

    .lvp__card__item-layout { display: block; }

    .lvp__card__item-block {
      margin-top: 12px;
      padding: 10px 20px;
      border-radius: 5px;
      background: #FFFFFF;
      box-shadow: 0 0 6px #00000029;

      .lvp__card__item-heading {
        padding: 0;
        border: unset;
        line-height: 16px;
        font-weight: 500;
        color: #3F4347;
        background: #FFFFFF;

        .lvp__card__item-info {
          width: 12px;
          height: 12px;
          margin-left: 4px;
          margin-bottom: 3px;
          display: flex;
          justify-content: center;
          align-items: center;

          .icon {
            width: 10px;
            height: 10px;
            margin: 0;
            display: flex;
            align-self: unset;
          }
        }
      }

      ul {
        margin-top: 3px;
        display: grid;

        &.two-block-column {
          &.ojasRelaylist {
            .relay-details {
              padding: 0 20px;

              &:first-child {
                padding-left: 0px;
              }
              &:last-child {
                padding-right: 0px;
              }

              p {
                width: unset;
                padding-left: 20px;
              }
            }
          }

          li {
            width: unset;
          }
        }

        li {
          width: unset;
          margin-top: 2px;
          padding: 0;
          flex-direction: unset;
          justify-content: space-between;
          border: unset;
          font-size: 12px;
          line-height: 16px;
          text-align: left;
          color: #7A8793;

          &:first-child { margin-top: 0; }

          p {
            width: 50%;
            margin: 0;

            &.label {
              width: 60%;
            }
          }
        }
      }

      &.enpower-relays {
        ul {
          margin: 0;
          grid-template-columns: 50% 50%;
          gap: 2.5px 18px;
          position: relative;

          &::after {
            content: '';
            width: 1px;
            height: 100%;
            display: block;
            background: #D8DCDE;
            position: absolute;
            left: 50%;
            top: 0;
          }

          li {
            width: unset;
            min-width: 100%;
            margin: 0;
            padding: 0;
            line-height: 18px;
            text-align: left;

            p { font-weight: 500; }
          }

          &.four-block-column {
            li {
              &:nth-child(1) { order: 0; }
              &:nth-child(2) { order: 2; }
              &:nth-child(3) { order: 1; }
              &:nth-child(4) { order: 3; }
            }
          }
        }
      }

      &.load-control {
        .lvp__card__item-heading { justify-content: center; }

        ul {
          margin: 0;

          li {
            width: unset;
            min-width: unset;
            padding: 0;
            text-align: left;

            span{
              width: 30px;
            }

            p.device-name {
              width: unset;
              flex-grow: 1;
              margin-left: 12px;
              font-size: 12px;
            }
            p.device-status {
              width:27%;
              font-weight: 500;
            }
          }
        }
      }

      &.pcu-list {
        .lvp__card__item-heading { justify-content: center; }

        ul.lvp__card__accordion {
          margin: 0;
          grid-template-columns: auto;
          gap: unset;

          li {
            display: flex;
            flex-direction: column;
            border-bottom: #F0F0F0 1px solid;

            &:last-child { border-bottom: unset; }

            p {
              width: unset;
              padding: 8px 5px;
              display: grid;
              grid-template-columns: 20% calc(100% - 20% - 18px) 18px;
              border: unset;
              font-size: 12px;
              line-height: 18px;
              color: #7A8793;
              text-align: left;

              span {
                text-align: left;
              }
              
              span.arrowDown, .arrowUp {
                width: 18px;
                height: 18px;
                padding: 0;
                display: flex;
                align-self: unset;
                justify-content: center;
                align-items: center;

                svg {
                  width: 7px;
                  height: auto;
                }
              }
            }

            .lvp__card__accordion--panel {
              margin: 0;
              padding: 0 5px;
              border: unset;

              ul {
                margin: 0;

                li {
                  border: unset;
                  flex-direction: row;
                  font-size: 12px;
                  line-height: 18px;
                }
              }
            }

            &.active-accordion {
              padding-bottom: 8px;

              p {
                padding-bottom: 0;
              }

              .lvp__card__accordion--panel { display: block; }
            }
          }
        }
      }

      &.voltage-frequency {
        ul {
          margin-top: 8px;
          display: flex;
          justify-content: center;

          li {
            margin: 0;
            padding: 0 27px;
            display: unset;
            border-right: #D8DCDE 1px solid;
            text-align: center;

            &:first-child { padding-left: 0; }
            &:last-child {
              padding-right: 0;
              border-right: unset;
            }

            p {
              width: unset;
              margin-top: 5px;
            }
            .four-block-column li {
              padding: 0 15px;
            }
          }
        }
      }

      &.builtInBattery {
        ul {
          li {
            p {
              &.temperature {
                &::after {
                  content: '';
                }
              }
            }
          }
        }
      }

      &.IQ8D_MicroInverters,
      &.powerIn,
      &.powerOut {
        ul {
          li {
            .label {
              .label-heading {
                span {
                  &:first-child {
                    width: 120px;
                    color: #7A8793;
                    display: inline-block;

                    &::after {
                      content: ':';
                      color: #3F4347;
                    }
                  }
                  &::before {
                    content: '';
                    margin-right: 0;
                  }
                }
              }
            }
          }
        }
      }
    }

    .lvp__card__item-desc {
      margin-top: 10px;
    }
  }

  &.pes-shimmer-main {

    .shimmer-effect {
      animation-duration: 1.2s;
      animation-fill-mode: forwards;
      animation-iteration-count: infinite;
      animation-name: shimmer;
      animation-timing-function: linear;
      background: #ddd;
      background: linear-gradient(to right, #eeededbe 8%, #e6e4e4ea 18%, #ebe9e9f4 33%);
      background-size: 1200px 100%;

      &.live-vital-icon-shimmer {
        width: 26px;
        height: 26px;
        margin-left: 5px;
      }

      &.live-vital-subtitle-shimmer {
        width: 132px;
        height: 12px;
        display: block;
      }

      &.battery-soc-shimmer,
      &.battery-mode-shimmer {
        width: 63px;
        height: 12px;
        margin-left: 5px;
      }

      &.battery-mode-shimmer {
        margin-bottom: 2px;
      }

      &.lvp__card__item-heading-shimmer {
        width: 100%;
        height: 12px;
      }

      &.lvp__card__item-shimmer {
        width: 100%;
        height: 12px;
        display: block;
      }

      &.lvp-view-more-shimmer {
        width: 169px;
        height: 12px;
        margin-top: 15px;
      }

      &.lvp__card__item-relay-shimmer {
        width: calc(50% - 5px);
        height: 12px;
        display: block;
        margin-right: 5px;
      }

      &.lvp__card__item-block-label-shimmer {
        width: 100%;
        height: 12px;
        display: block;
      }

      &.lvp__card__item-block-value-with-colon-shimmer {
        width: 100%;
        height: 12px;
        display: block;
      }

      &.lvp__card__item-block-temperature-shimmer {
        width: 100%;
        height: 12px;
        display: block;
      }

      &.lvp__card__item-block-element-shimmer {
        width: 112px;
        height: 12px;
      }
    }

    .lvp__card__siteInfo {
      .lvp__card__item {
        .lvp__card__item-block {
          &.voltage-frequency {
            ul {
              &.two-block-column {
                li {
                  width: 50%;
                }
              }

              li {
                padding: 0;

                &:first-child {
                  padding-right: 10px;
                }
                &:last-child {
                  padding-left: 10px;
                }
              }
            }
          }
        }
      }
    }

    .lvp__card__systemControls {
      .lvp__card__item {
        .lvp__card__item-block {
          ul {
            &.two-block-column {
              &.ojasRelaylist {
                .relay-details {
                  margin-top: 0;
                  padding-right: 10px;

                  &:last-child {
                    padding-left: 10px;
                    padding-right: 0px;
                  }

                  p {
                    text-align: right;
                    text-align: -webkit-right;
                    width: 50%;
                    padding-left: 0;

                    .lvp__card__item-relay-shimmer {
                      width: calc(100% - 5px);
                      margin-left: 5px;
                      margin-right: 0;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }

    .lvp__card__builtInBattery {
      .lvp__card__item {
        .lvp__card__item-list {
          li {
            &:first-child {
              margin-top: -5px;
            }
          }
        }

        .lvp__card__item-block {
          &.builtInBattery {
            li {
              p {
                width: 100%;

                &.temperature {
                  &::after {
                    content: '';
                  }
                }
              }
            }
          }
        }
      }
    }

    .lvp__card__IQ8D_MicroInverters,
    .lvp__card__powerIn,
    .lvp__card__powerOut {
      .lvp__card__item {
        .lvp__card__item-list {
          li {
            &:first-child {
              margin-top: -5px;
            }
          }
        }
      }

      .lvp__card__item-block {
        &.IQ8D_MicroInverters,
        &.powerIn,
        &.powerOut {
          ul {
            li {
              padding: 10px 4px 10px 0;
            }
          }
        }
      }
    }
  }
}
// Dark Mode CSS
.night {
  .live-vitals__main .lvp__card .lvp__card__item .lvp__card__item-block {
    border: 1px solid rgba(123, 135, 147, 0.3);
    background: unset;
    box-shadow: unset;

    &.voltage-frequency ul li,
    &.load-control ul li,
    &.pcu-list ul.lvp__card__accordion li {
      border-color: rgba(123, 135, 147, 0.3);

      .device-name {color: #a2a6a9;}
    }

    &.enpower-relays ul::after { background: rgba(123, 135, 147, 0.3); }
  }

  .live-vitals__main {
    &.pes-shimmer-main {
      .shimmer-effect {
        animation-duration: 14s;
        background: #262627;
        background: linear-gradient(to right, #6d6d6d2e 8%, #7e77773b 30%, #6d696940 50%);
      }
    }
  }
}

// Web browser CSS
