@import './mixin.scss';

/*
  --- --- --- --- --- ---
  How to use Extend
  --- --- --- --- --- ---
  .selector{
    @extend %hr-vr-center;
  }
*/

%hr-vr-center {
  left: 50%;
  top: 50%;

  @include transform(translate(-50%, -50%));
}

%vr-center {
  top: 50%;

  @include transform(translateY(-50%));
}

%hr-center {
  left: 50%;

  @include transform(translateX(-50%));
}
