.night .lightSettingItem .itemInfo {
  color: #ffffff;
}

.lightSettingItem {
  align-items: center;
  display: flex;
  flex-direction: column;
  z-index: 1;

  &.disabled {
    opacity: 0.5;
  }

  .img {
    width: 30px;
    height: 30px;
    margin-top: 10px;
    margin-bottom: 10px;
    margin-left: 14px;
    margin-right: 14px;

    &.alwaysOnEnable {
      background-image: url(../../images/ojasLightMenu/Light-always-on-switchedOn.svg);
      width: 40px;
      height: 40px;
      background-repeat: no-repeat;
      background-position: center;
      background-size: 24px;
      background-color: #01B5DE;
      border-radius: 40px;
    }

    &.alwaysOnDisable {
      background-image: url(../../images/ojasLightMenu/Light-always-on-switchedOff.svg);
      background-repeat: no-repeat;
      background-position: center;
      width: 40px;
      height: 40px;
    }

    &.automaticEnable {
      background-image: url(../../images/ojasLightMenu/Light-automatic-switchedOn.svg);
      width: 40px;
      height: 40px;
      background-repeat: no-repeat;
      background-position: center;
      background-size: 24px;
      background-color: #01B5DE;
      border-radius: 40px;
    }

    &.automaticDisable {
      background-image: url(../../images/ojasLightMenu/Light-automatic-switchedOff.svg);
      background-repeat: no-repeat;
      background-position: center;
      width: 40px;
      height: 40px;
    }

    &.alwaysOffEnable {
      background-image: url(../../images/ojasLightMenu/Light-always-off-switchedOn.svg);
      width: 40px;
      height: 40px;
      background-repeat: no-repeat;
      background-position: center;
      background-size: 24px;
      background-color: #01B5DE;
      border-radius: 40px;
    }

    &.alwaysOffDisable {
      background-image: url(../../images/ojasLightMenu/Light-always-off-switchedOff.svg);
      width: 40px;
      height: 40px;
      background-repeat: no-repeat;
      background-position: center;
    }
  }

  .itemInfo {
    font-size: 14px;
    font-weight: 500;
    font-weight: normal;
    color: #7B8793;
  }
}

.ojasSurroundLightSettings .settings-light {
  top: 20% !important
}
