.device-details-popup{
  &.popup__web{
    width: 800px;
    bottom: auto;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    .popup__header{
      height: 70px;
      
      p{
        font-size: 20px;
      }
    }
  }

  &.popup__mobile{
    top: auto;
  }

  .popup__body{
    .popup__body-content{
      padding: 0;
    }
  }
}

.led-status__indicator{
  position: relative;
  width: 10px;
  height: 10px;
  min-width: 10px;
  margin: 0 10px 0 0;
  border-radius: 50%;

  .led-status__indicator__led{
    width: 10px;
    height: 10px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: inherit;
    background-color: #eaeaea;
  }

  &.led-status__indicator__has-outline{
    width: 16px;
    height: 16px;
    min-width: 16px;
    border: 1px solid #dedbdb;
  }
  &.led-status__indicator__has-outline-enpower{
    width: 12px;
    height: 12px;
    min-width: 12px;
    border: 1px solid #dedbdb;
    margin: 0 3px 0px 3px;
  }
}

.led-status-indicator-info{
  max-height: 80vh;
  overflow-y: auto;
  
  .lsii__wrapper{
    padding: 20px 20px 110px;
  }

  .lsii__image{
    img{
      border-radius: 10px;
    }
  }

  .lsii__footer{
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
    background: #ffffff;
    padding: 20px;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.51);
  }

  .lsii__item{
    display: flex;
    padding: 20px 0 0;

    .lsii__item__wrapper{
      width: 100%;
    }

    &:not(:last-child){
      .lsii__item__wrapper{
        padding-bottom: 20px;
        border-bottom: solid 0.5px rgba(123, 135, 147, 0.3);
      }
    }

    .lsii__item__title{
      font-size: 15px;
      font-weight: 500;
      line-height: 1.2;
      color: #545456;
      @include title-2;
    }

    .lsii__item__desc{
      font-size: 15px;
      font-weight: 300;
      line-height: 1.2;
      margin-top: 5px;
      color: #545456;
      @include body-1;

      a{
        cursor: pointer;
        text-decoration: none;
        color: #01b5de;
      }
    }

    .led-status__indicator{
      margin: 1px 10px 0 0;
    }
  }

  .lsii__note{
    display: flex;
    align-items: center;
    font-size: 13px;
    font-weight: normal;
    line-height: 1.31;
    color: #f27320;
    padding: 15px;
    border-radius: 5px;
    border: 1px dashed #f27320;
    background-color: rgba(255, 216, 17, 0.02);
    font-style: italic;
    @include body-1;

    span{
      line-height: 1;
    }

    .icon{
      display: inline-flex;
      margin-right: 5px;

      svg{
        width: 14px;
        height: 14px;

        path{
          fill: #f27320;
        }
      }
    }
  }
}

.night{
  .led-status-indicator-info{
    .lsii__item{
      .lsii__item__title{
        color: #f8f8ff;
      }

      .lsii__item__desc{
        color:#a2a6a9;
      }
    }

    .lsii__footer{
      background-color: #262627;
      box-shadow: 0 2px 10px 0 rgba(0,0,0,.25);
    }
  }
}


/* Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) {
}

/* Medium devices (tablets, 768px and up) */
@media (min-width: 768px) {
  .led-status-indicator-info{
    max-height: none;

    .lsii__wrapper{
      display: flex;
      padding: 0;
    }

    .lsii__footer{
      position: relative;
      padding: 0;
      box-shadow: none;
    }

    .lsii__left{
      width: 50%;
      padding: 30px;
      border-right: .5px solid rgba(123,135,147,.3)
    }

    .lsii__right{
      width: 50%;
      height: auto;
      min-height: 400px;
      max-height: 500px;
      overflow-y: auto;
      padding: 30px;
    }

    .lsii__image{
      margin-bottom: 20px;
    }

    .lsii__item{
      &:first-child{
        padding-top: 0;
      }
    }
  }
}

/* Large devices (desktops, 992px and up) */
@media (min-width: 992px) {
  .led-status-indicator-info{
    .lsii__left{
      width: 48%;
    }

    .lsii__right{
      width: 52%;
    }
  }
}

