.settings-card {
  .heading_wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .heading {
      color: #545456;
      font-size: 16px;
      font-weight: 500;
      display: flex;
      justify-content: flex-start;
      margin-right: 10px;


      .om_asw_info_icon {
        display: flex;
        margin-left: 7.5px;
        margin-bottom: 2px;
        cursor: pointer;

        .icon {
          display: flex;
          height: auto;

          svg {
            width: 14px;
            height: 14px;
          }
        }
      }
    }
  }

  .offline_card_outline_btn {
    color: #00b6de !important;
    background-color: transparent;
    border: 1px solid #00b6de;
    margin: 15px auto 0px;
    width: -moz-fit-content;
    width: fit-content;
    font-family: "Visuelt", "Roboto", "Helvectica Neue", Helvetica, Arial, sans-serif;
    font-size: 16px;
    font-weight: 500;
    line-height: normal;
    padding: 0.8em 1.4em;
    border-radius: 100px;
    width: 100%;
    text-align: center;
  }

  .desc {
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    color: #6f6f8c;
    margin-top: 5px;
    text-align: left;
  }

  .status {
    display: flex;
    align-items: center;

    small {
      @extend .desc;
      color: #f37320;
      margin-right: 10px;
      margin-top: 0px;
      line-height: normal;
      font-size: 12px;
      font-weight: 500;
    }
  }

  .offline_card_loader {
    margin: 15px auto 0;
    .icon {
      height: 100%;
      svg {
        width: 40px;
        height: 40px;
      }
    }
  }

  .heading-righttext {
    color: #00b6de;
  }
}

.card-disabled {
  .heading,
  .desc,
  .heading-righttext {
    color: #b1afaf !important;
    pointer-events: none;
  }

  .heading_wrapper .heading .om__bsw_sm_ei_title {
    color: #b1afaf !important;
    pointer-events: none;
  }
}

.night {
  .settings-card {
    .heading_wrapper {
      .heading {
        color: #f8f8ff;

        .om_asw_info_icon {
          .icon {
            svg {
            }
          }
        }
      }
    }

    .offline_card_outline_btn {
      color: #00b6de !important;
    }

    .desc {
      color: #a2a6a9;
    }

    .status {
      small {
        color: #f37320;
      }
    }

    .offline_card_loader {
      .icon {
        svg {
        }
      }
    }

    .heading-righttext {
      color: #00b6de;
    }
  }

  .card-disabled {
    .heading,
    .desc,
    .heading-righttext {
      color: #a2abb3 !important;
    }

    .heading_wrapper .heading .om__bsw_sm_ei_title {
      color: #a2abb3 !important;
    }
  }
}
