@import "../font-style";
.comp-heading{
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;

  .comp-heading__wrapper{
    display: flex;
    align-items: center;
  }

  .comp-heading__icon{
    height: 28px;
    margin-right: 10px;

    .icon{
      height: inherit;
      display: inline-flex;

      svg{
        height: inherit;
        width: inherit;
      }
    }
  }

  .comp-heading__contents{
    display: block;
  }

  .comp-heading__title{
    font-size: 20px;
    font-weight: 500;
    color: #545456;
    @include title-2;
  }

  .comp-heading__sub-heading{
    display: flex;
    align-items: center;
    font-size: 12px;
    font-weight: 300;
    color: #6c7074;

    .comp-heading__info-icon{
      margin-left: 5px;
      cursor: pointer;

      .icon{
        display: flex;
        width: 14px;
        height: 14px;

        svg{
          width: inherit;
          height: inherit;
        }
      }
    }
  }


  &.comp-heading__has-icon{

  }

  &.comp-heading__has-sub-heading{
    
  }

  &.comp-heading__has-icon.comp-heading__has-sub-heading{

  }

  &.comp-heading__inline-icon{
    .comp-heading__wrapper{
      display: block;
    }

    .comp-heading__contents{
      display: flex;
      align-items: center;
    }

    .comp-heading__icon{
      height: 20px;
      margin-right: 5px;
    }

    .comp-heading__sub-heading{
      margin-top: 2px;
    }
  }
}

.night{
  .comp-heading{
    .comp-heading__title{
      color: #f8f8ff;
    }

    .comp-heading__sub-heading{
      color: #9aa2a8;
    }
  }
}
