.graph {
  height: 36%;
  position: relative;

  & > div {
    position: absolute;
  }

  .label {
    position: absolute;
    left: 2em;
    text-transform: uppercase;
    font-size: .8em;
    letter-spacing: .05em;
    padding: 0.3em 1em;
    border-radius: 1em;
    color: $medium-dark;
    &.top { top: 0em; }
    &.bottom { bottom: 0.8em; }
  }
  .events {
    width: 100%;
    height: 100%;
    z-index: 2;
  }
  .hover {
    stroke: $light !important;
    stroke-width: 0.1em;
  }
  .tracker {
    transform: translateY(-3.2em);
    transition: transform 100ms ease-in;

    polyline {
      fill: $secondary-dark;
      stroke-width: 0.1em;
    }
    line, path {
      stroke: $shadow;
    }
  }
  .active .tracker {
    transform: translateY(0);
  }
  .endmark line {
     stroke: $medium-dark;
  }
  svg {
    overflow: visible !important;
  }
  rect {
    fill: $super-light;
  }
  line {
    stroke: $light;
  }
  path {
    fill: none;
    stroke-width: 0;
  }
  circle {
    fill: $dark !important;
    stroke: $background !important;
  }
  .yesterday,
  .lastyear {
    stroke-width: 1.5px;
    /* mix-blend-mode: multiply; */
  }
}