.activationProgressWizard{
    border: 1px solid rgba(123, 135, 147, 0.3);
    border-radius: 3px;
    display: flex;
    align-items: center;
    padding: 15px 20px;

    .right{
        flex: 1;
        margin-left: 20px;

        progress {
            border-radius: 7px; 
            width: 100%;
            height: 5px;
          }
          progress::-webkit-progress-bar {
            background-color: rgba(1, 183, 223, 0.2);
            border-radius: 7px; 
          }
          progress::-webkit-progress-value {
            background-color: #01B4DE;
          }
    }

    p{
        margin: 0;
    }

    .title{
        margin-bottom: 8px;
        font-weight: 500;
    }
    .completion-percentage{
        font-size: 12px;
    }

    .activationProgressBar{
        width: 100%;
        height: 3px;
        .rangeslider__handle{
            display: none;
        }
    }

    .cta{
        color: #01B4DE;
        font-weight: 500;
        display: inline-block;
        margin-top: 12px;
    }
}

.activation-progress-modal{
    .showup__wrapper {
        .showup__inner-wrapper{
            border-radius: 0;

            .showup__body{
                max-height: none;
            }
        }
    }
    .activation-modal{
        .act-modal-header{
            padding: 18px 15px 15px;
            font-size: 16px;
            font-weight: 500;
            color: #131415;
            border-bottom: 1px solid #d8dcde;
        }
        .act-modal-body{
            padding: 20px;
        }
        .act-devices-status{
            table{
                th, td{
                    text-align: left;
                    padding: 8px 0;
                }
                th{
                    font-size: 14px;
                }
                td{
                    font-size: 12px;
                }
                .row {
                    display: flex;
                    align-items: center;
                }
                .device-name, .device-status{
                    .icon{
                        display: inline-block;
                        margin-right: 10px;

                        svg {
                            width: 15px;
                            height: 15px;

                            g {
                                fill: #545456;
                            }
                        }
                    }
                }
            }
        }
        .list-item{
            margin-bottom: 16px;
            font-size: 14px;
        }
        .list-item-indicator-dot{
            width: 5px;
            height: 5px;
            display: inline-block;
            background-color: #01B4DE;
            border-radius: 50%;
            vertical-align: middle;
            margin-right: 10px;
        }
        .act-info-header{
            font-size: 14px;
        }
        .act-info-icon{
            float: right;
        }

    }
}


.night{
    .activationProgressWizard{
        p{
            color: white;
        }
    }
    .activation-progress-modal {
        .activation-modal {
            .act-modal-header{
                color: white;
            }
            .act-devices-status{
                table{
                    th, td{
                        color: white;
                    }
                    path{
                        fill: white;
                    }
                }
            } 
            .act-info-header{
                color: white;
            }
            .list-item{
                color: white;
            }
        }
    }
}