#offline-mode {
    .OL_Pes_SettingsMenu {
        .OM_pesSubmenu {
            .pes-success-message-toast {
                background-color: #14C415 !important;
            }

            .pes-error-message-toast {
                background-color: #DE3C41 !important;
            }
        }
    }
}

.dialog-container {
    &.pes-confirmation-popup {
        width: 300px;
        &.settings {
            .data-area {
                border-bottom: 0;
                text-align: left;
        
                .title-area {
                    padding: 16px;
                }
        
                .data-area {
                    padding: 24px 16px !important;
        
                    .pes-confirm-content-area {
                        padding: 0;
                        margin: 0;
                    }
                }
            }
            .footer-area {
                padding: 0 16px 16px;
                button {
                    border: 1px solid #01B5DE;
                    border-radius: 8px;
                    padding: 8px 16px;
                    color: #01B5DE;
        
                    &.positive-action {
                        color: #fff;
                        background-color: #01B5DE;
                        margin-left: 16px;
                    }
                }
            }
        }
    }
}
