.contact-details-card{
  margin-bottom: 30px;

  .cc__heading{
  }

  .cc__sub-heading{
    font-size: 16px;
    color: #3f4347;
    @include body-1;
  }
}

.contact-details-card, .ls__contact-card{
  margin-bottom: 30px;

  .ls__heading{
    font-size: 14px;
    font-weight: normal;
    line-height: normal;
    color: #545456;
    opacity: .5;
    text-transform: uppercase;
    @include caption;
  }
}

.browser{
  .contact-details-card{
  }
}

/* Dark Mode */
.night{
  .contact-details-card, .ls__contact-card{
    .ls__heading{
      color: #8e9091;
    }
  }
}
