.actionButton{
    background: #00b6de;
    border: none;
    outline: none;
    border-radius: 20px;
    color: white;
    font-size: 12px;
    padding: 6px 48px;
    cursor: pointer;

    &.noBackGround{
        background: transparent;
        float: right;
        color: #00b6de;
        padding: 0;
    }
}