@import "fonts";
.contact-card{
  font-style: normal;
  font-stretch: normal;
  font-weight: normal;
  line-height: normal;
  letter-spacing: normal;
  width: 100%;
  margin: 18px 0 0;
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0 2px 10px 0 rgba(63, 67, 71, 0.08);

  .cc__wrapper{
    padding: 20px;
  }

  a{
    text-decoration: none;
    color: inherit !important;
  }

  .cc__hr{
    height: 1px;
    width: 100%;
    margin: 15px auto;
    background-color: rgba(123, 135, 147, 0.3); 
    border-style: none;
  }

  .cc__heading{
    font-size: 16px;
    font-weight: 500;
    color: #545456;
    @include title-2;

    &:not(:first-child){
      margin-top: 20px;
    }
  }

  .cc__sub-heading{
    font-size: 12px;
    font-weight: 300;
    margin-top: 5px;
    color: #7b8793;
  }

  .cc__customer-support{
    font-size: 16px;
    @include body-1;
  }

  .cc__technical-support{
    font-size: 12px;
    font-weight: 300;
    color: #7b8793;
    @include body-1;
  }

  .cc__icon{
    display: flex;
    align-items: center;
    height: 16px;
    width: 16px;

    svg{
      height: 100%;
      width: 100%;
    }

    &.world > svg { width: auto; }
  }

  .cc__text{
   font-size: 14px;
   font-weight: 500; 
   line-height: 1;
   @include body-2;
  }

  .cc__icon-text{
    display: flex;
    align-items: center;

    &:not(:first-child){
      margin-top: 15px;
    }

    a{
      display: flex;
      align-items: center;
    }

    .cc__icon{
      &+.cc__text{
        margin-left: 10px;
        word-break: break-all;
      }
    }
  }

  .cc__action-button{
    .cc__icon{
      path{
        fill: #01b4de;
      }
    }
  
    .cc__text{
      color: #01b4de;
    }
  }

  .cc__actions{
    display: inline-block;
    margin: auto;
    width: 100%;
  }
  .cc__button .cc__icon-text .cc__text{
    @include title-2;
  }
  & .cc__button__outline-blue, .cc__button__bg-blue{ width:100%; }
}

.cc__button{
  float: left;
  min-width: 130px;
  border: 1px solid #ffffff;
  border-radius: 22px;
  padding: 10px 15px;
  text-align: center;
  margin: 15px 15px 0 0;
  cursor: pointer;

  &:last-child { margin-right: 0; }
  
  .cc__icon-text{
    justify-content: center;
  }
  
  &.cc__button__outline-blue{
    border-color: #01b4de;
    color: #01b4de;
  }

  &.cc__button__bg-blue{
    background-color: #01b4de;
    color: #ffffff;

    .cc__icon{
      path{
        fill: #ffffff;
      }
    }
  }
}

@media (max-width:359px){
  .contact-card{
    .cc__button{
      float: none;

      &:not(:last-child){
        margin-right: 0;
      }
    }

    .cc__actions{
      display: inline-block;
      margin: auto;
      width: 100%;
    }
  }
}

@media (min-width:360px) and (max-width:374px){
  .contact-card{
    .cc__button{
      &:not(:last-child){
        margin-right: 10px;
      }
    }
  }
}

@media (min-width:768px){
  .contact-card{
    .cc__button{
      float: left;
  
      &:not(:last-child){
        margin-right: 20px;
      }
    }
    
    .cc__actions{
      display: inline-block;
      margin: auto;
      width: 100%;
    }
  }
}

/* Dark Mode */
.night{
  .contact-card{
    background-color: #262627;
    box-shadow: 0 2px 10px 0 rgba(0,0,0,.25);

    .cc__heading{
      color: #f8f8ff;
    }
  
    .cc__sub-heading{
      color: #a2a6a9;
    }

    .cc__button{
      border-color: #262627;

      &.cc__button__outline-blue{
        border-color: #01b4de;
        color: #01b4de;
      }
    }
  }
}