.sitedetails {
  .comp-notification.comp-notification__failure {
    background-color: #545456;

    &.permissioDenied-red{
      background: #ff5959;
    }
  }
}

.padding {
  padding: 20px;
  color: #545456;

  .comp-notification{
    position: absolute;
    top: $header-height;
    left: 0;
  }
  
  .heading {
    opacity: 0.5;
    font-size: 14px;
    line-height: normal;
    margin: 0 0 15px 0;
    text-transform: uppercase;
    @include caption;
  }

  .outer-block {
    border-radius: 10px;
    box-shadow: 0 2px 10px 0 rgba(63, 67, 71, 0.08);
    background-color: #ffffff;
    padding: 20px;
    margin-bottom: 30px;
    .address-details {
      color: #3f4347;
      margin-bottom: 15px;
      line-height: normal;
      border-bottom: 1px solid 0.5px rgba(123, 135, 147, 0.3);
      p:nth-child(1){
        margin: 0;
        font-size: 16px;
        font-weight: 500;
        margin-bottom: 4px;
        color: #545456;
        @include title-2;
      }
      p:nth-child(2){
        margin: 0;
        font-size: 16px;
        font-weight: 300;
        line-height: 1.38;
        color: #3f4347;
        margin-bottom: 10px;
        @include body-1;
      }      
      address {
        margin-bottom: 15px;
        font-size: 16px;
        font-weight: 300;
        line-height: 1.38;
        font-style: normal;
        @include body-1;
      }
    }
    .sys-info{
      margin-top: 20px;
      padding-top: 20px;
      border-top: solid 0.5px rgba(123, 135, 147, 0.3);
      p{
        margin: 0;
      }
      p:nth-child(1){
        padding-bottom: 4px;
        font-size: 16px;
        font-weight: 500;
        color: #545456;
      }
      p:nth-child(2){
        font-size: 16px;
        font-weight: 300;
        line-height: 1.38;
        color: #3f4347;
      }
    }
    .site-image {
      width: 100%;
      height: 200px;
      img{
        width: 100%;
        height: inherit;
        object-fit: cover;
        border-radius: 10px;
        @media screen and (min-width:768px) and (max-width:1024px){
          width: 280px;
        }
      }
    }
    .contact-info {
      a {
        display: flex;
        color: #3f4347;
        margin-bottom: 10px;
        text-decoration: none;
        svg {
          width: 16px;
          height: 16px;
          margin-right: 15px;
          display: flex;

          path{
            fill: #01b4de;
          }
        }
        span {
          font-size: 16px;
          font-weight: 300;
          line-height: 1.38;
          word-break: break-word;
          color: #01b4de;
        }
        span.icon {
          align-self: unset;
          margin-top: 3px;
        }
      }
    }
  }
  .m-b {
    margin-bottom: 20px;
  }
  .m-b-0 {
    margin-bottom: 0px !important;
  }
}
.carousel .carousel-slider {
  background-color: inherit;
}

.carousel .slide {
  background-color: inherit;
}
.carousel .slider-wrapper.axis-horizontal .slider .slide{
  padding-right: 10px;
  @media screen and (min-width:768px) and (max-width:1024px){
    min-width: 50% !important;
  }
  @media screen and (min-width:768px) and (max-width:1024px) and (orientation : landscape){
    min-width: 50% !important;
  }
}
.carousel .slider-wrapper.axis-horizontal .slider  li:last-child{
  padding-right: 0;
}
.carousel .slide img {
  height: 180px;
  border-radius: 10px;
  object-fit: cover;
}

.carousel .control-dots {
  position: unset;
}

.carousel .control-dots .dot { 
  border: solid 1px #01b5de; 
  box-shadow: unset;
  background-color: transparent;
  opacity: unset;
}

.carousel .control-dots .dot.selected{
  background-color: #01b5de;
  opacity: unset;
}

.rm-border{
  border-bottom: none !important;
}

.hidden {
  display: none !important;
}

.modal {
  height: calc(91vh - 134px);
  overflow: hidden;
  background-color: #262627 !important;
  color: #262627 !important;
  z-index: 1;
}

.dialog {
  width: 100%;
  height: 230px;
  position: static;
  left: 0%;
  top: 40%;
  border: none;
  padding: 0;
  margin: 0;
  background: #262627;
  @media screen and (min-width:768px) and (max-width:1024px){
    top: 28%;
  }
  @media screen and (min-width:768px) and (max-width:1024px) and (orientation : landscape){
    top: 35%;
  }
}

.image {
  width: inherit;
  height: inherit;
  display: block;
  margin-left: auto;
  margin-right: auto;
  @media screen and (min-width:768px) and (max-width:1024px) and (orientation : portrait){
    width:100%;
    height: 480px;
  }
  @media screen and (min-width:768px) and (max-width:1024px) and (orientation : landscape){
    width: 65%;
    height: 300px;
  }
}
.closeButton {
  cursor: pointer;
  padding: 10px;
  background-color: #262627;
  z-index: 1;
  svg {
    float: right;
  }
}
.carousel-modal{
  .carousel .slider-wrapper.axis-horizontal .slider .slide{
    padding: 0;
    @media screen and (min-width:768px) and (max-width:1024px){
      min-width: 100% !important;
    }
  }
  .carousel .slide img {
    width:100%;
    height: 230px;
    border-radius: 0;
    @media screen and (min-width:768px) and (max-width:1024px) and (orientation : portrait){
      width:100%;
      height: 480px;
    }
    @media screen and (min-width:768px) and (max-width:1024px) and (orientation : landscape){
      width: 65%;
      height: 300px !important;
    }
  }
  .carousel .control-dots {
    position: fixed;
    bottom: #{$footer-height};
  }
}

.bkp-config{
  margin-top: 15px;
  padding-top: 15px;
  border-top: 1px solid rgba(123,135,147,.3);

  .bkp-config__header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 16px;
    font-weight: 500;
    color: #545456;
    @include body-2;
  }

  .bkp-config__action{
    font-size: 12px;
    font-weight: 500;
    color: #01b4de;
    cursor: pointer;
  }

  .bkp-config__body{
    display: flex;
    align-items: center;
    margin-top: 10px;
  }

  .bkp-config__icon{
    margin-right: 10px;
  }

  .bkp-config__value{
    font-size: 16px;
    font-weight: 300;
    line-height: 1.38;
    letter-spacing: normal;
    color: #3f4347;
    @include body-1;

    &.bkp-config__not-selected{
      color: #a6a6a8;
    }
  }
}

.connection-type-card{
  margin-top: 15px;
  padding-top: 15px;
  border-top: 1px solid rgba(123,135,147,.3);
  position: relative;
  .connection-type-card_header{
    display: flex;
    align-items: baseline;
  }
    & .icon{
      display: inline;
    }

    .connection-type__configure {
      margin: 15px 0 0;
      padding: 0;
      border: unset;
      outline: unset;
      font-size: 14px;
      font-weight: 500;
      line-height: 1;
      color: #00b6de;
      background: unset;
      cursor: pointer;
      @include body-2;
    }

  .connection-type-header{
    margin-left: 15px;
    font-size: 16px;
    font-weight: 500;
    color: #545456;
    position: relative;
    @include body-2;
  }
  .connection-type-body{
    margin-top: 10px;
    font-size: 12px;
    font-weight: 300;
    color: #7b8793;
    margin-left: 35px;
  }
}

.site-image-wrapper{
  margin-top: 15px;
  border-top: 1px solid rgba(123, 135, 147, 0.3);

  .site-image-title{
    margin: 0;
    @include body-2;
    color: #545455;
    display: flex;
    align-items: center;
    margin: 12px 0; 
    
    .siteImageIcon{
      margin-right: 11px;
      svg {
        width: 18px;
        height: 18px;
      }
    }
  }
  .site-images{
    .slider-wrapper{
      .slider > .slide {
        .image-wrapper {
          padding: 12px;
  
          .image-wrapper__header {
            display: flex;
            justify-content: space-between;
            margin-bottom: 12px;
            
            .site-image-buttons{
              background: transparent;
  
              &.status-page-button{
                border: 1px solid #DEDEDE;
                border-radius: 8px;
                color: #7D8793;
                @include caption;
              }
  
              &.delete-button {
                border: none;
              }
  
              &.active{
                color: #01B5DE;
              }
            }
          }
        }
      }
    }
    .carousel-slider{
      padding-bottom: 25px;
      .control-dots {
        position: absolute;
        bottom: -15px;
      }

      .slider-wrapper{
        ul {
          .slide {
            padding: 0;
            border-style: dashed;
            border-color: #C3C3C3;
            border-radius: 8px;
            color: #01B4DE;
            @include caption;

            &:last-child {
              display: grid;
              place-content: center;
            }
          }
        }
      }
    }
    &.no-siteImage{
      .carousel-slider > .slider-wrapper > .slider {
        min-height: 257px;
      }
    }
  }
}

.site-image-popups{
  .showup__wrapper > .showup__inner-wrapper > .showup__body > .site-image-popups__wrapper{
    padding: 22px 21px;

    .error-text {
      margin: 21px 0 0;
    }
  }

  &.setAsSiteImage, &.deleteImage {
    .showup__wrapper > .showup__inner-wrapper { 
      .showup__body {
        .site-image-popups__wrapper{
          padding: 0;

          .site-image-popups__wrapper-header {
            @include title-2;
            text-align: center;
            border-bottom: 1px solid #D8DCDE;
            padding: 15px 0;
            color: #131415;
          }

          .site-image-popups__wrapper-content {
            padding: 16px 27px;

            .description1, .description2 {
              color: #545455;
              @include body-1;
              margin-bottom: 0;
            }
          }
        }
      }
    }
  }
}

  
.set-bkp-config .comp-radio-button {
  line-height: 23px;

  &:first-child { margin-bottom: 15px; }
}

.browser{
  .bkp-config{
    .bkp-config__header{
      justify-content: unset;
    }
  
    .bkp-config__action{
      margin-left: 20px;
    }
  }

  .set-bkp-config .comp-radio-button:first-child { margin-bottom: 30px; }
}

.night{
  .bkp-config{
    .bkp-config__header{
      color: #f8f8ff;
    }

    .bkp-config__icon {
      .icon svg > g > path { fill: #d8dcde; }
    }
  
    .bkp-config__value{
      color: #a2a6a9;
  
      &.bkp-config__not-selected{
        color: #a6a6a8;
      }
    }
  }
  .setAsSiteImage, .deleteImage {
    .showup__wrapper > .showup__inner-wrapper > .showup__body > .site-image-popups__wrapper{
      .site-image-popups__wrapper-header{
        color: white;
      }
      .site-image-popups__wrapper-content{
        .description1, .description2 { color: #A2A6A9 }
      }
    }
  }
  .site-images {
    .carousel > .slider-wrapper {
      ul > li {
        .no-siteImages > .no-image-action{
          .uploadImageSmall {
            svg {
              g > g {
                #Group_12045 {
                  circle { 
                    fill: #262627;
                    // opacity: 37%;
                  }
                }
                #Group_12769{
                  g {
                    path, circle, line { stroke: #01b4de;}
                  }
                  circle { fill: #262627; }
                }
                path { stroke: #01b4de;}
              }
            }
          }
        }
      }
    }
  }
}