.loadingCircleV2Container {
  position: relative;
  transform: scale(0.1);
  .circle {
    position: absolute;
    top: 0;
    left: 0;
    width: 2px;
    height: 2px;
    background-color: #01b4de;
    border-radius: 1px;
    transform-origin: 50px 50px;
    .breather {
      animation: breath 512ms alternate infinite;
      width: 10px;
      height: 10px;
      background-color: #01b4de;
      border-radius: 5px;
    }

    &.circle1 {
      .breather {
        animation-delay: 1s;
      }
      transform: scale(3) rotate(360deg);
    }
    &.circle2 {
      .breather {
        animation-delay: 125ms;
      }
      transform: scale(3) rotate(45deg);
    }
    &.circle3 {
      .breather {
        animation-delay: 250ms;
      }
      transform: scale(3) rotate(90deg);
    }
    &.circle4 {
      .breather {
        animation-delay: 375ms;
      }
      transform: scale(3) rotate(135deg);
    }
    &.circle5 {
      .breather {
        animation-delay: 500ms;
      }
      transform: scale(3) rotate(180deg);
    }
    &.circle6 {
      .breather {
        animation-delay: 625ms;
      }
      transform: scale(3) rotate(225deg);
    }
    &.circle7 {
      .breather {
        animation-delay: 750ms;
      }
      transform: scale(3) rotate(270deg);
    }
    &.circle8 {
      .breather {
        animation-delay: 875ms;
      }
      transform: scale(3) rotate(315deg);
    }
  }

  @keyframes breath {
    0% {
      transform: scale(1);
    }
    100% {
      transform: scale(3);
    }
  }
}

@keyframes breath {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(3);
  }
}
