.app-update-scrim {
    opacity: 0;
    position: fixed;
    width: 100vw;
    top: 0;
    z-index: -1;
    height: 100%;
    background-color: #000000B3;
    transition: opacity .2s;
    &.show {
        z-index: 1000;
        opacity: 1;
    }

    .app-update-dialog {
        min-width: 300px;
        max-width: 70%;
        margin: auto;
        background-color: #ffffff;
        border-radius: 10px;
        position: relative;
        top: 50%;
        transform: translateY(-50%);
        color: #545456;
        overflow: hidden;
    }

    .icon.logo {
        display: inline-block;
        margin: 2.3em 2.3em 0 2.3em;
        svg {
            width: 4.5em;
            height: 4.5em;
        }
    }

    .app-update-header {
        font-size: 1.5em;
        font-weight: 500;
        line-height: 1.3;
        margin: 0.75em 1.5em 0 1.5em;
        padding-bottom: 1em;
        border-bottom: solid 0.5px #d6d9de;
    }

    .feature-label {
        padding: 1em 1.9em 0 1.9em;
        font-size: 1.2em;
        font-weight: 500;
        line-height: 1.63;
    }

    .feature-points {
        min-height: 8em;
        max-height: 18em;
        overflow-y: auto;
        margin: 0.1em 2.2em;
        & div:first-child {
            margin-top: 1.3em;
        }
        & div:last-child {
            margin-bottom: 0.8em;
        }
        &.fade:after {
            content: "";
            position: absolute;
            z-index: 1;
            bottom: 0;
            left: 0;
            pointer-events: none;
            background-image: linear-gradient(180deg,#ffffff1a,#ffffffe6);
            width: 100%;
            height: 3.8em;
            transform: translateY(-102%);
          }
          &.fade:before {
            content: "";
            position: absolute;
            z-index: 1;
            left: 0;
            pointer-events: none;
            background-image: linear-gradient(0deg,#ffffff00,#ffffffff);
            width: 100%;
            height: 2.5em;
            transform: translateY(-10%);
          }
        &::-webkit-scrollbar {
            display: none;
        }
    }

    .feature-points div {
        position: relative;
        padding: 0.5em 0 0.5em 2.4em;
        font-size: 1.05em;
        line-height: 1.36;
    }

    .feature-points span {
        display: inline-block;
        position: absolute;
        transform: translate(-2.6em,-.2em);
        font-size: 0.9em;
        font-weight: 700;
        line-height: 1.58;
        color: #fff;
        background-color: #f37320;
        height: 1.85em;
        width: 1.85em;
        border-radius: 50%;
        text-align: center;
        padding-top: .2em;
    }

    .footer-area {
        width: 100%;
        border-top: solid 1px #d8d8d8;
        display: flex;
    }

    .footer-area button {
        border: unset;
        background-color: #ffffff;
        width: 50%;
        margin: 0;
        padding: 1em 0;
        font-size: 1.2em;
        outline: none;
        color: inherit;
        cursor: pointer;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }

    .footer-area button:first-child{
        border-bottom-left-radius: 0.6em;
    }

    .footer-area button:last-child {
        border-left: solid 1px #d6d9de;
        color: #01b4de;
        border-bottom-right-radius: 0.6em;
    }
    .footer-area .server-release-btn {
        border-bottom-left-radius: 0.6em;
        border-bottom-right-radius: 0.6em;
        border-left: 0px;
        width: 100%;
    }
}

@media screen and (max-width:480px) {
    .app-update-scrim{
        .app-update-dialog {
            max-width: 90%;
        }
    }
}

.night {
    .app-update-scrim {
        .app-update-dialog {
            background-color: #262627;
            color: #f8f8ff;
        }
    
        .footer-area {
            border-top: solid 1px #d8d8d859;
        }
        
        .footer-area button:last-child {
            border-left: solid 1px #9c9c9c59;
        }
        .footer-area button.server-release-btn {
            border-left: 0px;
        }
        
        .app-update-header {
            border-bottom: .5px solid #d6d9de59;
        }

        .feature-points {
            &.fade:after {
                background-image: linear-gradient(180deg,#2626271a,#262627e6);
            }
            &.fade:before {
                background-image: linear-gradient(0deg,#26262700,#262627ff);
            }
        }
    }
}

.browser .app-update-scrim {
    .app-update-dialog {
        min-width: 712px;
        max-width: 55%;
        border-radius: 3px;
        max-height: 100vh;
        overflow-y: auto;
    }
    .icon.logo {
        margin: 32px 32px 0 32px;
        svg {
            width: 100px;
            height: 100px;
        }
    }
    .app-update-header,
    .feature-label {
        font-size: 20px;
        font-weight: 500;
        line-height: 1.3;
        margin: 20px 32px 20px 32px;
    }
    .feature-label {
        padding: 0;
        margin-bottom: 0;
    }
    .feature-points{
        margin: 0.1em 32px;
        div {
            padding: 19px 0 15px 36px;
            line-height: 1.12;
            font-size: 17px;
            span {
                padding-top: 4px;
                font-size: 15px;
                font-weight: 500;
                line-height: 1.27;
                height: 26px;
                width: 26px;
                transform: translate(-36px, -4px);
            }
        }
    }
    .feature-points.fade:after {
        transform: translateY(-154%);
      }
    .feature-points div:first-child {
        margin-top: 10px;
    }

    .footer-area {
        border-top: none;
        .server-release-btn {
            width: fit-content;
            font-weight: 400;
            font-size: 17px;
            margin: 35px auto 32px auto;
            padding: 14px 4em;
            background-color: #01b4de;
            color: #fff;
            border-radius: 25px;
            text-transform: uppercase;
        }
    }
}
