.browser {
	.popup-container {
		&.battery-storage {
			.popup-wrapper {
				height: auto;
			}
		}
	}
	.advance-battery-settings.desktop{
    padding: 20px;
    border: solid 1px rgba(136, 136, 136, 0.2);
    border-radius: 10px;

    .overlay{
      position: absolute;
      left: 50%;
      top: 50%;
      background-color: $screen;
      height: 100%;
      width: 100%;
      transform: translate(-50%, -50%);
      z-index: 102;
    }
  }

	.scrim {
		.alertDialog.weather-alert {
			width: 450px;
			border-radius: 3px;
			.data-area {
				border-bottom: none;
				padding: 42px 52px;
				.cautionAlert {
					padding-top: 0px;
					padding-bottom: 0px;
					height: 60px;
					svg {
						width: 60px;
						height: 60px;
					}
				}
				.alertContent {
					font-size: 15px;
					line-height: 1.4;
					color: #545456;
					margin-top: 30px;

					a { color: #00b6de; }
				}
			}
			.footer-area {
				display: flex;
				align-items: center;
				justify-content: center;
				padding-bottom: 32px;
				#positiveAction {
					padding: 12px;
					width: 315px;
					color: #ffffff;
					height: 50px;
					border-radius: 25px;
					background-color: #00b6de;
					font-size: 17px;
					line-height: normal;
					text-transform: uppercase;
				}
			}
		}
	}
	.scrim-pop-up {
		.popup__grid {
			#popUpWrapper{
				&.weather-watch-info {
					top: 0;
					right: 0;
					left: 0;
					bottom: 0;
					width: 450px;
					height: max-content;
					.close_popup {
						.close {
							display: flex;
						}
						svg polygon {
							fill: #545456;
						}
					}
				}
				&.weather-watch-disclaimer {
					width: 710px;
					height: 570px;
					max-width: 90%;
					top: 0;
					bottom: 0;
					left: 0;
					right: 0;
					border-radius: 3px;
					.popup__header {
						height: 75px;
						border-bottom: solid 1px #d8dcde;
						p {
							font-size: 20px;
							font-weight: 500;
						}
					}
					.disclaimer-content {
						font-size: 17px;
						line-height: 1.41;
						color: #8e9091;
						overflow-y: scroll;
    				margin: 32px 0px;
						p {
							margin: 0px;
						}
						.shadowOverlay {
							display: none;
						}
					}
					.disclaimer-button-wrapper {
						display: flex;
						flex-direction: column;
						align-items: center;
						.disclaimer-button {
							text-transform: uppercase;
							&.agree-button {
								width: 315px;
							}
							&.disagree-button {
								height: auto;
								margin: 32px 0px;
							}
						}
					}
				}
			}
		}
	}
	.storage {
		height: 100%;
		margin: 32px;
    padding: 32px 0px;
		border-radius: 3px;
		box-shadow: 0 2px 10px 0 rgba(63, 67, 71, 0.08);
		background-color: #ffffff;
		overflow: scroll;
		.comp-notification.comp-notification__failure {
			background-color: #ff5959;
		}
		.storage-mgmt {
			padding: 24px 32px 0px;
			border: none;
			.img-blk {
				margin-right: 32px;
			}
			.text-blk {
				font-size: 25px;
				font-weight: 500;
				color: #545456;
			}
		}

		.storage-web {
			flex-wrap: nowrap !important;
			overflow-x: scroll !important;
		}
		.storage-list-wrapper {
			display: flex;
			justify-content: space-between;
			margin: 32px;
			flex-wrap: wrap;


      
      &.left-align{
        justify-content: flex-start;
      }

			.storage-blk {
				margin: 0px;
				flex: 0 0 33.33%;
				box-shadow: none;
				.storage-list {
					height: 100%;
					display: flex;
					flex-direction: column;
					margin-right: 32px;
					border-radius: 10px;
					border: solid 1px rgba(136, 136, 136, 0.2);
					background-color: #ffffff;
					p {
						font-size: 15px;
					}
					.edit-block {
						.storage-title {
							font-size: 20px;
						}
						svg {
							width: 20px;
							height: 20px;
						}
					}

          .electricity-rate{
            .button-link{
              font-size: 14px;
            }
          }
          
					.activate-mode {
						height: 100%;
						display: flex;
						flex-direction: column;
						justify-content: space-between;
						.storage-button {
							font-size: 17px;
							font-weight: 500;
							text-transform: uppercase;
						}
						.reserveBattery {
							span, .mainContent {
								font-size: 17px;
							}
							svg {
								width: 26px;
								height: 26px;
							}
							.contents {
								font-size: 12px;
							}
						}
					}
				}
				&:last-child {
					.storage-list {
						margin-right: 0px;
					}
				}
			}
		}
		.backup-history-wrapper {
			margin: 0px 32px;
			.backup-history-title {
				margin: 32px 0px;
				font-size: 20px;
				font-weight: 500;
				color: #545456;
			}
			.backup-list__header {
				border-bottom: 1px solid #f4f3f4;
				border-top: 1px solid #f4f3f4;
				.backup-list__header__content {
					padding: 15px 32px;
				}
				.backup-list__title {
					font-size: 17px;
				}
				.backup-list__number {
					font-size: 25px;
				}
			}
			.backup-history__body {
				height: auto;
				border: none;
				li {
					font-size: 17px;
					border: none;
					padding-bottom: 0px;
				}
			}
			.backup-list__wrapper {
				padding: 0px 32px;
				.backup-list__element{
         	span:nth-child(2){
						padding-left: 32px;
         	}
        }
			}
		}
		.weather-watch {
			display: flex;
			justify-content: space-between;
			margin: 0px;
			padding: 0px 32px 32px;
			.weather-watch-title-status {
				width: 100%;
				.infoIcon {
					align-self: center !important;
					margin-top: 0px !important;
				}
			}
			.weather-watch-title {
				font-size: 20px !important;
				margin-right: 12px !important;
			}
			.weather-watch-infoIcon {
				svg {
					width: 16px !important;
					height: 16px !important;
				}
			}
			.weather-watch-description {
				font-size: 15px;
				margin-top: 12px;
			}
			.weather-watch-status {
				display: flex;
				margin-left: auto;
				padding-right: 20px;
				font-size: 15px;
			}
			.weather-status-wrapper {
				display: flex;
				align-items: center;
			}
		}
	}
	.battery-reserve {
		padding: 0 30px 30px;

		.line-break {
			display: none;
		}
		.reserve__disclaimer {
			padding: 20px 0;
			font-size: 16px;
		}
		#save {
			position: static;
			margin-bottom: 20px;
			.text {
				font-size: 17px;
  			font-weight: 500;
			}
		}
		.reserve-container {
			padding: 25px 0px;
			font-size: 20px;
		}
		.slider {
			margin: 0px;
			padding: 25px 0px;
			.rangeslider {
				height: 7px;
			}
			.rangeslider__handle {
				width: 28px;
				height: 28px;
			}
			.rangeslider__label-item {
				font-size: 10px;
			}
			.rangeslider-horizontal {
				background-color: #e9e9e9;
			}
		}
	}
}

.menu-name {
	&.batteryStorage {
		.icon {
			&.advanceSettings {
				svg {
				width: 32px;
				height: 32px;
				}
			}
		}
	}
}

.battery-storage-notify {
	background-color: #545456 !important;
    z-index: 2 !important;
}

.info-popup span.status-text { 
	&.enabled{
		color: #f37320 !important;
		font-weight: bold;
	}
	&.disabled{
		color: #545456 !important;
		font-weight: 700;
	}
}

.advanceBatterySettings {
	.comp-notification {
		.icon {
			&.cautionAlert {
				svg {
					g {
						path:nth-child(2) {
							fill: black;
						}
					}
				}
			}
		}
	}
}

.advance-battery-settings {
	position: relative;
  padding: 15px;

	.rangeslider__labels {
		margin-top: 6px;
	}

	.overlay{
		position: absolute;
    left: 50%;
    top: 50%;
    background-color: $screen;
    height: 100%;
    width: 100%;
    transform: translate(-50%, -50%);
    z-index: 102;
    border-radius: 10px;
	}

  .inLineRequestStatus{
    display: flex;
    align-items: center;
    font-size: 12px;
    color: #545456;
    opacity: 0.6;
    padding: 0 14px 0 5px;
    .icon{
      height: fit-content;
      display: flex;

      &.clockCircular svg{
        width: 15px;

        g{
          fill : #01B4DE;

          path{
            fill: #01B4DE;
          }
        }
      }
    }

    label{
      padding-left: 5px;
      font-weight: 400;
    }
  }

  .buttonInProgress{
    position: relative;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    padding: 10px 0 0;

    .icon.loaderV2{
      display: flex;
      position: relative;
    }
  }

	
	&.desktop {
		.save-button {
      width: 300px;
      border-radius: 30px;
      margin: auto;
		}

		.info-1, .info-2 {
			font-size: 15px;
      line-height: 1.25;
      color: #6f6f8c;
      font-weight: 300;
		}

		.heading {
			font-size: 20px;
    	font-weight: normal;
		}

    .error-msg{
      font-size: 15px;
    }

    .save-button{
      &.buttonCancel{
        border: 1px solid #01b5de;
      }
    }
	}

	.rangeslider-horizontal {
		height: 7px;
		background-color: rgb(233, 233, 233);
		border: none;
	}

	.rangeslider .rangeslider__handle {
		border: 4px solid white;
		box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.3);
		
	}

  .icon.storageBattery{
    margin-right: 10px;
  }

	.shutdown-level-slider {
		width: 100%;

    .multi-slider{
      display: flex;
      align-items: center;
      justify-content: center;
    }
	}

	.heading {
    display: flex;
    justify-content: space-between;
    align-items: center;
		margin-bottom: 20px;
		font-weight: 500;
    color: #545456;
		
    .heading-span {
			color: #00b6de;
			font-size: 16px;
		}
	}

	.heading, .info-1, .info-2 {
		.heading-span {
			color: #00b6de;
		}
		font-size: 16px;
	}

	.info-1, .info-2 {
		font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    color: #6F6F8C;
	}

	.info-1 {
		margin-top: 30px;
		margin-bottom: 10px;
	}

	.info-2 {
		margin-top: 10px;
    margin-bottom: 0;
    color: #B2B6B7;
	}

  .heading_right{
    display: flex;
    align-items: center;
  }

	.pending {
		font-size: 12px;
    display: block;
    line-height: 14px;
		font-weight: 400;
    margin-left: 5px;
		
		&.buttonInActive {
		  color: #ffffff;
		}
	}

  .light {
    font-weight: 300;
  }

  .error-msg{
    font-size: 12px;
    line-height: 16px;
    margin: 10px 0 20px;
    color: #E3655E;
  }

	.save-button {
    display: flex;
    justify-content: center;
    font-size: 16px;
		box-shadow: 1px 2px 10px 0 rgba(63, 67, 71, 0.08);
		border: 1px solid #00b6de;
		background: #00b6de;
		color: white;
		text-align: center;
    width: calc(100% + 30px);
    margin: 15px -15px -15px -15px;
    border-radius: 0 0 10px 10px;
    height: 45px;
    line-height: 45px;
    padding: 0;

		&.buttonCancel {
      font-size: 18px;
		  background-color: #fff;
		  color: #01b5de !important;
		  border: none;
      border-top: 1px solid #E9E9E9;
		}
		&.buttonDisable {
		  background: #a2a6a9;
		  border: none;
      pointer-events: none;
		}
	
		&.quiet-margin {
		  margin-top: 40px;
		}
	}
}

.night {
	.advance-battery-settings {
    .overlay{
      background-color: rgba(38, 38, 38, .5);
    }

		.rangeslider__labels .rangeslider__label-item {
			color: #fff;
		}
		svg {
			g {
			  fill: #f8f8ff;
			}
		}
		.info-1, p.info-2 {
			color: #a2a6a9;
		}
		.heading {
			color: #F8F8FF;
		}

    .save-button{
      &.buttonCancel{
        background-color: #333333;
        border-color: #5b5b5b;
      }
    }
	}
}

.energyIndependenceBS{
	display: flex;
	align-content: space-between;
	padding: 0 20px 20px 20px;
	.info{
		flex-grow: 1;
		font-size: 12px;
		line-height: normal;
		.eIinfoIcon {
			margin: 5px;
			transform: translateY(2px);
			display: inline-block;
			.infoIcon{
				display: inline;
				svg {
					height: 14px;
					width: 14px;
				}
			}
		}
		.description{
 		  color: #545456 !important;
		}
		.switchStatus{
			&.disabled{
				color: rgba(84, 84, 86, 0.6) !important;
		    font-weight: 600;
			}
			&.enabled{
				color: #F37320;
				text-transform: capitalize;
			}
		}
		.pending{
			color: #545456;
		}
	}
	.eISwitch{

	}
}

.night {
	.energyIndependenceBS {
		.info {
			.description{
				color: #f8f8ff !important;
			}
			.switchStatus.disabled{
				color: #A2ABB3 !important;
			}
			.pending {
				color: #A2AAB3;
			}
		}
	}
	.info-popup span.status-text { 
	
		&.disabled{
			color: #A2ABB3 !important;
			font-weight: 700;
		}
	}

  .inLineRequestStatus{
    label{
      color: #ffffff;
    }
  }
}
