@import './colors.scss';

#appDemo-wrapper {
    width: 50%;
    height: 100%;
    padding: 1.875em 1.25em;
    flex-direction: column;
    justify-content: center;
    font-size: 16px;
    background-color: $primary;
    display: none;
}

.system-estimator {
    width: 100%;
    height: 100%;
    background-color: $background;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 9;
}

#appDemo-wrapper,
.system-estimator {
    & .offline-background {
        width: 100%;
        height: 100%;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 11;
    }

    & .spin-container {
        width: 100%;
        height: 100%;
        margin: 0;
        border-radius: 0;
        background: $background;
        left: 0;
        top: 0 !important;
    }

    & .appDemo-header {
        width: 100%;
        max-width: 450px;
        margin: 0 auto;

        & .appDemo-logo {
            max-width: 61.5em;
            margin: 0 auto 1.875em;

            & svg {
                height: 1.875em;
                margin: 0 auto;

                & .a,
                & .b,
                & .c { fill: $background; }
            }
        }

        & .appDemo-get-started {
            max-width: 61.5em;
            margin: 0 auto;
            padding: 1.25em;
            border-radius: 0.625em;
            box-shadow: 0.0625em 0.125em 0.625em 0 rgba(149, 73, 24, 0.1);
            background-color: $background;

            & h3 {
                margin: 0 0 1.25rem;
                font-size: 1.125em;
                line-height: normal;
                color: $dark;
            }

            & h2 {
                margin: 0.9090909090909091em 0 0;
                font-size: 1.375em;
                line-height: normal;
                letter-spacing: -0.24px;
                color: $dark;
            }

            & button {
                width: 100%;
                margin: 1.25em 0 0;
                padding: 0.5625em 2rem;
                display: flex;
                border: 2px solid #f57301;
                font-size: 1em;
                font-weight: bold;
                color: #f57301 !important;
                letter-spacing: normal;
                text-decoration: none;
                background-color: $background;

                &:disabled {
                    opacity: .75;
                    cursor: not-allowed;
                }

                & .icon {
                    &.playVideo { height: 100%; }
                    &.arrowCarousel {
                        stroke: #f57301;

                        & svg {
                            width: 0.4375em;

                            & path {
                                height: 0.75em;
                                fill: #f57301;
                            }
                        }
                    }

                    &.loading {
                        stroke: #f57301;
                        margin-left: 0;

                        svg {
                            width: 16px;
                            height: 16px;
                        }
                    }
                }
            }
        }
    }

    @media screen and (max-width: 360px) {
        font-size: 14px;
    }

    @media screen and (min-width: 375px) {
        & .appDemo-header .appDemo-logo { margin-bottom: 2.5em; }
    }

    @media screen and (min-width: 582px) {
        & .appDemo-header {
            & .appDemo-logo {
                margin-bottom: 2.5em;
    
                & svg { height: 2.8125em; }
            }

            & .getStarted {
                display: flex;
                justify-content: space-between;
                align-items: center;
            }
        }
    }

    @media screen and (min-width: 768px) {
        & .appDemo-header .appDemo-logo { margin-bottom: 2em; }
    }
}

.showAbove { z-index: 99999; }

.spin-overlay {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    -webkit-backdrop-filter: blur(2px);
    backdrop-filter: blur(2px);
    background-color: rgba(0, 0, 0, 0.75);
    z-index: 999;

    & .spin-container { background: unset; }
}

.demo-site-banner {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 19px 15px 16px;
    border-radius: 16px 16px 0 0;
    box-shadow: 0 -9px 12px #00000017;
    background: #323232;
    position: fixed;
    left: 0;
    bottom: #{$footer-height};
    z-index: 1;

    .exit-button-text{
        padding-top: 3px;
    }

    & p {
        margin: 0;
        font-size: 14px;
        line-height: 20px;
        color: #FFFFFF;
        @include body-1;
    }

    & button {
        display: flex;
        justify-content: space-between;
        align-items: center;
        border: unset;
        margin: 0;
        padding: 4px 12px;
        border-radius: 10px;
        font-size: 12px;
        line-height: 14px;
        text-transform: uppercase;
        color: #FFFFFF;
        background: #01B4DE;
        font-family: "t-star", "Visuelt", "Roboto", "Helvectica Neue", Helvetica, Arial, sans-serif;

        .icon {
            width: auto;
            height: 12px;
            margin-right: 4px;

            svg {
                width: auto;
                height: 100%;
            }
        }
    }
}

.night {
    .demo-site-banner {
        background: #6B7073;
    }
}