
.newNotices{
  .content{
    padding: 20px 15px 30px !important;
    font-size: 14px !important;
    line-height: 1.43 !important;
    letter-spacing: normal !important;
    .clear{
      padding: 20px !important;
      margin-top: -18px !important;
    }
    ul{
      margin: 0 !important;
    }
    li{
      border-bottom: solid 0.5px rgba(123, 135, 147, 0.3);
      margin: 0 -30px;
      padding: 20px 0 !important;
      border-top: none !important;
      span{
        width:24px;
        height: 24px;
      }
      .icon.close svg {
        width: 15px;
        height: 15px !important;
      }
      a{
        span{
          svg{
            width:15px;
            height:15px !important;
          }
        }
      }
      h4{
        margin: 0 0 0 58px!important;
      }
      .alert{
        font-size: 18px !important;
        font-weight: 500 !important;
        letter-spacing: normal;
        color: #ff4751 !important;
      }
      p,.get-help{
        margin: 0;
        padding: 0 30px 0 58px;
        font-size: 14px;
        line-height: 1.43;
        letter-spacing: normal;
        color: #5a5a5c;
      }
      .get-help{
        text-decoration: underline;
        font-weight: bold;
        color: #01b5de;
        cursor: pointer;
      }
      .alert{
        svg{
          stroke:#ff4751 !important
        }
      }
    }
  }
}
.envoy-confirm{
  background-color: #545456 !important;
  box-shadow: 1px 2px 10px 0 rgba(63, 67, 71, 0.08);
  position: relative;
  top:0;
  width: 100%;
  height: 55px;
  display: flex;
  align-items: center;
  padding: 12px 15px 12px 20px;
  .icon{
    width: 20px;
    height: 20px;
    margin-right: 10px;
    span{
      svg{
        width: 20px;
        height: 20px;
      }
    }
  }
  p{
    margin: 0;
    color: #fdfefe;
    font-size: 12px;
  }
}
.disable-envoy-confirmation{
  display: none;
}

.night{
  .newNotices{
    .content{
      p{
        color: #d8dcde;
      }
    }
    .icon.close svg{
      fill:#fff;
    }
  }
}