.currency-conversion-carousel {
  height: auto;
  border-radius: 5px;
  border: solid 1px rgba(123,135,147,.3);
  background-color: #ffffff;
  margin: 15px auto;
  max-width: 450px;
  width: calc(100% - 30px);

  .carousel-type-1 {
    margin: auto!important;

    .carousel__container {
      border: none;
      margin: 10px 0;

      .carousel__wrapper {
        padding: 0px;

        .carousel__arrow.carousel__arrow-left {
          left: 12px;
        }

        .carousel__item {
          width: auto;
          .energy-type:first-child {
            float: left;
          }

          .carousel-equal-currency {
            float: left;
            margin: 10px;
          }

          .energy-type {
            margin-bottom: auto;

            .energy-type__value {
              height: 18px;
              font-size: 16px;
              font-weight: bold;
            }

            .energy-type__value:first-child {
              text-align: right;
            }

            &.energy-currency-converted {
              .energy-type__value {
                color: #545456;
              }

              .energy-type__value:first-child {
                text-align: left;
              }
            }

            &.energy-production, &.energy-netenergyexported{
              .energy-type__value{
                color: #01b4de;
              }
            }

            &.energy-net_energy{
              .energy-type__value{
                opacity: 0.5;
                color: #01b4de;
              }
            }
          
            &.energy-consumption, &.energy-netenergyimported{
              .energy-type__value {
                color: #f37320;
              }
            }
          
            &.energy-discharged{
              .energy-type__value{
                color: #7acf38;
              }
            }
          
            &.energy-charged{
              .energy-type__value{
                color: #7acf38;
              }
            }
          
            &.energy-imported{
              .energy-type__value{
                color: #545456;
              }
            }
          
            &.energy-exported{
              .energy-type__value{
                color: #545456;
              }
            }
          }
        }
      }
    }
  }

  .currency-conversion-container {
    width: 100%;

    .subheading-currency-wrapper {
      padding: 0 15px;
      .subheading-currency-text {
        font-size: 12px;
        color: #7b8793;
      }
    }

    .currency-conversion-wrapper {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 10px 20px;
      margin: 0 auto;

      .currency-conversion-icon {
        display: flex;
        align-items: center;
      }
  
      .currency-conversion-text {
        padding-left: 10px;
        opacity: 0.99;
        font-size: 16px;
        font-weight: 500;
        color: #545456;
      }

      .setcurrency-conversion-text {
        padding-left: 10px;
        font-size: 16px;
        font-weight: 500;
        color: #01b5de;
      }
  
      .currency-conversion-rightIcon {
        display: inline-flex;
        align-items: center;
      }
    }
  }
}

.night {
  .currency-conversion-carousel {
    background-color: #262627;
    border: 1px solid #3c4044;

    .currency-conversion-container .currency-conversion-wrapper .currency-conversion-text {
      color: #fff;
    }

    .currency-conversion-container .currency-conversion-wrapper .currency-conversion-icon .icon.currency {
      path { 
        fill: #f8f8ff;
      }
    }

    .carousel-type-1 .carousel__container .carousel__wrapper .carousel__item {
      .energy-type.energy-currency-converted .energy-type__value {
        color: #fff;
      }

      .energy-type.energy-imported .energy-type__value, .energy-type.energy-exported .energy-type__value {
        color: #cbced1;
      }
    }
  }
}


@media (min-width:768px) and (max-width:1024px) {
  .currency-conversion-carousel {
    width:450px;
    margin: 0 auto;
  }
}
