@import '../../../style/font-style.scss';

.display-timeout-container {
  padding: 21px 18px 26px 18px;
  border-bottom: 0.5px solid #7B87934D;
  background-color: white;

  &.web-display-timeout-container {
    background-color: #fff;
    width: 904px;
    height: 714px;
    margin-top: 32px;
    margin-bottom: 14px;
    padding: 48px;
    margin-left: auto;
    margin-right: auto;
    border-radius: 8px;
  }

  .header-container {
    display: flex;
    justify-content: space-between;
    @include title-1;
    color: #545456;
    align-items: center;

    .percent-value {
      font-size: 14px;
      margin-left: auto;
      color: #01B5DE;
    }

    &.web-header-container {
      font-size: 20px;
    }
  }

  .description-container {
    font-size: 12px;
    color: #545456;
    padding-top: 32px;
    font-weight: normal;
    line-height: normal;

    &.web-description-container {
      padding-top: 60px;
      font-size: 16px;
      font-weight: 400;
    }
  }

  .description-container span {
    font-weight: 500;
  }

  .dropdown-container {
    display: flex;
    align-items: center;
    gap: 13.5px;

    span {
      @include body-2;
      color: #7B8793;
      font-size: 14px;
    }

    .dropdown-arrow {

      &.arrow-down {
        margin-top: 4px;
        transform: rotate(0deg);
        transition: transform .2s linear;
      }

      &.arrow-up {
        margin-top: -9px;
        transform: rotate(-180deg);
        transition: transform .2s linear;
      }
    }
  }

  .slider-container {
    opacity: 0;
    transition: ease-in-out .2s;
    opacity: 1;
    display: flex;
    align-items: flex-end;
    margin-top: 9.5px;
    // margin-bottom: 15.5px;
    gap: 15.58px;
  }

  .multiSlider-conatiner {
    width: 100%
  }

  .display-timeout-ticks {

    &.web-display-timeout-ticks {
      .multi-slider {
        .slider-ticks {
          &>div {
            div:nth-child(2) {
              font-size: 12px !important;
              font-weight: 700 !important;
            }

            #el-tick-40 {
              margin-left: -47px !important
            }
          }
        }
      }
    }

    #el-tick-dev40 {
      margin-left: -14px;
    }

    #el-tick-40 {
      margin-left: -27px !important;
    }

    #el-tick-240 {
      margin-left: -8.8% !important;
      text-align: right !important;
    }

    #el-tick-2 {
      margin-left: 0% !important;
      text-align: left !important;
    }

    #el-tick-dev2 {
      background: none !important;
    }
  }
}

.web-display-timeout-msg {
  display: flex;
  padding: 16px;
  border-radius: 8px;
  background: #545456;
  color: #FFF;
  width: 672px;
  top: 90px;
  position: absolute;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
}

.web-display-timeout-retry {
  background: #FFFFFF 0% 0% no-repeat padding-box;
  box-shadow: 0px 2px 10px #3F434714;
  border-radius: 10px;
  margin-left: 15%;
  margin-right: 15%;
  opacity: 1;
  padding-bottom: 20px;
  margin-top: 15px;
}

.display-timeout-button-wrapper {
  position: absolute;
  bottom: 105px;
  text-align: center;
  width: calc(100% - 32px);
  left: 16px;

  &.web-display-timeout-button-wrapper {
    position: relative;
    bottom: 98px;

    &>button {
      width: 440px !important;
      height: 60px !important;
      border-radius: 8px !important;
      font-size: 20px !important;
      font-weight: 500 !important;
      align-items: center;
    }
  }

  .save-button {
    box-shadow: 1px 2px 10px 0 rgba(63, 67, 71, 0.08);
    border: 1px solid #00b6de;
    padding: 8px 0 !important;
    background: #00b6de;
    color: #FFFFFF;
    text-align: center;
    border-radius: 8px;
    width: 100%;
    justify-content: center;
    display: flex;
    font-size: 15px !important;
    font-weight: normal !important;
    @include title-2;
    margin-left: auto;
    margin-right: auto;

    &.buttonCancel {
      background-color: transparent;
      color: #01b5de;
      border: 1px solid #01B5DE;
      font-size: 15px;

      &.buttonDisabled {
        color: #c7c8ca;
      }
    }

    &.quiet-margin {
      margin-top: 40px;
    }
  }

  .text {
    svg {
      g {
        g {
          fill: #ffffff;
        }
      }

      path {
        stroke: #ffffff;
        stroke-width: 1px;
      }
    }
  }
}

.bleConnected {
  bottom: 140px !important;
}

.night {
  .display-timeout-container {
    background-color: #262627;

    .description-container {
      color: #fff;
    }

    .header-container {
      color: #fff;
    }

    .slider-container {
      &.display-timeout-ticks {
        .description-container {
          color: #fff;
        }
      }
    }
  }
}

