.energy-layout {
  &.displayBlock{
    display: block;
  }
  display: flex;
  align-items: center;
  height: calc(82vh - 171px);
  justify-content: center;
  
  .energy-layout-wrapper {
    width:100%;
    margin: auto;

    @media screen and (max-width:375px) {
      margin-bottom: calc(max(24px, auto));
    }

    .energy-grid{
      position: relative;
      display: inline-block;
      width: 100%;

      &.energy-grid__consumed {
        margin-top: 3px;
        margin-bottom: 10px;
      }
      
      .energy-grid-wrapper {
        display: flex;
        justify-content: center;
        align-items: center;

        .enegy-arrows {
          display: flex;
          justify-content: center;
          align-items: center;
          position: relative;

          .energy-centre-arrows {
            display: flex;
            position: absolute;
            z-index: 1;
          }
        }
        
        .energy-grid-column {
          width: 120px;
          @media screen and (min-width:768px) and (max-width:1024px){
            width: 220px;
          }

          @media screen and (min-width:768px) and (max-width:1024px) and (orientation : landscape) {
            width: 120px;
          }

          .energy-independence {
            position: relative;
            top: 0;
            left: 0;
            transform: none;
            width: auto;
            margin-left: 16px;

            .energy-type__icon-with-details {
              text-align: left;

              .energy-type__icon-and-value {
                justify-content: flex-start;
              }
      
              .energy-type__details {
                text-align: left;
              }
            }
          }
        }

        .net-energy-wrapper {
          display: flex;
          margin-left: 15px;
      
          .text {
            font-size: 16px;
            opacity: 0.7;
            font-weight: bold;
            color: #545456;
            @include title-2;
            @media screen and (min-width:768px) and (max-width:1024px) {
              font-size: 20px;
            }
          }
      
          .text-units {
            top: -.4em;
            font-size: 12px;
            font-weight: 400;
            color: #545456;
            font-weight: normal;
            text-align: right;
            margin-left: 2px;
            @include body-1;
            @media screen and (min-width:768px) and (max-width:1024px) {
              font-size: 12px;
            }
          }
        }

        .net-energy-text {
          font-size: 12px;
          opacity: 0.7;
          font-weight: normal;
          color: #545456;
          padding-top: 2px;
          margin-left: 15px;
        }
      
        .net-energy .icon, .energy-layout .net-energy .icon {
          width: 16px;
          height: 16px;
          margin-right: 5px;
      
          svg {
            width: inherit;
            height: inherit;
          }
        }

        .net-energy-icon {
          .icon {
            opacity: 1 !important;
          }
        }

        .energy-grid-column-nobattery {
          width: 220px;
        }

        .energy-grid-column-nobattery-down {
          width: 420px;
          float: left;
        }

        .energy-arrow-top-left { margin-top: 14px; }

        .energy-arrow-top-right {
           margin-top: 14px;
           margin-left: 30px;
        }

        .energy-arrow-center { padding-left: 120px; }

        .extra-margin-right {
          margin-right: 10px;
          padding-right: 0;

          @media screen and (min-width:768px) and (max-width:1024px){
            margin-right: 10px;
          }
        }

        .extra-margin-left {
          margin-left: 10px;
          padding-left: 0;

          @media screen and (min-width:768px) and (max-width:1024px){
            margin-left: 10px;
          }
        }

        .disabled-arrow {
          width: 114px;

          @media screen and (min-width:768px) and (max-width:1024px){
            width: 216px;
          }

          @media screen and (min-width:768px) and (max-width:1024px) and (orientation : landscape) {
            width: 114px;
          }
        }

        .disabled {
          display: none;
        }

        .energy-arrow__icon {
          min-width: 114px;
          width: 114px;

          @media screen and (min-width:768px) and (max-width:1024px){
             min-width: 216px;
             width: 216px;
          }
       
          @media screen and (min-width:768px) and (max-width:1024px) and (orientation : landscape) {
            min-width: 114px;
            width: 114px;
          }
      
          .icon{
            width: inherit;
            height: inherit;

            svg{
              width: inherit;
              height: inherit;
            }
          }
        }

        .arrow-nobattery {
          width: 132px;
        }

        .arrow-nobattery-down {
          float: left;
          margin-right: 142px;
          width: 128px;
        }

        .energy-arrow-straight__icon {
          height: 30px;
          margin-left: 1px;

          @media screen and (min-width:768px) and (max-width:1024px){
            height: 55px;
          }

          @media screen and (min-width:768px) and (max-width:1024px) and (orientation : landscape) {
            height: 30px;
          }
      
          .icon{
            width: inherit;
            height: inherit;
      
            svg{
              width: inherit;
              height: inherit;
            }
          }
        }

        .energy-arrow-straight-down__icon {
          height: 30px;
          margin: 10px;

          @media screen and (min-width:768px) and (max-width:1024px){
            height: 55px;
          }

          @media screen and (min-width:768px) and (max-width:1024px) and (orientation : landscape) {
            height: 30px;
          }
      
          .icon{
            width: inherit;
            height: inherit;
      
            svg{
              width: inherit;
              height: inherit;
            }
          }
        }

        &.four-grid-wrapper {
          max-width: 460px;
          margin: 0 auto;

          .energy-grid-column { width: 115px !important; }

          @media screen and (min-width:768px) and (max-width:1024px) {
            max-width: 600px;
        
            .energy-grid-column { width: 150px !important; }
          }
        }
      }
    }

    .energy-grid-center {
      position: relative;
      cursor: pointer;
      .donut{
        margin: auto;
      }

      .energy-type{
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;

        .energy-type__icon{
          width: 20px;

          .icon{
            margin: auto;
          }
        }
      }
      &.energy-grid-center-tablet {
        .energy-type {
          width: auto;
        }
      }
    }

    .energy-grid-column {
      .energy-type{
        .energy-type__icon{
          min-width: 50px;
          width: 50px;
          height: 50px;

          @media screen and (min-width:768px) and (max-width:1024px){
            min-width: 79px;
            width: 79px;
            height: 79px;
          }

          @media screen and (min-width:768px) and (max-width:1024px) and (orientation : landscape) {
            min-width: 53px;
            width: 53px;
            height: 53px;
          }

          @media screen and (max-width:360px) {
            min-width: 30px;
            width: 30px;
            height: 30px;
          }
        }
      }
    }

    .energy-type{
      width: 75px;
      text-align: center;
      margin: auto;

      &.energy-consumed {
        .energy-type__value {
          @media screen and (min-width:768px) and (max-width:1024px){
            font-size: 16px!important;
          }
        }
      }
      
      .energy-type__icon-with-details{
        display: block;
        margin: auto;

        .energy-type__icon{
          margin: auto auto 5px;
          .energyConsumption{
            margin-top: -10px;
          }
        }

        .energy-type__value{
          font-size: 16px;
          font-weight: bold;
          @include title-2;
          line-height: 1;

          @media screen and (min-width:768px) and (max-width:1024px){
            font-size: 20px;
          }

          @media screen and (max-width:320px) {
            font-size: 14px;
          }

          @media screen and (max-width:360px) {
            font-size: 14px;
          }
        }

        .energy-type__value span{
          @media screen and (min-width:768px) and (max-width:1024px){
            font-size: 14px;
          }
        }

        .energy-type__name{
          font-size: 12px;
        }
      }
    }
    &.ojas-energy-layout{
      .energy-grid{
        .energy-grid-wrapper{
          .energy-arrow-straight-down__icon{
            .exportedStraightArrow{
              svg{
                g {
                  path{
                     fill: #5FD10B;
                     stroke: #5FD10B;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

.page:not(.en){
  .energy-layout{
      .energy-type{
        &.energy-consumed{
          .energy-type__name{
            font-size: 8px;
          }
        }
    }
  }
}


@media screen and (max-width:320px){
  .energy-layout {
    .energy-layout-wrapper{
      .energy-grid {
        .energy-grid-wrapper{
          .energy-arrow__icon {
            min-width: 90px;
            width: 90px;
          }
          .energy-arrow-straight__icon{
            height:25px;
          }
        }
      }
    }
  }
}
@media screen and (device-width:360px){
  .energy-layout {
    .energy-layout-wrapper{
      .energy-grid {
        .energy-grid-wrapper{
          .energy-arrow__icon {
            min-width: 103px;
            width: 103px;
          }
          .energy-arrow-straight__icon{
            height:27px;
          }
        }
      }
    }
  }
}
@media screen and (min-width:768px) and (max-width:1024px) {
  .energy-layout {
    height: calc(82vh - 204px);
    .energy-type__name {
      font-size: 15px !important;
    }
  }
  
}

.night {
  .energy-layout .energy-layout-wrapper .energy-grid .energy-grid-wrapper {
    .net-energy-wrapper {
      .text, .text-units {
        color: #a2a6a9;
      }
    }

    .net-energy-text {
      color: #a2a6a9;
    }

    .energy-grid-column .energy-type .energy-type__icon {
      span.energyGridCircle svg g circle { stroke: #cbced1; }
      span.energyGridCircle svg g path { fill: #cbced1; }
    }

    .energy-arrow-straight__icon {
      .generatorArrow svg g > path:first-child { fill: #dca2ff; }
      .generatorArrow svg g > path:last-child {
        stroke: #dca2ff;
      }
    }
  }

  .energy-arrow-right .generatorCurveArrow svg g > path:first-child { fill: #dca2ff; }
  .energy-arrow-right .generatorCurveArrow svg g > path:last-child { stroke: #dca2ff; }
}