.form-contact-support{
  font-style: normal;
  font-stretch: normal;
  font-weight: normal;
  line-height: normal;
  letter-spacing: normal;
  background-color: #ffffff;
  height: 100%;
  margin: 0;

  form{
    margin: 0 !important;
    min-height: 100% !important;

    &.fcs__form-not-validated, &.fcs__form-submitted{
      .cc__button{
        background-color: #cbcbcb;
        color: #ffffff;
        pointer-events: none;
      }
    }
  }

  label{
    font-size: 12px;
    font-weight: 300;
    color: #939598;
  }

  input, select{
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    width: 100%;
    padding: 7px 0 7px 0;
    color: #545456;
    font-size: 14px;
    font-weight: 400;
    background-color: transparent;
    border: none;
    border-bottom: 1px solid #c6c8ca;
    border-radius: 0;

    &:readonly{
      color: #939598;
    }

    &:focus { border-color: $secondary; }
  }

  textarea{
    width: 100%;
    min-height: 100px;
    font-size: 14px;
    line-height: 1.29;
    color: #545456;
    padding: 15px;
    border-radius: 4px;
    border: solid 1px #c3c6ca;
    resize: vertical;

    &:focus { border-color: $secondary; }
  }

  input, textarea{
    &::-webkit-input-placeholder,
    &::-moz-placeholder,
    &::-ms-input-placeholder,
    &::placeholder{
      color: #939598;
    }
  }

  &.contact-card{
    box-shadow: none;

    .cc__button{
      float: none;
      margin: auto;

      .cc__text{
        font-size: 16px;
      }
    }
  }

  &.fcs__submitted{
    .fcs__wrapper{
      height: 100%;
    }

    .fcs__body{
      display: none;
    }
  }

  .fcs__wrapper{
    padding: 20px 30px;
  }

  .fcs__input-field{
    &:not(:first-of-type){
      margin-top: 25px;
    }
   
    .cc__button{
      width: 240px;
      margin: 0 auto;
    }

    &.error {
      input {
        border-bottom: 1px solid #ff5959;
      }
      textarea {
        border-color: #ff5959;
      }
    }

    .error-message {
      color: #ff5959;
    }
  }

  .fcs__response{
    height: inherit;
    display: flex;
    align-items: center;
    justify-content: center;

    .fcs__response__wrapper{
    }

    .fcs__response__icon{
      .cc__icon{
        height: 60px;
        width: 60px;
        margin: auto;
      }
    }

    .fcs__response__header{
      font-size: 16px;
      line-height: 1.38;
      color: #5a5a5c;
      margin-top: 20px;
      text-align: center;
    }

    .fcs__response__description{
      font-size: 14px;
      line-height: 1.38;
      color: #838384;
      margin-top: 20px;
      text-align: center;
    }

    .cc__button{
      width: 160px;
      margin-top: 30px;

      .cc__text{
        text-transform: uppercase; 
      }
    }

    &.fcs__success{
      
    }

    &.fcs__error{
      .fcs__response__description{
        margin-top: 0;
      }
    }
  }
}

/* Dark Mode */
.night{
  .form-contact-support{
    background-color: #262627;

    form{
      &.fcs__form-not-validated, &.fcs__form-submitted{
        .cc__button{
          background-color: #484A4D;
          color: #8A8B8D;
        }
      }
    }
    
    label{
      color: #939598;
    }
  
    input, select{
      color: #f8ffff;
      border-color: #c6c8ca;

      &:focus { border-color: $secondary; }
    }
  
    textarea{
      background-color: #262627;
      color: #f8ffff;
      border-color: #c6c8ca;

      &:focus { border-color: $secondary; }
    }
  
    input, textarea{
      &::-webkit-input-placeholder,
      &::-moz-placeholder,
      &::-ms-input-placeholder,
      &::placeholder{
        color: #939598;
      }
    }

    .fcs__response{  
      .fcs__response__header{
        color: #f8f8ff;
      }
    }
  }
}