.header-title-bar-browser {
  background-color: #ffffff;
  display: flex;
  min-height: 70px;
  max-height: 70px;
  border-bottom: solid 1px rgba(123, 135, 147, 0.3);
  .titlebar {
    display: flex;
    align-items: center;
    width: 100%;
    margin: auto;
    background-color: $background;
    height: 100%;
    padding: 12px 32px;
    z-index: 0;
  }
  .period-select-wrapper, .period-select {
    width: auto;
    li {
      flex-grow: 0;
      color: #6c7073;
      padding: 0;
      margin: 0 20px;
      span {
        display: flex;
        font-size: 15px;
        font-weight: 500;
        font-style: normal;
        font-stretch: normal;
        line-height: normal;
        letter-spacing: normal;
        height: 100%;
        align-items: center;
        justify-content: center;
        padding: 0 7px;
      }
      &.active {
        background-color: #01b5de;
      }
    }
  }
  .period-select-wrapper {
    display: flex;
    justify-content: space-between;
  }
  .change-graph-view {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    .icon {
      width: 30px;
      height: 30px;
      margin-left: 30px;
      cursor: pointer;

      &.active {
        path {
          fill: $secondary;
        }
      }
    }
  }
  .comp-cw__date-selector {
    width: auto;
  }
}
