/* Energy Type */
.energy-stats__center .energy-donut-chart .energy-type .energy-type__details{ margin-left: 0px; }
.energy-type{
  display: flex;
  align-items: center;
  width: 100px;
  margin: 6px 0;

  .energy-type__icon-with-details{
    display: inline-flex;
    align-items: center;
  }  

  .energy-type__icon{
    min-width: 18px;
    // width: 18px;
    height: auto;
    border-radius: 50%;
    margin: 0 8px 0 0;
    .deviceStorageChargingIcon{
      height:30px
    }
    .iqcpEnergyConsumedMini {
      margin-left: 2px;
    }
    .icon{
      width: inherit;
      height: auto;
      display: flex;
      align-items: center;
      justify-content: center;  
      svg{
        height: inherit;
        width: inherit;
        margin: 0;
      }

    }

    &.has_generator {
      margin: 0 8px 10px 0;
    }
  }

  .energy-type__details{
    p{
      margin: 0;
    }
  }

  .energy-type__value{
    font-size: 18px;
    font-weight: bold;
    text-transform: capitalize;
    @include title-2;
    line-height: 1;

    .energy-type__unit{
      font-size: 12px;
      font-weight: normal;
      margin-left: 4px;
      text-transform: none;
      align-self: flex-end;
      @include body-1;
      line-height: 1;
    }

    .energy-type__value-info{
      margin-left: 2px;
      margin-top: -5px;
      cursor: pointer;
      display:inline-flex;

      .icon.infoIcon{
        display: flex;
      }
    }
  }

  .energy-type__name{
    font-size: 12px;
    color: #545456;
  }

  .energy-type__name_pes{
    font-size: 12px;
    color: #545456;
  }

  .energy-type__icon-with-details{
    .energy-type__icon-and-value{
      display: inline-flex;
      justify-content: center;
      align-items: center;

      .energy-type__icon{
        margin: 0 5px 0 0 !important;
      }

      .energy-type__value{
        margin: 0;
      }
    }
  }

  &.energy-type__icon__has-animation{
    width: 100% !important;
    margin-top: calc(32px - 75px);

    .energy-type__icon-with-details{
      width: 100% !important;

      .energy-type__icon{
        display: flex;
        align-items: center;
        justify-content: center;
        overflow: hidden;
        position: relative;
        height: 150px;
        width: 150px;
        margin-bottom: 20px;

        .icon{
          z-index: 1;
        }
      }
    }

    .energy-type__circle{
      border-radius: 50%;
      background-color: grey;
      width: 70px;
      height: 70px;
      position: absolute;
      opacity: 0;
      animation: scaleIn 4s infinite cubic-bezier(.36, .11, .89, .32)
    }
  }

  &.energy-current{
    .energy-type__value{
      color: #01b4de;
    }
  }

  &.energy-previous{
    .energy-type__value{
      color: #6c7073;
    }
  }

  &.energy-evse{
    .energy-type__value{
      color: #BE4B00 !important;
    }
  }

  &.energy-netenergy{
    position: relative;

    .energy-type__value{
      color: #545456;
      opacity: 0.7;
    }
    .energy-type__name{
      opacity: 0.7;
    }

    &.net-energy-icon .energy-type__icon-with-details .energy-type__icon {
      .icon.energyNetExportedCircle {
        opacity: 0.5!important;
      }
    }
  }

  &.energy-produced{
    .energy-type__value{
      color: #01b4de !important;
    }

    .energy-type__value-loading{
      .threeDotLoader {
        svg{
          fill: #01b4de;
        }
      }
    }

    &.disconnected {
      .energy-type__icon, .energy-type__value {
        opacity: 0.5;
      }
    }
  }

  &.energy-pes-produced{
    .energy-type__value{
      color: #01b4de !important;
    }

    .energy-type__value-loading{
      .threeDotLoader {
        svg{
          fill: #01b4de;
        }
      }
    }
  }

  &.energy-consumed{
    width: auto;
    margin: 0;
    height: auto;

    .energy-type__value{
      font-size: 16px;
      color: #f37320 !important;
    }

    .energy-type__name{
      font-size: 11px;
      @include body-1;
    }

    .energy-type__value-loading{
      .threeDotLoader {
        svg{
          fill: #f37320;
        }
      }
    }

    .energy-type__icon-with-details{
      margin-top: -5px;
    }
  }

  &.energy-pes-consumed{
    width: auto;
    margin: 0;
    height: auto;

    .energy-type__value{
      font-size: 16px;
      color: #f37320 !important;
    }

    .energy-type__name{
      font-size: 11px;
      @include body-1;
    }

    .energy-type__value-loading{
      .threeDotLoader {
        svg{
          fill: #f37320;
        }
      }
    }

    .energy-type__icon-with-details{
      margin-top: -5px;
    }
  }

  &.energy-dcport-hidden{
    visibility: hidden;
    margin: 0 !important;
  }
  &.energy-dcport {
    margin: 0 !important;

    .energy-type__icon-with-details{
      .energy-type__icon{
        display: flex;
        align-items: center;
        justify-content: center;
        .icon{
          width: 32px;
          height: 32px;
        }
      }
      .energy-type__details{
        .energy-type__value{
          color: #F37320;
        }
      }
    }
  }
  
  &.energy-acport {
    margin: 0 !important;
    .energy-type__icon-with-details{
      .energy-type__icon{
        display: flex;
        align-items: center;
        justify-content: center;
        .icon{
          width: 32px;
          height: 32px;
        }
      }
      .energy-type__details{
        .energy-type__value{
          color: #F37320;
        }
      }
    }
  }

  &.energy-acport-hidden{
    visibility: hidden;
    margin: 0 !important;
  }

  &.energy-discharged{
    .energy-type__value{
      color: #7acf38 !important;
    }

    .energy-type__value-loading{
      .threeDotLoader {
        svg{
          fill: #7acf38;
        }
      }
    }
  }

  &.energy-pes-discharged{
    .energy-type__value{
      color: #7acf38 !important;
    }

    .energy-type__value-loading{
      .threeDotLoader {
        svg{
          fill: #7acf38;
        }
      }
    }
  }

  &.energy-charged{
    .energy-type__value{
      color: #7acf38;
      margin-top: 10px;
    }

    .energy-type__value-loading{
      .threeDotLoader {
        svg{
          fill: #7acf38;
        }
      }
    }
  }

  &.energy-consumption_ac{
    .energy-type__value{
      color: #f37320;
    }

    .energy-type__value-loading{
      .threeDotLoader {
        svg{
          fill: #f37320;
        }
      }
    }
  }

  &.energy-consumption_dc{
    .energy-type__value{
      color: #f37320;
    }

    .energy-type__value-loading{
      .threeDotLoader {
        svg{
          fill: #f37320;
        }
      }
    }
  }

  &.energy-imported{
    .energy-type__value{
      color: #545456;
    }

    .energy-type__value-loading{
      .threeDotLoader {
        svg{
          fill: #545456;
        }
      }
    }
  }

  &.energy-pes-imported{
    .energy-type__value{
      color: #545456 !important;
    }

    .energy-type__value-loading{
      .threeDotLoader {
        svg{
          fill: #545456;
        }
      }
    }
  }

  &.energy-pes-aux{
    .energy-type__value{
      color: #545456 !important;
    }

    .energy-type__value-loading{
      .threeDotLoader {
        svg{
          fill: #545456;
        }
      }
    }
  }

  &.energy-exported{
    .energy-type__value{
      color: #545456;
    }

    .energy-type__value-loading{
      .threeDotLoader {
        svg{
          fill: #545456;
        }
      }
    }
  }

  &.energy-generator{
    .energy-type__value{
      color: #9062AC !important;
    }

    .energy-type__value-loading{
      .threeDotLoader {
        svg{
          fill: #9062AC;
        }
      }
    }
  }

  &.energy-device-storage {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    justify-content: center;
    width: auto;
    display: flex;
    align-items: center;
    height: 100px;

    .energy-type__icon-with-details {
      display: block;
      text-align: center;

      .energy-type__icon {
        display: block;
        margin: auto;
      }

      .energy-type__details {
        margin-top: 4px;
      }
    }

    .energy-type__value {
      font-size: 20px;
      font-weight: bold;
      color: #7b8793;
    }
  }
  &.energy-consumedactive-nomargin, &.energy-consumednomargin{
    // margin: 0 !important;
    .energy-type__icon-with-details{
      .energy-type__icon{
        .energyHomeCircle{
          svg{
            g{
              circle{
                fill: #F37320;
              }
              path{
                fill: #FFFFFF;
              }
            }
          }
        }
      }
    }
    .energy-type__icon-with-details{
      .energy-type__details{
        .energy-type__value{
          color: #F37320;
        }
      }
    }
  }
}

.energy-stats{
  .energy-stats__wrapper{
    .energy-stats__top{
      .energy-type{
        .energy-type__name_pes{
          width: 80px;
        }
        .energy-type__name_pes.energy-type__battery-status_pes{
          width: 75px;
        }
      }
    }
  }
}

.energy-generator-column {
  display: flex;

  .generatorArrow { margin-left: 5px; }

  .energy-generator,
  .energy-generator > div { margin: auto 0 !important; }
}

/* Screen Size :: 320 */
@media (max-width: 359px) {
  .energy-type{
    .energy-type__icon-with-details{
      display: block;

      .energy-type__icon{
        margin-bottom: 8px;
      }
    }
  }
  .energy-stats.production-consumption, .energy-stats.production-consumption-storage{
    .energy-type{
      width: 100%;
      justify-content: center;

      .energy-type__icon-with-details{
        display: block;
        text-align: center;
      }

      .energy-type__icon{
        display: block;
        margin: auto;
      }

      .energy-type__details{
        display: block;
        margin-top: 5px;
        text-align: center;
        width: 75px;
      }
    }
  }

  .energy-type__icon__has-animation{
    width: 100% !important;
    margin-top: calc(32px - 75px);

    .energy-type__icon-with-details{
      width: inherit !important;
    }
  }
}

.hems-enabled-site .energy-type.energy-consumed.dark-color {
  .energy-type__value.value_orange {
    color: #F37320 !important;
    line-height: 12px;
  }
}

/* Screen Size :: 375 - 424 */
@media (min-width: 375px) and (max-width: 424px) {
  
}

.night {
  .energy-type{
    .energy-type__name{
      color: #a2a6a9 !important;
    }

    .energy-type__name_pes{
      color: #fff;
    }

    &.energy-device-storage {
      .energy-type__details {
        color: #8e9091;
      }
    }

    &.energy-produced .energy-type__value { color: #01b4de; }
    &.energy-pes-produced .energy-type__value { color: #01b4de; }
    &.energy-discharged .energy-type__value { color: #7acf38; }
    &.energy-consumed .energy-type__value { color: #f37320; }
    &.energy-consumption_ac .energy-type__value { color: #f37320; }
    &.energy-consumption_dc .energy-type__value { color: #f37320; }
    &.energy-generator {
      .energy-type__value { color: #dca2ff !important; }
    }

    .energy-type__icon {
      .icon.energyGrid {
        path {
          stroke: #CBCED1;
        }
      }

    }

    &.energy-evse{
      .energy-type__value{
        color: #F3B106 !important;
      }
    }

    .energy-type__icon {
      .icon.energyGeneratorCircle {
        path:nth-child(4) {
          fill: #262627;
        }
      }
    }
  }

  .ojas-status__wrapper {
    .energy-type{
      .energy-type__name{
        color: #fff !important;
      }
      
      &.disconnected {
        .energy-type__name{
         opacity: 0.4;
        }
      }
    }
  }
  .hems-enabled-site .energy-type.energy-consumed.dark-color {
    .energy-type__value.value_orange {
      color: #F37320 !important;
    }
  }

  .energy-aux_in {
    .pesEnergyAux {
      svg {
        #Oval_Copy_2, path, line, #Oval_Copy_3, #Rectangle_5242, #Rectangle_5240 {
          stroke: #CBCED1
        }
        #Path_19923 {
          fill: transparent;
          path:last-child {
            stroke: transparent;
            fill: #CBCED1;
          }
        }
        #Rectangle_5241, #Rectangle_5240 {
          fill: transparent;
        }
        #Rectangle_5241, #Rectangle_5240 {
          path:first-child {
            stroke: transparent;
            
          }
        }
      }
    }
  }
}

@keyframes scaleIn {
  from {
    transform: scale(.5, .5);
    opacity: .5;
  }
  to {
    transform: scale(2.5, 2.5);
    opacity: 0;
  }
}

.ojas-status__wrapper {
  .live-status__energy-flow__top {
    margin-bottom: 5px;
  }
  .live-status__system-grid-status  {
    .energy-imported {
      margin-top: 57px !important;
    }
  }
  .live-status__ojasConsumed {
    .energy-consumednomargin, .energy-consumedactive-nomargin {
      margin-left: -1px !important;
      margin-top: 51px !important;
    }
  }

  .live-status__energy-flow__center {
    .live-status__energy-flow__arrows {
      height: 185px !important;
    }
    .live-status__energy-flow__arrows-horizontal {
      .energyGridToHomeConnected {
        right: -3px !important;
        top: 94px !important;
        left: -9px;
        width: 208px !important;
      }

      .auxInToHome {
        position: absolute;
        right: -49px !important;
        top: 94px !important;
        left: -9px;
        width: 209px;
      }
    }
    .live-status__energy-flow__arrows-top {
      .energySolarToHome  {
        top: 3px !important;
        left: 97px !important;
      }
    }
    .live-status__energy-flow__arrows-bottom {
      .energyBatteryToHome  {
        top: 105px !important;
        right: -9px !important;
      }

      .energyGridToBattery{
        top: 105px !important;
        right: 104px !important;
        left: -9px !important;
      }
      .auxInToCharging  {
        top: 105px !important;
        right: 99px !important;
        position: absolute;
      }
    }
    .live-status__energy-flow__arrows-vertical {
      .energySolarToBattery {
        height: calc(97% + 4px) !important;
        top: 3px !important;

        svg {
          height: calc(106% - 1px);

          path, circle {
            stroke: rgb(1, 180, 222);
            fill: rgb(1, 180, 222)
          }

          path#Arrow_head {
            transform: translate(182px, 0.5px) scale(0.85)
          }
        }
      }
    }
  }

  .disconnected {
    .icon, .energy-type__value {
      opacity: 0.4 !important;
    }
  }

  .live-status__energy-flow__bottom  {
    margin-top: 20px !important;
  }
}

