.refer-friends {
  width: 100%;
  height: 100%;
}

.referral__wrapper {
  width: 100%;
  min-height: 100%;
  background-color: #fafafb;

  .referral__card {
    max-width: 513px;
    margin: 0 auto;
    padding: 20px;
    position: relative;

    svg {
      width: 100%;
      height: auto;
      display: block;
    }

    p {
      width: calc(100% - 40px);
      margin: 0;
      font-size: 12px;
      font-weight: 400;
      text-align: center;
      color: #ffffff;
      position: absolute;
      bottom: 30px;

      strong {
        display: block;
        font-size: 20px;
        font-weight: 500;
      }
    }
  }

  .referral__description {
    max-width: 513px;
    margin: 0 auto 20px;
    padding: 0 20px;
    font-size: 16px;
    line-height: 1.25;
    letter-spacing: -0.11px;
    color: #545456;
    @include body-1;

    span {
      color: #01B4DE;
      text-decoration: underline;
    }

    p {
      margin: 0;

      strong { font-weight: 500; }
    }

    ul {
      margin: 10px 0 0 20px;

      li { list-style: disc; }
    }
  }

  .referral__url {
    max-width: 513px;
    margin: 0 auto;
    padding: 0 20px 20px;

    p {
      margin: 0;
      font-size: 12px;
      color: #a2abb3;
      @include body-1;
    }

    .referral__url-copy {
      margin: 10px 0 20px;
      padding: 15px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-radius: 10px;
      border: 1px dashed rgba(151, 151, 151, .6);

      p {
        flex-grow: 1;
        margin: 0;
        font-size: 16px;
        line-height: 1.38;
        color: #545456;
        word-break: break-all;
        position: relative;
        @include title-1;

        & > span {
          width: 100%;
          height: 100%;
          position: absolute;
          -webkit-backdrop-filter: blur(4px);
          backdrop-filter: blur(4px);
        }
      }

      button {
        padding: 0;
        margin-left: 10px;
        border: unset;
        background: unset;

        .icon {
          height: 20px;

          svg {
            height: 20px;
            -webkit-transform: scaleX(-1);
            transform: scaleX(-1);

            path { fill: #545456; }
          }
        }
      }
    }

    & > button {
      min-width: 240px;
      margin: 0 auto;
      padding: 10px;
      display: block;
      border: unset;
      border-radius: 20px;
      outline: unset;
      font-size: 16px;
      font-weight: 500;
      line-height: 1.25;
      text-align: center;
      color: #ffffff;
      background-color: #01b4de;
    }
  }

  .referral__toast-message {
    width: 192px;
    padding: 25px;
    border-radius: 10px;
    -webkit-backdrop-filter: blur(1px);
    backdrop-filter: blur(1px);
    background-color: rgba(0, 0, 0, 0.75);
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);

    .icon {
      width: 66px;
      height: auto;
      margin: 0 auto 20px;

      svg {
        width: 100%;
        height: auto;

        circle {
          stroke: #5cd482;
          stroke-width: 1px;
        }

        path {
          fill: #5cd482;
          stroke: transparent;
        }
      }
    }

    p {
      margin: 0;
      font-size: 16px;
      text-align: center;
      color: #fafafb;
    }
  }
}

.browser {
  .referral__wrapper {
    max-width: 876px;
    height: calc(100% - 60px);
    min-height: 1px;
    margin: 30px auto;
    border-radius: 3px;
    box-shadow: 0 2px 10px 0 rgba(63, 67, 71, 0.08);
    background-color: #ffffff;
    overflow: auto;
    text-align: center;

    .referral__card {
      padding: 30px;

      p {
        width: calc(100% - 60px);
        font-size: 30px;
        line-height: 1.17;
        bottom: 60px;

        strong {
          font-size: 30px;
          font-weight: bold;
        }
      }
    }

    .referral__description,
    .referral__url {
      max-width: 668px;
      margin-bottom: 30px;
      padding: 0;
    }

    .referral__url > p { font-size: 14px; }

    .referral__url .referral__url-copy {
      display: inline-block;
      margin: 10px auto 30px;
      padding: 20px;
      background-color: #fafafb;
    }

    .referral__description,
    .referral__url .referral__url-copy p {
      font-size: 17px;
      line-height: 1.29;
      letter-spacing: normal;
    }

    .referral__description span { cursor: pointer; }

    .referral__url > button {
      padding: 15px;
      border-radius: 25px;
      font-size: 17px;
      line-height: normal;
    }
  } 
}

.night {
  .referral__wrapper {
    background-color: transparent;

    .referral__card {
      svg > g > rect { fill: none; }

      p { color: #ffffff; }
    }

    .referral__description,
    .referral__url .referral__url-copy p,
    .referral__toast-message p { color: #f8f8ff; }
  }
}


@media (max-width:410px) {
  .referral__wrapper .referral__card p { bottom: 17px; }
}

@media (min-width: 576px) {
  .referral__wrapper .referral__card p { bottom: 35px; }
}

@media (min-width: 768px) {
  .referral__wrapper .referral__card p { bottom: 40px; }
}